import React from "react";
import { useStyles } from "hooks/useStyles";
import { getAnswerSectionStyles } from "./answerSectionStyles";
import { css } from "wraps/styleCreators";
import { Icon } from "quiz/components/icon/Icon";
import { T } from "quiz/components/text/T";

export type AnswerSectionData = [string, string];

export type AnswerSectionProps = {
	isValid: boolean;
	required: boolean;
	header?: string;
	description?: string;
	hideDescription?: boolean;
};

export const AnswerSection: React.FC<AnswerSectionProps> = React.memo(props => {
	const styles = useStyles(getAnswerSectionStyles);
	const { isValid, required, header, description, hideDescription } = props;
	const showDescriptionSection = !hideDescription || !isValid;

	return (
		<div className={css(styles.wrap)}>
			{header && (
				<div className={css(styles.header)}>
					<T styles={styles.headerText}>{header}</T>
					{required && <Icon icon={["fas", "asterisk"]} styles={styles.asterisk} />}
				</div>
			)}
			{props.children}
			{description && showDescriptionSection && (
				<div className={css(styles.description)}>
					{!isValid && <T styles={styles.descriptionText}>{description}</T>}
				</div>
			)}
		</div>
	);
});

AnswerSection.displayName = "AnswerSection";
