import * as React from "react";
import { StyleDeclarationValue, CSSProp, css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import { Theme } from "theme/theme";

interface RowProps {
	styles?: StyleDeclarationValue;
}

const getRowStyles = (theme: Theme) => ({
	row: {
		display: "flex",
		width: "100%",
		alignItems: "center",
		marginBottom: theme.sizing.sizes.sm,
		lineHeight: "1em"
	} as CSSProp
});

export const Row: React.FC<RowProps> = props => {
	const styles = useStyles(getRowStyles);
	return <div className={css(styles.row, props.styles)}>{props.children}</div>;
};
