import { useStyles } from "hooks/useStyles";
import {
	MerchandiserMainPage,
	beforeMerchandiserPageInit,
	afterMerchandiserPageOut
} from "quiz/pages/merchandiser/MerchandiserMainPage";
import { ProfilePage } from "quiz/pages/profile/profilePage/Profile";
import { SupervisorMainPage } from "quiz/pages/SupervisorMainPage";
import * as React from "react";
import { css } from "wraps/styleCreators";
import { historyIcon } from "./history";
import { posIcon } from "./pos";
import { profileIcon } from "./profile";
import { QuizPageComponentProps } from "quiz/QuizAppRoutes";
import { toTop } from "utils/helpers";
import { getTabNavigationStyles } from "./getTabNavigationStyles";
import { TabTransition } from "./TabTransition";

const tabs = [
	{
		icon: historyIcon,
		type: "stroke",
		content: SupervisorMainPage,
		title: "" //,    
	},
	{
		icon: posIcon,
		type: "stroke",
		content: MerchandiserMainPage,
		title: "Outlets",
		search: true
	},
	{
		icon: profileIcon,
		type: "fill",
		content: ProfilePage,
		title: "Profile"
	}
];

interface TabNavigationProps extends QuizPageComponentProps {
	activeTab: number;
}

export const TabNavigation: React.FC<TabNavigationProps> = props => {
	const [active, setActiveState] = React.useState(props.activeTab || 0);
	const lineRef = React.useRef<HTMLDivElement>();
	const styles = useStyles(getTabNavigationStyles, tabs.length);

	const Component = tabs[active].content;

	const getIconStyles = tab => ({
		baseStyles: tab.type == "stroke" ? styles.tabIconStroke : styles.tabIconFill,
		activeStyles: tab.type == "stroke" ? styles.tabIconStrokeActive : styles.tabIconFillActive
	});

	const setActive = idx => {
		lineRef.current.style.transform = `translate3d(${idx * 100}%, 0px, 0px)`;

		props.setTitle(tabs[idx].title);
		props.setSearch(tabs[idx].search || false);
		setActiveState(idx);
	};

	React.useEffect(() => {
		lineRef.current.style.transform = `translate3d(${active * 100}%, 0px, 0px)`;
		props.setTitle(tabs[active].title);
		props.setSearch(tabs[active].search || false);
	}, []);

	return (
		<div className={css(styles.wrap)}>
			<TabTransition animationKey={active}>
				<Component {...props} />
			</TabTransition>
			<div className={css(styles.tabs)}>
				<div className={css(styles.line)} ref={lineRef}></div>
				{tabs.map((tab, idx) => {
					const { baseStyles, activeStyles } = getIconStyles(tab);
					return (
						<div
							className={css(styles.tab, baseStyles, idx == active && activeStyles)}
							onClick={() => {
								const MerchandiserMainPageIdx = 1;
								if (idx === MerchandiserMainPageIdx) {
									beforeMerchandiserPageInit();
								} else {
									afterMerchandiserPageOut();
								}
								localStorage.setItem("currentTab", String(idx));
								toTop();
								setActive(idx);
							}}
							key={idx}
						>
							{tab.icon}
						</div>
					);
				})}
			</div>
		</div>
	);
};
