import { QrScannerExtension, QrCodeScannerOptions } from "../qrScanner";

const backButtonHandler = (event: Event) => {
	event.preventDefault();
	document.removeEventListener("backbutton", backButtonHandler, false);
};

export class NativeQrScanner implements QrScannerExtension {
	scan(onScan: (result: string) => void, onError: (err: any) => void, options?: QrCodeScannerOptions) {
		document.addEventListener("backbutton", backButtonHandler, false);
		window.cordova.plugins.barcodeScanner.scan(
			result => {
				if (!result.cancelled) {
					document.removeEventListener("backbutton", backButtonHandler, false);
				}
				onScan(result.text);
			},
			err => {
				onError(err);
			},
			{
				disableAnimations: true,
				disableSuccessBeep: true,
				orientation: "portrait",
				resultDisplayDuration: options.onScanDelayMs
			}
		);
		return null;
	}
}
