import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export function getAvatarStyles(theme: Theme) {
	const wrapBase: CSSProp = {
		display: "flex",
		justifyContent: "center",
		alignItems: "center"
	};
	const textBase: CSSProp = {
		color: theme.colors.base.texts.contrast,
		fontSize: theme.sizing.fontSizes.caption,
		lineHeight: theme.sizing.lineHeights.caption
	};
	const bigWrapSize = scale(theme.sizing.sizes.xl, ScaleEnum.Double);
	return {
		wrap: {
			...wrapBase,
			width: theme.sizing.sizes.xl,
			height: theme.sizing.sizes.xl,
			borderRadius: theme.sizing.sizes.md
		} as CSSProp,
		text: {
			...textBase
		} as CSSProp,
		wrapSmall: {
			...wrapBase,
			height: theme.constants.gridPadding,
			width: theme.constants.gridPadding
		} as CSSProp,
		textSmall: {
			...textBase
		} as CSSProp,
		wrapBig: {
			...wrapBase,
			height: bigWrapSize,
			width: bigWrapSize,
			borderRadius: theme.sizing.sizes.xl //toDO: calc
		} as CSSProp,
		textBig: {
			color: theme.colors.base.texts.contrast,
			fontSize: theme.sizing.fontSizes.title
		},
		wrapBadge: {
			padding: `0 ${theme.sizing.sizes.xs}`,
			borderRadius: theme.sizing.sizes.xs,
			overflow: "hidden",
			flexWrap: "nowrap",
			whiteSpace: "nowrap",
			display: "flex"
		} as CSSProp,
		textBadge: {
			...textBase,
			lineHeight: theme.sizing.lineHeights.bodyMax,
			whiteSpace: "nowrap",
			overflow: "hidden",
			textOverflow: "ellipsis"
		} as CSSProp
	};
}
