import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getQuizFadeStyles = (theme: Theme) => {
	return {
		fadeWrap: {
			position: "relative"
		} as CSSProp
	};
};

export const customFadeStyles = `
	.custom-fade-enter {
		opacity: 0;
	}
	.custom-fade-enter-active {
		opacity: 1;
		transition: 500ms ease;
	}
	.custom-fade-exit {
		opacity: 1;
		transition: 1000ms ease;
	}
	.custom-fade-exit-active {
		opacity: 0;
		transition: 1000ms;
		position: absolute !important;
		top: 0;
		left: 0;
		right: 0;
	}
	.custom-fade-exit-done: {
		opacity: 0;
	}`.trim();
