import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { Text } from "quiz/components/text/Text";
import { getBlockStyles, getBlockHeaderStyles, getBlockTitleStyles, getBlockDescriptionStyles } from "./blockStyles";
import { TextSizeType } from "theme/sizing";
import { StyleDeclarationValue, css } from "wraps/styleCreators";
import { ColorTypes } from "theme/colors";

export interface BlockProps {
	styles?: StyleDeclarationValue;
	secondary?: boolean;
}

export const Block: React.FC<BlockProps> = React.memo(props => {
	const styles = useStyles(getBlockStyles, props);
	return <div className={css(styles.block, props.styles && props.styles)}>{props.children}</div>;
});
Block.displayName = "Block";

export const BlockHeader: React.FC = function BlockHeader(props) {
	const styles = useStyles(getBlockHeaderStyles);
	return (
		<div className={css(styles.blockHeader)}>
			<Text styles={styles.blockHeaderText} size={TextSizeType.bodyMax}>
				{props.children}
			</Text>
		</div>
	);
};
BlockHeader.displayName = "BlockHeader";

type BlockTitleProps = {
	title: string;
	counter?: number; //toDO: rename
};

export const BlockTitle: React.FC<BlockTitleProps> = props => {
	const styles = useStyles(getBlockTitleStyles);
	return (
		<div>
			<Block styles={styles.blockTitle}>
				<Text styles={styles.title}>{props.title}</Text>
				<Text styles={styles.title}>{props.counter}</Text>
			</Block>
		</div>
	);
};
BlockTitle.displayName = "BlockTitle";

interface BlockDescriptionProps extends Pick<BlockProps, "styles"> {
	text: string;
	colorType?: ColorTypes;
	muted?: boolean;
}

export const BlockDescription: React.FC<BlockDescriptionProps> = ({ text, colorType, muted = true, styles }) => {
	const s = useStyles(getBlockDescriptionStyles);
	return (
		<Block>
			<Text muted={muted} styles={[s.description, styles]} tag="p" colorType={colorType}>
				{text}
			</Text>
		</Block>
	);
};
