import { IconProp } from "@fortawesome/fontawesome-svg-core";
import { StyleDeclarationValue, css } from "aphrodite";
import { useStyles } from "hooks/useStyles";
import React from "react";
import { Icon } from "quiz/components/icon/Icon";
import { getTextWithIconStyles } from "./textWithIconStyles";

type QuestionIconProps = {
	icon: IconProp;
	styles?: StyleDeclarationValue;
};

export const TextWithIcon: React.FC<QuestionIconProps> = props => {
	const style = useStyles(getTextWithIconStyles);

	return (
		<div className={css(style.row)}>
			<div className={css(style.iconBlock)}>
				<Icon icon={props.icon} styles={props.styles || style.icon} />
			</div>
			{props.children}
		</div>
	);
};
