import { CSSProp } from "wraps/styleCreators";
import { Theme } from "theme/theme";
import { QuestionActionButtonProps } from "./QuestionActionButton";

export const getQuestionActionButtonStyles = (theme: Theme, props: QuestionActionButtonProps) => {
	return {
		wrap: {
			display: "flex",
			height: "100%",
			paddingLeft: props.fullWidth ? "0" : theme.constants.gridPadding,
			paddingRight: props.fullWidth ? "0" : theme.constants.gridPadding
		} as CSSProp,
		content: {
			display: "flex",
			justifyContent: "space-between",
			height: "100%",
			alignItems: "center"
		} as CSSProp,
		text: {
			whiteSpace: "normal"
		} as CSSProp,
		iconRight: {
			color: theme.colors.primary.main
		} as CSSProp,
		iconRightWrap: {
			width: theme.sizing.sizes.lg,
			height: theme.sizing.sizes.lg,
			display: "flex",
			alignItems: "center",
			justifyContent: "flex-end",
			padding: `${theme.sizing.sizes.md} 0`
		} as CSSProp
	};
};
