import * as deepExtend from "deep-extend";
import { ThemeDefaults } from "./themeDefaults";

export interface ThemeTransitions {
	base: string;
	fade: string;
	collapse: string;
}

export function createThemeTransitions(options: Partial<ThemeTransitions> = {}): ThemeTransitions {
	return deepExtend(
		{
			base: "all 0.5s linear",
			fade: "none",
			collapse: "none"
		},
		ThemeDefaults.transitions,
		options
	);
}
