import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export function getQuestionnaireStyles(theme: Theme) {
	return {
		wrap: {
			height: "100%",
			display: "block",
			overflowY: "hidden"
		} as CSSProp,
		content: {
			height: "100%",
			overflow: "hidden",
			width: "100%",
			marginBottom: scale(theme.sizing.sizes.lg, ScaleEnum.Double),
			overflowY: "scroll"
		} as CSSProp,
		top: {
			paddingTop: scale(theme.sizing.sizes.xs, ScaleEnum.Triple)
		},
		question: {
			marginTop: theme.sizing.sizes.lg
		},
		backlink: {
			justifyContent: "center",
			paddingRight: 0
		} as CSSProp,
		submitButtonWrap: {
			padding: `${theme.sizing.sizes.md} 0 ${theme.sizing.sizes.xl}`
		} as CSSProp,
		actionGroupIcon: {
			marginRight: theme.sizing.sizes.md
		} as CSSProp,
		reportIcon: {
			width: theme.sizing.sizes.md
		} as CSSProp
	};
}
