import * as React from "react";
import { nav } from "quiz/QuizApp";
import { css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import { getCameraNavbarStyles } from "./cameraNavbarStyles";

export const CameraNavbar: React.FC = React.memo(() => {
	const styles = useStyles(getCameraNavbarStyles);

	return (
		<div className={css(styles.wrap)}>
			<div onClick={() => nav.back()} className={css(styles.iconWrap)}>
				<i className="icon icon-back color-white" />
			</div>
		</div>
	);
});
CameraNavbar.displayName = "CameraNavbar";
