import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const getOutletRoutesStyles = (theme: Theme) => {
	return {
		content: {
			display: "flex",
			justifyContent: "space-between",
			paddingTop: scale(theme.sizing.sizes.lg, ScaleEnum.Half),
			paddingBottom: scale(theme.sizing.sizes.lg, ScaleEnum.Half)
		} as CSSProp,
		hideVisited: {
			marginRight: theme.sizing.sizes.md
		},
		contentDatePicker: {
			width: "40%"
		} as CSSProp,
		contentToggle: {
			width: "60%"
		} as CSSProp,
		toggle: {
			justifyContent: "flex-end"
		} as CSSProp,
		toggleText: {
			marginRight: theme.sizing.sizes.sm
		}
	};
};
