import { Theme } from "theme/theme";
import { QuizPreloaderProps } from "./QuizPreloader";
import { CSSProp } from "wraps/styleCreators";

export const quizPreloaderStyles = (theme: Theme, props: QuizPreloaderProps) => {
	const { preloaderAreaWidth, preloaderAreaHeight } = props;
	let mainAxis = preloaderAreaWidth;
	if (preloaderAreaHeight && preloaderAreaWidth > preloaderAreaHeight) {
		mainAxis = preloaderAreaHeight;
	}

	return {
		wrapper: {
			width: preloaderAreaWidth,
			height: preloaderAreaHeight || preloaderAreaWidth,
			display: "flex",
			justifyContent: "center",
			alignItems: "center"
		} as CSSProp,
		preloader: {
			width: mainAxis / 2,
			height: mainAxis / 2
		} as CSSProp
	};
};
