import * as React from "react";
import { CameraIconbar } from "./cameraIconBar/CameraIconBar";
import { quizLocalStorageService, t } from "quiz/QuizApp";
import { Dialog } from "quiz/components/interaction/Dialog";

type CameraProps = {
	onTakePhoto: (data: string) => void;
};

type CameraState = {
	constraints: MediaStreamConstraints;
	deviceIdx: number;
	buttonsEnable: boolean;
};

let currentStream: MediaStream;

const cameraOptions = {
	width: { ideal: 3264 },
	height: { ideal: 2160 }
};

const cameraLocalId = "cameraLocalIndex";

export const Camera: React.FC<CameraProps> = props => {
	const cameraRef = React.useRef<HTMLVideoElement>();
	const [state, setState] = React.useState<CameraState>({
		constraints: {
			video: {
				...cameraOptions
			}
		},
		deviceIdx: 0,
		buttonsEnable: false //todo:      
	});

	const handleUserMedia = (stream: MediaStream) => {
		if (cameraRef && cameraRef.current) {
			cameraRef.current.srcObject = stream;
			cameraRef.current.onloadeddata = () => setState(prev => ({ ...prev, buttonsEnable: true }));
		}
	};

	const stopUserMedia = React.useCallback(() => {
		if (currentStream) {
			currentStream.getVideoTracks().forEach(track => track.stop());
			currentStream = null;
		}
	}, [currentStream]);

	const getStream = (constraints: MediaStreamConstraints) => {
		navigator.mediaDevices
			.getUserMedia(constraints)
			.then(stream => {
				currentStream = stream;
				return handleUserMedia(currentStream);
			})
			.catch(e => {
				throw e;
			});
	};

	const getLocalDeviceIndex = React.useCallback(() => quizLocalStorageService.get(cameraLocalId), []);

	const getDevicesIds = React.useCallback(async () => {
		const devices = await navigator.mediaDevices.enumerateDevices();
		const deviceIds = devices.filter(d => d.kind === "videoinput").map(d => d.deviceId);
		return deviceIds;
	}, []);

	React.useEffect(() => {
		const deviceIndex = +getLocalDeviceIndex();
		setState(prev => ({ ...prev, buttonsEnable: false }));

		getDevicesIds()
			.then(d => {
				// eslint-disable-next-line promise/always-return
				if (deviceIndex != null) {
					getStream({ video: { deviceId: { exact: d[deviceIndex] }, ...cameraOptions } });
				} else {
					getStream(state.constraints);
				}
			})
			.catch(e => {
				getStream(state.constraints);
				throw e;
			});

		return () => {
			stopUserMedia();
		};
	}, [state.deviceIdx]);

	const getPhotoData = React.useCallback((): string => {
		const video = cameraRef.current;

		const canvasWidth = video.videoWidth;
		const canvasHeight = video.videoHeight;

		const canvas = document.createElement("canvas");
		canvas.width = canvasWidth;
		canvas.height = canvasHeight;
		const ctx = canvas.getContext("2d");

		ctx.drawImage(video, 0, 0, canvasWidth, canvasHeight);

		return canvas.toDataURL("image/jpeg");
	}, [cameraRef.current]);

	const takePhoto = React.useCallback(() => {
		const data = getPhotoData();
		data && props.onTakePhoto(data);
	}, []);

	const changeCamera = React.useCallback(async () => {
		const devices = await navigator.mediaDevices.enumerateDevices();
		const deviceIds = devices.filter(d => d.kind === "videoinput").map(d => d.deviceId);

		const currentIndex = +getLocalDeviceIndex() || 0;

		const nextIndex = currentIndex + 1;
		const idx = nextIndex > deviceIds.length - 1 ? 0 : nextIndex;

		await quizLocalStorageService.set(cameraLocalId, idx.toString());

		setState(prev => ({
			...prev,
			deviceIdx: idx
		}));
	}, []);

	return (
		<>
			<div>
				<video autoPlay muted playsInline ref={cameraRef} style={{ width: "100%" }}></video>
			</div>
			<CameraIconbar
				onSwitchCamera={() => currentStream && changeCamera()}
				onTakePhoto={() => cameraRef.current?.srcObject && state.buttonsEnable && takePhoto()}
			/>
		</>
	);
};
