import { Theme } from "theme/theme";
import { applyCssVars, CssVariablesMap } from "utils/cssHelpers";

export type InputType = "text" | "password" | "number";

export function createThemeApplier(mapTheme: (theme: Theme) => CssVariablesMap, useCache = true): Function {
	let currentTheme;
	return (theme: Theme, el?: HTMLElement) => {
		if (useCache && currentTheme == theme) return;
		currentTheme = theme;
		applyCssVars(mapTheme(theme), el);
	};
}
