import { CSSProp } from "wraps/styleCreators";
import { Theme } from "theme/theme";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const getProgressBarStyles = (theme: Theme, online: boolean) => {
	return {
		wrap: {
			background: theme.colors.primary.background,
			marginTop: scale(theme.sizing.sizes.lg, ScaleEnum.Double),
			bottom: online ? theme.sizing.sizes.xs : "0",
			left: theme.sizing.sizes.xs,
			display: "flex",
			width: "100%",
			position: "absolute"
		} as CSSProp,
		blockWrap: {
			width: "100%",
			position: "relative",
			display: "flex",
			marginRight: theme.sizing.sizes.xs
		} as CSSProp
	};
};
