import * as React from "react";
import { VisitManager, VisitManagerOptions } from "./VisitManager";
import { OutletFullData, IQuestionnaire, Employee, Visit } from "quiz/services/models";
import { quizDraftService, quizQstService } from "quiz/QuizApp";

export function useVisitManager(
	questionnaire: IQuestionnaire,
	outlet: OutletFullData,
	employee: Employee,
	options: VisitManagerOptions
): { visit: Visit; visitManager: VisitManager } {
	const visitManager = React.useMemo(
		() => new VisitManager(questionnaire, outlet, employee, options, quizDraftService, quizQstService),
		[]
	);
	const [visit, setVisit] = React.useState(visitManager.getVisit());
	React.useEffect(() => visitManager.subscribe("changed", ({ visit }) => setVisit(visit)), []);
	return { visit, visitManager };
}
