import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { getSpinnerStyles } from "./spinnerStyles";
import { css } from "wraps/styleCreators";

export const Spinner: React.FC = () => {
	const styles = useStyles(getSpinnerStyles);

	const triangleClass = css(styles.triangle);
	return (
		<>
			<style>
				{`
.${triangleClass}:first-child {
  opacity: .1;
  transform: rotate(30deg);
  }
.${triangleClass}:nth-child(2) {
  transform: rotate(90deg);
  opacity: .3;
  }
.${triangleClass}:nth-child(3) {
  transform: rotate(150deg);
  opacity: .5;
  }
.${triangleClass}:nth-child(4) {
  transform: rotate(210deg);
  opacity: .7;
  }
.${triangleClass}:nth-child(5) {
  transform: rotate(270deg);
  opacity: .9;
  }
.${triangleClass}:nth-child(6) {
  transform: rotate(330deg);
  opacity: 1;
  }
@keyframes spin {
    0 {transform: rotate(60deg);}
    100% {transform: rotate(360deg);}
}`}
			</style>
			<div className={css(styles.hexagonWrapper)}>
				<div className={triangleClass}></div>
				<div className={triangleClass}></div>
				<div className={triangleClass}></div>
				<div className={triangleClass}></div>
				<div className={triangleClass}></div>
				<div className={triangleClass}></div>
			</div>
		</>
	);
};
