import { VisitAnswer, AnswerType, VisitAnswerItem, QuestionAttachPhoto } from "quiz/services/models";
import React from "react";
import { ValidationError, VisitManager, ValidationErrorType } from "quiz/pages/questionnaire/VisitManager";
import { IconProp } from "@fortawesome/fontawesome-svg-core";
import { t } from "quiz/QuizApp";
import { QuestionReadonlyItem } from "./item/QuestionReadonlyItem";
import { QuestionReadonlyGallery } from "./gallery/QuestionReadonlyGallery";
import { useStyles } from "hooks/useStyles";
import { getQuestionReadonlyControlStyles } from "./questionReadonlyControlStyles";
import { css } from "wraps/styleCreators";

const formatAnswer = (answerType: AnswerType, items: VisitAnswerItem[]): string[] => {
	switch (answerType) {
		case "B": {
			return items.some(item => item.answer)
				? items.filter(item => item.value).map(item => item.answer)
				: items.map(item => (item.value ? t("Yes") : t("No")));
		}
		case "M": {
			return items
				.filter(item => (typeof item.value === "boolean" ? item.value : item.answer))
				.map(item => item.answer);
		}
		case "Y":
			return items.filter(x => x.value).map(() => t("QR code successfully recognized"));
		case "O":
			return items.map(item => item.answer && item.answer.toString());
		default: {
			return items.filter(x => x.value).map(item => item.value && item.value.toString());
		}
	}
};

const galleryRequired = (answer: VisitAnswer): boolean => {
	return (
		answer.answerType === AnswerType.AttachPhoto ||
		answer.question.questionAttachPhoto == QuestionAttachPhoto.Available ||
		answer.question.questionAttachPhoto == QuestionAttachPhoto.Required
	);
};

const readonlyComponentIconMap: { [answerType in AnswerType]: IconProp } = {
	[AnswerType.AttachPhoto]: ["far", "camera"],
	[AnswerType.Boolean]: ["far", "list-alt"],
	[AnswerType.ManyOptions]: ["far", "clipboard-list-check"],
	[AnswerType.Number]: ["far", "calculator-alt"],
	[AnswerType.OneOption]: ["far", "list-alt"],
	[AnswerType.ScanQrCode]: ["far", "qrcode"],
	[AnswerType.ScanQrCodeWithHideText]: ["far", "qrcode"],
	[AnswerType.Text]: ["far", "file-alt"]
};

export type QuestionReadonlyControlProps = {
	answer: VisitAnswer;
	visitManager?: VisitManager;
	errors: ValidationError[];
};

export const QuestionReadonlyControl: React.FC<QuestionReadonlyControlProps> = React.memo(props => {
	const styles = useStyles(getQuestionReadonlyControlStyles);
	const { answer, visitManager, errors } = props;
	const icon = readonlyComponentIconMap[answer.answerType];

	const answerValue = formatAnswer(answer.answerType, answer.items || []).join(", ");
	const answerErrors = errors.filter(err => err.type === ValidationErrorType.RequiredAnswer);
	const photoErrors = errors.filter(err => err.type === ValidationErrorType.RequiredPhoto);

	return (
		<div className={css(styles.wrap)}>
			{galleryRequired(answer) &&
				(photoErrors.length ? (
					<QuestionReadonlyItem icon={["far", "camera"]} text={null} errors={photoErrors} />
				) : answer.files && answer.files.length ? (
					<div className={css(styles.gallery)}>
						<QuestionReadonlyGallery answer={answer} visitManager={visitManager} />
					</div>
				) : null)}
			{answer.answerType !== AnswerType.AttachPhoto && (
				<QuestionReadonlyItem icon={icon} text={answerValue} errors={answerErrors} />
			)}
			{answer.comment && (
				<QuestionReadonlyItem icon={["far", "comment-alt"]} text={props.answer.comment} errors={[]} />
			)}
		</div>
	);
});

QuestionReadonlyControl.displayName = "QuestionReadonlyControl";
