import React from "react";
import { useStyles } from "hooks/useStyles";
import { getQrCodeQuestionControlStyles } from "./qrScannerErrorStyles";
import { Icon } from "quiz/components/icon/Icon";
import { T } from "quiz/components/text/T";
import { theme } from "themes/defaultTheme";

export const QrNotAllowedError: React.FC = props => {
	const styles = useStyles(getQrCodeQuestionControlStyles);
	return (
		<>
			<Icon
				styles={styles.cameraIcon}
				explicitSize={theme.sizing.sizes.xxl}
				color={theme.colors.primary.main}
				icon={["far", "camera"]}
			></Icon>
			<T>Camera unavailable. Please enable application access to the camera in the settings</T>
		</>
	);
};
QrNotAllowedError.displayName = "QrNotAllowedError";

export const QrNoVideoInputDevicesError: React.FC = props => {
	return <T>QR code unsupported</T>;
};
QrNoVideoInputDevicesError.displayName = "QrNoVideoInputDevicesError";

export const QrUnknownError: React.FC = props => {
	return <T>Camera error</T>;
};
QrUnknownError.displayName = "QrUnknownError";
