import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const getListNearOutletsStyles = (theme: Theme) => {
	return {
		content: {
			display: "flex",
			justifyContent: "space-between",
			padding: `${scale(theme.sizing.sizes.lg, ScaleEnum.Half)} ${theme.constants.gridPadding}`,
			boxSizing: "border-box",
			minHeight: "48px"
		} as CSSProp,
		lastUpdate: {
			display: "flex"
		} as CSSProp,
		icon: {
			marginRight: `calc(${theme.sizing.sizes.lg} / 2)`,
			fontSize: theme.sizing.fontSizes.caption
		} as CSSProp
	};
};
