import * as React from "react";
import { ActionButtonProps } from "quiz/components/actionButton/ActionButton";
import { useStyles } from "hooks/useStyles";
import { getFeedbackStyles } from "./feedbackStyles";
import { css } from "wraps/styleCreators";
import { TextareaControl } from "../textarea/TextareaControl";
import { SelectControl } from "../select/SelectControl";
import { ColorTypes } from "theme/colors";
import { BlockTitle, Block, BlockDescription } from "quiz/components/block/Block";
import { useKeyboardEnter } from "hooks/useKeyboardEnter";
import { FAQListItem } from "quiz/pages/profile/profilePage/Profile";
import { List } from "quiz/components/listView/ListView";
import { ServerInteractionButton } from "../button/ServerInteractionButton";

export enum FeedbackTypeEnum {
	Problem = "Problem",
	Idea = "Idea",
	Comment = "Comment"
}

export type FeedbackTypeTranslate = {
	[key in FeedbackTypeEnum]: string;
};

type FeedbackState = {
	value: FeedbackTypeEnum | string;
	color: ColorTypes;
};

export type FeedbackFormData = {
	comment: string;
	type: string;
};

export type FeedbackContext = {
	type: FeedbackTypeEnum;
	value: string;
	errorContext?: any;
};

export type FeedbackProps = {
	title: string;
	feedbackTypeLabel: string;
	inputLabel: string;
	sendButton: Pick<ActionButtonProps, "icon" | "title" | "fullWidth" | "size">;
	onSubmit: (value: FeedbackFormData) => void | Promise<void>;
	types: FeedbackTypeTranslate;
	description: string;
	context?: FeedbackContext;
};

export const Feedback: React.FC<FeedbackProps> = props => {
	const styles = useStyles(getFeedbackStyles);
	const enter = useKeyboardEnter();

	const [type, setType] = React.useState<FeedbackState>({ value: null, color: null });
	const [comment, setComment] = React.useState<FeedbackState>({ value: null, color: null });

	React.useEffect(() => {
		if (props.context) {
			setType({ value: props.context.type, color: null });
			setComment({ value: props.context.value, color: null });
		}
	}, []);

	return (
		<>
			<div className={css(styles.wrap)}>
				<div className={css(styles.faqLinkWrap)}>
					<List>
						<FAQListItem />
					</List>
				</div>
				<BlockTitle title={props.title} />
				<Block secondary>
					<div className={css(styles.select)}>
						<SelectControl
							label={props.feedbackTypeLabel}
							colorType={type.color}
							value={type.value}
							onChange={type => setType({ value: type, color: null })}
						>
							<option key={0} hidden></option>
							{Object.keys(props.types).map((type, idx) => {
								return (
									<option key={idx + 1} value={type}>
										{props.types[type]}
									</option>
								);
							})}
						</SelectControl>
					</div>
					<div className={css(styles.feedback)}>
						<TextareaControl
							label={props.inputLabel}
							colorType={comment.color}
							value={comment.value}
							onChange={comment => setComment({ value: comment, color: null })}
							onKeyDown={enter}
						/>
					</div>
				</Block>
				<BlockDescription text={props.description} />
			</div>
			<ServerInteractionButton
				{...props.sendButton}
				wrap
				command={() => {
					if (!comment.value) setComment({ ...comment, color: ColorTypes.danger });
					if (!type.value) setType({ ...type, color: ColorTypes.danger });

					return comment.value && type.value && props.onSubmit({ comment: comment.value, type: type.value });
				}}
			/>
		</>
	);
};

Feedback.displayName = "Feedback";
