import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { QuizNavBarProps } from "../QuizNavBar";

export const getQuizNavLeftStyles = (theme: Theme, props: Pick<QuizNavBarProps, "backLink" | "noShadow">) => {
	return {
		navtitle: {
			//marginLeft: props.backLink ? 0 : theme.constants.gridPadding,
			display: "flex",
			justifyContent: "center",
			flexDirection: "column",
			flexWrap: "nowrap",
			whiteSpace: "nowrap",
			overflow: "hidden"
		} as CSSProp,
		navLeft: {
			flexShrink: "unset",
			minWidth: "0"
		} as CSSProp
	};
};
