import * as piexif from "piexifjs";
import * as loadImage from "blueimp-load-image";

type TaggedTemplateFunction = (literals: TemplateStringsArray, ...placeholders: string[]) => string;

export type UploadOptions = {
	types?: string[];
	minSize?: number;
	maxSize?: number;

	messageTpl?: TaggedTemplateFunction;
};

export class UploadFileManagerError extends Error {
	name = "Upload FileManager Error";
	message: string;
	messageTemplate: string[];

	constructor(data: { message: string }) {
		super();
		this.message = data.message;

		//for "instance of" checks
		Object.setPrototypeOf(this, UploadFileManagerError.prototype);
	}

	toString(): string {
		return `${this.name}: ${this.message}`;
	}
}

export type PreparedImage = { full: Blob; thumb: Blob };

export class UploadHelper {
	public upload(fileList: FileList, options: UploadOptions): File[] {
		const files = Array.from(fileList);

		const isPassedAllChecks = files.every(
			file =>
				this.checkSize(file.size, options.maxSize, options.minSize) && this.checkType(options.types, file.type)
		);

		if (!isPassedAllChecks) {
			const t = options.messageTpl || this.defaultTaggedFn;
			let message = t`One or more files didn't pass the validity check!\n`;
			if (options.types) message += t`File extension should be one of ${options.types.join(", ")}. `;
			if (options.maxSize || options.minSize) {
				const min = options.minSize ? t`more than ${options.minSize.toString()}` : "";
				const and = options.maxSize && options.minSize ? t` and ` : t` `;
				const max = options.maxSize ? t`less than ${options.maxSize.toString()}` : "";
				message += t`File size should be ${min}${and}${max}. `;
			}
			throw new UploadFileManagerError({ message });
		}
		return files;
	}

	private defaultTaggedFn: TaggedTemplateFunction = (literals, ...placeholders) =>
		literals.reduce((str, v, idx) => (str += v + (placeholders[idx] ?? "")), "");

	private checkType(types: string[], fileType: string): boolean {
		return types ? types.includes(fileType) : true;
	}

	private checkSize(fileSize: number, maxSize: number, minSize = 0): boolean {
		return (!minSize || fileSize > minSize) && (!maxSize || fileSize < maxSize);
	}

	public async prepareImage(file: Blob): Promise<string> {
		return new Promise<string>((resolve, reject) => {
			const reader = new FileReader();
			reader.onloadend = async event => {
				resolve(event.target.result as string);
				reader.onerror = error => reject(error);
			};
			reader.readAsDataURL(file);
		});
	}

	private async getBase64(image: Blob | string): Promise<string> {
		if (typeof image === "string") {
			return image;
		}
		return await this.prepareImage(image);
	}

	public processImageData(
		image: Blob | string,
		quality = 0.2,
		withThumb = false,
		thumbSize = 200
	): Promise<PreparedImage> {
		return new Promise((resolve, reject) => {
			this.getBase64(image)
				.then(async base64 => {
					const fullSizeImage = await this.getBlob(base64, quality);
					const thumb = withThumb ? await this.getBlob(base64, quality, thumbSize) : null;
					resolve({
						full: fullSizeImage,
						thumb: thumb
					});
					return;
				})
				.catch(err => reject(err));
		});
	}

	private getEXIFObject(dataUrl: string) {
		if (!dataUrl.includes("image/jpeg") && !dataUrl.includes("image/jpg")) return null;
		return piexif.load(dataUrl);
	}

	private getEXIFOrientation(exifObj): number {
		return exifObj?.["0th"]?.[piexif.ImageIFD.Orientation];
	}

	private async getBlob(img: string, quality: number, thumbSize?: number): Promise<Blob> {
		const exifObj = this.getEXIFObject(img);
		const orientation = this.getEXIFOrientation(exifObj);
		const options: any = {
			canvas: true,
			orientation,
			meta: true
		};
		if (thumbSize) {
			options.maxWidth = 200;
			options.maxHeight = 200;
		}
		const imageCanvas = await loadImage(img, options);
		let canvas = imageCanvas.image;
		const blob = await this.toBlob(canvas, quality);
		// for iOS garbage collection bug with canvas
		canvas.width = 0;
		canvas.height = 0;
		canvas = null;
		return blob;
	}

	private toBlob(canvas: HTMLCanvasElement, quality: number): Promise<Blob> {
		return new Promise(resolve => {
			canvas.toBlob(blob => resolve(blob), "image/jpeg", quality);
		});
	}
}
