import { Block } from "quiz/components/block/Block";
import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { getContextInfoBlockStyles, getErrorSectionStyles } from "./contextInfoBlockStyles";
import { Text } from "quiz/components/text/Text";
import { css } from "wraps/styleCreators";
import { TextSizeType } from "theme/sizing";
import { t } from "quiz/QuizApp";
import { ButtonType } from "quiz/components/button/Button";
import { List, ListItem } from "quiz/components/listView/ListView";
import { IconName, IconPrefix } from "@fortawesome/fontawesome-common-types";
import { IconProp, Icon } from "quiz/components/icon/Icon";
import { MobileButton } from "quiz/components/button/MobileButton";

export type InfoBlockActionButton = {
	title: string;
	icon: IconProp;
	command: (arg?: any) => void;
};

export interface ContextInfoBlockProps {
	icon: [IconPrefix, IconName] | JSX.Element;
	title: string;
	message: string;
	buttons?: InfoBlockActionButton[];
	separateBlock?: boolean;
	withAccordion?: boolean;
	colorType?: string;
}

type ErrorSectionProps = {
	title: string;
	message: string;
	icon: [IconPrefix, IconName] | JSX.Element;
	iconColor: object;
};

const ErrorSection: React.FC<ErrorSectionProps> = ({ title, message, icon, iconColor }) => {
	const styles = useStyles(getErrorSectionStyles, iconColor);
	const I = Array.isArray(icon) ? <Icon style={{ width: "100%", height: "100%" }} icon={icon} /> : icon;

	return (
		<div className={css(styles.errorSection)}>
			<div className={css(styles.icon, iconColor)}>{I}</div>
			<div className={css(styles.errorInfo)}>
				<Text styles={styles.title} bold>
					{t(title)}
				</Text>
				<Text styles={styles.errorText} tag="pre" size={TextSizeType.caption} muted>
					{t(message)}
				</Text>
			</div>
		</div>
	);
};

const ErrorSectionAccordion: React.FC<ErrorSectionProps> = props => {
	return (
		<List accordionList noHairlinesMd>
			<ListItem accordionItem>
				<div slot="inner-start">
					<ErrorSection {...props} />
				</div>
				{props.children}
			</ListItem>
		</List>
	);
};

export const ContextInfoBlock: React.FC<ContextInfoBlockProps> = React.memo(props => {
	const styles = useStyles(getContextInfoBlockStyles, props);
	const { icon, title, message, buttons, withAccordion } = props;
	const errorSectionProps = {
		title,
		message,
		icon,
		iconColor: styles.iconColor
	};

	return (
		<div className={css(styles.errorBlock)}>
			<Block styles={styles.wrap}>
				{withAccordion ? (
					<ErrorSectionAccordion {...{ ...props, ...errorSectionProps }} />
				) : (
					<ErrorSection {...errorSectionProps} />
				)}
				<div className={css(styles.actionButtons)}>
					{buttons.map((button, idx) => (
						<div className={css(styles.actionButton)} key={idx}>
							<MobileButton
								icon={button.icon}
								onClick={button.command}
								iconProps={{ icon: button.icon, styles: styles.buttonIcon }}
								type={ButtonType.link}
								enabled
							>
								{t(button.title)}
							</MobileButton>
						</div>
					))}
				</div>
			</Block>
		</div>
	);
});

ContextInfoBlock.displayName = "InfoBlock";
