import * as React from "react";
import { cloudOffIcon } from "../infoScreenIcons/cloudOffIcon";
import { OfflineError } from "quiz/services/OfflineService";
import { ColorTypes } from "theme/colors";
import { InfoBlock } from "../infoBlock/InfoBlock";
import { t } from "quiz/QuizApp";
import { f7 } from "framework7-react";

export type QuizOfflineErrorProps = {
	error: OfflineError;
};

export const QuizOfflineError: React.FC<QuizOfflineErrorProps> = ({ error }) => {
	return (
		<InfoBlock
			icon={cloudOffIcon}
			title={t("No internet connection")}
			message={t(error.message)}
			primaryButton={{
				title: t("Refresh"),
				icon: ["far", "sync"],
				command: () => f7.views.main.router.refreshPage()
			}}
			colorType={ColorTypes.warning}
		/>
	);
};
