import { CSSProp } from "wraps/styleCreators";
import { Theme } from "theme/theme";
import { Toast } from "framework7/components/toast/toast";

const mapPostion2Style = {
	top: "var(--f7-navbar-height)",
	center: "calc(100vh - 50%)",
	bottom: "calc(100vh - var(--f7-navbar-height))"
};

export const getToastStyles = (theme: Theme, position: Toast.Parameters["position"]) => {
	const vars = {
		top: mapPostion2Style[position],
		marginTop: position == "bottom" ? "unset" : theme.constants.gridPadding
	};
	return {
		toast: {
			wordBreak: "keep-all",
			whiteSpace: "nowrap",
			filter: "opacity(80%)",
			...vars
		} as CSSProp
	};
};
