import { GalleryControl } from "quiz/components/gallery/GalleryControl";
import { useStyles } from "hooks/useStyles";
import * as immutable from "object-path-immutable";
import { VisitManager } from "quiz/pages/questionnaire/VisitManager";
import { textService, quizExtensionService, uploadHelper, CONFIG, t } from "quiz/QuizApp";
import { IQuestion, VisitAnswerFile, AnswerType, QuestionAttachPhoto } from "quiz/services/models";
import * as React from "react";
import { UploadOptions } from "utils/UploadHelper";
import { css } from "wraps/styleCreators";
import { getQuestionGalleryControlStyles } from "./styles/questionGalleryControlStyles";
import { GalleryImage } from "quiz/components/gallery/Gallery";
import { Dialog } from "quiz/components/interaction/Dialog";
import { QuizError } from "quiz/components/errors/QuizError";

const placeholder = require("./img_placeholder.svg");

interface QuestionGalleryControlProps {
	question: IQuestion;
	visitManager: VisitManager;
	files: VisitAnswerFile[];
	readonly?: boolean;
}

export const uploadHelperOptions: UploadOptions = {
	types: ["image/jpeg", "image/jpg", "image/png"],
	messageTpl: (...args) => textService.templateFn(...args)
};

const getUrl = (data: string | Blob) => {
	if (typeof data === "string") {
		return data;
	}
	return URL.createObjectURL(data);
};

export const QuestionGalleryControl: React.FunctionComponent<QuestionGalleryControlProps> = React.memo(
	({ visitManager, question, files, readonly }) => {
		const [images, setImages] = React.useState<GalleryImage[]>([]);
		const [err, setError] = React.useState();

		const styles = useStyles(getQuestionGalleryControlStyles);

		const onChange = React.useCallback(
			async (originalPhotos: Blob[] | string[]) => {
				const dialog = Dialog.preloader(t("Image preparation"));
				try {
					const photos = [];
					for await (const img of originalPhotos) {
						const photoId = visitManager.createFileId();
						const photo = await uploadHelper.processImageData(img, CONFIG.imageCompressQuality, true);
						const photoItem = {
							...photo,
							photoId
						};
						await visitManager.addFiles(question.id, [photoItem]);
						photos.push(photoItem);
					}
					setImages(prev =>
						prev.concat(
							photos.map(p => ({ url: getUrl(p.full), thumbUrl: getUrl(p.thumb), id: Math.random() }))
						)
					);
				} finally {
					dialog.close();
				}
			},
			[images]
		);

		const onImageClick = (index: number): void => {
			visitManager.removeFile(question.id, index);
			setImages(immutable.del(images, index.toString()));
		};

		React.useEffect(() => files && setImages(files.map(r => ({ url: placeholder, id: r.draftFileId }))), []);
		React.useEffect(() => {
			if (files) {
				visitManager
					.getFilesSrc(files)
					.then(recs =>
						setImages(
							recs.map(r => ({
								url: getUrl(r.data.full),
								thumbUrl: getUrl(r.data.thumb),
								id: r.draftFileId
							}))
						)
					)
					.catch(err => setError(err));
			}
		}, []);

		if (err) return <QuizError error={err} />;

		const isAttachPhoto = question.answerType === AnswerType.AttachPhoto;
		const isAnswerRequired = question.flagAnswerRequired;
		const isAnswerPhotoRequired = isAttachPhoto && isAnswerRequired;
		const isQuestionAttachPhotoRequired = question.questionAttachPhoto === QuestionAttachPhoto.Required;

		return (
			<div className={images.length > 0 && !readonly ? css(styles.wrap) : null}>
				<GalleryControl images={images} onImageClick={onImageClick} readonly={readonly}>
					{quizExtensionService.uploadPhoto.getUploadButton({
						required: (isAnswerPhotoRequired || isQuestionAttachPhotoRequired) && images.length == 0,
						commonLogic: onChange
					})}
				</GalleryControl>
			</div>
		);
	}
);
QuestionGalleryControl.displayName = "QuestionGalleryControl";
