import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { css } from "aphrodite";
import { t } from "quiz/QuizApp";
import { Icon, IconProp } from "quiz/components/icon/Icon";
import { getQuizListInputItemStyles } from "./quizListInputItemStyles";
import { QstListItemProps } from "../questionListView/QuestionListItem";
import { useKeyboardEnter } from "hooks/useKeyboardEnter";
import { TextWithIcon } from "../question/common/textWithIcon/TextWithIcon";

export interface QuizListInputItemProps extends QstListItemProps {
	value: string;
	placeholder?: string;
	icon: IconProp;
	type?: "number" | "text" | "password";
	inputMode?: "decimal" | "numeric" | "email" | "text" | "url";
	danger?: boolean;
	onChange: (val: string) => void;
	required?: boolean;
}

export const QuizListInputItem: React.FC<QuizListInputItemProps> = props => {
	const style = useStyles(getQuizListInputItemStyles);
	const ref = React.useRef<HTMLInputElement>();
	const enter = useKeyboardEnter();

	return (
		<div className={css(props.dash && style.dash, props.danger && style.danger)}>
			<TextWithIcon icon={props.icon} styles={props.danger && style.danger}>
				<div className={css(style.inputWrap)}>
					<input
						type={props.type || "text"}
						inputMode={props.inputMode || "text"}
						placeholder={t(props.placeholder) || t("Enter an answer")}
						value={props.value || ""}
						onInput={e => props.onChange((e.target as any).value)}
						onChange={e => props.onChange((e.target as any).value)}
						onKeyDown={enter}
						className={css(style.input, props.danger && style.danger)}
						ref={ref}
					></input>
				</div>
				{props.value?.length > 0 ? (
					<Icon
						onClick={() => {
							props.onChange("");
							ref.current.focus();
						}}
						icon={["far", "times"]}
						styles={style.icon}
					/>
				) : (
					props.required && <Icon icon={["fas", "asterisk"]} styles={style.asterisk} />
				)}
			</TextWithIcon>
		</div>
	);
};
QuizListInputItem.displayName = "QuizListInputItem";
