export class MobileAppFacade {
	constructor(private initialized = false) {}

	public ready(): boolean {
		return this.initialized;
	}

	public statusbar(o: { height: number }): void {
		window.alert(`statusbar method called, height is: ${o.height}px`);
	}

	public startQr(): void {
		const w = window as any;
		w.nativeAppFacade && w.nativeAppFacade.startQr();
	}

	public resultQr(o: { result: string; error: string }): string {
		return !o.error && o.result;
	}

	public startShare(o: { title: string; url: string }): void {
		const w = window as any;
		w.nativeAppFacade && w.nativeAppFacade.startShare(o.title, o.url);
	}
}
