import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const getSignatureControlStyles = (theme: Theme) => {
	return {
		img: {
			maxWidth: "100%"
		} as CSSProp,
		signature: {
			paddingTop: `${theme.sizing.sizes.md}`
		} as CSSProp,
		infoItemWrap: {
			width: "100%",
			display: "flex",
			alignItems: "center"
		} as CSSProp,
		iconWrap: {
			display: "flex",
			height: theme.sizing.sizes.lg,
			width: theme.sizing.sizes.lg,
			alignItems: "center",
			justifyContent: "center",
			marginRight: theme.sizing.sizes.md,
			padding: `${scale(theme.sizing.sizes.lg, ScaleEnum.Half)} 0`
		} as CSSProp
	};
};
