import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { CONFIG } from "quiz/QuizApp";

export const getSignaturePadStyles = (theme: Theme) => {
	return {
		content: {
			backgroundColor: theme.colors.base.main,
			width: "100vw",
			height: CONFIG.appMode == "prod" ? "100%" : "calc(100% - var(--instanceBarIndent))",
			display: "flex",
			alignItems: "center",
			paddingTop: "var(--f7-safe-area-top)"
		} as CSSProp,
		padWrap: {
			display: "flex",
			flexDirection: "column",
			backgroundColor: theme.colors.base.main,
			boxSizing: "border-box",
			padding: `${theme.sizing.sizes.md} ${theme.constants.gridPadding}`,
			"@media (max-width: 767px)": {
				flexGrow: 2,
				flexDirection: "row",
				height: "100%",
				boxSizing: "border-box",
				paddingLeft: "0",
				paddingBottom: `calc(${theme.sizing.sizes.md} + var(--f7-safe-area-top))`
			}
		} as CSSProp,
		signatureWrap: {
			boxSizing: "border-box",
			border: `1px solid ${theme.colors.base.border}`,
			marginBottom: theme.sizing.sizes.md,
			"@media (max-width: 767px)": {
				flexGrow: 2,
				marginBottom: "0"
			}
		} as CSSProp,
		buttons: {
			display: "flex",
			justifyContent: "space-between",
			alignItems: "center",
			"@media (max-width: 767px)": {
				flexDirection: "column",
				order: "-1"
			}
		} as CSSProp,
		buttonClear: {
			"@media (max-width: 767px)": {
				transform: "rotateZ(90deg) scale(1.0) translateX(45%)"
			}
		} as CSSProp,
		buttonSave: {
			"@media (max-width: 767px)": {
				transform: " rotateZ(90deg) scale(1.0)translateX(-40%)"
			}
		} as CSSProp,
		clearLink: {
			position: "absolute",
			right: 0,
			top: 0
		} as CSSProp,
		clearIconWrap: {
			display: "flex",
			height: theme.sizing.sizes.lg,
			width: theme.sizing.sizes.lg,
			alignItems: "center",
			justifyContent: "center"
		} as CSSProp,
		frame: {
			border: `1px dashed ${theme.colors.base.border}`,
			textAlign: "center",
			display: "flex",
			justifyContent: "center",
			alignItems: "center"
		} as CSSProp,
		text: {
			color: theme.colors.base.border
		} as CSSProp
	};
};
