import * as React from "react";
import { getDividerStyles } from "./dividerStyles";
import { css, StyleDeclarationValue } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";

export interface DividerProps {
	dash?: boolean;
	danger?: boolean;
	styles?: StyleDeclarationValue;
}

export const Divider: React.FC<DividerProps> = React.memo(props => {
	const styles = useStyles(getDividerStyles, props);
	return <div className={css(props.dash ? styles.dash : styles.divider, props.styles)}></div>;
});
Divider.displayName = "Divider";
