import { Theme } from "theme/theme";
import { CSSProp, CSSSizeValue } from "wraps/styleCreators";
import { ColorTypes } from "theme/colors";

export type AppMode = "test" | "dev" | "preprod" | "prod";

export const appMode2ColorMap: { [key in AppMode]: ColorTypes } = {
	dev: ColorTypes.danger,
	test: ColorTypes.warning,
	preprod: ColorTypes.success,
	prod: ColorTypes.primary
};

export type InstanceBarStylesProps = {
	appMode: AppMode;
	sizeOfBar: CSSSizeValue;
};

export const getInstanceBarStyles = (theme: Theme, props: InstanceBarStylesProps) => {
	const color = appMode2ColorMap[props.appMode] || ColorTypes.danger;
	return {
		bar: {
			background: theme.colors[color].main,
			display: "flex",
			position: "fixed",
			width: "100%",
			height: props.sizeOfBar,
			justifyContent: "center",
			alignItems: "center",
			zIndex: 10,
			bottom: "0px"
		} as CSSProp,
		text: {
			lineHeight: "0px",
			whiteSpace: "nowrap",
			color: theme.colors.primary.background,
			fontSize: theme.sizing.sizes.sm
		} as CSSProp
	};
};
