interface VisitSendErrorData {
	err: Error;
	context?: any;
}

export class VisitSendError extends Error {
	name = "Visit send error";
	message: string;
	context: any;

	constructor(public data: VisitSendErrorData) {
		super();
		this.message = data.err.message;
		this.name = data.err.name;
		this.context = data.context;
		Object.setPrototypeOf(this, VisitSendError.prototype);
	}

	toString() {
		return `${this.name}: ${this.message}`;
	}
}
