import * as React from "react";
import { QuestionActionButton } from "quiz/components/question/common/questionActionButton/QuestionActionButton";
import { IconProp } from "@fortawesome/fontawesome-svg-core";
import { useStyles } from "hooks/useStyles";
import { getUploadPhotoButtonStyles } from "./uploadPhotoButtonStyles";
import { QstContext } from "quiz/components/question/common/QstContext";

type UploadPhotoButtonProps = {
	text?: string;
	icon: IconProp;
	required: boolean;
	onClick?: () => void;
};

export const UploadPhotoButton: React.FC<UploadPhotoButtonProps> = props => {
	const styles = useStyles(getUploadPhotoButtonStyles);
	const ctx = React.useContext(QstContext);

	return (
		<QuestionActionButton
			icon={props.icon}
			text={props.text || "Upload Photo"}
			onClick={() => props.onClick && props.onClick()}
			iconStyles={ctx.attachPhotoIsValid ? null : styles.danger}
			{...(props.required ? { iconRight: ["fas", "asterisk"], iconRightStyles: styles.asteriksIcon } : null)}
		/>
	);
};
