import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { css } from "wraps/styleCreators";
import { getTakePhotoButtonStyles } from "./takePhotoButtonStyles";

type TakePhotoButtonProps = {
	onClick: () => void;
};

export const TakePhotoButton: React.FC<TakePhotoButtonProps> = React.memo(props => {
	const styles = useStyles(getTakePhotoButtonStyles);

	return (
		<div onClick={props.onClick} className={css(styles.wrap)}>
			<div className={css(styles.outerCircle)}>
				<div className={css(styles.innerCircle)}></div>
			</div>
		</div>
	);
});
TakePhotoButton.displayName = "TakePhotoButton";
