import { Theme } from "theme/theme";

const navbarClass = ".md .navbar";
export const navbarStyles = (theme: Theme) =>
	`
${navbarClass} .navbar-inner {
	justify-content: space-between;
	padding-left: ${theme.constants.gridPadding};
	padding-right: ${theme.constants.gridPadding};
}
${navbarClass} a.link {
	padding: 0;
	height: 100%;
	min-width: 0;
}
`.trim();
