import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { DividerProps } from "./Divider";

export const getDividerStyles = (theme: Theme, props: DividerProps) => {
	const vars = {
		opacity: 0.6
	};
	return {
		divider: {
			borderTop: `1px solid ${props.danger ? theme.colors.danger.main : theme.colors.base.background}`,
			...vars
		} as CSSProp,
		dash: {
			borderTop: `1px dashed ${props.danger ? theme.colors.danger.main : theme.colors.base.border}`,
			...vars
		} as CSSProp
	};
};
