import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const getRenderErrorStyles = (theme: Theme) => {
	return {
		wrap: {
			borderLeft: `${theme.sizing.sizes.xs} solid ${theme.colors.danger.main}`
		} as CSSProp,
		errorText: {
			marginTop: "0",
			backgroundColor: theme.colors.base.background,
			padding: `${scale(theme.sizing.sizes.lg, ScaleEnum.Half)} ${theme.sizing.sizes.md}`,
			whiteSpace: "normal",
			wordBreak: "break-word"
		} as CSSProp,
		header: {
			width: "100%"
		} as CSSProp,
		title: {
			display: "flex",
			alignItems: "center",
			marginBottom: theme.sizing.sizes.xs
		} as CSSProp,
		iconWrap: {
			height: theme.sizing.sizes.lg,
			width: theme.sizing.sizes.lg,
			alignItems: "center",
			textAlign: "center",
			marginRight: theme.sizing.sizes.sm
		} as CSSProp,
		description: {
			lineHeight: theme.sizing.lineHeights.heading
		} as CSSProp,
		buttonsWrap: {
			marginBottom: theme.sizing.sizes.md,
			display: "flex"
			//justifyContent: "space-between"
		} as CSSProp,
		feedbackIcon: {
			color: theme.colors.danger.main
		} as CSSProp,
		stackTitle: {
			marginBottom: theme.sizing.sizes.sm,
			marginTop: "0px"
		},
		stackText: {
			marginTop: "0px",
			marginBottom: scale(theme.sizing.sizes.lg, ScaleEnum.Half),
			fontFamily: "Roboto Mono",
			whiteSpace: "pre",
			overflowX: "scroll",
			overflowY: "hidden"
		} as CSSProp,
		accordionContent: {
			marginBottom: theme.sizing.sizes.md
		}
	};
};
