import * as React from "react";
import { Transition } from "react-transition-group";
import { useStyles } from "hooks/useStyles";
import { getTabNavigationStyles } from "./getTabNavigationStyles";
import { css } from "wraps/styleCreators";

interface TabTransitionProps {
	animationKey: number;
}

export const TabTransition: React.FC<TabTransitionProps> = props => {
	const styles = useStyles(getTabNavigationStyles);

	const initStyle = {
		transition: `opacity ${250}ms ease-in-out`,
		opacity: 0
	};

	const transitionStyles = {
		entering: { opacity: 1 },
		entered: { opacity: 1 },
		exiting: { opacity: 0 },
		exited: { opacity: 0 }
	};

	return (
		<Transition in={true} timeout={250}>
			{state => (
				<div
					style={{
						...initStyle,
						...transitionStyles[state]
					}}
					key={props.animationKey}
				>
					<div className={css(styles.content)}>{props.children}</div>
				</div>
			)}
		</Transition>
	);
};
