import * as React from "react";
import { RefObject } from "react";
import { Actions as ActionsF7, ActionsGroup, ActionsLabel, ActionsButton, F7Actions } from "framework7-react";

export type TActions = F7Actions;

export interface ActionsProps extends F7Actions.Props {}

export const Actions = React.forwardRef<TActions, React.PropsWithChildren<ActionsProps>>(
	(props, ref: RefObject<TActions>) => {
		return (
			<ActionsF7
				{...props}
				onActionsOpen={() => {
					window.onpopstate = () => {
						ref.current && ref.current.close(false);
					};
				}}
				onActionsClose={() => {
					window.onpopstate = null;
				}}
				ref={ref}
			>
				{props.children}
			</ActionsF7>
		);
	}
);

Actions.displayName = "f7-actions";

export { ActionsGroup, ActionsLabel, ActionsButton };
