import * as React from "react";
import { css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import { uploadHelperOptions } from "quiz/components/question/controls/QuestionGalleryControl";
import { getChoosePhotoButtonStyles } from "./choosePhotoButtonStyles";

type ChosePhotoButtonProps = {
	button(): React.ReactElement;
	onChange: (e: HTMLInputElement) => void;
};

export const ChosePhotoButton: React.FC<ChosePhotoButtonProps> = props => {
	const styles = useStyles(getChoosePhotoButtonStyles);

	return (
		<label className={css(styles.label)}>
			{props.button()}
			<input
				className={css(styles.input)}
				type="file"
				onChange={e => {
					props.onChange && props.onChange(e.target as HTMLInputElement);
				}}
				onClick={e => ((e?.target as HTMLInputElement).value = null)}
				multiple
				accept={uploadHelperOptions.types.join(",")}
			/>
		</label>
	);
};
