import * as React from "react";
import { OutletFullData } from "quiz/services/models";
import { OutletView, OutletViewProps } from "quiz/components/outletView/OutletView";
import { useStyles } from "hooks/useStyles";
import { getOutletItemStyles } from "./outletItemStyles";
import { css } from "wraps/styleCreators";
import { ThemeContext } from "themeContext/ThemeContext";
import { Divider } from "quiz/components/divider/Divider";
import { f7 } from "framework7-react";
import { QuizDeleteSwipeout } from "../swipeouts/QuizDeleteSwipeout";

export interface OutletItemProps {
	outlet: OutletViewProps;
	style?: React.CSSProperties;
	itemLink?: boolean;
	onClick?: (outlet: OutletFullData) => void;
	onDeleteSwipeout?: () => void;
}

export const OutletItem: React.FunctionComponent<OutletItemProps> = React.memo(props => {
	const { outlet, style, itemLink, onClick, onDeleteSwipeout } = props;
	const styles = useStyles(getOutletItemStyles);
	const theme = React.useContext(ThemeContext);

	const clickCb = React.useCallback((e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
		e.preventDefault();
		onClick && onClick(outlet);
	}, [onClick]);

	const swipeoutDeleteCb = React.useCallback(() => {
		f7.swipeout.delete(".swipeout-opened", onDeleteSwipeout);
	}, [onDeleteSwipeout]);

	return (
		<li
			style={{
				backgroundColor: outlet.flagQuestionnairesEmpty && theme.colors.primary.disabled,
				...style
			}}
			className={"swipeout"}
		>
			<div
				className={`${css(styles.wrap)} ${itemLink !== false ? "item-link" : ""}` + " swipeout-content"}
				onClick={clickCb}
			>
				<div className={css(styles.itemContent)}>
					<div className={css(styles.divider)}>
						<OutletView {...outlet}></OutletView>
					</div>
					<Divider />
				</div>
			</div>
			{onDeleteSwipeout && <QuizDeleteSwipeout onDelete={swipeoutDeleteCb} />}
		</li>
	);
}); //toDO: flagQuestionnairesEmpty, rewrite with CustomListItem
OutletItem.displayName = "f7-list-item";
