import { ActionsGroup, TActions, Actions } from "quiz/components/actions/Actions";
import { ActionSheetItem } from "../actionSheetItem/ActionSheetItem";
import React, { RefObject } from "react";
import { nav, t, quizAuthService } from "quiz/QuizApp";
import { f7 } from "framework7-react";
import { ColorTypes } from "theme/colors";

type QuizActionsProps = {
	extend?: boolean;
	actions?: () => React.ReactElement;
};

export const QuizActions = React.forwardRef<TActions, React.PropsWithChildren<QuizActionsProps>>(
	(props, ref: RefObject<TActions>) => {
		return (
			<Actions ref={ref}>
				{props.extend != null && (
					<ActionsGroup>
						<ActionSheetItem
							onClick={() => nav.toFeedback()}
							icon={["far", "comment-alt-lines"]}
							text={t("Feedback")}
						/>
						<ActionSheetItem
							onClick={() => nav.toAbout()}
							icon={["far", "info-circle"]}
							text={t("About App")}
						/>
					</ActionsGroup>
				)}
				{props.actions != null && <ActionsGroup>{props.actions()}</ActionsGroup>}
				<ActionsGroup>
					<ActionSheetItem icon={["far", "times"]} text={t("Close")} />
				</ActionsGroup>
			</Actions>
		);
	}
);
QuizActions.displayName = "QuizActions";
