import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getQuestionGalleryControlStyles = (theme: Theme) => {
	return {
		wrap: {
			paddingTop: theme.sizing.sizes.md,
			background: theme.colors.primary.background
		} as CSSProp,
		danger: {
			color: theme.colors.danger.main
		}
	};
};
