import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { QuestionViewProps } from "./QuestionView";

export const getQuestionView = (theme: Theme, props: QuestionViewProps) => {
	return {
		questionViewWrap: {
			margin: `${theme.sizing.sizes.md} 0`
		} as CSSProp,
		headerWrap: {
			display: "flex",
			justifyContent: "space-between"
		} as CSSProp,
		index: {
			marginRight: theme.sizing.sizes.sm,
			lineHeight: theme.sizing.lineHeights.title,
			color: props.danger ? theme.colors.danger.main : theme.colors.base.texts.muted
		} as CSSProp,
		titleWrap: {
			wordBreak: "break-word",
			alignItems: "baseline"
		} as CSSProp,
		titleTextWrap: {
			marginTop: theme.sizing.sizes.sm
		} as CSSProp,
		titleText: {
			lineHeight: theme.sizing.sizes.lg,
			color: props.danger ? theme.colors.danger.main : theme.colors.base.texts.main
		} as CSSProp,
		asteriksWrap: {
			display: "flex",
			width: theme.sizing.sizes.lg,
			height: theme.sizing.sizes.lg,
			justifyContent: "flex-end"
		} as CSSProp,
		asteriks: {
			fontWeight: theme.sizing.fontWeights.bold,
			fontSize: theme.sizing.sizes.sm
		} as CSSProp,
		image: {
			width: "100%",
			paddingBottom: theme.sizing.sizes.lg
		},
		description: {
			marginTop: theme.sizing.sizes.sm,
			paddingRight: `calc(${theme.sizing.sizes.lg} * 2)`,
			lineHeight: "1em"
		},
		wrapGallery: {
			marginTop: theme.sizing.sizes.md,
			marginBottom: theme.sizing.sizes.md
		}
	};
};
