import { RadioItem, RadioList } from "quiz/components/radioList/RadioList";
import { t } from "quiz/QuizApp";
import * as React from "react";
import { QuestionControlProps } from "../Question";
import { QuestionManyOptionControl } from "./QuestionManyOptionControl";
import { SwiperSettings } from "utils/constants";

const boolQuestionValues: () => RadioItem[] = () => [
	{ value: 1, title: t("Yes") },
	{ value: 0, title: t("No") }
];

export const QuestionBooleanControl: React.FC<QuestionControlProps> = React.memo(props => {
	if (props.question.items?.length) return <QuestionManyOptionControl {...props} />;
	const id = props.question.id;

	const onChange = React.useCallback(
		e => {
			props.visitManager.setAnswer(id, [
				{
					value: Boolean(parseInt(e.target.value))
				}
			]);
			if (props.visitManager.isQuickTransition(props.question)) {
				const timerId = setTimeout(() => {
					props.toNextQuestion();
					clearInterval(timerId);
				}, SwiperSettings.quickTransitionDelayMs);
			}
		},
		[id]
	);

	const item = props.valueItems && props.valueItems[0];

	return (
		<RadioList
			items={boolQuestionValues()}
			onChange={e => {
				onChange(e);
			}}
			value={item ? Number(item.value) : null}
		></RadioList>
	);
});
QuestionBooleanControl.displayName = "QuestionBooleanControl";
