import { logger, quizMetricsService } from "quiz/QuizApp";
import { LogType } from "./Logger";

export class QuizLocalStorageService {
	private db = localStorage;

	async set(key: string, data: string) {
		try {
			return this.db.setItem(key, data);
		} catch (e) {
			await quizMetricsService.send();
			logger.log(
				"metrics",
				{
					error: e
				},
				LogType.error
			);
		}
	}

	get length() {
		return this.db.length;
	}

	get(id: string) {
		return this.db.getItem(id);
	}

	getKey(key: number) {
		return this.db.key(key);
	}

	remove(keys: string[]) {
		keys.forEach(key => this.db.removeItem(key));
	}
}
