import { TabNavigation } from "quiz/components/tabBar/TabNavigation";
import * as React from "react";
import { quizAuthService } from "quiz/QuizApp";

export const QuizMainPage = props => {
	const currentTab = localStorage.getItem("currentTab");
	return (
		<TabNavigation
			{...props}
			activeTab={currentTab ? Number(currentTab) : quizAuthService.isSupervisor() ? 0 : 1}
		/>
	);
};
