import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";
import { FullscreenInfoProps } from "./FullscreenInfo";

export const getFullscreenErrorStyles = (
	theme: Theme,
	props: Pick<FullscreenInfoProps, "fullHeight" | "colorType" | "transparent">
) => {
	const { fullHeight, colorType, transparent } = props;
	const color = theme.colors[colorType] || theme.colors.primary;

	return {
		wrap: {
			display: "flex",
			justifyContent: "center",
			height: fullHeight ? "100vh" : "100%",
			backgroundColor: transparent ? "transparent" : theme.colors.base.main
		} as CSSProp,
		content: {
			display: "flex",
			flexDirection: "column",
			alignItems: "center",
			justifyContent: "center"
		} as CSSProp,
		iconWrap: {
			width: "80px",
			height: "80px",
			alignItems: "center",
			textAlign: "center",
			justifyContent: "center",
			display: "flex",
			marginBottom: theme.sizing.sizes.lg,
			color: color.main
		} as CSSProp,
		title: {
			textAlign: "center",
			marginBottom: theme.sizing.sizes.sm
		} as CSSProp,
		description: {
			textAlign: "center",
			marginBottom: scale(theme.constants.gridPadding, ScaleEnum.Double)
		} as CSSProp,
		primaryButton: {
			marginBottom: theme.sizing.sizes.lg
		} as CSSProp,
		secondaryButton: {
			marginBottom: theme.sizing.sizes.sm
		} as CSSProp
	};
};
