import { Theme } from "theme/theme";
import { scale, ScaleEnum } from "utils/cssHelpers";
import { CSSProp } from "wraps/styleCreators";
import { BackLinkProps } from "./BackLink";

export const getBackLinkStyles = (theme: Theme, props: BackLinkProps) => {
	return {
		link: {
			marginRight: props.noMargin ? "unset" : scale(theme.sizing.sizes.lg, ScaleEnum.Half)
		} as CSSProp
	};
};
