import { OutletFullData } from "quiz/services/models";

export type SearchPageState = {
	value?: string;
	delayedValue?: string;
	loading?: boolean;
	outlets?: OutletFullData[];
	requestError?: any;
	onlyEmployeeOutlets?: boolean;
};

type SearchPageActions =
	| { type: "SUCCESS_REQUEST"; payload: OutletFullData[] }
	| { type: "ERROR_REQUEST"; payload: any }
	| { type: "INPUT_NOT_CHANGED" }
	| { type: "DELAYED_INPUT" }
	| { type: "INPUT_CHANGED"; payload: string }
	| { type: "INPUT_CLEARED" }
	| { type: "TOGGLE_CHANGED"; payload: boolean }
	| { type: "HINTS_CLICKED"; payload: string };

export const searchPageReducer = (state: SearchPageState, action: SearchPageActions): SearchPageState => {
	switch (action.type) {
		case "SUCCESS_REQUEST":
			return { ...state, requestError: null, outlets: action.payload, loading: false };
		case "ERROR_REQUEST":
			return { ...state, requestError: action.payload, loading: false };
		case "INPUT_NOT_CHANGED":
			return state;
		case "DELAYED_INPUT":
			return { ...state, delayedValue: state.value, loading: true };
		case "INPUT_CHANGED":
			return { ...state, value: action.payload };
		case "INPUT_CLEARED":
			return { ...state, value: "", delayedValue: "", outlets: [], loading: state.onlyEmployeeOutlets };
		case "TOGGLE_CHANGED":
			return {
				...state,
				onlyEmployeeOutlets: action.payload,
				outlets: action.payload ? state.outlets : [],
				loading: true
			};
		case "HINTS_CLICKED":
			return { ...state, value: action.payload, loading: true };
	}
};
