import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getOfflineIndicatorStyles = (theme: Theme) => {
	return {
		wrap: {
			display: "flex",
			alignItems: "center"
		} as CSSProp,
		iconWrap: {
			marginRight: theme.sizing.sizes.sm
		} as CSSProp
	};
};
