import { IFilter, FilterUserId } from "./filter";

//todo: copy-paste from mdt

export type QuerySorting = string | { path: string; asc?: boolean };

export interface Query {
	children?: Query[];

	select?: string[];
	sorting?: QuerySorting[];
	filter?: IFilter;
	// options?: QueryOptions;
	count?: boolean;
	top?: number;
	skip?: number;
	distinct?: boolean;
	key?: any;
	params?: any;
	dependency?: { table: string; field: string };
	aggregate?: boolean;
	export?: string;
	format?: string;
	hierarchy?: {
		parentPath: string;
		idPath: string;
		mode?: HierarchyMode;
	};
	placeIn?: any;
	date?: Date;

	table?: string;
	parent?: Query;
	groups?: GroupQuery;

	disableDefaultSorting?: boolean;
}

export enum HierarchyMode {
	up,
	down,
	upDown
}

export interface GroupQuery {
	groups?: GroupItem[];
	allParents?: boolean;
	orphans?: boolean;
	toggledValues?: any[];
}

export interface GroupItem {
	fieldPath: string;
	select?: string[];
	toggledValues?: any[];
	withEmpty?: boolean;
	collapsed?: boolean;
}

export class ApiUrlHelper {
	static query2Str(query: Query) {
		if (query.children && query.children.length) {
			var batch = [query];
			query.children.forEach(q => batch.push(q));
			delete query.children;
			return batch.map(q => ApiUrlHelper.query2Str(q));
		}

		var request = <any>{};
		if (query.select && query.select.length) {
			request.select = query.select.join(","); //todo: alises - "ID_TextKey/Code as $key, Value as $value"
		}
		if (query.filter) {
			var filter = ApiUrlHelper.filter2Str(query.filter, query.table, query.parent);
			if (filter) request.filter = filter;
		}
		if (query.date) request.date = (query.date as any).toISOString(true);
		if (query.sorting) request.orderby = ApiUrlHelper.sort2Str(query.sorting);
		if (query.skip) request.skip = query.skip;
		if (query.top != null) request.top = query.top;
		if (query.count) request.withCount = true;
		if (query.distinct) request.distinct = query.distinct;
		if (query.placeIn) request.placeIn = query.placeIn;
		if (query.hierarchy) {
			let mode = this.hierarchyKeys[query.hierarchy.mode ?? HierarchyMode.upDown];
			request.hierarchy = `${mode}${query.hierarchy.parentPath}:${query.hierarchy.idPath}`;
		}
		if (query.export) request.export = query.export;
		if (query.format) request.format = query.format;
		if (query.dependency) request.checkDependency = query.dependency.table + "/" + query.dependency.field;
		if (query.table) request.table = query.table;
		if (query.aggregate) request.aggregate = true;
		if (query.key) request.key = query.key;
		if (query.params) {
			if (typeof query.params == "string") {
				request.params = query.params;
			} else {
				Object.keys(query.params).forEach(k => (request[k] = query.params[k]));
			}
		}
		if (query.groups) {
			request.groups = JSON.stringify(query.groups);
		}
		return request;
	}

	private static hierarchyKeys = {
		[HierarchyMode.up]: "-",
		[HierarchyMode.down]: "+",
		[HierarchyMode.upDown]: "#"
	};

	static prepareQueryParams(...params) {
		return params.length ? "[" + params.map(p => JSON.stringify(p)).join(",") + "]" : "";
	}

	static negationFilterAliases = {
		notStartsWith: "startsWith",
		notContains: "contains",
		notEndsWith: "endsWith"
	};

	static filter2Str(f, table?: string, parent?) {
		if (!f) return null;
		if (f.groups && f.groups.length) {
			var result = f.groups
				.map(x => ApiUrlHelper.filter2Str(x, table, parent))
				.filter(x => !!x)
				.join(" " + f.op + " ");
			return result ? (f.not ? "not" : "") + "(" + result + ")" : "";
		} else if (f.p1 && f.p2 !== undefined) {
			let op = ApiUrlHelper.prepareOp(f.op);
			let not = f.not;
			if (f.op == "null" || f.op == "notNull") {
				op = f.op == "null" ? "eq" : "ne";
				f.p2 = null;
			} else if (ApiUrlHelper.negationFilterAliases[f.op]) {
				op = ApiUrlHelper.negationFilterAliases[f.op];
				not = true;
			}

			var arg1 = ApiUrlHelper.arg2Str(f.p1, true, op),
				arg2 = ApiUrlHelper.arg2Str(f.p2, false, op, parent);

			// if (f.op == FilterUserId) [op, arg2] = ["eq", _(SecurityManager).user().$id];

			if (f.cast) {
				arg1 = f.cast + "(" + arg1 + ")";
				if (f.cast == "date") {
					if (f.p2 instanceof Date) arg2 = '"' + f.p2.toISOString(true) + '"';
					else if (Array.isArray(f.p2) && f.p2.every(x => x instanceof Date)) {
						arg2 = f.p2.map(x => '"' + x.toISOString(true) + '"').join(",");
					}
				}
			} else arg1 = "[" + arg1 + "]";

			// if (table) {
			// 	let tbl = _(SchemaManager)._table(table);
			// 	let field = _(SchemaManager).field(tbl, f.p1, true);
			// 	if (field) {
			// 		if (field.type == "date" && f.p2 instanceof Date) {
			// 			arg2 = '"' + f.p2.toISOString(true) + '"';
			// 		}
			// 	}
			// }
			if (f.p2 instanceof Date) {
				arg2 = '"' + f.p2.toISOString(true) + '"';
			}

			var wrap = ["startsWith", "endsWith", "contains", "in", "fulltext", "equalTemplate"].indexOf(op) >= 0;
			var r = arg1 + " " + op + (wrap ? "(" + arg2 + ")" : " " + arg2);
			if (not) r = "not(" + r + ")";
			return r;
		} else {
			return null;
		}
	}

	private static prepareOp(op) {
		let tbl = {
			queryIn: "in"
		};
		return tbl[op] || op;
	}

	private static arg2Str(arg, isFirst?, op?, parent?) {
		/*
		SchemaManager.isField(arg)
			? arg.code
			:
		*/
		return isFirst
			? arg
			: arg instanceof Array
			? JSON.stringify(arg).match(/^.(.*).$/)[1]
			: ApiUrlHelper.value2Str(arg, parent, op);
	}

	public static prepareValue(arg) {
		var result = ApiUrlHelper.value2Str(arg);
		return arg instanceof Date || typeof arg == "string" ? result.match(/^.(.*).$/)[1] : result;
	}

	private static value2Str(arg, parent?, op?) {
		if (arg && arg.table) {
			var tres = arg.table + ":" + arg.field;
			if (arg.filter !== undefined || arg.args !== undefined)
				tres += ", " + (arg.filter ? ApiUrlHelper.filter2Str(arg.filter, arg.table) : "null");
			if (arg.args !== undefined) tres += ", " + "(" + (arg.args ? ApiUrlHelper.arg2Str(arg.args) : "") + ")";
			return tres;
		}

		var res =
			typeof arg === "string" && arg.charAt(0) === "#" && op == "in"
				? parent
					? "#" + parent + (arg.indexOf("/") > 0 ? arg.substring(arg.indexOf("/")) : "")
					: arg
				: JSON.stringify(arg);
		return res == null ? "null" : res;
	}

	private static filterQuery2Str(arg) {
		return arg.table + ":" + arg.field + (arg.filter ? ", " + ApiUrlHelper.filter2Str(arg.filter, arg.table) : "");
	}

	private static sort2Str(sorting: QuerySorting[]) {
		return sorting
			.map(x => (typeof x === "string" ? x : x.path + " " + (x.asc !== false ? "asc" : "desc")))
			.join(",");
	}

	static getQueryString(obj: Object) {
		var a = Object.keys(obj || {})
			.map(k => (obj[k] == null ? null : encodeURIComponent(k) + "=" + encodeURIComponent(obj[k])))
			.filter(x => x);
		return !a.length ? "" : "?" + a.join("&");
	}
}
