import * as React from "react";
import { Page } from "framework7-react";
import { QuizNavBar } from "quiz/components/quizNavBar/QuizNavBar";
import { t } from "quiz/QuizApp";
import { FullscreenInfo } from "quiz/components/infoBlock/fullscreenInfoBlock/FullscreenInfo";
import { emptyIcon } from "quiz/components/infoScreenIcons/emptyIcon";
import { ColorTypes } from "theme/colors";

export const PageNotFound: React.FC = () => {
	return (
		<Page>
			<QuizNavBar title={t("Page not found")} backLink />
			<FullscreenInfo icon={emptyIcon} title="404" message={t("Page not found")} colorType={ColorTypes.warning} />
		</Page>
	);
};
