import * as React from "react";
import { ProgressBlock, ProgressBlockState } from "./ProgressBlock";
import { useStyles } from "hooks/useStyles";
import { css } from "wraps/styleCreators";
import { getProgressBarStyles } from "./progressBarStyles";
import { useNavigatorOnLine } from "hooks/useNavigatorOnline";

type ProgressBarProps = {
	items: ProgressBlockState[];
};

export const ProgressBar: React.FC<ProgressBarProps> = ({ items }) => {
	const online = useNavigatorOnLine();
	const styles = useStyles(getProgressBarStyles, online);
	return (
		<div className={css(styles.wrap)}>
			<div className={css(styles.blockWrap)}>
				{items.map((i, idx) => (
					<ProgressBlock status={i} key={idx} />
				))}
			</div>
		</div>
	);
};
ProgressBar.displayName = "ProgressBar";
