import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { css } from "wraps/styleCreators";
import { Block } from "quiz/components/block/Block";
import { T } from "quiz/components/text/T";
import { t } from "quiz/QuizApp";
import { getHintsStyles } from "./hintsStyles";

type HintsProps = {
	onExampleClick: (text: string) => void;
};

const searchFields = ["outlet name", "chain name", "legal entity name", "address", "employee name", "code of outlet"];
const searchExample = "Mass 39";

export const Hints: React.FC<HintsProps> = React.memo(props => {
	const styles = useStyles(getHintsStyles);
	return (
		<Block styles={styles.wrap}>
			<div>
				<div className={css(styles.description)}>
					<T muted tag="p" styles={styles.title}>
						Start entering the first letters of the point of sale attributes:
					</T>

					<ul className={css(styles.searchFields)}>
						{searchFields.map((el, idx) => (
							<li key={idx} className={css(styles.searchElement)}>
								<T muted>{el}</T>
							</li>
						))}
					</ul>
				</div>
				<div className={css(styles.exampleTitle)}>
					<T muted>For example</T>
					<span
						onClick={e => {
							e.preventDefault();
							props.onExampleClick(t(searchExample));
						}}
					>
						<T muted styles={styles.exampleText}>
							{searchExample}
						</T>
					</span>
				</div>
			</div>
		</Block>
	);
});
Hints.displayName = "Hints";
