import { IconWrap } from "quiz/components/iconWrap/IconWrap";
import { Link } from "quiz/components/link/Link";
import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";

type ActionSheetLinkProps = {
	onClick: () => void;
};

export const ActionSheetLink: React.FC<ActionSheetLinkProps> = props => {
	const theme = React.useContext(ThemeContext);
	return (
		<Link onClick={props.onClick}>
			<IconWrap icon={["far", "ellipsis-v"]} alignment="center-right" iconSize={theme.sizing.sizes.md} />
		</Link>
	);
};

ActionSheetLink.displayName = "ActionSheetLink";
