import NanoEvents from "nanoevents";
import { QuizUser, QuizAuthService } from "./QuizAuthService";
import { MDTApiClient, MDTRecordId } from "mdtApiClient/mdtApiClient";
import { TextService } from "./text/TextService";
import { logger } from "quiz/QuizApp";

export interface LanguageModel {
	ID_Language: number;
}

export type StorageInfo = {
	usage?: number;
	quota?: number;
	usageDetails?: any;
};

export type UserDeviceInfo = {
	userAgent: string;
	platform: string;
	storage?: StorageInfo;
};

export type QuizUserServiceEvents = {
	languagechanged: number;
};

export class QuizUserService {
	events = new NanoEvents<QuizUserServiceEvents>();
	user: QuizUser;
	userDeviceInfo: UserDeviceInfo;
	privacyPolicyAccepted = true;

	constructor(
		private apiClient: MDTApiClient,
		private textService: TextService,
		private quizAuthService: QuizAuthService
	) {}

	async init() {
		const [userState] = await Promise.all([this.quizAuthService.init(), this.prepareLogData()]);
		this.user = userState.user;
		this.log();
		return;
	}

	log() {
		const logData = {
			userId: this.user.id,
			userDevice: this.userDeviceInfo
		};
		logger.log("user-info", logData);
	}

	async checkPrivacyPolicy(): Promise<any> {
		if (this.user.isAnonymous) {
			return;
		}
		if (localStorage.getItem("privacyPolicyAccepted")) {
			await this.savePrivacyPolicyAcceptToServer();
			this.privacyPolicyAccepted = true;
			localStorage.removeItem("privacyPolicyAccepted");
			return;
		}
		if (this.user.userSettings) {
			this.privacyPolicyAccepted = this.user.userSettings.flagAcceptPrivacyPolicy;
			return;
		}
		return;
	}

	async savePrivacyPolicyAcceptToServer() {
		return this.apiClient.save({
			$table: "qst.UserSettings",
			$id: this.user?.userSettings?.id,
			flagAcceptPrivacyPolicy: true,
			id_mdt_Principal: this.user.id
		} as ApiUserSettings);
	}

	savePrivacyPolicyAcceptLocaly() {
		localStorage.setItem("privacyPolicyAccepted", "1");
	}

	privacyPolicyIsAccepted() {
		return this.privacyPolicyAccepted;
	}

	async prepareLogData() {
		const userDeviceInfo: UserDeviceInfo = {
			userAgent: navigator.userAgent,
			platform: navigator.platform
		};
		if (navigator.storage && navigator.storage.estimate) {
			const info = await navigator.storage.estimate();
			const storageInfo = {
				quota: info.quota,
				usage: info.usage,
				usageDetails: (info as any).usageDetails || {}
			};
			userDeviceInfo.storage = storageInfo;
		}
		this.userDeviceInfo = userDeviceInfo;

		return;
	}

	async changeLanguage(langId: number) {
		const data: LanguageModel = {
			ID_Language: langId
		};
		return this.apiClient.request("mdt/profile", data).then(() => this.events.emit("languagechanged", langId));
	}

	async updateUserSettings(user: QuizUser) {
		this.user = user;
		await Promise.all([
			this.user.isAnonymous ? [] : this.textService.getAvailableLanguages(),
			this.checkPrivacyPolicy()
		]);
		await this.textService.init();
		this.textService.setQuizLocalDict(user.languageId);
		return;
	}

	subscribe<U extends keyof QuizUserServiceEvents>(event: U, cb: (arg: QuizUserServiceEvents[U]) => any) {
		return this.events.on(event, cb);
	}
}

interface ApiUserSettings {
	$table: "qst.UserSettings";
	$id?: MDTRecordId;
	flagAcceptPrivacyPolicy: boolean;
	id_mdt_Principal: MDTRecordId;
}
