import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getGalleryStyles = (theme: Theme) => {
	return {
		image: {
			width: "100%",
			height: "100%",
			objectFit: "cover",
			overflow: "hidden",
			objectPosition: "center",
			"-moz-user-select": "none",
			"-webkit-user-select": "none",
			"user-select": "none"
		} as CSSProp,
		wrapper: {
			overflow: "hidden",
			position: "relative",
			width: "100%",
			paddingBottom: "100%"
		} as CSSProp,
		content: {
			position: "absolute",
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			top: 0,
			left: 0,
			width: "100%",
			height: "100%"
		} as CSSProp,
		container: {
			width: "100%"
		} as CSSProp,
		title: {
			margin: `${theme.sizing.sizes.md} 0`
		} as CSSProp
	};
};
