type Unpacked<T> = T extends (...args: any[]) => infer U ? U : T;

export type Val<T> = T | Unpacked<T>;

export function isFunction(x: any): x is Function {
	return typeof x === "function";
}

export function v<T>(value: Val<T>, ...args: any[]): Unpacked<T> | undefined {
	return isFunction(value) ? (value as Function)(...args) : value;
}

/** Evaluates `val` if exist or returns default value */
export function vd<T>(val: T, defaultValue: Unpacked<T>, ...args: any[]): Unpacked<T> | undefined {
	return val != null ? v<T>(val, ...args) : defaultValue;
}

export function removeFromArray<T = any>(arr: T[], item: T): void {
	const p = arr.indexOf(item);
	p >= 0 && arr.splice(p, 1);
}

export function updateArray<T>(
	orig: T[],
	values: T[],
	condition: (origItem: T, newItem: T) => boolean,
	prepare?: (origItem: T, newItem: T) => T
): T[] {
	const r = [];
	const vals = [...values];
	orig.forEach(o => {
		const v = vals.find(v => condition(o, v));
		if (v) {
			removeFromArray(vals, v);
			r.push(prepare ? prepare(o, v) : v);
		} else {
			r.push(o);
		}
	});
	vals.forEach(v => r.push(v));
	return r;
}

/** Upper first letter of a string */
export function upperFirst(str: string): string {
	return str[0].toUpperCase() + str.substr(1);
}

export enum MoveDirection {
	start = "start",
	end = "end",
	next = "next",
	prev = "prev"
}

export function lastIndex(arr: any[]): number {
	return arr.length - 1;
}

export function move(idx: number, lastIndex: number, dir: MoveDirection): number {
	let nextIdx = 0;
	if (dir == MoveDirection.start) nextIdx = 0;
	else if (dir == MoveDirection.end) nextIdx = lastIndex;
	else if (dir == MoveDirection.prev) nextIdx = idx == 0 ? lastIndex : idx - 1;
	else if (dir == MoveDirection.next) nextIdx = idx == lastIndex ? 0 : idx + 1;
	return nextIdx;
}

//todo: typings
export function filterProps(props, allowedKeys, include = true) {
	const res = {};
	Object.keys(props).forEach(objKey => {
		if (allowedKeys.includes(objKey) == include) res[objKey] = props[objKey];
	});
	return res;
}

//Date
export function toLocalISOString(date: Date): string {
	date.setMinutes(date.getMinutes() - date.getTimezoneOffset());
	return date.toISOString();
}

export function formatDateTime(value: Date, locale?: string, options?: Intl.DateTimeFormatOptions): string {
	return value?.toLocaleDateString(locale || FormatSettings.locale, options);
}

export function getTodayOrFormatDateTime(value: Date) {
	return value.toDateString() === new Date().toDateString() ? "Today" : formatDateTime(value);
}

function format(s: string): string {
	return s.length === 1 ? "0" + s : s;
}

export function partOFTime(time: Date): string {
	const h = format(time.getHours().toString());
	const m = format(time.getMinutes().toString());

	return h + ":" + m;
}

export function getVisitDuration(elapsedSeconds: number): string {
	const hours = Math.floor(elapsedSeconds / 60 / 60);
	const minutes = Math.floor((elapsedSeconds - hours * 60 * 60) / 60);
	const seconds = Math.floor(elapsedSeconds - hours * 60 * 60 - minutes * 60);
	return [hours, minutes, seconds].map(value => format(value.toString())).join(":");
}

export function formatMetricsDate(date: Date): string {
	const r = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
	return r;
}

//     "dd.mm.yy"
export function compareStringDate(a: string, b: string): number {
	const firstDate = a
		.split(".")
		.reverse()
		.join(".");
	const secondDate = b
		.split(".")
		.reverse()
		.join(".");

	return +new Date(firstDate) - +new Date(secondDate);
}

export function getTimeDiffInMinutes(d1: Date, d2: Date) {
	let diff = (d1.getTime() - d2.getTime()) / 1000;
	diff /= 60;
	return Math.round(diff);
}

//Img
export function isImg(file: File): void {
	if (!/^image\//i.test(file.type)) {
		console.error("error");
	}
}

export async function readImg(file: File): Promise<string | ArrayBuffer> {
	return new Promise((resolve, reject) => {
		const fr = new FileReader();
		fr.readAsDataURL(file);
		fr.onloadend = () => resolve(fr.result);
		fr.onerror = () => reject("Error");
	});
}

export function convertImg(inputFile: FileList): Promise<string | ArrayBuffer>[] {
	const files = Array.from(inputFile);
	files.forEach(isImg);
	return files.map(readImg);
}

//Number
export function formatNumber(value: string, locale?: string, options?: Intl.NumberFormatOptions): string {
	return Number.parseFloat(value).toLocaleString(locale || FormatSettings.locale, options);
}

export function formatDistance(value: string) {
	const parsedVal = Number.parseFloat(value);
	if (!Number.isInteger(parsedVal)) throw new Error("Failed to format distance");
	return parsedVal < 1000
		? { value: parsedVal.toString(), measure: "meter" }
		: { value: (parsedVal / 1000).toFixed(1), measure: "km" };
}

//String
export function abbr(text: string, options?: { max: number }): string {
	const opts = { max: 2, ...options };
	return text
		.trim()
		.replace(/\b\w/g, l => l.toUpperCase())
		.split(" ")
		.map(s => s[0])
		.join("")
		.slice(0, opts.max);
}

export function makeInitials(employee: string): string {
	return employee
		.split(" ")
		.map(x => x[0].toUpperCase())
		.join("");
}

export function removeAllSymbols(value: string): string {
	return value
		.match(/[A-z-0-9]*/g)
		.filter(el => el != "")
		.join(" ");
}

export function shortString(str: string, limit: number): string {
	const finish = limit > str.length ? str.length : limit;
	return str.slice(0, finish);
}

