type color = string;
const colorGenCache = {};
export function colorGen(txt: string, useCache = true): color {
	if (useCache && colorGenCache[txt]) return colorGenCache[txt];
	const color =
		colors[
			txt
				.split("")
				.map(c => c.charCodeAt(0))
				.reduce((a, b) => a + b, 0) % colors.length
		];
	colorGenCache[txt] = color;
	return color;
}

//Scroll
