import { ButtonSize } from "quiz/components/button/Button";
import { Feedback, FeedbackTypeEnum, FeedbackTypeTranslate, FeedbackContext } from "quiz/components/feedback/Feedback";
import { Page } from "quiz/components/page/Page";
import { feedbackClient, quizAuthService, t, logger, nav, CONFIG } from "quiz/QuizApp";
import * as React from "react";
import { Toast } from "quiz/components/interaction/toast/Toast";
import { QuizNavBar } from "quiz/components/quizNavBar/QuizNavBar";

export type QuizFeedbackPage = {
	context?: FeedbackContext;
};

const appModeToInstanceIdMap = {
	prod: 2455,
	preprod: 2452,
	test: 2458,
	dev: 2457
}; //todo:   

export const QuizFeedbackPage: React.FC<QuizFeedbackPage> = props => {
	return (
		<Page>
			<QuizNavBar title={t("Feedback")} backLink />
			<Feedback
				context={props.context}
				title={t("Review")}
				description={t("Your feedback helps us improve the application")}
				feedbackTypeLabel={t("Report type")}
				inputLabel={t("Enter your comment")}
				sendButton={{ icon: "paper-plane", title: t("Send"), fullWidth: true, size: ButtonSize.large }}
				types={Object.keys(FeedbackTypeEnum).reduce(
					(obj, item) => Object.assign(obj, { [item]: t(item) }),
					{} as FeedbackTypeTranslate
				)}
				onSubmit={formData => {
					return new Promise((resolve, reject) => {
						const user = quizAuthService.state.user;
						feedbackClient
							.send({
								...formData,
								originUrl: location.href,
								version: `${CONFIG.version}:${CONFIG.revision}`,
								instanceId: `${appModeToInstanceIdMap[CONFIG.appMode]}`,
								...(user && {
									email: user.email,
									user: JSON.stringify({
										id: user.id,
										email: user.email,
										login: user.identityName,
										fullName: user.fullName
									})
								}),
								debug: JSON.stringify({
									userAgent: navigator.userAgent,
									actions: logger.getLog().map(item => ({
										time: item.time,
										type: item.type,
										code: item.code,
										details: item.data
									})),
									errorContext: props.context?.errorContext
								})
							})
							.then(() => {
								Toast.create({
									type: "success",
									text: t("Feedback successfully sent")
								}).open();
								nav.back();
								resolve();
								return;
							})
							.catch(err => {
								Toast.create({
									type: "error",
									text: t("Failed to send feedback")
								}).open();
								nav.back();
								reject();
							});
					});
				}}
			/>
		</Page>
	);
};

QuizFeedbackPage.displayName = "QuizFeedbackPage";
