import { IconProp } from "@fortawesome/fontawesome-svg-core";
import { Text } from "quiz/components/text/Text";
import { useStyles } from "hooks/useStyles";
import { T } from "quiz/components/text/T";
import { OutletFullData } from "quiz/services/models";
import * as React from "react";
import { TextSizeType } from "theme/sizing";
import { ThemeContext } from "themeContext/ThemeContext";
import { formatDateTime, partOFTime, formatDistance, getOutletChainName } from "utils/helpers";
import { css } from "wraps/styleCreators";
import { getOutletViewStyles } from "./outletViewStyles";
import { t } from "quiz/QuizApp";
import { Row } from "../row/Row";
import { Fragment } from "react";
import { IconWrap } from "../iconWrap/IconWrap";
import { Avatar } from "../user/Avatar";

export interface OutletViewProps extends OutletFullData {
	time?: Date;
	questionnaireName?: string;
	iconView?: React.ReactNode;
}

export const OutletView: React.FC<OutletViewProps> = React.memo(props => {
	const theme = React.useContext(ThemeContext);
	const styles = useStyles(getOutletViewStyles);
	const {
		iconName,
		name: title,
		distance,
		address,
		lastVisit,
		time,
		questionnaireName,
		outletChain,
		format,
		iconView
	} = props;

	const lastVisitorName = lastVisit?.employee?.name;

	return (
		<div className={css(styles.wrap)}>
			<Row styles={[styles.titleRow, styles.information]}>
				<div className={css(styles.iconAndTitle, styles.outletTitle, styles.information)}>
					{iconName && <IconWrap icon={["far", iconName] as IconProp} alignment="center-left" />}
					<Text size={TextSizeType.body} styles={styles.title}>
						{outletChain ? getOutletChainName(outletChain) : title}
					</Text>
				</div>
				<Fragment>
					{distance && !time && (
						<div className={css(styles.distance)}>
							<T size={TextSizeType.bodyMin} styles={styles.distanceContent}>
								{Object.values(formatDistance(distance as string)).reduce((prev, current) => {
									return `${prev} ${t(current)}`;
								})}
							</T>
						</div>
					)}
					{time && <T size={TextSizeType.bodyMin}>{partOFTime(time)}</T>}
				</Fragment>
			</Row>
			<Row styles={styles.titleRow}>
				<div className={css(styles.information)}>
					{format && (
						<Row>
							<T muted size={TextSizeType.caption}>
								{`${format}`.concat(outletChain && outletChain.type ? ` / ${outletChain.type}` : "")}
							</T>
						</Row>
					)}
					{address && (
						<div className={css(styles.address)}>
							<T muted={true} size={TextSizeType.caption} styles={styles.captionText}>
								{address}
							</T>
						</div>
					)}
				</div>
				{iconView || <IconWrap icon={["far", "angle-right"] as IconProp} alignment="center-right" />}
			</Row>
			{!questionnaireName && (
				<Row styles={[styles.titleRow, styles.lastRow]}>
					<div className={css(styles.iconAndTitle, styles.titleRow, styles.bottowRow)}>
						<div className={css(styles.iconAndTitle)}>
							<IconWrap
								icon={["far", "history"] as IconProp}
								alignment="center-left"
								iconColor={theme.colors.secondary.border}
							/>
							{lastVisit?.dateEnd ? (
								<T size={TextSizeType.caption}>
									{formatDateTime(lastVisit.dateEnd, null, {
										hour: "numeric",
										minute: "numeric"
									})}
								</T>
							) : (
								<T size={TextSizeType.caption}>{"No visits"}</T>
							)}
						</div>
						{lastVisitorName && <Avatar text={lastVisitorName} type="badge" />}
					</div>
				</Row>
			)}
			{questionnaireName != null && (
				<div className={css(styles.iconAndTitle, styles.lastRow)}>
					<IconWrap
						icon={["far", "file-chart-line"] as IconProp}
						alignment="center-left"
						iconColor={theme.colors.secondary.border}
					/>
					<Text size={TextSizeType.caption}>{questionnaireName}</Text>
				</div>
			)}
		</div>
	);
});

OutletView.displayName = "OutletView";
