import { Coords } from "hooks/usePosition";
import { CoordsCacheType, outletWithDistanceCacheCode } from "./ListNearOutlets";
import { quizDataService } from "quiz/QuizApp";

export interface LatLng {
	latitude: number;
	longitude: number;
}

export function distanceInMeterBetweenToGeographyPoint(start: LatLng, end: LatLng) {
	const EARTH_RADIUS = 6371000; // meters

	const toRad = (num: number) => {
		return (num * Math.PI) / 180;
	};

	const dLat = toRad(end.latitude - start.latitude);
	const dLon = toRad(end.longitude - start.longitude);
	const lat1 = toRad(start.latitude);
	const lat2 = toRad(end.latitude);

	const a =
		Math.sin(dLat / 2) * Math.sin(dLat / 2) +
		Math.sin(dLon / 2) * Math.sin(dLon / 2) * Math.cos(lat1) * Math.cos(lat2);
	const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

	return EARTH_RADIUS * c;
}

export function getTimeChange(lastUpdateTime: number): boolean {
	const refreshingTime = 1 * 90 * 1000; //1,5 

	const currentTime = new Date().getTime();
	if (currentTime - lastUpdateTime < refreshingTime) {
		return false; //   allowedTime,  ,    
	} else return true;
}

export function compareCoords(prev: Coords, cur: CoordsCacheType): boolean {
	const allowedDistance = 100;

	const distance = distanceInMeterBetweenToGeographyPoint(
		{ latitude: +prev.latitude, longitude: +prev.longitude },
		{ latitude: +cur.latitude, longitude: +cur.longitude }
	);

	if (distance < allowedDistance) {
		return false;
	} //       allowedDistance,  ,    

	return true;
}

export const getCacheOutlets = () => {
	const cache = quizDataService.getCache(outletWithDistanceCacheCode);
	const cacheData = cache ? quizDataService.prepareOutletWithDistance(cache.records) : null;

	return cacheData;
};
