import * as React from "react";
import { Sheet } from "framework7-react";
import { T } from "../text/T";
import { useStyles } from "hooks/useStyles";
import { Theme } from "theme/theme";
import { CSSProp, css } from "wraps/styleCreators";

const getStyles = (theme: Theme) => {
	return {
		sheet: {
			background: theme.colors.danger.main,
			color: theme.colors.danger.texts.contrast,
			height: "auto",
			padding: `${theme.sizing.sizes.md} ${theme.constants.gridPadding}`
		} as CSSProp,
		header: {
			fontSize: theme.sizing.fontSizes.bodyMax,
			color: theme.colors.danger.texts.contrast,
			display: "block",
			fontWeight: "bold",
			marginBottom: theme.sizing.sizes.sm
		} as CSSProp,
		text: {
			display: "inline-block",
			color: theme.colors.danger.texts.contrast,
			fontSize: theme.sizing.fontSizes.caption,
			lineHeight: theme.sizing.lineHeights.caption,
			marginBottom: theme.sizing.sizes.lg
		} as CSSProp,
		buttonsWrap: {
			display: "flex"
		} as CSSProp
	};
};

type ErrorUnexpectedProps = {
	title: string;
	errorText: string;
	isOpen: boolean;
	buttons?(): React.ReactElement;
	onClose?(): void;
};

export const ErrorUnexpected: React.FC<ErrorUnexpectedProps> = props => {
	const styles = useStyles(getStyles);

	return (
		<Sheet className={css(styles.sheet)} opened={props.isOpen} onSheetClosed={props.onClose}>
			<div>
				<T styles={styles.header}>{props.title}</T>
				<T styles={styles.text}>{props.errorText}</T>
				<div className={css(styles.buttonsWrap)}>{props.buttons && props.buttons()}</div>
			</div>
		</Sheet>
	);
};
