import { ListItem } from "quiz/components/listView/ListView";
import { MDTRecordId } from "mdtApiClient/mdtApiClient";
import { quizDataService, nav } from "quiz/QuizApp";
import * as React from "react";
import { QuizList } from "../quizList/QuizList";
import { T } from "../text/T";
import { formatDateTime } from "utils/helpers";
import { useAnimation } from "hooks/useAnimation";
import { css } from "wraps/styleCreators";

type QstListProps = {
	id: MDTRecordId;
};

export type TOutletVisits = {
	id: MDTRecordId;
	name: string;
	dateEnd: Date;
};

const loadMoreItems = 10;

export const OutletVisits: React.FC<QstListProps> = props => {
	let [state, setState] = React.useState<{ visits: TOutletVisits[]; hasMore: boolean }>({
		visits: [],
		hasMore: false
	});
	let [requestError, setRequestError] = React.useState();
	let [skip, setSkip] = React.useState(0);

	const animation = useAnimation();

	React.useEffect(() => {
		let active = true;
		const abortCtrl = new AbortController();

		quizDataService.getOutletVisits(props.id, skip, loadMoreItems + 1, { signal: abortCtrl.signal }).then(
			q => {
				active &&
					setState({
						visits: [...state.visits, ...q.slice(0, loadMoreItems)],
						hasMore: q.length > loadMoreItems
					});
			},
			err => active && setRequestError(err.message || err.toString())
		);

		return () => {
			active = false;
			abortCtrl.abort();
		};
	}, [skip]); //toDO Jackie Chan click (   skip)

	const animationClassName = css(animation.className);
	// todo remove
	return (
		<QuizList isLoading={!state.visits} isEmpty={state.visits && state.visits.length === 0} errors={[requestError]}>
			{state.visits &&
				state.visits.map((v, idx) => (
					<ListItem
						link="#"
						key={v.id}
						onClick={() => nav.toVisit(v.id)}
						className={animationClassName}
						style={animation.animationDelay(idx)}
					>
						<div slot="title">
							<div>
								<T>{formatDateTime(v.dateEnd, null, { hour: "numeric", minute: "numeric" })}</T>
							</div>
							{v.name && (
								<div>
									<T>{v.name}</T>
								</div>
							)}
						</div>
						<div slot="after">
							<T muted>Report</T>
						</div>
					</ListItem>
				))}
			{state.hasMore && (
				<ListItem noChevron link="#" onClick={() => setSkip(skip + loadMoreItems)} key="load_more">
					<T>Load more</T>
				</ListItem>
			)}
		</QuizList>
	);
};
OutletVisits.displayName = "QstList";
