import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { QuizNavBar, QuizNavBarProps } from "quiz/components/quizNavBar/QuizNavBar";
import { getQuestionnaireNavbarStyles } from "./questionnaireNavbarStyles";
import { Fragment } from "react";

type QuestionnaireNavbarProps = Pick<QuizNavBarProps, "title" | "actions" | "noShadow">;

export const QuestionnaireNavbar: React.FC<QuestionnaireNavbarProps> = React.memo(props => {
	const { children, ...navbarProps } = props;
	const s = useStyles(getQuestionnaireNavbarStyles, props.noShadow);

	return (
		<Fragment>
			<QuizNavBar styles={s.navbar} {...navbarProps} backLink>
				{children}
			</QuizNavBar>
		</Fragment>
	);
});
QuestionnaireNavbar.displayName = "f7-navbar";
