import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getCameraIconBarStyles = (theme: Theme) => {
	return {
		wrap: {
			width: "100%",
			position: "absolute",
			bottom: theme.sizing.sizes.lg,
			overflow: "hidden"
		} as CSSProp,

		row: {
			position: "relative",
			display: "flex",
			alignItems: "center",
			justifyContent: "center"
		} as CSSProp,
		switchCameraWrap: {
			position: "absolute",
			right: "50%",
			bottom: "50%",
			transform: "translate(50%, 50%)",
			zIndex: theme.zIndex.appBar
		} as CSSProp,
		tab: {
			position: "relative",
			flexGrow: 1
		} as CSSProp,
		takePhoto: {
			display: "flex",
			alignItems: "center",
			justifyContent: "center"
		} as CSSProp
	};
};
