import { Page } from "quiz/components/page/Page";
import { quizAuthService, t } from "quiz/QuizApp";
import { QuizAuthData } from "quiz/services/QuizAuthService";
import * as React from "react";
import { getApiErrorForT } from "utils/helpers";
import { getMobileLoginPageStyles } from "./loginPageStyles";
import { css } from "wraps/styleCreators";
import { Image } from "quiz/components/image/Image";
import { useStyles } from "hooks/useStyles";
import { QuizNavBar } from "quiz/components/quizNavBar/QuizNavBar";
import { Block, BlockDescription } from "quiz/components/block/Block";
import { QuizListInputItem } from "quiz/components/quizListInputItem/QuizListInputItem";
import { ColorTypes } from "theme/colors";
import { ServerInteractionButton } from "quiz/components/button/ServerInteractionButton";
import { ButtonSize, ButtonType } from "quiz/components/button/Button";
import { f7 } from "framework7-react";
import { FieldControlLayoutProps } from "quiz/components/fieldControlLayout/FieldControlLayout";

const logo = require("../logo.svg");

export type InputControlProps = Partial<Pick<FieldControlLayoutProps, "colorType" | "description">> & {
	value?: string;
};

interface LoginPageState extends Partial<QuizAuthData> {
	email: string;
	password: string;
	error: any;
	ready: boolean;
}

const initialState: LoginPageState = {
	email: "",
	password: "",
	error: null,
	ready: false
};

export const LoginPage: React.FC = () => {
	const styles = useStyles(getMobileLoginPageStyles);
	const [state, setState] = React.useState<LoginPageState>(initialState);

	React.useEffect(() => {
		quizAuthService
			.getLoginData()
			.then(authData => setState(prev => ({ ...prev, ready: true, ...authData })))
			.catch(() => void 0); // do notnig
	}, []);

	const onEmailChange = React.useCallback((v: string) => setState(p => ({ ...p, email: v, error: null })), []);
	const onPassChange = React.useCallback((v: string) => setState(p => ({ ...p, password: v, error: null })), []);
	const onSignIn = React.useCallback(() => {
		return new Promise<void>((resolve, reject) => {
			quizAuthService
				.signIn(state.email, state.password)
				.then(() => {
					f7.views.main.router.refreshPage();
					return resolve();
				})
				.catch(err => {
					const [error, args] = getApiErrorForT(err);
					setState(prev => ({ ...prev, error: t(error, args) }));
					reject();
				});
		});
	}, [state.email, state.password]);

	const signInEnabled = React.useCallback(() => {
		return !!(state.email && state.password);
	}, [state.email, state.password]);

	const onForgotPass = React.useCallback(() => {
		f7.views.main.router.navigate("/forgot-password/", { context: { login: state.email } });
	}, [state.email]);

	const onRegister = React.useCallback(() => {
		f7.views.main.router.navigate("/registration/");
	}, []);

	return (
		<Page>
			<QuizNavBar title={t("Authorization")} faq />
			<div className={css(styles.wrap)}>
				<div className={css(styles.iosSaveArea)}>
					<div className={css(styles.content)}>
						<div className={css(styles.logoWrap)}>
							<Image src={logo} />
						</div>
						<div className={css(styles.relativeForm)}>
							<div className={css(styles.inputsWrap)}>
								<Block secondary>
									<QuizListInputItem
										required
										type="text"
										inputMode="email"
										icon={["far", "envelope"]}
										value={state.email}
										onChange={onEmailChange}
										placeholder={t("Enter e-mail")}
										dash
									/>
									<QuizListInputItem
										required
										type="password"
										inputMode="text"
										icon={["far", "lock-alt"]}
										value={state.password}
										onChange={onPassChange}
										placeholder={t("Enter password")}
									/>
								</Block>
								{state.error && <BlockDescription text={state.error} styles={styles.error} />}
							</div>
							<div className={css(styles.buttonsWrap)}>
								<div>
									<Block styles={styles.signInBtnBlock}>
										<ServerInteractionButton
											enabled={signInEnabled}
											type={ButtonType.normal}
											colorType={ColorTypes.primary}
											fullWidth={true}
											size={ButtonSize.large}
											title={t("Enter")}
											icon={["far", "sign-in"]}
											command={onSignIn}
										/>
									</Block>
									<Block>
										<ServerInteractionButton
											type={ButtonType.link}
											fullWidth={true}
											size={ButtonSize.large}
											title={t("Forgot password?")}
											command={onForgotPass}
										/>
									</Block>
								</div>
								<Block>
									<ServerInteractionButton
										type={ButtonType.transparent}
										fullWidth={true}
										colorType={ColorTypes.primary}
										size={ButtonSize.large}
										title={t("Registration")}
										icon={["far", "user-plus"]}
										command={onRegister}
									/>
								</Block>
							</div>
						</div>
					</div>
				</div>
			</div>
		</Page>
	);
};
