import { CSSProp } from "wraps/styleCreators";

export const getChoosePhotoButtonStyles = () => {
	return {
		input: {
			width: "0.1px",
			height: "0.1px",
			opacity: 0,
			overflow: "hidden",
			position: "absolute"
		} as CSSProp,
		label: {
			display: "inherit"
		} as CSSProp
	};
};
