import * as React from "react";
import { RenderErrorType, RenderError } from "../renderError/RenderError";

interface ErrorBoundaryState extends RenderErrorType {
	hasError: boolean;
}

interface ErrorBoundaryProps {
	separateBlock?: boolean;
}

export class ErrorBoundary extends React.Component<ErrorBoundaryProps, ErrorBoundaryState> {
	constructor(props) {
		super(props);
		this.state = {
			hasError: false,
			error: null,
			errorInfo: null
		};
	}

	componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
		this.setState({
			hasError: true,
			error: error,
			errorInfo: errorInfo
		});
	}

	render() {
		return this.state.hasError ? (
			<RenderError {...this.state} separateBlock={this.props.separateBlock} />
		) : (
			this.props.children
		);
	}
}
