import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { BlockProps } from "./Block";

export const getBlockStyles = (theme: Theme, props: BlockProps) => {
	const backgroundColor = props.secondary ? theme.colors.primary.background : "transparent";
	return {
		block: {
			backgroundColor,
			width: "100%",
			padding: `0 ${theme.constants.gridPadding}`,
			boxSizing: "border-box",
			overflow: "hidden"
		} as CSSProp
	};
};

export const getBlockHeaderStyles = (theme: Theme) => {
	return {
		blockHeader: {
			marginTop: theme.sizing.sizes.md,
			marginBottom: theme.sizing.sizes.sm,
			lineHeight: theme.sizing.sizes.xxs
		} as CSSProp,
		blockHeaderText: {
			lineHeight: theme.sizing.sizes.lg
		}
	};
};

export const getBlockTitleStyles = (theme: Theme) => {
	return {
		blockTitle: {
			paddingTop: theme.sizing.sizes.md,
			marginBottom: theme.sizing.sizes.sm,
			display: "flex",
			justifyContent: "space-between",
			lineHeight: "1em"
		} as CSSProp,
		title: {
			color: theme.colors.base.hover,
			lineHeight: theme.sizing.sizes.md
		} as CSSProp
	};
};

export const getBlockDescriptionStyles = (theme: Theme) => {
	return {
		description: {
			lineHeight: theme.sizing.lineHeights.bodyMin,
			fontSize: theme.sizing.fontSizes.caption,
			margin: `${theme.sizing.sizes.sm} 0 ${theme.sizing.sizes.md} 0`
		} as CSSProp
	};
};
