import * as React from "react";
import { Image } from "quiz/components/image/Image";
import { useStyles } from "hooks/useStyles";
import { getSwitchCameraButtonStyles } from "./getSwitchCameraButtonStyles";
import { css } from "wraps/styleCreators";

const cameraIcon = require("./switchButton.svg");

type SwitchCameraButtonProps = {
	onClick: () => void;
};

export const SwitchCameraButton: React.FC<SwitchCameraButtonProps> = React.memo(props => {
	const styles = useStyles(getSwitchCameraButtonStyles);

	return (
		<div onClick={props.onClick} className={css(styles.wrap)}>
			<Image src={cameraIcon} styles={styles.icon} />
		</div>
	);
});
SwitchCameraButton.displayName = "SwitchCameraButton";
