import { f7 } from "framework7-react";
import { Dialog as F7Dialog } from "framework7/components/dialog/dialog";

import "./customDialogStyles.css";

export class Dialog {
	static create(parameters: F7Dialog.Parameters): F7Dialog.Dialog {
		return f7.dialog.create({
			cssClass: "quiz-dialog",
			...parameters
		});
	}

	static alert(text: string, title: string, callback?: () => void): F7Dialog.Dialog;
	static alert(text: string, callback?: () => void): F7Dialog.Dialog;

	/** create Alert Dialog and open it */
	static alert(text: string, ...args): F7Dialog.Dialog {
		let cb = () => {};
		let title = "Quiz";
		for (let arg of args) {
			if (typeof arg == "string") {
				title = arg;
			} else {
				cb = arg;
			}
		}

		const alert = f7.dialog.create({
			cssClass: "quiz-dialog",
			text,
			title,
			buttons: [
				{
					text: "Ok",
					close: true,
					onClick: cb
				}
			]
		});
		alert.open();

		return alert;
	}

	/** create Preloader Dialog and open it */
	static preloader(title?: string, color?: string): F7Dialog.Dialog {
		return f7.dialog.preloader(title, color);
	}

	static customPreloader(parameters: F7Dialog.Parameters) {
		return Dialog.create({
			...parameters,
			cssClass: "dialog-preloader quiz-dialog",
			content: '<div class="preloader">' + f7.utils.mdPreloaderContent + "</div>"
		});
	}
}

export { F7Dialog };
