import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";
import { QuizSkeleton } from "../QuizSkeleton";
import { SkeletonItemWrap } from "../SkeletonItem/SkeletonItem";
import { SkeletonIcon } from "../SkeletonItem/SkeletonIcon";
import { SkeletonRow } from "../SkeletonItem/SkeletonRow";
import { QuizListSkeletonProps, QuizSkeletonList } from "../SkeletonList/SkeletonList";
import { BlockTitleSkeleton } from "./BlockTitleSkeleton";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const OutletSkeleton: React.FC<QuizListSkeletonProps> = props => {
	const theme = React.useContext(ThemeContext);
	const margin = scale(theme.sizing.sizes.lg, ScaleEnum.Half);

	return (
		<div>
			{props.blockTitle && <BlockTitleSkeleton />}
			<QuizSkeletonList>
				{Array.from(new Array(props.totalBlocks)).map((el, idx) => (
					<QuizSkeleton key={idx}>
						<SkeletonItemWrap spacing={"space-between"} marginBottom={margin}>
							<SkeletonItemWrap>
								<SkeletonIcon size="small" marginRight={margin} />
								<SkeletonRow width={164} type="title" />
							</SkeletonItemWrap>
							<SkeletonRow type="text" width={36} />
						</SkeletonItemWrap>
						<SkeletonRow type="text" width={160} marginBottom={margin} />
						<SkeletonItemWrap spacing={"space-between"} marginBottom={theme.sizing.sizes.sm}>
							<SkeletonRow width={216} type="text" />
							<SkeletonIcon size="small" />
						</SkeletonItemWrap>
						<SkeletonRow type="text" width={116} marginBottom={theme.constants.gridPadding} />
						<SkeletonItemWrap spacing="space-between">
							<SkeletonItemWrap align="center">
								<SkeletonIcon size="small" marginRight={margin} />
								<SkeletonRow width={100} type="text" />
							</SkeletonItemWrap>
							<SkeletonItemWrap>
								<SkeletonIcon size="small" marginRight={margin} />
								<SkeletonIcon size="small" marginRight={margin} />
								<SkeletonIcon size="small" />
							</SkeletonItemWrap>
						</SkeletonItemWrap>
					</QuizSkeleton>
				))}
			</QuizSkeletonList>
		</div>
	);
};
