import * as React from "react";
import { VisitReport, VisitReportProps } from "./VisitReport";
import { t, nav } from "quiz/QuizApp";
import { accessDeniedIcon } from "quiz/components/infoScreenIcons/accessDeniedIcon";
import { ColorTypes } from "theme/colors";
import { FeedbackTypeEnum } from "quiz/components/feedback/Feedback";
import { FullscreenInfo } from "quiz/components/infoBlock/fullscreenInfoBlock/FullscreenInfo";
import { f7 } from "framework7-react";

export interface ReportPageContext extends VisitReportProps {
	open: boolean;
}

type ReportPageProps = {
	context?: ReportPageContext;
};

export const ReportPage: React.FC<ReportPageProps> = props => {
	const { visit, visitManager, outlet, onAnswerClick } = props.context || {
		visit: null,
		visitManager: null,
		outlet: null,
		onAnswerClick: null
	};

	return (
		<>
			{props.context?.visit ? (
				<VisitReport
					visit={visit}
					visitManager={visitManager}
					outlet={outlet}
					isCompleted={false}
					onAnswerClick={onAnswerClick}
				/>
			) : (
				<FullscreenInfo
					icon={accessDeniedIcon}
					title={t("Access error")}
					message={t("Access denied")}
					colorType={ColorTypes.danger}
					primaryButton={{
						title: t("Complain"),
						icon: ["far", "comment-alt-lines"],
						command: () => nav.toFeedback({ type: FeedbackTypeEnum.Problem, value: t("Access denied") })
					}}
					secondaryButton={{
						title: t("Refresh"),
						icon: ["far", "sync"],
						command: () => f7.views.main.router.refreshPage()
					}}
				/>
			)}
		</>
	);
}; //toDO: fetch data?
ReportPage.displayName = "ReportPage";
