import { Text } from "quiz/components/text/Text";
import { ListItem, List } from "quiz/components/listView/ListView";
import { T } from "quiz/components/text/T";
import { t, nav } from "quiz/QuizApp";
import { Visit, VisitAnswer, OutletFullData } from "quiz/services/models";
import * as React from "react";
import { TextSizeType } from "theme/sizing";
import { VisitManager } from "../questionnaire/VisitManager";
import { SignatureControl } from "quiz/components/signature/SignatureControl";
import { BlockTitle, Block } from "quiz/components/block/Block";
import { useStyles } from "hooks/useStyles";
import { getVisitReportStyles } from "./visitReportStyles";
import { css } from "wraps/styleCreators";
import { formatDateTime, getVisitDuration } from "utils/helpers";
import { OutletExtendableItem } from "quiz/components/outletExtendableView/OutletExtendableItem";
import { QuestionReadonlyControl } from "quiz/components/question/controls/readonly/QuestionReadonlyControl";
import { QstListItemView } from "quiz/components/qstListItem/QstListItemView";
import { Divider } from "quiz/components/divider/Divider";
import { ThemeContext } from "themeContext/ThemeContext";
import { getCSSSizeValue } from "utils/cssHelpers";
import { WrappedMobileActionButton } from "quiz/components/button/WrappedMobileActionButton";
import { ButtonSize, ButtonType } from "quiz/components/button/Button";
import { Toast } from "quiz/components/interaction/toast/Toast";
import { QuestionKpiControl } from "quiz/components/question/controls/kpi/QuestionKpiControl";
import { QstContext } from "quiz/components/question/common/QstContext";

export interface VisitReportProps {
	visit: Visit;
	visitManager?: VisitManager;
	outlet?: OutletFullData;
	isCompleted: boolean;
	onAnswerClick?: (answer: VisitAnswer) => void;
}

export const VisitReport: React.FC<VisitReportProps> = props => {
	const styles = useStyles(getVisitReportStyles);
	const theme = React.useContext(ThemeContext);
	const { onAnswerClick, isCompleted, visit, visitManager, outlet } = props;
	const [signatureIsValid, setSignatureIsValid] = React.useState<boolean>(true);
	const prevVisit = visit.questionnaire?.prevVisit;

	React.useEffect(() => {
		if (!isCompleted && visit.questionnaire.flagSignature) {
			setSignatureIsValid(visitManager && visitManager.validateSignature());
		}
	}, []);

	return (
		visit && (
			<>
				<List noHairlinesMd>
					<BlockTitle title={t("Common info")} />
					<OutletExtendableItem {...visit.outlet} />
					<div className={css(styles.infoWrap)}>
						<Divider />
					</div>
					<ListItem>
						<QstListItemView
							name={visit.questionnaire.name}
							description={visit.questionnaire.description}
						/>
					</ListItem>
					<div className={css(styles.infoWrap)}>
						<Divider />
						<div className={css(styles.infoRow)}>
							<div className={css(styles.infoText)}>
								<T muted size={TextSizeType.caption}>
									Date start
								</T>
							</div>
							<div className={css(styles.infoText)}>
								<Text size={TextSizeType.caption}>
									{formatDateTime(visit.dateBegin, null, {
										hour: "numeric",
										minute: "numeric"
									})}
								</Text>
							</div>
						</div>
						{visit.elapsedSeconds && (
							<>
								<Divider dash />
								<div className={css(styles.infoRow)}>
									<div className={css(styles.infoText)}>
										<T muted size={TextSizeType.caption}>
											Duration
										</T>
									</div>
									<div className={css(styles.infoText)}>
										<Text size={TextSizeType.caption}>
											{getVisitDuration(visit.elapsedSeconds)}
										</Text>
									</div>
								</div>
							</>
						)}
					</div>
				</List>
				{visit.answers &&
					visit.answers.map((answer, idx) => {
						const errors = (visitManager && visitManager.validateAnswer(answer)) || [];
						const question = answer.question;
						const prevAnswer = prevVisit?.answers?.find(a => a.questionId == question.id);
						//toDO: QuestionView
						return (
							<div key={answer.questionId} onClick={() => onAnswerClick && onAnswerClick(answer)}>
								<div className={css(styles.answerSpacing)} />
								<List noHairlinesMd noHairlinesBetweenMd>
									<ListItem>
										<div slot="inner-start">
											<div className={css(styles.questionName)}>
												<T size={TextSizeType.bodyMax}>
													{`${idx + 1}. ${answer.question.name}`}
												</T>
											</div>
											<div className={css(styles.questionDescription)}>
												<T muted size={TextSizeType.caption}>
													{answer.question.description}
												</T>
											</div>
										</div>
										<div slot={"root-end"}>
											<Divider />
										</div>
									</ListItem>
									{question.kpi?.length && (
										<>
											<QstContext.Provider
												value={{
													answer: answer,
													prevAnswer: prevAnswer,
													prevVisitDate: prevVisit?.dateEnd
												}}
											>
												<QuestionKpiControl question={question} />
											</QstContext.Provider>
											<div className={css(styles.kpiDivider)}>
												<Divider />
											</div>
										</>
									)}
									<QuestionReadonlyControl
										answer={answer}
										errors={errors}
										visitManager={visitManager}
									/>
								</List>
							</div>
						);
					})}
				{visit.questionnaire.flagSignature ? (
					<div className={css(styles.signatureWrap)}>
						<BlockTitle title={t("Signature")} />
						<Block secondary>
							<SignatureControl
								visitManager={visitManager}
								readonly={!visitManager}
								visit={visit}
								emptyText={t("Place for signature")}
								width={document.body.clientWidth - getCSSSizeValue(theme.constants.gridPadding) * 2}
								onSignatureStateChange={() => {
									setSignatureIsValid(visitManager && visitManager.validateSignature());
								}}
							/>
						</Block>
					</div>
				) : (
					<div className={css(styles.answerSpacing)} />
				)}
				{!isCompleted && (
					<WrappedMobileActionButton
						icon={"paper-plane"}
						title={t("Send")}
						size={ButtonSize.large}
						type={ButtonType.normal}
						fullWidth={true}
						command={() => {
							visitManager?.finishVisit().then(() => {
								Toast.create({
									type: "success",
									text: t("Questionnaire finished!")
								}).open();
							});
							Toast.create({
								type: "info",
								text: t("Report sync...")
							}).open();
							nav.toOutlet(visit.outletId, { reloadAll: true });
						}}
						enabled={() => visitManager && visitManager.isValidQuestionnaire() && signatureIsValid}
					/>
				)}
			</>
		)
	);
};

VisitReport.displayName = "VisitReport";
