import { MDTApiClient, MDTRequestSettings } from "mdtApiClient/mdtApiClient";
import { getUrlParams } from "utils/helpers";

export type setPasswordData = {
	password: string;
	confirmPassword: string;
	key: string;
};

export type changePasswordData = {
	oldPassword: string;
	newPassword: string;
};

export class ApiClientPassword {
	public PasswordUrl = "/password/:key";

	constructor(private apiClient: MDTApiClient) {}

	private request(url: string, data?: Object, settings: MDTRequestSettings = {}, requestOptions: RequestInit = {}) {
		return this.apiClient.request(url, data, settings, requestOptions);
	}

	async checkSystemCanLogin() {
		return this.request("password/checksystemcanlogin");
	}

	async checkRegistration(): Promise<{ enabled: boolean }> {
		return this.request("password/checkregistration", void 0, { camel: true }).then(r => r.json());
	}

	async register(user: { Code: string; Title: string; Email: string }) {
		let data = { User: user, Url: this.getPasswordUrl() };
		return this.request("password/register", data);
	}

	async checkPasswordExpired(): Promise<{ days?: number; expired?: boolean; date?: string }> {
		return this.request("password/checkpasswordexpired", void 0, { camel: true }).then(
			r => r.json().catch(() => ({})) //todo: catch error
		);
	}

	async sendInvitation(login: string) {
		var data = { Login: login, Url: this.getPasswordUrl() };
		return this.request("password/sendinvitation", data);
	}

	async sendForgotPassword(login: string) {
		var data = { Login: login, Url: this.getPasswordUrl() };
		return this.request("password/sendforgotpassword", data);
	}

	async sendInvitations(ids: number[]) {
		var data = { UserIds: ids, Url: this.getPasswordUrl() };
		return this.request("password/sendinvitations", data);
	}

	async checkInvitationKey(key: string) {
		return this.request("password/checkinvitationkey", { key });
	}

	async setPassword(data: setPasswordData) {
		return this.request("password/setpassword", {
			Password: data.password,
			Password2: data.confirmPassword,
			Key: data.key
		});
	}

	async changePassword(data: changePasswordData, reset: boolean) {
		const method = reset ? "ResetPassword" : "ChangePassword";
		const params = getUrlParams(data);
		return this.request(`security/${method}?${params}`);
	}

	private getPasswordUrl() {
		// return location.href.replace(location.hash, "#!" + this.PasswordUrl); //toDo ?
		return this.apiClient.appUrl + "/#!" + this.PasswordUrl;
	}
}
