import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const getVisitReportStyles = (theme: Theme) => {
	return {
		infoWrap: {
			display: "flex",
			flexDirection: "column",
			width: "100%",
			padding: `0 ${theme.constants.gridPadding}`,
			boxSizing: "border-box",
			backgroundColor: theme.colors.base.main
		} as CSSProp,
		infoRow: {
			padding: `${theme.sizing.sizes.sm} 0`,
			display: "flex",
			justifyContent: "space-between"
		} as CSSProp,
		infoText: {
			lineHeight: theme.sizing.lineHeights.heading
		} as CSSProp,
		questionName: {
			lineHeight: theme.sizing.lineHeights.bodyMax
		} as CSSProp,
		questionDescription: {
			lineHeight: theme.sizing.lineHeights.caption
		} as CSSProp,
		signatureWrap: {
			paddingBottom: scale(theme.sizing.sizes.lg, ScaleEnum.Half)
		} as CSSProp,
		answerSpacing: {
			paddingTop: theme.sizing.sizes.lg
		} as CSSProp,
		kpiDivider: {
			height: "0px"
		} as CSSProp
	};
};
