import { IconWrap } from "quiz/components/iconWrap/IconWrap";
import { Link } from "quiz/components/link/Link";
import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";
import { navToFAQ } from "utils/helpers";

export const FAQLink: React.FC = () => {
	const theme = React.useContext(ThemeContext);
	return (
		<Link onClick={navToFAQ}>
			<IconWrap icon={["far", "life-ring"]} alignment="center-right" iconSize={theme.sizing.sizes.md} />
		</Link>
	);
};

FAQLink.displayName = "FAQLink";
