import * as React from "react";
import { QuizEmptyList, QuizEmptyListProps } from "./QuizEmptyList";
import { SimpleListSkeleton } from "../quizSkeleton/SkeletonList/SkeletonList";
import { List } from "quiz/components/listView/ListView";
import { QuizError } from "../errors/QuizError";

interface QuizListProps {
	isLoading: boolean;
	isEmpty: boolean;
	emptyList?: QuizEmptyListProps;
	errors: Error[];
	separateErrorBlock?: boolean;
	loading?: () => React.ReactElement;
	noHairlines?: boolean;
}

export const QuizList: React.FC<QuizListProps> = React.memo(props => {
	const errors = props.errors.filter(x => x);
	const loading = () => (props.loading ? props.loading() : <SimpleListSkeleton totalBlocks={3} />);
	const errorsBlock = () =>
		errors.map((err, idx) => <QuizError key={idx} error={err} separateBlock={props.separateErrorBlock} />);
	const list = () => {
		return (
			<>
				{props.isEmpty ? (
					<QuizEmptyList {...props.emptyList} />
				) : (
					<List noHairlines={props.noHairlines}>{props.children}</List>
				)}
			</>
		);
	};

	return <>{props.isLoading && !errors.length ? loading() : errors.length ? <div>{errorsBlock()}</div> : list()}</>;
});

QuizList.displayName = "f7-list";
