import { Fragment, useContext } from "react";
import { useStyles } from "hooks/useStyles";
import { MDTRecordId } from "mdtApiClient/mdtApiClient";
import { Text } from "quiz/components/text/Text";
import { t } from "quiz/QuizApp";
import { AnswerType } from "quiz/services/models";
import * as React from "react";
import { css } from "wraps/styleCreators";
import { getQuestionTextControlStyles } from "./styles/questionTextControlStyles";
import { QuestionControlProps } from "../Question";
import { QuizListInputItem } from "../../quizListInputItem/QuizListInputItem";
import { Block } from "quiz/components/block/Block";
import { QuestionList } from "quiz/components/questionListView/QuestionList";
import { GroupedAnswerHeader } from "../common/groupedAnswerHeader/GroupedAnswerHeader";
import { useGroupedAnswers } from "hooks/useGroupedAnswers";
import { QstContext } from "../common/QstContext";
import { T } from "quiz/components/text/T";

const numberValueIsValid = (v: string) => {
	const regex = /^.*(\.|\,).*$/.test(v) ? /^\-?\d{0,7}[\.{1}|\,{1}]\d{0,5}$/ : /^\-?\d{0,7}$/;
	return regex.test(v);
};

const isLastItemInGroups = (totalItems: MDTRecordId[], itemId: MDTRecordId) => {
	return totalItems[totalItems.length - 1] == itemId;
};

type onChangeCallback = (v: string, id?: MDTRecordId, answer?: string) => void;

export const QuestionTextControl: React.FC<QuestionControlProps> = React.memo(
	({ question, visitManager, valueItems, answer }) => {
		const id = question.id;
		const ctx = useContext(QstContext);
		const styles = useStyles(getQuestionTextControlStyles);
		const isNumber = answer?.answerType == AnswerType.Number;

		const onChange = React.useCallback<onChangeCallback>(
			(v, itemId = null, answer = null) => {
				if (isNumber && !numberValueIsValid(v)) {
					return;
				}
				visitManager.setAnswer(id, [{ value: v, id: itemId, answer }]);
			},
			[id]
		);
		const items = question?.items || [];
		const vItems = valueItems || [];

		if (items && items.length) {
			const g = useGroupedAnswers(items);
			const invalidItems = ctx.validationErrors?.map(e => e.itemsId)[0] || [];
			const totalItems = vItems.map(i => i.id);

			return (
				<QuestionList>
					{Object.keys(g).map(group => {
						const groupItems = g[group].map(i => i.id);
						const values = valueItems.filter(i => groupItems.includes(i.id));
						return (
							<Fragment key={group}>
								<GroupedAnswerHeader
									header={group}
									counter={`${values.filter(i => i.value).length}/${groupItems.length}`}
								/>
								{g[group].map(item => {
									const valueItem = vItems.find(i => i.id == item.id);
									const itemInvalid = invalidItems.includes(item.id);

									return (
										<Fragment key={item.id}>
											<ControlItem
												onChange={onChange}
												header={item.name}
												id={item.id}
												key={item.id}
												value={valueItem && valueItem.value}
												isNumber={isNumber}
												isDash={!isLastItemInGroups(totalItems, item.id) || itemInvalid}
												required={question.flagAnswerRequired}
												danger={itemInvalid}
											/>
											{itemInvalid && (
												<div className={css(styles.validationWrap)}>
													<T styles={styles.validationText}>{`Need enter a ${
														isNumber ? "numeric" : "text"
													}`}</T>
												</div>
											)}
										</Fragment>
									);
								})}
							</Fragment>
						);
					})}
				</QuestionList>
			);
		}
		const item = valueItems && valueItems[0];

		return (
			<Block secondary>
				{isNumber ? (
					<QuizListInputItem
						type="text"
						inputMode="decimal"
						value={item?.value}
						onChange={onChange}
						icon={["far", "calculator-alt"]}
						placeholder={t("Enter a number")}
						danger={!ctx.answerIsValid}
						required={question.flagAnswerRequired}
					/>
				) : (
					<QuizListInputItem
						value={item?.value}
						onChange={onChange}
						icon={["far", "file-alt"]}
						placeholder={t("Enter an answer")}
						danger={!ctx.answerIsValid}
						required={question.flagAnswerRequired}
					/>
				)}
			</Block>
		);
	}
);
QuestionTextControl.displayName = "QuestionTextControl";

export interface ControlItemProps {
	onChange: onChangeCallback;
	value: string;
	header: string;
	id: MDTRecordId;
	isNumber: boolean;
	isDash: boolean;
	required: boolean;
	danger: boolean;
}

const ControlItem: React.FC<ControlItemProps> = React.memo(
	({ onChange, value, id, header, isNumber, isDash, required, danger }) => {
		const styles = useStyles(getQuestionTextControlStyles);
		const onChangeT = React.useCallback(v => onChange(v, id, header), [id]);

		return (
			<Block secondary>
				<div className={css(styles.itemWrap)}>
					<div className={css(styles.textWrap)}>
						<Text>{header}</Text>
					</div>
				</div>
				{isNumber ? (
					<QuizListInputItem
						type="text"
						inputMode="decimal"
						value={value}
						onChange={onChangeT}
						icon={["far", "calculator-alt"]}
						dash={isDash}
						required={required}
						placeholder={t("Enter a number")}
						danger={danger}
					/>
				) : (
					<QuizListInputItem
						value={value}
						onChange={onChangeT}
						icon={["far", "file-alt"]}
						dash={isDash}
						required={required}
						placeholder={t("Enter an answer")}
						danger={danger}
					/>
				)}
			</Block>
		);
	}
);

ControlItem.displayName = "ControlItem";
