import { IconProp } from "@fortawesome/fontawesome-svg-core";

export enum QuestionAttachPhoto {
	None = "N",
	Available = "A",
	Required = "R"
}

export enum AnswerType {
	Number = "N",
	Text = "T",
	OneOption = "O",
	ManyOptions = "M",
	Boolean = "B",
	AttachPhoto = "F",
	ScanQrCode = "Q",
	ScanQrCodeWithHideText = "Y"
}

export type ID = number | string;

interface Record {
	id: ID;
}

interface Sortable {
	sortOrder?: number;
}

interface Nameable {
	name: string;
}

export interface IQuestionnaire extends Record, Nameable {
	code: string;
	description?: string;
	flagSignature?: boolean;
	questions: IQuestion[];
	prevVisit?: Visit;
}

export interface IQuestion extends Record, Sortable, Nameable {
	description?: string;
	flagAnswerRequired?: boolean;
	answerType: AnswerType;
	questionAttachPhoto?: QuestionAttachPhoto;
	flagComment?: boolean;
	items?: IQuestionItem[];
	images?: IQuestionImage[];
	sortOrder?: number;
	// required for save
	id_mdt_ObjectListItems?: ID;
	kpi?: KPI[];
}

export interface IQuestionItem extends Record, Sortable, Nameable {
	group?: string;
	id_mdt_ObjectListItems?: number | string;
}

export interface IQuestionImage {
	id?: ID;
	fileId?: ID;
	uid?: string;
}

export type QuestionValue = boolean | number | string | ID | Object | any[];

export type Visit = {
	id?: ID;
	dateBegin?: Date;
	dateEnd?: Date;
	dateSubmitLocal?: Date;
	comment?: string;
	commentSignature?: string;
	outletId: ID;
	questionnaireId: ID;
	questionnaire?: IQuestionnaire;
	outlet?: OutletFullData;
	employeeId?: ID;
	employee?: Employee;
	signatureFile?: string;
	signatureId?: ID;
	signatureUid?: string;
	answers?: VisitAnswer[];
	elapsedSeconds?: number;
	latitude?: number;
	longitude?: number;
	latitudeEnd?: number;
	longitudeEnd?: number;
	flagRoute?: boolean;
};

export type VisitAnswer = {
	questionId: ID;
	question?: IQuestion;
	questionnaireId: ID;
	answerType: AnswerType;
	items: VisitAnswerItem[];
	comment?: string;
	files?: VisitAnswerFile[];
	// required for progressBar
	viewed?: boolean;
};

export type VisitAnswerFile = {
	id?: ID;
	fileId?: ID;
	draftFileId?: ID;
	visitAnswerId?: ID;
	data?: any;
	uid?: string;
};

export type VisitAnswerItem = {
	id?: ID;
	value?: any;
	answer?: string;
};

export interface Employee extends Nameable {
	id: ID;
	positionName?: string;
}

export interface OutletChainParent {
	name: string;
	nameShort: string;
}

export interface OutletChain extends Record, Nameable {
	type: string;
	nameShort: string;
	parent: OutletChainParent;
}

export interface LegalEntity extends Record, Nameable {
	inn: string;
}

export interface Outlet extends Record, Nameable {
	code?: string;
	iconName?: IconProp | string;
	address: string;
	phone?: string;
	type?: string;
	format?: string;
	legalEntity?: LegalEntity;
	outletChain?: OutletChain;
	employees?: Employee[];
}

export interface OutletFullData extends Outlet {
	distance?: string | number;
	flagQuestionnairesEmpty?: boolean;
	employee?: Employee; //todo: remove?
	lastVisit?: Visit;
	flagVisited?: boolean;
}

export type Position = {
	latitude: number;
	longitude: number;
};

export enum KPIType {
	Defect = "DF"
}

export interface KPI {
	name: string;
	type: KPIType;
}

export interface UserSettings extends Record {
	flagAcceptPrivacyPolicy?: boolean;
	id_mdt_Principal?: number;
}
