import QrReader from "react-qr-reader";
import * as React from "react";
import { QrScannerExtension, QrCodeScannerOptions } from "../qrScanner";

export class BrowserQrScanner implements QrScannerExtension {
	scan(onScan: (result: string) => void, onError: (err: any) => void, options?: QrCodeScannerOptions) {
		return <QrReader onScan={onScan} facingMode="environment" onError={onError} delay={options.onScanDelayMs} />;
	}
}
