import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getPrivacyPolicyStyles = (theme: Theme) => {
	return {
		wrap: {
			display: "flex",
			flexDirection: "column",
			height: "calc(100% - var(--instanceBarIndent))"
		} as CSSProp,
		content: {
			flexGrow: 2,
			overflow: "scroll",
			height: "100%",
			padding: `${theme.sizing.sizes.lg} 0`
		} as CSSProp,
		policyBlock: {
			overflow: "auto",
			boxSizing: "border-box",
			marginBottom: theme.sizing.sizes.md
		} as CSSProp,
		policyText: {
			boxSizing: "border-box",
			paddingTop: theme.sizing.sizes.md,
			paddingBottom: theme.sizing.sizes.md
		} as CSSProp,
		buttonsWrapper: {
			padding: `${theme.sizing.sizes.md} ${theme.constants.gridPadding}`,
			display: "flex",
			justifyContent: "space-between",
			backgroundColor: theme.colors.base.main,
			boxShadow: theme.shadows.up.md,
			zIndex: theme.zIndex.appBar
		} as CSSProp,
		button: {
			width: "100%",
			display: "flex",
			alignItems: "center",
			":not(:last-child)": {
				marginRight: `calc(${theme.sizing.sizes.md} - ${theme.sizing.sizes.xxs})` //14px
			}
		} as CSSProp,
		textWrapper: {
			boxSizing: "border-box",
			backgroundColor: theme.colors.base.main,
			border: "none",
			width: "100%",
			overflow: "scroll",
			fontSize: "12px",
			padding: `0 ${theme.constants.gridPadding}`
		} as CSSProp,
		errorWrapper: {
			height: "calc(100% - var(--instanceBarIndent))"
		} as CSSProp,
		preloaderWrapper: {
			height: "calc(100% - var(--instanceBarIndent))",
			display: "flex",
			justifyContent: "center",
			alignItems: "center"
		} as CSSProp
	};
};
