import { OutletExtendableItem } from "quiz/components/outletExtendableView/OutletExtendableItem";
import { MDTRecordId } from "mdtApiClient/mdtApiClient";
import { PageWrap } from "quiz/components/pageWrap/PageWrap";
import { QstList } from "quiz/components/qstList/QstList";
import { quizDataService, t, quizDraftService, logger, cleaningService } from "quiz/QuizApp";
import { QuizPageComponentProps } from "quiz/QuizAppRoutes";
import { OutletFullData } from "quiz/services/models";
import * as React from "react";
import { QuizNavBar } from "quiz/components/quizNavBar/QuizNavBar";
import { useStyles } from "hooks/useStyles";
import { F7Toggle } from "framework7-react";
import { TextSizeType } from "theme/sizing";
import { Toggle } from "quiz/components/toggle/Toggle";
import { T } from "quiz/components/text/T";
import { ExtendableNavbarPage } from "quiz/components/extendableNavbarPage/ExtendableNavbarPage";
import { Dialog } from "quiz/components/interaction/Dialog";
import { getOutletPageStyles } from "./quizOutletPageStyles";
import { css } from "wraps/styleCreators";
import { useNavigatorOnLine } from "hooks/useNavigatorOnline";
import { BlockDescription } from "quiz/components/block/Block";
import { getTimeDiffInMinutes, cacheIsValid } from "utils/helpers";
import { OutletCache } from "quiz/services/QuizDraftService";
import { Toast } from "quiz/components/interaction/toast/Toast";
import { LogType } from "quiz/services/Logger";
import { QuizError } from "quiz/components/errors/QuizError";
import { QuizOutletPageSkeleton } from "../../components/quizSkeleton/skeletons/QuizOutletPageSkeleton";

const timerUpdateDelay = 1 * 60 * 1000; // 1m

interface QuizOutletPage extends QuizPageComponentProps {
	id: MDTRecordId;
}

type OutletPageState = {
	outlet?: OutletFullData;
	cacheDateExpired?: Date;
	error?: any;
};

export const QuizOutletPage: React.FC<QuizOutletPage> = props => {
	const [state, setState] = React.useState<OutletPageState>({
		outlet: null,
		cacheDateExpired: null,
		error: null
	});
	const styles = useStyles(getOutletPageStyles, state.cacheDateExpired);
	const toggleRef = React.useRef<F7Toggle>();
	const isOnline = useNavigatorOnLine();

	const handleOutletCache = React.useCallback(async () => {
		const cache = await quizDraftService.getOutletCache(props.id);
		if (cache && cacheIsValid(cache.dateExpired)) {
			setState(prev => ({ ...prev, cacheDateExpired: cache?.dateExpired }));
			return;
		}
		setState(prev => ({ ...prev, cacheDateExpired: null }));
	}, []);

	React.useEffect(() => {
		handleOutletCache();
	}, []);

	React.useEffect(() => {
		const interval = window.setInterval(() => {
			handleOutletCache();
		}, timerUpdateDelay);
		return () => window.clearInterval(interval);
	}, []);

	return (
		<ExtendableNavbarPage
			navbarContent={() => <QuizNavBar title={t("Outlet")} backLink noShadow />}
			extendableContent={() => (
				<div className={css(styles.toggleWrap)}>
					<div className={css(styles.toggleContent)}>
						<div onClick={() => isOnline && !state.error && toggleRef.current?.toggle()}>
							<T size={TextSizeType.bodyMin}>Offline mode</T>
						</div>
						<div
							onClick={e => {
								e.preventDefault();
								isOnline && !state.error && toggleRef?.current?.toggle();
							}}
						>
							<Toggle
								ref={toggleRef}
								disabled={!isOnline || state.error}
								checked={!!state.cacheDateExpired}
								onToggleChange={async checked => {
									let data: OutletCache = null;
									if (checked) {
										const controller = new AbortController();
										const dialog = Dialog.customPreloader({
											title: t("Saving questionnaires"),
											buttons: [
												{
													text: t("Cancel"),
													onClick: () => controller.abort()
												}
											]
										});
										dialog.open();
										try {
											data = await quizDataService.getDataForOutletCaching(props.id, {
												signal: controller.signal
											});
											await quizDraftService.setOutletCache(props.id, data);
										} catch (e) {
											if (e?.message != "The user aborted a request.") {
												logger.log(
													"offline-mode",
													{
														error: e,
														stage: "Save oulet cache"
													},
													LogType.error
												);
												Toast.create({
													type: "error",
													text: t("Failed to save questionnaires")
												}).open();
											}
											setState(prev => ({ ...prev, cacheDateExpired: null }));
											await cleaningService.removeOutletCache(props.id);
										} finally {
											dialog.close();
										}
									} else {
										await cleaningService.removeOutletCache(props.id);
									}
									setState(prev => ({
										...prev,
										cacheDateExpired: data?.dateExpired
									}));
								}}
							/>
						</div>
					</div>
				</div>
			)}
			hasError={state.error}
		>
			<PageWrap
				fetchData={async a => quizDataService.getOutletInfo(props.id, { signal: a })}
				onSuccess={(data: OutletFullData) => setState(prev => ({ ...prev, outlet: data }))}
				deps={[props.id]}
				isState={!!state.outlet}
				onError={err => setState(prev => ({ ...prev, error: err }))}
				loading={() => <QuizOutletPageSkeleton />}
				errorBlock={err => <QuizError error={err} fullScreen />}
			>
				<>
					<div className={css(styles.contentWrap)}>
						{state.cacheDateExpired && (
							<BlockDescription
								text={
									t("Qusetionnaires saved, left") +
									` ${getTimeDiffInMinutes(state.cacheDateExpired, new Date())} ` +
									t("min")
								}
							/>
						)}
						<OutletExtendableItem {...state.outlet}></OutletExtendableItem>
						<div className={css(styles.content)}>{<QstList {...props}></QstList>}</div>
					</div>
				</>
			</PageWrap>
		</ExtendableNavbarPage>
	);
};
QuizOutletPage.displayName = "QuizOutletPage";
