import React from "react";
import { t } from "quiz/QuizApp";

interface Groupable {
	group?: string;
}

export function useGroupedAnswers<T extends Groupable>(items: T[] = []) {
	const noCat = t("No category");
	const groups = React.useMemo(() => {
		const groupArr: { [key: string]: T[] } = {};
		items.forEach(item => {
			const groupCode = item.group || noCat;
			if (!groupArr[groupCode]) groupArr[groupCode] = [];
			groupArr[groupCode].push(item);
		});
		return groupArr;
	}, items);
	return groups;
}
