import { createThemeBorder, ThemeBorders } from "./borders";
import { Breakpoints, createBreakpoints } from "./breakpoints";
import { createThemeColors, ThemeColors } from "./colors";
import { createThemeShadows, ThemeShadows } from "./shadows";
import { createThemeSizing, ThemeSizing } from "./sizing";
import { createThemeTransitions, ThemeTransitions } from "./transition";
import { createTypography, ThemeTypography } from "./typography";
import { ThemeZIndexes, createZIndexes } from "./zIndex";
import { DeepPartial } from "../types";
import { ThemeConstants, createConstants } from "./constants";

export interface Theme {
	breakpoints: Breakpoints;
	colors: ThemeColors;
	typography: ThemeTypography;
	shadows: ThemeShadows;
	borders: ThemeBorders;
	sizing: ThemeSizing;
	transitions: ThemeTransitions;
	zIndex: ThemeZIndexes;
	constants: ThemeConstants;
	overrides?: any;
}

interface ThemeBase extends DeepPartial<Theme> {}

export function createTheme(base: ThemeBase = {}): Theme {
	const theme = {
		breakpoints: createBreakpoints(base.breakpoints as any), //todo typing
		colors: createThemeColors(base.colors),
		typography: createTypography(base.typography),
		shadows: createThemeShadows(base.shadows),
		borders: createThemeBorder(base.borders),
		sizing: createThemeSizing(base.sizing),
		transitions: createThemeTransitions(base.transitions),
		zIndex: createZIndexes(base.zIndex),
		constants: createConstants(base.constants),
		overrides: { ...base.overrides }
	};
	return theme;
}
