import { MDTApiClient } from "mdtApiClient/mdtApiClient";
import { FeedbackFormData } from "quiz/components/feedback/Feedback";

type User = {
	id: string | number;
	login: string;
	email: string;
	fullName: string;
};

export interface FeedbackData extends Pick<FeedbackFormData, "type" | "comment"> {
	email: string;
	originUrl: string;
	user: string;
	instanceId?: number | string;
	version: string;
	debug?: string; //strinified object { userAgent: string }

	//todo
	//fileIds?: number[];
}

export class FeedbackClient {
	constructor(private apiClient: MDTApiClient) {}

	async send(data: FeedbackData): Promise<Response> {
		return this.apiClient.request("send", { ...data });
	}

	//todo methods (download, upload, send)
}
