import * as React from "react";
import { Page } from "quiz/components/page/Page";
import { quizAuthService, t, nav } from "quiz/QuizApp";
import { QuizNavBar } from "quiz/components/quizNavBar/QuizNavBar";
import { Block, BlockDescription, BlockHeader } from "quiz/components/block/Block";
import { QuizListInputItem } from "quiz/components/quizListInputItem/QuizListInputItem";
import { ServerInteractionButton } from "quiz/components/button/ServerInteractionButton";
import { ButtonSize, ButtonType } from "quiz/components/button/Button";
import { ColorTypes } from "theme/colors";
import { css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import { getApiErrorForT } from "utils/helpers";
import { getSendPasswordPageStyles } from "./sendPasswordPageStyles";

type QuizSendPasswordPageProps = {
	passwordKey?: string;
};

type SendPasswordState = {
	password: string;
	confirmPassword: string;
	error?: any;
};

const initialState: SendPasswordState = {
	password: "",
	confirmPassword: "",
	error: null
};

export const SendPasswordPage: React.FC<QuizSendPasswordPageProps> = props => {
	const [state, setState] = React.useState<SendPasswordState>(initialState);
	const styles = useStyles(getSendPasswordPageStyles);

	const onPassChange = React.useCallback(
		(v: string) => setState(prev => ({ ...prev, password: v, error: null })),
		[]
	);
	const onConfirmChange = React.useCallback(
		(v: string) => setState(prev => ({ ...prev, confirmPassword: v, error: null })),
		[]
	);
	const sendEnabled = React.useCallback(() => !!(state.password && state.confirmPassword), [state]);
	const command = React.useCallback(() => {
		return new Promise<void>((resolve, reject) => {
			quizAuthService
				.setPassword({
					password: state.password,
					confirmPassword: state.confirmPassword,
					key: props.passwordKey
				})
				.then(() => {
					nav.toMain();
					return resolve();
				})
				.catch(err => {
					const [error, args] = getApiErrorForT(err);
					setState(prev => ({ ...prev, error: t(error, args) }));
					reject();
				});
		});
	}, [state]);

	return (
		<Page>
			<QuizNavBar backLink title={t("Restore password")} />
			<div className={css(styles.wrap)}>
				<BlockHeader />
				<Block secondary>
					<QuizListInputItem
						required
						type="password"
						inputMode="text"
						icon={["far", "lock-alt"]}
						value={state.password}
						onChange={onPassChange}
						placeholder={t("Password")}
						dash
					/>
				</Block>
				<Block secondary>
					<QuizListInputItem
						required
						type="password"
						inputMode="text"
						icon={["far", "lock-alt"]}
						value={state.confirmPassword}
						onChange={onConfirmChange}
						placeholder={t("Confirm password")}
					/>
				</Block>
				<BlockDescription text={state.error} styles={styles.error} />
				<ServerInteractionButton
					wrap
					type={ButtonType.normal}
					colorType={ColorTypes.primary}
					fullWidth={true}
					size={ButtonSize.large}
					title={t("Send")}
					icon={["fas", "paper-plane"]}
					enabled={sendEnabled}
					command={command}
				/>
			</div>
		</Page>
	);
};

SendPasswordPage.displayName = "SendPasswordPage";
