import { CSSProp } from "wraps/styleCreators";
import { theme } from "themes/defaultTheme";

export const SplashScreenStyles = () => {
	return {
		wrap: {
			position: "relative",
			top: 0,
			bottom: 0,
			left: 0,
			right: 0,
			display: "flex",
			justifyContent: "center",
			height: "100%"
		} as CSSProp,
		content: {
			display: "flex",
			width: "fit-content",
			height: "100%",
			flexDirection: "column",
			alignContent: "space-between"
		} as CSSProp,
		logoWrap: {
			width: "100%",
			flexGrow: 2,
			alignItems: "center",
			textAlign: "center",
			justifyContent: "center",
			display: "flex"
		} as CSSProp,
		secondaryBlockWrap: {
			display: "flex",
			flexFlow: "column",
			alignItems: "center",
			justifyContent: "flex-start",
			position: "relative",
			width: "100%",
			flexGrow: 2
		} as CSSProp,
		secondaryBlock: {
			width: "100%",
			display: "flex",
			alignItems: "center",
			flexDirection: "column",
			padding: theme.sizing.sizes.lg,
			borderRadius: theme.sizing.sizes.sm
		} as CSSProp,
		textWrap: {
			marginBottom: theme.sizing.sizes.lg,
			display: "flex",
			width: "100%",
			justifyContent: "center"
		} as CSSProp,
		text: {
			textOverflow: "ellipsis",
			overflow: "hidden",
			whiteSpace: "nowrap",
			fontSize: theme.sizing.fontSizes.bodyMax,
			lineHeight: theme.sizing.lineHeights.title
		} as CSSProp
	};
};
