import * as React from "react";
import { Navbar as NavbarBase, NavRight, NavLeft, NavTitle, F7Navbar } from "framework7-react";
import { navbarStyles } from "./navbarStyles";
import { ThemeContext } from "themeContext/ThemeContext";

export interface NavbarProps extends F7Navbar.Props {}

export const Navbar: React.FunctionComponent<NavbarProps> = props => {
	const theme = React.useContext(ThemeContext);

	return (
		<>
			<style>{navbarStyles(theme)}</style>
			<NavbarBase {...props}>{props.children}</NavbarBase>
		</>
	);
};
Navbar.displayName = "f7-navbar"; //important for other F7 components

export { NavRight, NavLeft, NavTitle };
