import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const getOutletPageStyles = (theme: Theme, cacheDateExpired: Date) => {
	return {
		toggleWrap: {
			height: scale(theme.sizing.sizes.lg, ScaleEnum.Double),
			background: theme.colors.primary.background,
			width: "100%",
			display: "flex",
			justifyContent: "center"
		} as CSSProp,
		toggleContent: {
			padding: `0 ${theme.constants.gridPadding}`,
			display: "flex",
			width: "100%",
			justifyContent: "space-between",
			alignItems: "center"
		} as CSSProp,
		contentWrap: {
			marginTop: cacheDateExpired ? "unset" : theme.sizing.sizes.md
		} as CSSProp,
		content: {
			paddingBottom: `calc(env(safe-area-inset-bottom) + var(--instanceBarIndent) + ${theme.sizing.sizes.lg})`
		}
	};
};
