import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getGlobalErrorHandlerStyles = (theme: Theme) => {
	return {
		button: {
			color: theme.colors.danger.texts.contrast,
			flexGrow: 1,
			borderColor: theme.colors.danger.texts.contrast,
			marginRight: theme.sizing.sizes.md,
			":last-child": {
				marginRight: 0
			}
		} as CSSProp,
		icon: {
			color: theme.colors.danger.texts.contrast,
			marginRight: theme.sizing.sizes.sm
		} as CSSProp,
		iconText: {
			color: theme.colors.danger.texts.contrast
		} as CSSProp
	};
};
