export interface APIVisit {
	id?: MDTRecordId;
	$table: "qst.Visit";
	dateBegin: string;
	dateEnd: string;
	dateSubmitLocal: string;
	elapsedSeconds?: number;
	id_Outlet: MDTRecordId;
	id_Outlet$?: APIOutlet;
	id_em_Employee: MDTRecordId;
	id_em_Employee$?: APIEmployee;
	items: APIVisitItem[];
	geo_Latitude?: number;
	geo_LatitudeEnd?: number;
	geo_Longitude?: number;
	geo_LongitudeEnd?: number;
	flagRoute?: boolean;
	comment?: string;
	commentSignature?: string;
	id_mdt_FileSignature?: MDTRecordId;
	id_mdt_FileSignature$?: {
		name?: string;
		uid?: string;
	};
}

interface APIVisitItem {
	$table: "qst.VisitItem";
	$fk: "ID_Visit";
	id?: MDTRecordId;
	id_Questionnaire: MDTRecordId;
	id_Question: MDTRecordId;
	id_Question$?: APIQuestion;
	id_AnswerType: AnswerType;
	items: APIVisitItemList[];
	comment: string;
	files?: APIVisitItemFile[];
	mdt_DateCreate?: string;
	mdt_id_PrincipalCreatedBy?: string;
}

interface APIVisitItemList {
	$table: "qst.VisitItemList";
	$fk: "ID_VisitItem";
	id?: MDTRecordId;
	valueText?: string;
	valueBit?: boolean;
	valueNumber?: number;
	id_mdt_ObjectListItems?: MDTRecordId;
	id_ListItem?: MDTRecordId;
	answer?: string;
}

interface APIVisitItemFile {
	$table: "qst.VisitItem_mdt_File";
	id?: MDTRecordId;
	id_mdt_File?: MDTRecordId;
	id_mdt_File$?: {
		name: string;
		uid: string;
	};
	id_VisitItem?: MDTRecordId;
	mdt_DateCreate?: string;
}

export interface APIFile {
	$table: "mdt.File";
	data: any;
	mdt_DateCreate?: Date;
	name: string;
	type?: string;
	visits?: APIVisit[];
	visitItemPhotos?: APIVisitItemFile[];
}

export interface APIEmployee {
	id: MDTRecordId;
	name: string;
}

