import { IconProp as FAIconProp } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon, Props as FontAwesomeIconProps } from "@fortawesome/react-fontawesome";
import classNames from "classnames";
import * as React from "react";
import { css, CSSSizeValue, StyleDeclarationValue } from "wraps/styleCreators";

export type IconProp = FAIconProp;

export interface IconProps extends FontAwesomeIconProps {
	/** Explicit icon size in [CSS Units](https://learn.javascript.ru/css-units) */
	explicitSize?: CSSSizeValue;

	/** CSS in JS styles for icon */
	styles?: StyleDeclarationValue;
}

export const Icon: React.FunctionComponent<IconProps> = React.memo(props => {
	const style = {
		fontSize: props.explicitSize,
		...props.style
	};

	const faProps = { style, ...props };
	delete faProps.explicitSize;
	delete faProps.styles;
	const className = classNames(props.className, props.styles && css(props.styles));
	return <FontAwesomeIcon {...faProps} className={className} />;
});
Icon.displayName = "Icon";
