export const FilterUserId = "userId";

export const FilterConstants = {
	currentLanguage: "ID_CURRENT_LANGUAGE"
};

export interface IFilter {
	op: string;
	p1?: string;
	p2?: any;
	not?: boolean;
	groups?: IFilter[];
}

export class Filter {
	static empty = {};
	static and(...groups) {
		return { op: "and", groups };
	}
	static or(...groups) {
		return { op: "or", groups };
	}
	static eq(p1, p2) {
		return { op: "eq", p1, p2 };
	}
	static ne(p1, p2) {
		return { op: "ne", p1, p2 };
	}
	static gt(p1, p2) {
		return { op: "gt", p1, p2 };
	}
	static ge(p1, p2) {
		return { op: "ge", p1, p2 };
	}
	static lt(p1, p2) {
		return { op: "lt", p1, p2 };
	}
	static le(p1, p2) {
		return { op: "le", p1, p2 };
	}
	static startsWith(p1, p2) {
		return { op: "startsWith", p1, p2 };
	}
	static endsWith(p1, p2) {
		return { op: "endsWith", p1, p2 };
	}
	static contains(p1, p2) {
		return { op: "contains", p1, p2 };
	}
	static fulltext(p1, p2) {
		return { op: "fulltext", p1, p2 };
	}
	static not(f) {
		f.not = !f.not;
		return f;
	}
	static in(p1, ar) {
		return { op: "in", p1, p2: ar };
	}
	static queryIn(field, table, subField, filter, args?) {
		return { op: "in", p1: field, p2: { table, field: subField, filter, args } };
	}
	static cast(filter, cast) {
		filter["cast"] = cast;
		return filter;
	}
	static equalTemplate(p1, p2) {
		return { op: "equalTemplate", p1, p2 };
	}
}
