import React from "react";
import { MDTFileGallery } from "quiz/components/mdtImages/MdtFileGallery";
import { QuestionGalleryControl } from "../../QuestionGalleryControl";
import { QuestionReadonlyControlProps } from "../QuestionReadonlyControl";

export const QuestionReadonlyGallery: React.FC<Pick<
	QuestionReadonlyControlProps,
	"answer" | "visitManager"
>> = props => {
	return props.visitManager ? (
		<QuestionGalleryControl
			question={props.answer.question}
			visitManager={props.visitManager}
			files={props.answer.files}
			readonly={true}
		/>
	) : (
		<MDTFileGallery fileRecords={props.answer.files.map(f => ({ id: f.fileId, uid: f.uid }))} />
	);
};
