import * as React from "react";
import { BlockTitle, Block, BlockDescription } from "quiz/components/block/Block";
import { t, quizAuthService } from "quiz/QuizApp";
import { QuizListInputItem, QuizListInputItemProps } from "quiz/components/quizListInputItem/QuizListInputItem";
import { ColorTypes } from "theme/colors";
import { ButtonType, ButtonSize } from "quiz/components/button/Button";
import { Toast } from "quiz/components/interaction/toast/Toast";
import { getApiErrorForT } from "utils/helpers";
import { ServerInteractionButton } from "quiz/components/button/ServerInteractionButton";

type PasswordItemProps = Pick<QuizListInputItemProps, "onChange" | "value" | "placeholder" | "dash" | "danger">;

const PasswordItem: React.FC<PasswordItemProps> = props => (
	<QuizListInputItem type="password" icon={["far", "key"]} {...props} />
);

export const ChangePasswordPage: React.FC = props => {
	const [oldPassword, setOldPassword] = React.useState<string>();
	const [newPassword, setNewPassword] = React.useState<string>();
	const [confirmPassword, setConfirmPassword] = React.useState<string>();
	const [error, setError] = React.useState<any>();

	return (
		<>
			<BlockTitle title={t("Old password")} />
			<Block secondary>
				<PasswordItem
					value={oldPassword}
					onChange={s => setOldPassword(s)}
					placeholder="Enter the current password"
				/>
			</Block>

			<BlockTitle title={t("New password")} />
			<Block secondary>
				<PasswordItem
					value={newPassword}
					onChange={s => {
						setNewPassword(s);
						setError(null);
					}}
					dash
					danger={!!error}
					placeholder={"Enter a new password"}
				/>
				<PasswordItem
					value={confirmPassword}
					onChange={s => {
						setConfirmPassword(s);
						setError(null);
					}}
					danger={!!error}
					placeholder={"Repeat new password"}
				/>
			</Block>
			<BlockDescription text={t(error)} colorType={ColorTypes.danger} />
			<ServerInteractionButton
				wrap
				type={ButtonType.normal}
				colorType={ColorTypes.primary}
				fullWidth={true}
				size={ButtonSize.large}
				title={t("Send")}
				icon="paper-plane"
				command={(): any => {
					if (!oldPassword || !(newPassword && confirmPassword)) {
						return;
					} //toDo:    
					return newPassword === confirmPassword
						? quizAuthService.changePassword({ oldPassword: oldPassword, newPassword: newPassword }).then(
								res => {
									Toast.create({
										type: "success",
										text: t("Password successfully changed")
									}).open();
									return res;
								},
								reject => {
									const [error, args] = getApiErrorForT(reject);
									setError(t(error, args));
								}
						  )
						: setError(t("Passwords must match"));
				}}
			/>
		</>
	);
}; //toDO: button to component
