import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { css } from "wraps/styleCreators";
import { getRenderErrorStyles } from "./renderErrorStyles";
import { TextSizeType } from "theme/sizing";
import { AccordionContent } from "framework7-react";
import { Text } from "quiz/components/text/Text";
import { nav, CONFIG, t } from "quiz/QuizApp";
import { ThemeContext } from "themeContext/ThemeContext";
import { FeedbackTypeEnum } from "quiz/components/feedback/Feedback";
import { ContextInfoBlock } from "../infoBlock/contextInfoBlock/ContextInfoBlock";
import "typeface-roboto-mono";
import { unexpectedIcon } from "../infoScreenIcons/unexpectedErrorIcon";
import { ColorTypes } from "theme/colors";

export type RenderErrorType = {
	error: Error;
	errorInfo: React.ErrorInfo;
	separateBlock?: boolean;
};

const listItemClassName = "renderError__accordion";
const appModesWithAccordion = ["dev", "test"];

export const RenderError: React.FC<RenderErrorType> = props => {
	const styles = useStyles(getRenderErrorStyles);
	const theme = React.useContext(ThemeContext);

	const stylesCSS = React.useMemo(
		() =>
			`.${listItemClassName} .item-link .item-inner:before {
				color: ${theme.colors.danger.main};
			}
			`.trim(),
		[]
	); //toDO: refactor

	return (
		<>
			<style>{stylesCSS}</style>
			<div className={listItemClassName}>
				<ContextInfoBlock
					icon={unexpectedIcon}
					title="Render error"
					message={props.error.message}
					withAccordion={appModesWithAccordion.includes(CONFIG.appMode)}
					buttons={[
						{
							title: "Complain",
							icon: ["far", "comment-alt-lines"],
							command: () =>
								nav.toFeedback({ type: FeedbackTypeEnum.Problem, value: props.error.message })
						},
						{
							title: t("Refresh"),
							icon: ["far", "sync"],
							command: () => location.reload()
						}
					]}
					colorType={ColorTypes.danger}
					separateBlock={props.separateBlock}
				>
					<AccordionContent>
						<div className={css(styles.accordionContent)}>
							<div className={css(styles.errorText)}>
								<Text tag="p" bold size={TextSizeType.bodyMin} styles={styles.stackTitle}>
									Call stack
								</Text>
								<Text tag="pre" size={TextSizeType.caption} muted styles={styles.stackText}>
									{props.error.stack}
								</Text>
								<div>
									<Text tag="p" bold size={TextSizeType.bodyMin} styles={styles.stackTitle}>
										Component stack
									</Text>
								</div>
								<Text tag="pre" size={TextSizeType.caption} muted styles={styles.stackText}>
									{props.errorInfo.componentStack}
								</Text>
							</div>
						</div>
					</AccordionContent>
				</ContextInfoBlock>
			</div>
		</>
	);
};
