import { Navbar, NavRight } from "quiz/components/navbar/Navbar";
import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { css, StyleDeclarationValue } from "wraps/styleCreators";
import { getQuizNavBarStyles } from "./quizNavBarStyles";
import { QuizNavLeft } from "./quizNavLeft/QuizNavLeft";
import { ActionSheetLink } from "./icons/actionSheetLink/ActionSheetLink";
import { SearchLink } from "./icons/searchLink/SearchLink";
import { FAQLink } from "./icons/faqLink/FAQLink";

export interface QuizNavBarProps {
	title: string;
	subtitle?: string;
	backLink?: boolean;
	search?: boolean;
	settings?: boolean;
	styles?: StyleDeclarationValue;
	dark?: boolean;
	noShadow?: boolean;
	actions?: {
		show?: boolean;
		cb?: () => void;
	};
	faq?: boolean;
}

export interface QuizNavIconProps {
	clickAction?: () => void;
}

export const QuizNavBar: React.FC<QuizNavBarProps> = props => {
	const styles = useStyles(getQuizNavBarStyles, { noShadow: props.noShadow });

	return (
		<Navbar
			className={`${css(styles.navbar, props.styles)} ${props.dark != null ? "navbar-photo-browser-dark" : ""}`}
		>
			<QuizNavLeft backLink={props.backLink} title={props.title} subtitle={props.subtitle} />
			<NavRight>
				{props.faq && <FAQLink />}
				{props.search && <SearchLink />}
				{props?.actions?.show && <ActionSheetLink onClick={() => props.actions.cb()} />}
			</NavRight>
			{props.children}
		</Navbar>
	);
};
QuizNavBar.displayName = "f7-navbar";
