import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getQuestionTextControlStyles = (theme: Theme) => {
	return {
		itemWrap: {
			width: "100%",
			borderBottom: `1px dashed ${theme.colors.base.secondary}`,
			padding: `${theme.sizing.sizes.md} 0`
		},
		textWrap: {
			lineHeight: "100%"
		} as CSSProp,
		gap: {
			height: `${theme.sizing.sizes.sm}`,
			background: theme.colors.base.background,
			width: "100%"
		} as CSSProp,
		divider: {
			height: "1px",
			width: "100%",
			borderBottom: `1px dashed ${theme.colors.base.secondary}`
		} as CSSProp,
		validationWrap: {
			display: "flex",
			//background: theme.colors.base.background,
			padding: `${theme.sizing.sizes.sm} ${theme.constants.gridPadding} ${theme.sizing.sizes.md}`
			// ":last-child": {
			// 	paddingBottom: "0"
			// } as CSSProp
		} as CSSProp,
		validationText: {
			fontSize: theme.sizing.fontSizes.caption,
			color: theme.colors.danger.main,
			lineHeight: "128%"
		} as CSSProp
	};
};
