import * as React from "react";
import { Toggle as ToggleBase } from "framework7-react";
import { applyTheme, toggleStyles } from "./toggleStyles";
import { ThemeContext } from "themeContext/ThemeContext";
import { RefObject } from "react";

export const Toggle = React.forwardRef<ToggleBase, ToggleBase.Props>((props, ref: RefObject<ToggleBase>) => {
	const theme = React.useContext(ThemeContext);
	React.useEffect(() => {
		applyTheme(theme);
	});

	return (
		<div>
			<style>{toggleStyles(theme)}</style>
			<ToggleBase {...props} ref={ref} />
		</div>
	);
});

Toggle.displayName = "Toggle";
