import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { FunctionComponent } from "react";
import { filterProps } from "utils/helpers";
import { css } from "wraps/styleCreators";
import { FieldControlLayout, FieldControlLayoutProps } from "../fieldControlLayout/FieldControlLayout";
import { getInputStyles } from "./inputStyles";
import { useKeyboardEnter } from "hooks/useKeyboardEnter";

export const inheritFieldControlProps = [
	"value",
	"onChange",
	"label",
	"labelSecondary",
	"description",
	"colorType",
	"descriptionColorType"
] as const;

type InputHTMLProps = React.DetailedHTMLProps<React.InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>;

interface InputControlProps
	extends Pick<FieldControlLayoutProps, typeof inheritFieldControlProps[number]>,
		Omit<InputHTMLProps, typeof inheritFieldControlProps[number]> {}

export const InputControl: FunctionComponent<InputControlProps> = function InputCtrl(props) {
	const s = useStyles(getInputStyles);

	const fieldControlLayoutProps = filterProps(props, inheritFieldControlProps);
	const htmlProps = filterProps(props, inheritFieldControlProps, false);
	const inputRef = React.useRef<HTMLInputElement>();
	const enter = useKeyboardEnter();

	return (
		<FieldControlLayout
			{...fieldControlLayoutProps}
			value={props.value}
			readOnly={props.readOnly}
			onClearClick={() => inputRef && inputRef.current && inputRef.current.focus()}
			customControl={(controlProps, styles) => (
				<input
					{...controlProps}
					{...htmlProps}
					onKeyDown={enter}
					className={css(styles, s.input)}
					type={props.type || "text"}
					value={controlProps.value != null ? controlProps.value : ""}
					ref={inputRef}
				/>
			)}
		/>
	);
};
