import { f7, F7Page } from "framework7-react";
import { Router } from "framework7/modules/router/router";
import pathToRegExp from "path-to-regexp";
import { QuizFeedbackPage } from "quiz/pages/feedback/QuizFeedbackPage";
import { FilterPage } from "quiz/pages/filter/FilterPage";
import { QuestionnairePage } from "quiz/pages/questionnaire/QuestionnairePage";
import { QuizMainPage } from "quiz/pages/QuizMainPage";
import { QuizOutletPage } from "quiz/pages/quizOutletPage/QuizOutletPage";
import { SupervisorMainPage } from "quiz/pages/SupervisorMainPage";
import { VisitPage } from "quiz/pages/visit/VisitPage";
import * as React from "react";
import { ID, Visit } from "./models";
import { SearchPage } from "quiz/pages/search/SearchPage";
import { QuizNavBarProps } from "quiz/components/quizNavBar/QuizNavBar";
import { ChangePasswordPage } from "quiz/pages/profile/changePasswordPage/ChangePasswordPage";
import { AboutPage } from "quiz/pages/profile/aboutPage/AboutPage";
import { FeedbackContext } from "quiz/components/feedback/Feedback";
import { ReportPage, ReportPageContext } from "quiz/pages/visit/ReportPage";
import { UnsentReportPageContext, UnsentReportPage } from "quiz/pages/visit/UnsentReportPage";
import NanoEvents from "nanoevents";
import { VisitUploadData } from "./QuizDraftService";
import { beforeMerchandiserPageInit, afterMerchandiserPageOut } from "quiz/pages/merchandiser/MerchandiserMainPage";
import { questionnaireBeforeOut, questionnaireBeforeIn } from "quiz/components/questionnaire/Questionnaire";
import { CameraPage } from "./extension/uploadPhoto/cameraPage/CameraPage";
import { quizExtensionService } from "quiz/QuizApp";
import { PageNotFound } from "quiz/pages/pageNotFound/PageNotFound";
import { ChooseLanguagePage } from "quiz/pages/profile/chooseLanguagePage/ChooseLanguagePage";
import { PrivacyPolicy } from "quiz/components/privacyPolicy/PrivacyPolicy";
import {
	SignaturePad,
	SignaturePadPageContext,
	onSignaturePadPageMounted
} from "quiz/components/signature/SignaturePad";

export type F7PageEvents = Partial<Omit<F7Page, keyof React.Component<F7Page, {}>>>;

interface InnerRoute {
	path: string;
	pageEvents?: F7PageEvents;
	wrapperProps?: {
		navbar?: QuizNavBarProps;
	};
	component: React.ComponentType;
}

interface NavigationManagerEvents {
	beforeMerchandiserPageInit: { reports: VisitUploadData[]; drafts: Visit[] };
	afterMerchandiserPageOut: void;
	questionnaireBeforeIn: void;
	questionnaireBeforeOut: any; //todo page typing
}

export class QuizNavigationManager {
	events = new NanoEvents<NavigationManagerEvents>();
	readonly paths = {
		visit: "visit",
		report: "report",
		unsentReport: "unsentReport",
		outlet: "outlet",
		feedback: "feedback",
		filter: "filter",
		supervisorMainPage: "supervisorMainPage",
		profile: "profile",
		points: "points",
		search: "search",
		changePassword: "changePassword",
		changeLanguage: "changeLanguage",
		about: "about",
		camera: "camera",
		privacyPolicy: "privacyPolicy"
	};

	innerRoutes: InnerRoute[] = [
		{
			path: "/",
			component: QuizMainPage,
			pageEvents: {
				onPageBeforeIn: async () => {
					await beforeMerchandiserPageInit();
				},
				onPageAfterOut: () => {
					afterMerchandiserPageOut();
				}
			},
			wrapperProps: {
				navbar: {
					title: "Home",
					backLink: false,
					search: true
				}
			}
		},
		{
			path: `/${this.paths.changePassword}/`,
			component: ChangePasswordPage,
			wrapperProps: {
				navbar: {
					title: "Change Password"
				}
			}
		},
		{
			path: `/${this.paths.changeLanguage}/`,
			component: ChooseLanguagePage,
			wrapperProps: {
				navbar: {
					title: "Choose language"
				}
			}
		},
		{
			path: `/${this.paths.about}/`,
			component: AboutPage,
			wrapperProps: {
				navbar: {
					title: "About"
				}
			}
		},
		{
			path: `/${this.paths.outlet}/:id`,
			component: QuizOutletPage
		},
		{
			path: "/outlet/:outletId/qst/:qstId",
			pageEvents: {
				onPageBeforeIn: () => questionnaireBeforeIn(),
				onPageBeforeRemove: page => questionnaireBeforeOut(page)
			},
			component: QuestionnairePage
		},
		{
			path: `/${this.paths.visit}/:id`,
			component: VisitPage,
			wrapperProps: {
				navbar: {
					title: "Visit"
				}
			}
		},
		{
			path: "/feedback/",
			component: QuizFeedbackPage,
			wrapperProps: {
				navbar: {
					title: "Feedback"
				}
			}
		},
		{
			path: `/${this.paths.supervisorMainPage}/`,
			component: SupervisorMainPage,
			wrapperProps: {
				navbar: {
					title: "Outlets",
					search: true
				}
			}
		},
		{
			path: `/${this.paths.filter}/`,
			component: FilterPage,
			wrapperProps: {
				navbar: {
					title: "Filter"
				}
			}
		},
		{
			path: `/${this.paths.search}/`,
			component: SearchPage
		},
		{
			path: `/${this.paths.report}/`,
			component: ReportPage,
			wrapperProps: {
				navbar: {
					title: "Visit report"
				}
			}
		}, //toDO:  url,       
		{
			path: `/${this.paths.report}/signature`,
			component: SignaturePad,
			pageEvents: {
				onPageAfterIn: () => onSignaturePadPageMounted()
			}
		},
		{
			path: `/${this.paths.unsentReport}/`,
			component: UnsentReportPage,
			wrapperProps: {
				navbar: {
					title: "Visit report"
				}
			}
		},
		{
			path: `/${this.paths.camera}/`,
			component: CameraPage,
			pageEvents: {
				onPageBeforeIn: () => {
					quizExtensionService.statusBar.setDarkStyle();
				},
				onPageBeforeOut: () => {
					quizExtensionService.statusBar.setLightStyle();
				}
			}
		},
		{
			path: `/${this.paths.privacyPolicy}/`,
			component: PrivacyPolicy,
			wrapperProps: {
				navbar: {
					title: "Privacy policy"
				}
			}
		}
	];

	getNavData(url: string, context: any) {
		let component: React.ComponentType = () => <PageNotFound />,
			wrapProps = {},
			pageEvents = {};
		const componentProps = { context };
		this.innerRoutes.forEach(r => {
			const keys: pathToRegExp.Key[] = [];
			const data = pathToRegExp(r.path, keys).exec(url);
			if (data) {
				component = r.component;
				keys.forEach((k, idx) => (componentProps[k.name] = data[idx + 1]));
				wrapProps = r.wrapperProps;
				pageEvents = r.pageEvents;
			}
		});
		return { component, wrapProps, componentProps, pageEvents };
	}

	navigate(url: string, options?: Router.RouteOptions) {
		return f7.views.main.router.navigate(url, options);
	}

	back() {
		return this.hasHistory() ? f7.views.main.router.back() : this.toMain();
	}

	hasHistory() {
		const scrollHistory = Object.keys((f7.views.current as any).scrollHistory).length > 0;
		const routerHistory = f7.views.current.history.length > 1;
		return routerHistory && scrollHistory;
	}

	toMain(options?: Router.RouteOptions) {
		this.navigate("/", options);
	}

	toVisit(visitId: ID) {
		this.navigate(`/${this.paths.visit}/${visitId}`);
	}

	toOutlet(outletId: ID, options?: Router.RouteOptions) {
		this.navigate(`/${this.paths.outlet}/${outletId}`, { ...options });
	}

	toFeedback(context?: FeedbackContext) {
		this.navigate(`/${this.paths.feedback}/`, { props: { context } });
	}

	toFilterPage() {
		this.navigate(`/${this.paths.filter}/`);
	}

	toSearchPage() {
		//todo search page
		this.navigate(`/${this.paths.search}/`);
	}

	toQst(outletId: ID, qstID: ID, context?: any) {
		this.navigate(`/outlet/${outletId}/qst/${qstID}`, { context });
	}

	toChangePassword() {
		this.navigate(`/${this.paths.changePassword}/`);
	}

	toChangeLanguage() {
		this.navigate(`/${this.paths.changeLanguage}/`);
	}

	toAbout() {
		this.navigate(`/${this.paths.about}/`);
	}

	toReport(context: ReportPageContext) {
		this.navigate(`/${this.paths.report}/`, {
			context: {
				visit: context.visit,
				visitManager: context.visitManager,
				outlet: context.outlet,
				onAnswerClick: context.onAnswerClick,
				open: context.open
			}
		});
	}

	toSignature(context: SignaturePadPageContext) {
		this.navigate(`/${this.paths.report}/signature`, {
			context: {
				onEnd: context.onEnd,
				img: context.img
			}
		});
	}

	toUnsentReport(context: UnsentReportPageContext) {
		this.navigate(`/${this.paths.unsentReport}/`, {
			context: {
				visitData: context.visitData,
				open: context.open,
				isCompleted: context.isCompleted
			}
		});
	}

	toCamera(options?: Router.RouteOptions) {
		this.navigate(`/${this.paths.camera}/`, options);
	}

	toPrivacyPolicy() {
		this.navigate(`/${this.paths.privacyPolicy}/`);
	}

	subscribe<T extends keyof NavigationManagerEvents>(event: T, callback: (arg: NavigationManagerEvents[T]) => any) {
		return this.events.on(event, callback);
	}
}
