import { SkeletonItemStyles, skeletonClass } from "./SkeletonItem";
import React from "react";
import { useStyles } from "hooks/useStyles";
import { getSkeletonIconStyles } from "./SkeletonItemStyles";
import { SkeletonBlock } from "framework7-react";
import { css } from "wraps/styleCreators";

type SkeletonIconSize = "small" | "medium" | "large";

export type SkeletonIconProps = {
	size: SkeletonIconSize;
} & { [key in keyof SkeletonItemStyles]: SkeletonItemStyles[key] };

export const SkeletonIcon: React.FC<SkeletonIconProps> = props => {
	const styles = useStyles(getSkeletonIconStyles, props);
	return <SkeletonBlock className={`${skeletonClass} ${css(styles.icon)}`} />;
};

SkeletonIcon.displayName = "SkeletonIcon";
