import { CSSStyleSize, CSSStyleSizes, SizeType } from "./themeTypes";
import { DeepPartial } from "../types";
import { ThemeDefaults } from "./themeDefaults";
import { objectFromEnum } from "../utils/enum";
import * as deepExtend from "deep-extend";

export interface ThemeBorders {
	borderWidth: CSSStyleSize;
	borderWidths: CSSStyleSizes;

	borderRadius: CSSStyleSize;
	borderRadiuses: CSSStyleSizes;
}

export function createThemeBorder(
	options: DeepPartial<ThemeBorders> = {}
): ThemeBorders {
	let widthBase =
		options.borderWidth || ThemeDefaults.borders.borderWidth || "0";
	let radiusBase =
		options.borderRadius || ThemeDefaults.borders.borderRadius || "none";

	return deepExtend(
		{
			borderWidth: widthBase,
			borderRadius: radiusBase,
			borderWidths: objectFromEnum(SizeType, () => widthBase),
			borderRadiuses: objectFromEnum(SizeType, () => radiusBase)
		},
		ThemeDefaults.borders,
		options
	) as ThemeBorders;
}
