import * as React from "react";
import { radioListStyles } from "./radioListStyles";
import { ThemeContext } from "themeContext/ThemeContext";
import { Fragment } from "react";
import { QuestionListItem } from "../questionListView/QuestionListItem";
import { QuestionList } from "../questionListView/QuestionList";
import { useGroupedAnswers } from "hooks/useGroupedAnswers";
import { GroupedAnswerHeader } from "../question/common/groupedAnswerHeader/GroupedAnswerHeader";
import { t } from "quiz/QuizApp";

type RadioItemValue = string | number;

export interface RadioItem {
	value: RadioItemValue;
	title: string;
	group?: string;
}

export interface RadioListProps {
	onChange: (event: React.ChangeEvent<HTMLSelectElement>) => void;
	items: RadioItem[];
	value: RadioItemValue;
	danger?: boolean;
}

function hasSingleNoCategory<T>(group: { [key: string]: ArrayLike<T> }) {
	const g = Object.keys(group);
	return g?.length > 1 ? false : g.includes(t("No category"));
}

export const RadioList: React.FunctionComponent<RadioListProps> = React.memo(({ onChange, items, value, danger }) => {
	const theme = React.useContext(ThemeContext);
	const g = useGroupedAnswers(items);

	return (
		<Fragment>
			<style>{radioListStyles(theme, danger)}</style>
			<QuestionList>
				{Object.keys(g).map(group => {
					return (
						<Fragment key={group}>
							{!hasSingleNoCategory(g) && <GroupedAnswerHeader header={group} />}
							{g[group].map((item, idx, arr) => (
								<QuestionListItem
									radio
									key={item.value}
									title={item.title}
									value={item.value}
									onChange={onChange}
									checked={value === item.value}
									dash={arr.length > idx + 1}
									className={value === item.value ? "check" : ""}
									danger={true}
								/>
							))}
						</Fragment>
					);
				})}
			</QuestionList>
		</Fragment>
	);
});
RadioList.displayName = "RadioList";
