declare const EXT_CONFIG;
const config = typeof EXT_CONFIG == "undefined" ? {} : EXT_CONFIG;

export const CONFIG = {
	apiUrl: config.apiUrl || "https://stage.ics-it.ru/rb/test/api",
	apiVersion: "new",
	appUrl: config.appUrl || "https://stage.ics-it.ru/rb/test/quiz2",
	appMode: config.appMode || "test",
	appName: config.appName || "Quiz",
	version: config.version || shortString(window.location.href, 50),
	feedbackUrl: config.feedbackUrl || "https://ics.ics-it.ru/api/feedback",
	imageCompressQuality: config.imageCompressQuality || 0.2,
	revision: config.revision
};

export const logger = new Logger();
export const uploadHelper = new UploadHelper();
export const quizExtensionService = new QuizExtensionService();
export const cleaningService = new CleaningService(quizExtensionService.db);
export const quizQrService = new QuizQrService();
export const apiClient = new MDTApiClient(CONFIG.apiUrl, CONFIG.apiVersion, null, {
	appUrl: CONFIG.appUrl,
	onRequestError: (err, url, data) => {
		return new Promise((resolve, reject) => {
			const isMdtApiError = err instanceof MDTApiError;
			logger.log(
				"Request error",
				{
					error: err,
					url,
					data
				},
				LogType.error
			);

			if (isMdtApiError) {
				switch ((err as MDTApiError).code) {
					case "SecurityTableAccess":
						if (err?.args?.User == "Anonymous") location.reload();
						break;
					default: {
						throw err;
					}
				}
			}
			reject(err);
		});
	}
});
export const quizDataService = new QuizDataService(apiClient);
export const quizLocalStorageService = new QuizLocalStorageService();
export const quizDraftService = new QuizDraftService({
	events: { modified: [(instance: QuizDraftService) => instance.$data.changes++] },
	$initialData: { changes: 0 }
});
export const quizCompareQstService = new QuizCompareQstService();
export const quizQstService = new QuizQstService(apiClient, quizDraftService);
export const textService = new TextService(apiClient);
export const quizAuthService = new QuizAuthService(apiClient, {
	initDependency: user => {
		if (user.isAnonymous) return new Promise(resolve => resolve());
		return quizQstService.init();
	}
});
export const quizUserService = new QuizUserService(apiClient, textService, quizAuthService);

export const t = (text: string, args?: Record<string, string | number>): string => textService.t(text, args);
