	f7.views.main.router.navigate(url, options);
export const quizMetricsService = new QuizMetricsService();
export const updateManager = new UpdateManager();

type QuizAppProps = {
	onInit?(): void;
};

const appDepsReady = (state: QuizAppState) => {
	const deps = state.deps;
	const ready = Object.keys(deps).every(dep => deps[dep] === true);
	return ready;
};

export const QuizApp: React.FC<QuizAppProps> = props => {
	const [state, dispatch] = React.useReducer(quizAppReducer, initialQuizAppState);
	const [userLanguageId, setUserLanguageId] = React.useState<number>();
	localStorage.removeItem("currentTab");

	React.useEffect(() => {
		const quizUserReady = quizUserService.init();
		Promise.all([quizUserReady])
			.then(() => {
				return dispatch({ type: quizAppActions.USER_READY });
			})
			.catch(e => dispatch({ type: quizAppActions.ERROR, payload: e }));
	}, [userLanguageId]);

	React.useEffect(() => {
		const removeInvalidCache = cleaningService.removeAllInvalidOutletCache();
		const removeInvalidFiles = cleaningService.removeInvalidFiles();
		Promise.all([removeInvalidCache, removeInvalidFiles])
			.then(() => dispatch({ type: quizAppActions.DATA_READY }))
			.catch(e => dispatch({ type: quizAppActions.ERROR, payload: e }));
	}, []);

	React.useEffect(() => {
		const logVisibility = () => logger.log("app-visibility-change", { visibilityState: document.visibilityState });
		document.addEventListener("visibilitychange", logVisibility);
		return () => document.removeEventListener("visibilitychange", logVisibility);
	}, []);

	React.useEffect(() => {
		const userServiceUnsub = quizUserService.subscribe("languagechanged", id => setUserLanguageId(id));
		return () => userServiceUnsub();
	}, []);

	if (appDepsReady(state) && !state.ready) {
		dispatch({ type: quizAppActions.APP_READY });
	}

	return state.ready ? (
		<ErrorBoundary>
			<GlobalErrorHandler />
			<App
				routes={routes(state.error)}
				appParams={{
					name: CONFIG.appName,
					on: {
						init: () => {
							props.onInit && props.onInit();
							(window as any).mobileAppFacade = new MobileAppFacade(true);
						}
					},
					touch: {
						tapHold: true,
						touchClicksDistanceThreshold: 25
					}
				}}
				CONFIG={CONFIG}
			></App>
		</ErrorBoundary>
	) : (
		<SplashScreen />
	);
};
