import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { getFullscreenErrorStyles } from "./fullscreenInfoStyles";
import { css } from "wraps/styleCreators";
import { MobileActionButton } from "quiz/components/button/MobileActionButton";
import { ButtonSize, ButtonType } from "quiz/components/button/Button";
import { Text } from "quiz/components/text/Text";
import { Block } from "quiz/components/block/Block";
import { TextSizeType } from "theme/sizing";
import { InfoBlockProps } from "../InfoBlock";
import { Icon } from "quiz/components/icon/Icon";

export interface FullscreenInfoProps extends InfoBlockProps {
	fullHeight?: boolean;
	transparent?: boolean;
}

export const FullscreenInfo: React.FC<FullscreenInfoProps> = props => {
	const { colorType, title, message, icon, primaryButton, secondaryButton, fullHeight, transparent } = props;
	const styles = useStyles(getFullscreenErrorStyles, { fullHeight, colorType, transparent });
	const I = Array.isArray(icon) ? <Icon style={{ width: "100%", height: "100%" }} icon={icon} /> : icon;

	return (
		<div className={css(styles.wrap)}>
			<div className={css(styles.content)}>
				<div className={css(styles.iconWrap)}>
					<div>{I}</div>
				</div>
				<Block styles={styles.title}>
					<Text bold size={TextSizeType.title}>
						{title}
					</Text>
				</Block>
				<Block styles={styles.description}>
					<Text size={TextSizeType.body}>{message}</Text>
				</Block>
				{props.primaryButton && (
					<div className={css(styles.primaryButton)}>
						<MobileActionButton size={ButtonSize.large} colorType={colorType} {...primaryButton} />
					</div>
				)}
				{props.secondaryButton && (
					<div className={css(styles.secondaryButton)}>
						<MobileActionButton
							size={ButtonSize.large}
							type={ButtonType.link}
							colorType={colorType}
							{...secondaryButton}
						/>
					</div>
				)}
			</div>
		</div>
	);
};
