import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";
import { CustomColors } from "utils/constants";

export const getCameraNavbarStyles = (theme: Theme) => {
	return {
		wrap: {
			position: "relative",
			width: "100%",
			backgroundColor: CustomColors.black
		} as CSSProp,
		iconWrap: {
			position: "relative",
			paddingTop: scale(theme.sizing.sizes.xs, ScaleEnum.Triple),
			paddingBottom: scale(theme.sizing.sizes.xs, ScaleEnum.Triple),
			paddingLeft: theme.constants.gridPadding
		} as CSSProp
	};
};
