import * as React from "react";
import { css, StyleDeclarationValue } from "wraps/styleCreators";
import { TActions } from "quiz/components/actions/Actions";
import { Gallery, GalleryProps } from "./Gallery";
import { ColorTypes } from "theme/colors";
import { ActionSheetItem } from "quiz/components/actionSheetItem/ActionSheetItem";
import { t } from "quiz/QuizApp";
import { QuizActions } from "quiz/components/actions/QuizActions";

export interface GalleryControlProps extends Omit<GalleryProps, "onLongTap"> {
	styles?: StyleDeclarationValue;
}

export const GalleryControl: React.FunctionComponent<GalleryControlProps> = React.memo(props => {
	const actionsRef = React.useRef<TActions>();
	const imgIdx = React.useRef<number>();

	return (
		<div>
			{props.images != null && (
				<div className={css(props.styles)}>
					<Gallery
						{...props}
						onLongTap={idx => {
							imgIdx.current = idx;
							actionsRef.current && actionsRef.current.open();
						}}
					/>
				</div>
			)}
			{!props.readonly && props.children}
			{!props.readonly && (
				<QuizActions
					ref={actionsRef}
					actions={() => (
						<>
							<ActionSheetItem
								text={t("Delete")}
								icon={["far", "trash-alt"]}
								colorType={ColorTypes.danger}
								onClick={() => props.onImageClick && props.onImageClick(imgIdx.current)}
							/>
						</>
					)}
				/>
			)}
		</div>
	);
});
GalleryControl.displayName = "GalleryControl";
