import * as React from "react";
import { FullscreenInfo, FullscreenInfoProps } from "./FullscreenInfo";
import { Page } from "quiz/components/page/Page";
import { QuizNavBar } from "../../quizNavBar/QuizNavBar";
import { t } from "quiz/QuizApp";

export const FullscreenErrorPage: React.FC<FullscreenInfoProps> = props => {
	return (
		<Page>
			<QuizNavBar title={t(props.title)} />
			<FullscreenInfo {...props}></FullscreenInfo>
		</Page>
	);
};
