import NanoEvents from "nanoevents";
import { quizMetricsService } from "quiz/QuizApp";

interface LoggerEvents {
	preLog: LogItem;
	log: LogItem;
	clear: void;
}

export enum LogType {
	info = "info",
	warning = "warning",
	error = "error"
}

type LogCode = string;

type LogData = {
	[prop: string]: any;
	message?: any;
	context?: string;
	stage?: string;
};

export interface LogItem {
	code: LogCode;
	data: LogData;
	type: LogType;
	time: number;
}

export class Logger {
	private logData = [];
	private events = new NanoEvents<LoggerEvents>();
	private logStack = {};

	log(code: LogCode, data?: LogData, type: LogType = LogType.info) {
		const timestamp = new Date().getTime();
		const logData: LogData = {
			...data,
			...(type === LogType.error || data?.error
				? { stack: data?.error?.stack?.toString() || data?.error?.reason?.stack?.toString() }
				: {}),
			...(data?.error ? { error: data.error.reason?.toString() || data.error.toString() } : {})
		};

		const item: LogItem = {
			code,
			data: logData,
			type,
			time: timestamp
		};

		this.events.emit("preLog", item);
		this.logData.push(item);

		quizMetricsService.set({ ...item }); //toDO: refactor
		this.events.emit("log", item);
	}

	intervalLogStart(logName) {
		this.logStack[logName] = {
			start: new Date().getTime()
		};
	}

	intervalLogEnd(logCode, logName, data) {
		const log = this.logStack[logName];
		if (!log) {
			return;
		}
		const startTime = log.start;
		const spentTime = new Date().getTime() - startTime;
		const logData = {
			...data,
			spentTime
		};
		this.log(logCode, logData);
		delete this.logStack[logName];
	}

	getLog() {
		return this.logData;
	}

	clearLog() {
		this.logData = [];
		this.events.emit("clear", void 0);
	}

	subscribe<U extends keyof LoggerEvents>(event: U, callback: (arg: LoggerEvents[U]) => any) {
		return this.events.on(event, callback);
	}
}
