import { MDTApiClient, MDTRecordId, MDTRequestSettings } from "mdtApiClient/mdtApiClient";

export class ApiClientUser {
	id: MDTRecordId;
	permissions: { [permission: string]: number | Object };
	fullName: string;
	email: string;
	identityName: string;
	isAdmin: boolean;
	isAnonymous: boolean;
	roles: string[];
	languageId: number;

	isImpersonated: boolean;
}

export class ApiClientAuth {
	constructor(private apiClient: MDTApiClient) {}

	private request(url: string, data?: Object, settings: MDTRequestSettings = {}, requestOptions: RequestInit = {}) {
		return this.apiClient.request(url, data, settings, requestOptions);
	}

	async signIn(login: string, password: string, rememberMe?: boolean) {
		return this.request("security/signin", { login, password, rememberMe });
	}

	async signOut() {
		return this.request("security/signout");
	}

	async changePassword(data: { oldPassword: string; newPassword: string }) {
		return this.request("security/ChangePassword", data);
	}

	async resetPassword(data: { userId: number; newPassword: string }) {
		return this.request("security/ResetPassword", data);
	}

	async impersonate(id?: number) {
		return this.request("security/impersonate", { id });
	}

	async user(): Promise<ApiClientUser> {
		return this.request("mdt/user", void 0, { camel: true }).then(r => r.json());
	}
}
