import * as React from "react";
import { ActionItem, useActionItem } from "quiz/components/actionItem/actionItem";
import { MobileButton, MobileButtonProps, F7ButtonPartialProps } from "./MobileButton";
import { filterProps } from "utils/helpers";

const restrictedButtonProps = ["icon", "iconProps", "onClick", "enabled", "colorType", "command", "title"] as const;

export interface MobileActionButtonProps
	extends ActionItem,
		Omit<MobileButtonProps, typeof restrictedButtonProps[number]> {}

export const MobileActionButton: React.FunctionComponent<MobileActionButtonProps> = function MobileActionButton(props) {
	const action = useActionItem(props);
	const btnProps = filterProps(props, restrictedButtonProps, false);

	return (
		action.visible() && (
			<MobileButton
				icon={action.icon()}
				iconProps={action.iconProps()}
				onClick={action.clickHandler}
				enabled={action.enabled()}
				colorType={action.colorType()}
				{...btnProps}
			>
				{action.title || props.children}
			</MobileButton>
		)
	);
};

MobileActionButton.displayName = "MobileActionButton";
