import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { ThemeContext } from "themeContext/ThemeContext";
import { css } from "wraps/styleCreators";
import { Text } from "quiz/components/text/Text";
import { TextSizeType } from "theme/sizing";
import { getQstListItemViewStyles } from "./qstListItemViewStyles";
import { t } from "quiz/QuizApp";
import { Icon } from "quiz/components/icon/Icon";

export type QstListItemViewProps = {
	name: string;
	description: string;
	flagDisable?: boolean;
	lastVisit?: string;
};

export const QstListItemView: React.FC<QstListItemViewProps> = React.memo(
	({ name, description, flagDisable, lastVisit }) => {
		const styles = useStyles(getQstListItemViewStyles);
		const theme = React.useContext(ThemeContext);

		return (
			<>
				<div slot="inner-start" className={css(styles.listItem)}>
					<div className={css(styles.titleWrap)}>
						<Text muted={flagDisable} styles={styles.titleText}>
							{name}
						</Text>
					</div>
					{description && (
						<div className={css(styles.descriptionWrap)}>
							<Text muted size={TextSizeType.caption}>
								{description}
							</Text>
						</div>
					)}
					<div className={css(styles.lastVisitWrap)}>
						<div className={css(styles.lastVisitIconWrap)}>
							<Icon color={theme.colors.base.texts.muted} icon={["far", "history"]} />
						</div>
						<div className={css(styles.lastVisitTextWrap)}>
							<Text size={TextSizeType.caption}>{lastVisit || t("No visits")}</Text>
						</div>
					</div>
				</div>
				{flagDisable && (
					<div>
						<Icon color={theme.colors.danger.main} icon={["far", "lock-alt"]} />
					</div>
				)}
			</>
		);
	}
);

QstListItemView.displayName = "QstListItemView";
