import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { ButtonProps, ButtonSize, ButtonType } from "./Button";

export function getButtonStyles(theme: Theme, props: ButtonProps) {
	const colors = theme.colors[props.colorType] || theme.colors.primary;
	const vars = {
		button: {
			width: "fit-content",
			cursor: "pointer",
			fontSize: theme.sizing.fontSizes.body,
			lineHeight: theme.sizing.fontSizes.body,
			borderRadius: theme.sizing.sizes.xs,
			background: colors.main,
			color: colors.texts.contrast,
			padding: `${theme.sizing.sizes.xs} ${theme.sizing.sizes.xs}`,
			":hover": {
				background: colors.hover,
				color: colors.texts.contrast
			},
			":focus": {
				background: colors.focus,
				color: colors.texts.contrast
			},
			":active": {
				background: colors.active,
				color: colors.texts.contrast
			}
		},
		innerWrap: {
			height: theme.sizing.sizes.lg
		},
		textWrap: {
			height: theme.sizing.sizes.lg,
			margin: `0 ${theme.sizing.sizes.xs}`
		},
		text: {
			color: "inherit"
		},
		iconWrap: {
			display: "flex",
			alignItems: "center",
			width: theme.sizing.sizes.lg,
			height: theme.sizing.sizes.lg
		},
		icon: {
			color: "inherit",
			fontSize: theme.sizing.sizes.md
		}
	};

	if (props.size == ButtonSize.large) {
		vars.button.padding = `${theme.sizing.sizes.sm} ${theme.sizing.sizes.md}`;
	}
	if (props.type == ButtonType.link) {
		vars.button.padding = "0";
		const textColor = props.colorType ? colors : theme.colors.base;
		vars.button.background = "transparent";
		vars.button.color = textColor.text;
		vars.button[":hover"] = {
			background: "transparent",
			color: textColor.hover
		};
		vars.button[":focus"] = {
			background: "transparent",
			color: textColor.focus
		};
		vars.button[":active"] = {
			background: "transparent",
			color: textColor.active
		};
	}
	if (props.enabled === false) {
		const textColor = theme.colors.base.texts.muted;
		const bgColor = props.type == ButtonType.link ? "transparent" : colors.disabled;
		vars.button.cursor = "not-allowed";
		vars.button.background = bgColor;
		vars.button.color = textColor;
		vars.button[":hover"] = {
			background: bgColor,
			color: textColor
		};
		vars.button[":focus"] = {
			background: bgColor,
			color: textColor
		};
		vars.button[":active"] = {
			background: bgColor,
			color: textColor
		};
	}
	if (props.fullWidth) {
		vars.button.width = "100%";
	}

	return {
		button: {
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			border: "none",
			outline: "none",
			...vars.button
		} as CSSProp,
		innerWrap: {
			display: "flex",
			...vars.innerWrap
		} as CSSProp,
		textWrap: {
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			overflow: "hidden",
			...vars.textWrap
		} as CSSProp,
		text: {
			...vars.text
		},
		iconLeft: {
			justifyContent: "flex-start",
			...vars.iconWrap
		} as CSSProp,
		iconRight: {
			justifyContent: "center",
			...vars.iconWrap
		} as CSSProp,
		icon: {
			...vars.icon
		}
	};
}
