import { CSSProp } from "wraps/styleCreators";

const opacityFadeInKeyframes = {
	from: {
		opacity: 0
	},

	to: {
		opacity: 1
	}
};

const opacityFadeOutKeyframes = {
	from: {
		opacity: 1
	},

	to: {
		opacity: 0
	}
};

export const getAnimationStyles = () => {
	return {
		fadeIn: {
			opacity: 0,
			animationName: [opacityFadeInKeyframes],
			animationDuration: "500ms",
			animationIterationCount: 1,
			animationTimingFunction: "cubic-bezier(0.17, -0.67, 0.83, 0.67)",
			animationFillMode: "forwards"
		} as CSSProp,
		fadeOut: {
			animationName: [opacityFadeOutKeyframes],
			animationDuration: "1s, 1200ms",
			animationIterationCount: 1,
			animationTimingFunction: "cubic-bezier(0.17, -0.67, 0.83, 0.67)"
		}
	};
};

export const getAnimationDelay = (count: number) => ({ animationDelay: `${count * 60}ms` });
