import { Theme } from "theme/theme";
import { FieldControlLayoutProps } from "./FieldControlLayout";
import { getCSSSizeValue } from "utils/cssHelpers";
import { CSSProp } from "wraps/styleCreators";

export function getFieldControlLayoutStyles(theme: Theme, props: FieldControlLayoutProps) {
	const vars = {
		labelColor: props.colorType ? theme.colors[props.colorType].text : theme.colors.base.texts.muted,
		labelFontSize: theme.sizing.fontSizes.caption,
		labelLineHeight: theme.sizing.fontSizes.caption,
		labelEmptyValueFontSize: theme.sizing.fontSizes.body,
		labelEmptyValueLineHeight: theme.sizing.lineHeights.body,
		labelTransition: theme.transitions.base,
		labelSecondaryColor: theme.colors.base.texts.muted,
		inputPadding: theme.sizing.sizes.xxs,
		descriptionColor: props.descriptionColorType
			? theme.colors[props.descriptionColorType].text
			: theme.colors.base.texts.muted
	};

	return {
		wrap: {
			position: "relative",
			overflow: "hidden"
		} as CSSProp,
		top: {
			display: "flex",
			justifyContent: "space-between"
		} as CSSProp,
		label: {
			height: vars.labelLineHeight,
			whiteSpace: "nowrap",
			lineHeight: vars.labelLineHeight
		} as CSSProp,
		labelText: {
			color: vars.labelColor,
			transition: vars.labelTransition,
			position: "absolute",
			top: 0,
			lineHeight: vars.labelLineHeight
		} as CSSProp,
		labelTextEmptyValue: {
			pointerEvents: "none",
			//base label height + inner padding
			top: getCSSSizeValue(vars.labelLineHeight as string) + getCSSSizeValue(vars.inputPadding),
			fontSize: vars.labelEmptyValueFontSize,
			lineHeight: vars.labelEmptyValueLineHeight
		} as CSSProp,
		labelSecondary: {
			height: vars.labelLineHeight,
			whiteSpace: "nowrap",
			lineHeight: vars.labelLineHeight
		} as CSSProp,
		labelSecondaryText: {
			color: vars.labelSecondaryColor,
			position: "absolute",
			top: 0,
			right: 0,
			lineHeight: vars.labelLineHeight
		} as CSSProp,
		bottom: {},
		description: {
			color: vars.descriptionColor
		}
	};
}
