import { Theme } from "theme/theme";
import { ProgressBlockState } from "./ProgressBlock";
import { scale, ScaleEnum } from "utils/cssHelpers";
import { CSSProp } from "wraps/styleCreators";

export const getProgressBlockStyles = (theme: Theme, status: ProgressBlockState) => {
	const colorMap = {
		success: theme.colors.success.main,
		invalid: theme.colors.danger.main,
		unchanged: theme.colors.base.border,
		viewed: theme.colors.primary.main
	};

	return {
		wrap: {
			height: theme.sizing.sizes.xs,
			borderRadius: scale(theme.sizing.sizes.xs, ScaleEnum.Half),
			flexGrow: 1,
			marginRight: theme.sizing.sizes.xs,
			backgroundColor: colorMap[status],
			position: "relative"
		} as CSSProp,
		arrow: {
			position: "absolute",
			top: "-8px",
			left: "50%",
			transform: "translateX(-50%)",
			width: 0,
			height: 0,
			borderLeft: "3px solid transparent",
			borderRight: "3px solid transparent",
			borderTop: `4px solid ${theme.colors.primary.main}`
		} as CSSProp
	};
};
