import { f7 } from "framework7-react";
import { applyCssVars } from "utils/cssHelpers";
import { quizExtensionService } from "quiz/QuizApp";
import { CustomColors } from "utils/constants";

declare global {
	interface Window {
		StatusBar: { styleDefault(): void; styleLightContent(): void; backgroundColorByName(name: string): void };
	}
}

const textColorWhite = "white";
const bgColorGray = "#80FFFFFF";

export class QuizStatusBar {
	public static defaultStatusBarColor = "#ffffff";

	hide(): void {
		return f7.statusbar.hide();
	}

	show(): void {
		return f7.statusbar.show();
	}

	setLightStyle(): void {
		if (window.StatusBar) {
			window.StatusBar?.styleDefault();
			quizExtensionService.customizeStatusBar();
		}
		applyCssVars({ statusBarBackgroundColor: QuizStatusBar.defaultStatusBarColor });
		//todo
	}

	setDarkStyle(): void {
		if (window.StatusBar) {
			window.StatusBar.styleLightContent();
			applyCssVars({ statusBarBackgroundColor: CustomColors.black });
		}
	}

	setFullScreen(): void {
		applyCssVars({
			"--statusBarIndent": "0px"
		});
	}

	setGrayStyle(): void {
		f7.statusbar.setBackgroundColor(bgColorGray);
		f7.statusbar.setTextColor(textColorWhite);
	}
}
