import { BlockTitle } from "quiz/components/block/Block";
import { OutletItem } from "quiz/components/outletItem/OutletItem";
import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { t } from "quiz/QuizApp";
import { QuizList } from "../quizList/QuizList";
import { getVisitedOutletStyles } from "./visitedOutletStyles";
import { OutletFullData, ID } from "quiz/services/models";
import { css } from "wraps/styleCreators";
import { useAnimation } from "hooks/useAnimation";
import { formatDateTime, compareStringDate, getAnimationKey } from "utils/helpers";
import { VisitedOutletsSkeleton } from "../quizSkeleton/skeletons/VisitedOutletsSkeleton";
import { QuizFade } from "../fade/QuizFade";

interface VisitedOutletsProps {
	visits: ViewVisitInfo[];
	requestError: any;
	onOutletClick: (idVisit: ID) => void;
}

export interface ViewVisitInfo extends OutletFullData {
	idVisit: ID;
}

export const VisitedOutlets: React.FC<VisitedOutletsProps> = ({ visits, requestError, onOutletClick }) => {
	const styles = useStyles(getVisitedOutletStyles);
	const animation = useAnimation();

	const gHash: [string, ViewVisitInfo[]][] = React.useMemo(() => {
		const groupHash: { [key: string]: ViewVisitInfo[] } = {};
		visits?.forEach(visit => {
			const key = formatDateTime(visit.lastVisit.dateEnd);
			const gCode = key === formatDateTime(new Date()) ? t("Today") : key;
			if (!groupHash[gCode]) groupHash[gCode] = [];
			groupHash[gCode].push(visit);
		});
		return Object.entries(groupHash).sort((a, b) => compareStringDate(b[0], a[0]));
	}, [visits]); //toDO: to hook?

	const [isEmpty, isLoading] = [visits?.length == 0, !visits?.length];
	const fadeAnimationKey = getAnimationKey(!isEmpty && isLoading, [requestError?.message]);

	return (
		<QuizFade animationKey={fadeAnimationKey}>
			<div className={css(styles.visited)}>
				{gHash?.length > 0 ? (
					gHash?.map(el => {
						const title = el[0];
						const dayVisit: ViewVisitInfo[] = el[1];
						return (
							<div key={el[0]}>
								{title && (
									<>
										<BlockTitle title={title} counter={dayVisit.length} />
										<QuizList
											isEmpty={false}
											isLoading={false}
											errors={[requestError]}
											separateErrorBlock
										>
											{dayVisit &&
												dayVisit.map((visit, idx) => {
													return (
														<OutletItem
															key={idx}
															outlet={{
																...visit,
																distance: null,
																time: visit.lastVisit.dateEnd
															}}
															onClick={() => onOutletClick(visit.idVisit)}
															style={animation.animationDelay(idx)}
														/>
													);
												})}
										</QuizList>
									</>
								)}
							</div>
						);
					})
				) : (
					<QuizList
						isEmpty={isEmpty}
						isLoading={!isEmpty && isLoading}
						emptyList={{ separateBlock: true }}
						errors={[requestError]}
						loading={() => <VisitedOutletsSkeleton totalBlocks={10} />}
					/>
				)}
			</div>
		</QuizFade>
	);
};
VisitedOutlets.displayName = "VisitedOutlets";
