import { CSSProp } from "wraps/styleCreators";
import { iconsSize } from "../cameraIconBar/CameraIconBar";
import { Theme } from "theme/theme";

export const getTakePhotoButtonStyles = (theme: Theme) => {
	const common: CSSProp = {
		position: "absolute",
		borderRadius: "50%"
	};

	return {
		outerCircle: {
			...common,
			height: iconsSize,
			width: iconsSize,
			backgroundColor: "hsla(0,0%,100%,.4)",
			zIndex: 1
		} as CSSProp,
		innerCircle: {
			...common,
			left: "50%",
			top: "38px",
			height: "44px",
			width: "44px",
			background: "#fff",
			margin: "-22px 0 0 -22px",
			zIndex: 2
		} as CSSProp,
		wrap: {
			position: "relative",
			height: iconsSize,
			width: iconsSize
		} as CSSProp
	};
};
