import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { css } from "wraps/styleCreators";
import { getProgressBlockStyles } from "./progressBlockStyles";

export enum ProgressBlockState {
	Success = "success",
	Invalid = "invalid",
	Unchanged = "unchanged",
	Viewed = "viewed"
}

export type ProgressBlockProps = {
	status: ProgressBlockState;
};

export const ProgressBlock: React.FC<ProgressBlockProps> = React.memo(props => {
	const styles = useStyles(getProgressBlockStyles, props.status);
	return (
		<div className={css(styles.wrap)}>
			{props.status == ProgressBlockState.Viewed ? <div className={css(styles.arrow)}></div> : null}
		</div>
	);
});
ProgressBlock.displayName = "ProgressBlock";
