import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getForgotPasswordPageStyles = (theme: Theme) => {
	return {
		forgotPage: {
			height: "100%",
			display: "flex",
			flexFlow: "column"
		} as CSSProp,
		description: {
			marginBottom: theme.sizing.sizes.xl,
			color: theme.colors.base.hover
		} as CSSProp,
		error: {
			color: theme.colors.danger.main,
			marginBottom: "0"
		} as CSSProp
	};
};
