import { useNavigatorOnLine } from "hooks/useNavigatorOnline";
import React from "react";
import { ActionItem } from "../actionItem/actionItem";
import { MobileActionButton, MobileActionButtonProps } from "./MobileActionButton";
import { WrappedMobileActionButton } from "./WrappedMobileActionButton";

interface ServerInteractionButtonProps extends MobileActionButtonProps {
	wrap?: boolean;
}

export const ServerInteractionButton: React.FC<ServerInteractionButtonProps> = props => {
	const { wrap, ...btnProps } = props;
	const online = useNavigatorOnLine();
	const ButtonComponent = wrap ? WrappedMobileActionButton : MobileActionButton;

	const enabled = React.useCallback(
		(action: ActionItem) => {
			const isEnabled = props.enabled instanceof Function ? props.enabled(action) : enabled;
			return isEnabled && online;
		},
		[props.enabled, online]
	);

	return <ButtonComponent {...btnProps} enabled={enabled}></ButtonComponent>;
};
