import * as React from "react";
import { TakePhotoButton } from "../takePhotoButton/TakePhotoButton";
import { SwitchCameraButton } from "../switchCameraButton/SwitchCameraButton";
import { css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import { getCameraIconBarStyles } from "./cameraIconBarStyles";

export const iconsSize = "75px";

type CameraIconBarProps = {
	onTakePhoto: () => void;
	onSwitchCamera: () => void;
};

export const CameraIconbar: React.FC<CameraIconBarProps> = React.memo(props => {
	const style = useStyles(getCameraIconBarStyles);

	return (
		<div className={css(style.wrap)}>
			<div className={css(style.row)}>
				<div className={css(style.tab)}></div>
				<div className={css(style.takePhoto)}>
					<TakePhotoButton onClick={() => props.onTakePhoto()} />
				</div>
				<div className={css(style.tab)}>
					<div className={css(style.switchCameraWrap)}>
						<SwitchCameraButton onClick={() => props.onSwitchCamera()} />
					</div>
				</div>
			</div>
		</div>
	);
});
CameraIconbar.displayName = "CameraIconBar";
