import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { ControlLayoutProps } from "./ControlLayout";

export function getControlLayoutStyles(theme: Theme, props: ControlLayoutProps) {
	const vars = {
		transition: theme.transitions.base,
		padding: theme.sizing.sizes.xxs,
		inputFontSize: theme.sizing.fontSizes.body,
		inputLineHeight: theme.sizing.lineHeights.body,
		inputFontFamily: theme.typography.fonts.base,
		inputColor: theme.colors.base.text,
		borderWidth: theme.borders.borderWidth,
		borderColor: props.colorType ? theme.colors[props.colorType].main : theme.colors.base.texts.muted,
		borderColorActive: props.colorType ? theme.colors[props.colorType].active : theme.colors.info.main,
		clearIconWrapSize: theme.sizing.sizes.lg,
		clearIconSize: theme.sizing.sizes.md,
		clearIconColor: theme.colors.base.texts.muted,
		clearIconColorHover: theme.colors.info.main
	};

	return {
		wrap: {
			display: "flex",
			borderBottom: `${vars.borderWidth} solid ${vars.borderColor}`,
			transition: vars.transition
		} as CSSProp,
		wrapActive: {
			borderBottom: `${vars.borderWidth} solid ${vars.borderColorActive}`
		},
		left: {
			paddingTop: vars.padding,
			paddingBottom: vars.padding
		},
		right: {
			paddingTop: vars.padding,
			paddingBottom: vars.padding
		},
		center: {
			display: "flex",
			flexGrow: 1,
			paddingTop: vars.padding,
			paddingBottom: vars.padding
		} as CSSProp,
		control: {
			fontSize: vars.inputFontSize,
			lineHeight: vars.inputLineHeight,
			fontFamily: vars.inputFontFamily,
			width: "100%",
			outline: "none",
			border: "none",
			padding: 0,
			background: "transparent",
			color: vars.inputColor
		} as CSSProp,
		clearWrap: {
			height: vars.clearIconWrapSize,
			width: vars.clearIconWrapSize,
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			cursor: "pointer"
		} as CSSProp,
		icon: {
			color: vars.clearIconColor,
			transition: vars.transition,
			":hover": {
				color: vars.clearIconColorHover
			}
		}
	};
}
