import { f7 } from "framework7-react";
import { useCancellablePromise } from "hooks/useCancellablePromise";
import * as React from "react";
import { QuizError } from "../errors/QuizError";

interface PageWrapProps<T = any> {
	isState: boolean;
	loading?: () => React.ReactElement;
	fetchData: (a: AbortSignal) => Promise<T>;
	onSuccess: (x: T) => void;
	onError?: (err?: any) => void;
	errorBlock?: (err?: Error) => React.ReactElement;
	deps?: any[];
}

export const PageWrap: React.FC<PageWrapProps> = props => {
	const [error, setError] = React.useState();
	const [isLoading, setLoading] = React.useState<boolean>();

	// const show = React.useCallback(() => (props.loading ? setLoading(true) : f7.preloader.show("main")), []);
	const show = React.useCallback(() => (props.loading ? setLoading(true) : <div></div>), []); //todo ?
	const hide = React.useCallback(() => (props.loading ? setLoading(false) : f7.preloader.hide()), []);

	useCancellablePromise(
		a => {
			show();
			return props.fetchData(a);
		},
		{
			onSuccess: x => {
				hide();
				props.onSuccess(x);
			},
			onError: err => {
				hide();
				setError(err);
				props.onError && props.onError(err);
			},
			deps: props.deps
		}
	);

	const errorBlock = () => {
		if (props.errorBlock != null) {
			return props.errorBlock(error);
		}
		return <QuizError error={error} />;
	};

	return <>{isLoading ? props.loading() : error ? errorBlock() : props.isState && props.children}</>;
};
PageWrap.displayName = "PageWrap";
