import * as React from "react";
import SignatureCanvas from "react-signature-canvas";
import { ThemeContext } from "themeContext/ThemeContext";
import { useStyles } from "hooks/useStyles";
import { getSignaturePadStyles } from "./signaturePadStyles";
import { css } from "wraps/styleCreators";
import { ButtonSize, ButtonType } from "quiz/components/button/Button";
import { ColorTypes } from "theme/colors";
import { MobileActionButton } from "quiz/components/button/MobileActionButton";
import { t, nav, quizAuthService } from "quiz/QuizApp";
import { getCSSSizeValue } from "utils/cssHelpers";
import { FullscreenErrorPage } from "../infoBlock/fullscreenInfoBlock/FullscreenErrorPage";
import { accessDeniedIcon } from "../infoScreenIcons/accessDeniedIcon";
import { FeedbackTypeEnum } from "quiz/components/feedback/Feedback";
import { f7 } from "framework7-react";
import * as loadImage from "blueimp-load-image";

export type SignaturePadPageContext = {
	onEnd: (base64: string) => void;
	img?: string;
};

export type SignatureProps = {
	emptyText?: string;
	img?: string;
	width?: number;
	onEnd: (img: string) => void;
	context?: SignaturePadPageContext;
};

const signatureHeightHard = 240;
const smallScreen = document.body.clientWidth < 768;

const rotateImg = async (img: string, afterRotateCb) => {
	const canvas = await loadImage(img, {
		orientation: 6
	});
	afterRotateCb(canvas.image);
};

let mountedCb;
export const onSignaturePadPageMounted = () => {
	mountedCb();
};

let scalling = false;

export const SignaturePad: React.FC<SignatureProps> = props => {
	const styles = useStyles(getSignaturePadStyles, smallScreen);
	const theme = React.useContext(ThemeContext);

	const sign = React.useRef<SignatureCanvas>();
	const signatureDivRef = React.useRef<HTMLDivElement>();

	const [signatureEmpty, setSignatureEmpty] = React.useState<boolean>(true);
	const [signatureWidth, setSignatureWidth] = React.useState<number>();
	const [signatureHeight, setSignatureHeight] = React.useState<number>();
	const [mounted, setMounted] = React.useState(false);
	mountedCb = () => setMounted(true);

	const ratio = Math.max(window.devicePixelRatio || 1, 1);
	const width = props.width || document.body.clientWidth;

	const canvasSizes = () => {
		if (!smallScreen) {
			return {
				height: signatureHeightHard * ratio + 5,
				width: width * ratio - getCSSSizeValue(theme.constants.gridPadding) * 4
			};
		}

		return {
			height: signatureHeight * ratio || 0,
			width: signatureWidth * ratio || 0
		};
	};

	const drawImage = React.useCallback((img, ctx) => {
		ctx.drawImage(img, 0, 0);
		ctx.scale(ratio, ratio);
		setSignatureEmpty(false);
	}, []);

	React.useEffect(() => {
		if (!props.context) {
			return void 0;
		}

		const canvas = sign.current.getCanvas();
		const ctx = canvas.getContext("2d");

		setSignatureWidth(signatureDivRef.current.clientWidth);
		setSignatureHeight(signatureDivRef.current.clientHeight);

		if (props.context?.img && mounted && signatureWidth && signatureHeight) {
			if (smallScreen) {
				rotateImg(props.context?.img, img => {
					drawImage(img, ctx);
				});
			} else {
				const img = new Image();
				img.onload = function() {
					drawImage(img, ctx);
				};
				img.src = props.context?.img;
			}
		} else if (!props.context?.img && scalling) ctx.scale(ratio, ratio);

		sign.current.on();

		return () => {
			sign.current.off();
		};
	}, [mounted]);

	if (!props.context) {
		return (
			<FullscreenErrorPage
				icon={accessDeniedIcon}
				title={t("Access error")}
				message={t("Access denied")}
				colorType={ColorTypes.danger}
				primaryButton={{
					title: t("Complain"),
					icon: ["far", "comment-alt-lines"],
					command: () => nav.toFeedback({ type: FeedbackTypeEnum.Problem, value: t("Access denied") })
				}}
				secondaryButton={{
					title: t("Logout"),
					icon: ["far", "sign-out-alt"],
					command: async () => {
						await quizAuthService.signOut();
						f7.views.main.router.navigate("/");
						f7.views.main.router.refreshPage();
					}
				}}
			/>
		);
	}

	return (
		<>
			<style>
				{`.signature canvas {
				width: 100%;
				height: 100%;
				box-sizing: border-box;
			}`}
			</style>
			<div className={css(styles.content)}>
				<div className={css(styles.padWrap)}>
					<div ref={signatureDivRef} className={css(styles.signatureWrap)}>
						<div className="signature">
							<SignatureCanvas
								ref={sign}
								onBegin={() => {
									setSignatureEmpty(false);
									if (signatureEmpty && !scalling && smallScreen) {
										const canvas = sign.current.getCanvas();
										const ctx = canvas.getContext("2d");
										ctx.scale(ratio, ratio);
										scalling = true;
									}
								}}
								canvasProps={{ ...canvasSizes() }}
							/>
						</div>
					</div>
					<div className={css(styles.buttons)}>
						<div className={css(styles.buttonClear)}>
							<MobileActionButton
								type={ButtonType.transparent}
								colorType={ColorTypes.info}
								size={ButtonSize.large}
								title={t("Clear")}
								icon={["far", "eraser"]}
								command={() => {
									sign.current.clear();
									setSignatureEmpty(true);
								}}
							/>
						</div>
						<div className={css(styles.buttonSave)}>
							<MobileActionButton
								type={ButtonType.normal}
								colorType={ColorTypes.info}
								size={ButtonSize.large}
								title={t("Save")}
								icon={["far", "signature"]}
								command={async () => {
									let source = sign.current;
									if (smallScreen && !signatureEmpty) {
										const canvas = await loadImage(sign.current.toDataURL(), {
											canvas: true,
											orientation: 8
										});
										source = canvas.image;
									}
									!signatureEmpty
										? props.context?.onEnd(source.toDataURL())
										: props.context?.onEnd(null);
									nav.back();
								}}
							/>
						</div>
					</div>
				</div>
			</div>
		</>
	);
};
