import * as React from "react";
import { css } from "wraps/styleCreators";
import { useAnimation } from "hooks/useAnimation";
import { nav, t, quizDataService } from "quiz/QuizApp";
import { QuizList } from "quiz/components/quizList/QuizList";
import { OutletFullData } from "quiz/services/models";
import { OutletItem } from "quiz/components/outletItem/OutletItem";
import { OutletSkeleton } from "quiz/components/quizSkeleton/skeletons/OutletSkeleton";
import { BlockTitle } from "quiz/components/block/Block";
import { useCancellablePromise } from "hooks/useCancellablePromise";
import { usePosition } from "hooks/usePosition";
import { QuizFade } from "quiz/components/fade/QuizFade";
import { getAnimationKey } from "utils/helpers";
import { Hints } from "./hints/Hints";
import { SearchFilter } from "./searchFilter/SearchFilter";
import { ExtendableNavbarPage } from "quiz/components/extendableNavbarPage/ExtendableNavbarPage";
import { SearchBar } from "./searchBar/SearchBar";
import { useStyles } from "hooks/useStyles";
import { getSearchPageStyles } from "./searchPageStyles";
import { QuizError } from "quiz/components/errors/QuizError";
import { searchPageReducer, SearchPageState } from "./searchPageReducer";
import { Fragment } from "react";

const initialState: SearchPageState = {
	value: "",
	delayedValue: "",
	loading: false,
	outlets: [],
	requestError: null,
	onlyEmployeeOutlets: false
};

export const SearchPage: React.FC = React.memo(() => {
	const [state, dispatch] = React.useReducer(searchPageReducer, initialState);
	const { latitude, longitude } = usePosition();
	const animation = useAnimation();
	const styles = useStyles(getSearchPageStyles);

	useCancellablePromise(
		a => {
			return quizDataService.findOutlets(
				state.delayedValue,
				{ latitude, longitude },
				0,
				50,
				Boolean(state.onlyEmployeeOutlets),
				{ signal: a }
			);
		},
		{
			onSuccess: data => dispatch({ type: "SUCCESS_REQUEST", payload: data }),
			onError: err => dispatch({ type: "ERROR_REQUEST", payload: err }),
			deps: [state.delayedValue, state.onlyEmployeeOutlets]
		}
	);

	React.useEffect(() => {
		if (state.value === state.delayedValue) {
			if (state.value === "") return void 0;
			dispatch({ type: "INPUT_NOT_CHANGED" });
			return void 0;
		}
		const handler = setTimeout(() => dispatch({ type: "DELAYED_INPUT" }), 750);
		return () => clearTimeout(handler);
	}, [state.value]);

	const showTitle = !state.loading && state.outlets?.length > 0 && !state.requestError?.message;
	const isEmpty = !!state.delayedValue && state.outlets?.length === 0;
	const isLoading = state.loading;
	const showHints = !isEmpty && !showTitle && !isLoading && !state.requestError?.message;
	const fadeAnimationKey = getAnimationKey(isLoading, [state.requestError?.message]);

	return (
		<Fragment>
			<ExtendableNavbarPage
				navbarContent={() => (
					<SearchBar
						value={state.value}
						onInputChange={e => {
							e.persist();
							dispatch({ type: "INPUT_CHANGED", payload: e.target.value });
						}}
						onInputClear={() => dispatch({ type: "INPUT_CLEARED" })}
					/>
				)}
				extendableContent={() => (
					<SearchFilter
						title={t("Only my outlets")}
						onToggleChange={checked => dispatch({ type: "TOGGLE_CHANGED", payload: checked })}
					/>
				)}
				hasError={state?.requestError}
			>
				<Fragment>
					{state.requestError ? (
						<QuizError error={state.requestError} fullScreen />
					) : (
						<QuizFade animationKey={fadeAnimationKey}>
							<div className={css(styles.fadeContent)}>
								{showHints && (
									<Hints
										onExampleClick={example =>
											dispatch({ type: "HINTS_CLICKED", payload: example })
										}
									/>
								)}
								{showTitle && (
									<BlockTitle title={t("Search result")} counter={state.outlets?.length || 0} />
								)}
								<QuizList
									isLoading={isLoading}
									isEmpty={isEmpty}
									errors={[state.requestError]}
									loading={() => <OutletSkeleton totalBlocks={3} blockTitle />}
									emptyList={{ title: t("Nothing found"), separateBlock: true }}
									separateErrorBlock
								>
									{state.outlets?.map((outlet: OutletFullData, idx: number) => (
										<OutletItem
											key={outlet.id}
											outlet={outlet}
											onClick={() => nav.toOutlet(outlet.id)}
											style={animation.animationDelay(idx)}
										/>
									))}
								</QuizList>
							</div>
						</QuizFade>
					)}
				</Fragment>
			</ExtendableNavbarPage>
		</Fragment>
	);
});

SearchPage.displayName = "QuizSearchPage";
