import * as React from "react";
import { Page } from "framework7-react";
import { nav } from "quiz/QuizApp";
import { CSSProp, css } from "wraps/styleCreators";
import { CustomColors } from "utils/constants";
import { useStyles } from "hooks/useStyles";
import { PageNotFound } from "quiz/pages/pageNotFound/PageNotFound";
import { Camera } from "../camera/Camera";
import { CameraNavbar } from "../camera/cameraNavbar/CameraNavbar";

export type CustomCameraProps = {
	context: { onTakePhoto: (dataUri: string[]) => void };
};

const cameraPageStyles = () => ({
	content: {
		display: "flex",
		flexFlow: "column",
		alignItems: "center",
		justifyContent: "flex-start",
		height: "100%",
		width: "100%",
		backgroundColor: CustomColors.black
	} as CSSProp
});

export const CameraPage: React.FC<CustomCameraProps> = React.memo(props => {
	const style = useStyles(cameraPageStyles);

	return (
		<>
			{props.context != null ? (
				<Page className="photo-browser-page photo-browser-page-dark no-toolbar pb-tweaks">
					<CameraNavbar />
					<div className={css(style.content)}>
						<Camera
							onTakePhoto={data => {
								props.context.onTakePhoto([data]);
								nav.back();
							}}
						/>
					</div>
				</Page>
			) : (
				<PageNotFound />
			)}
		</>
	);
});
CameraPage.displayName = "CameraPage";
