import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export function getSwiperBarStyles(theme: Theme) {
	return {
		tabBar: {
			position: "fixed",
			bottom: 0,
			width: "100%",
			height: scale(theme.sizing.sizes.xl, ScaleEnum.Double),
			background: "#FFF",
			boxShadow: theme.shadows.up.md,
			zIndex: theme.zIndex.appBar,
			paddingBottom: `calc(env(safe-area-inset-bottom) + ${theme.sizing.sizes.sm})`
		} as CSSProp,
		swiperButtons: {
			display: "flex",
			height: "100%",
			justifyContent: "space-around",
			alignItems: "center",
			zIndex: 100
		} as CSSProp,
		buttonsSignatures: {
			position: "absolute",
			bottom: theme.sizing.sizes.xs,
			display: "flex",
			justifyContent: "space-around",
			width: "100%",
			zIndex: -1
		} as CSSProp,
		signature: {
			display: "inline-block",
			width: "100%",
			fontSize: "10px",
			textAlign: "center"
		} as CSSProp,
		mapButtonWrap: {
			display: "flex",
			justifyContent: "center",
			position: "relative"
		} as CSSProp,
		buttonWrap: {
			width: "100%",
			height: "100%",
			display: "flex",
			justifyContent: "center",
			alignItems: "center"
		} as CSSProp,
		button: {
			height: "100%",
			width: "100%",
			justifyContent: "center",
			alignItems: "center"
		} as CSSProp
	};
}
