import * as React from "react";
import { QuizPage } from "quiz/components/quizPage/QuizPage";
import { t } from "quiz/QuizApp";
import { PrivacyPolicy } from "quiz/components/privacyPolicy/PrivacyPolicy";

export const AcceptPrivacyPolicyPage: React.FC = React.memo(() => {
	return (
		<QuizPage
			component={PrivacyPolicy}
			componentProps={{ isButtonShow: true }}
			navbar={{ title: t("Terms of use") }}
		/>
	);
});

AcceptPrivacyPolicyPage.displayName = "AcceptPrivacyPolicyPage";
