import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getQstListItemViewStyles = (theme: Theme) => {
	return {
		listItem: {
			width: "100%"
		} as CSSProp,
		titleWrap: {
			display: "flex",
			flexWrap: "nowrap",
			whiteSpace: "nowrap",
			overflow: "hidden"
		} as CSSProp,
		titleText: {
			textOverflow: "ellipsis",
			overflow: "hidden"
		} as CSSProp,
		descriptionWrap: {
			lineHeight: theme.sizing.lineHeights.caption,
			margin: `${theme.sizing.sizes.xs} 0 ${theme.sizing.sizes.sm} 0`
		} as CSSProp,
		lastVisitWrap: {
			display: "flex",
			alignItems: "center",
			textAlign: "center"
		} as CSSProp,
		lastVisitIconWrap: {
			display: "flex",
			height: theme.sizing.sizes.lg,
			width: theme.sizing.sizes.lg,
			alignItems: "center",
			justifyContent: "flex-start",
			marginRight: theme.sizing.sizes.xs
		} as CSSProp,
		lastVisitTextWrap: {
			lineHeight: theme.sizing.lineHeights.heading
		} as CSSProp
	};
};
