import { MDTRecordId } from "mdtApiClient/mdtApiClient";
import { quizQstService } from "quiz/QuizApp";
import { QuizPageComponentProps } from "quiz/QuizAppRoutes";
import { Visit } from "quiz/services/models";
import * as React from "react";
import { VisitReport } from "./VisitReport";
import { VisitReportSkeleton } from "quiz/components/quizSkeleton/skeletons/VisitReportSkeleton";
import { getAnimationKey, toLocalISOString } from "utils/helpers";
import { QuizFade } from "quiz/components/fade/QuizFade";
import { useCancellablePromise } from "hooks/useCancellablePromise";
import { QuizList } from "quiz/components/quizList/QuizList";

export interface VisitPageProps extends QuizPageComponentProps {
	id: MDTRecordId;
}

type VisitPageState = {
	visit?: Visit;
	requestError?: any;
};

//todo:    
export const VisitPage: React.FC<VisitPageProps> = props => {
	const [state, setVisit] = React.useState<VisitPageState>({ visit: null, requestError: null });
	const fadeAnimationKey = getAnimationKey(!state.visit, [state.requestError?.message]);

	useCancellablePromise(
		a =>
			quizQstService.getVisit(props.id, { signal: a }).then(async visit => {
				const dateSubmit = visit?.dateEnd ? toLocalISOString(visit.dateEnd).replace(/\.\d+Z$/, "") : null;
				const prevVisit = await quizQstService.getQstPrevVisit(
					visit.outletId,
					visit.questionnaireId,
					dateSubmit,
					{ signal: a }
				);
				if (prevVisit) visit.questionnaire.prevVisit = prevVisit;
				return visit;
			}),
		{
			onSuccess: v => setVisit({ visit: v, requestError: null }),
			onError: err => setVisit({ requestError: err })
		}
	);

	return (
		<>
			<QuizFade animationKey={fadeAnimationKey}>
				<QuizList
					isLoading={!state.visit}
					isEmpty={state?.visit == null}
					errors={[state.requestError]}
					loading={() => <VisitReportSkeleton totalBlocks={7} />}
				>
					<VisitReport visit={state.visit} outlet={state.visit?.outlet} isCompleted />
				</QuizList>
			</QuizFade>
		</>
	);
};
VisitPage.displayName = "VisitPage";
