import { IQuestionItem } from "quiz/services/models";
import React, { useCallback, Fragment } from "react";
import { QuestionControlProps } from "../Question";
import { Text } from "quiz/components/text/Text";
import { QuestionList } from "../../questionListView/QuestionList";
import { QuestionListItem } from "../../questionListView/QuestionListItem";
import { t } from "quiz/QuizApp";
import { GroupedAnswerHeader } from "../common/groupedAnswerHeader/GroupedAnswerHeader";

export const QuestionManyOptionControl: React.FC<QuestionControlProps> = React.memo(props => {
	const { question, visitManager, valueItems } = props;
	const items = question.items || [];

	const groupHash = React.useMemo(() => {
		const groupHash: { [key: string]: IQuestionItem[] } = {};
		items.forEach(item => {
			const groupCode = item.group || t("No category");
			if (!groupHash[groupCode]) groupHash[groupCode] = [];
			groupHash[groupCode].push(item);
		});
		return groupHash;
	}, [items]);

	const values = valueItems || [];

	const setValue = useCallback(
		(item: IQuestionItem, v: boolean) =>
			visitManager.setAnswer(question.id, [{ answer: item.name, value: v, id: item.id }]),
		[question.id]
	);
	const checked = (item: IQuestionItem) => {
		const curItemValue = values.find(v => v.id == item.id);
		return !!curItemValue && curItemValue.value;
	};

	const groupChecked = (group: string) => groupHash[group].every(item => checked(item));
	const groupIndeterminate = (group: string) => groupHash[group].some(item => checked(item)) && !groupChecked(group);
	const setGroupValue = (group: string, v: boolean) => {
		const newVals = groupHash[group].map(item => ({ answer: item.name, value: v, id: item.id }));
		visitManager.setAnswer(question.id, newVals);
	};
	return (
		<QuestionList>
			{Object.keys(groupHash).map((group, groupIdx, arr) => {
				const gChecked = groupChecked(group);
				const counter = `${groupHash[group].filter(item => checked(item)).length}/${groupHash[group].length}`;

				return (
					<Fragment key={group}>
						<GroupedAnswerHeader
							key={`group_${group}`}
							checkbox
							checked={gChecked}
							indeterminate={groupIndeterminate(group)}
							onChange={() => setGroupValue(group, !gChecked)}
							groups
							header={group}
							counter={counter}
						/>
						{groupHash[group].map((i, idx) => {
							const v = checked(i);
							const isDash = idx + 1 != groupHash[group].length || arr[groupIdx + 1];

							return (
								<QuestionListItem
									key={i.id}
									checkbox
									value={i.id}
									onChange={() => setValue(i, !v)}
									checked={v}
									dash={!!isDash}
								>
									<div slot="title">
										<Text>{i.name}</Text>
									</div>
								</QuestionListItem>
							);
						})}
					</Fragment>
				);
			})}
		</QuestionList>
	);
});
QuestionManyOptionControl.displayName = "QuestionManyOptionControl";
