import { IQuestion } from "quiz/services/models";
import NanoEvents from "nanoevents";

export class VirtualDataManager {
	private events = new NanoEvents<any>();
	virtualData;

	constructor(virtualData) {
		this.virtualData = virtualData;
	}

	subscribe(event, callback) {
		this.events.on(event, callback);
	}

	setVD(data) {
		this.virtualData = data;
	}

	updateVD() {
		this.events.emit("changed", this.virtualData);
	}
}
