import { CSSPropertiesComplete } from "aphrodite";
import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { IconAlignment, IconWrapProps } from "./IconWrap";

type MapAlign2CssProp = {
	[key in IconAlignment]: [CSSPropertiesComplete["justifyContent"], CSSPropertiesComplete["alignItems"]];
};

const mapAlign2CssProp: MapAlign2CssProp = {
	"top-left": ["flex-start", "flex-start"],
	"top-center": ["center", "flex-start"],
	"top-right": ["flex-end", "flex-start"],
	"center-left": ["flex-start", "center"],
	center: ["center", "center"],
	"center-right": ["flex-end", "center"],
	"bottom-left": ["flex-start", "flex-end"],
	"bottom-center": ["center", "flex-end"],
	"bottom-right": ["flex-end", "flex-end"]
};

export const getIconWrapStyles = (theme: Theme, props: IconWrapProps) => {
	const [hAlign, vAlign] = mapAlign2CssProp[props.alignment];
	return {
		iconWrap: {
			display: "flex",
			height: theme.sizing.sizes.lg,
			width: theme.sizing.sizes.lg,
			justifyContent: hAlign,
			alignItems: vAlign
		} as CSSProp,
		icon: {
			color: props.iconColor || theme.colors.primary.main
		} as CSSProp
	};
};
