import { faAngleRight } from "@fortawesome/pro-regular-svg-icons/faAngleRight";
import { faExclamationCircle } from "@fortawesome/pro-solid-svg-icons/faExclamationCircle";
import { Icon } from "quiz/components/icon/Icon";
import { setupIcons } from "quiz/components/icon/setup";
import { Text } from "quiz/components/text/Text";
import { Gauge } from "framework7-react";
import { useStyles } from "hooks/useStyles";
import { quizQstService } from "quiz/QuizApp";
import { VisitUploadData } from "quiz/services/QuizDraftService";
import * as React from "react";
import { Theme } from "theme/theme";
import { ThemeContext } from "themeContext/ThemeContext";
import { css, CSSProp } from "wraps/styleCreators";
import { getCSSSizeValue } from "utils/cssHelpers";

setupIcons(faExclamationCircle, faAngleRight);

const getStyle = (theme: Theme) => {
	return {
		progressWrap: {
			display: "flex",
			alignItems: "center"
		} as CSSProp,
		progressText: {
			fontSize: theme.sizing.fontSizes.caption,
			lineHeight: theme.sizing.lineHeights.caption,
			color: theme.colors.base.texts.muted,
			marginRight: theme.sizing.sizes.sm
		},
		exclamationIcon: {
			color: theme.colors.danger.main,
			marginRight: theme.sizing.sizes.sm
		},
		angleIcon: {
			color: theme.colors.danger.main
		},
		errorZone: {
			display: "flex"
		} as CSSProp
	};
};

interface UnsentReportIconProps {
	data: VisitUploadData;
}

export const UnsentReportIcon: React.FC<UnsentReportIconProps> = React.memo(({ data }) => {
	const styles = useStyles(getStyle);
	const theme = React.useContext(ThemeContext);

	const hasErrors = quizQstService.hasUploadErrors(data);

	if (hasErrors) {
		return (
			<div className={css(styles.errorZone)}>
				<Icon icon={["fas", "exclamation-circle"]} styles={styles.exclamationIcon} />
				<Icon icon={["far", "angle-right"]} styles={styles.angleIcon} />
			</div>
		);
	}

	const progress = quizQstService.getUploadProgress(data);

	return (
		<div className={css(styles.progressWrap)}>
			<Text styles={styles.progressText}>{Math.round(progress * 100)}%</Text>
			<Gauge
				type="circle"
				value={progress}
				size={getCSSSizeValue(theme.sizing.sizes.md)}
				borderColor={theme.colors.primary.main}
				borderWidth={getCSSSizeValue(theme.sizing.sizes.xs)}
			/>
		</div>
	);
});
UnsentReportIcon.displayName = "UnsentReportIcon";
