import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { ExtendableNavbarProps } from "./ExtendableNavbarPage";

export const getExtendableNavbarStyles = (theme: Theme, props: ExtendableNavbarProps) => {
	const { wrap, hasError } = props;
	return {
		extendableWrap: {
			display: "flex",
			flexDirection: "column",
			width: "100%",
			height: "fit-content",
			boxShadow: theme.shadows.normal,
			position: "relative",
			zIndex: 500
		} as CSSProp,
		navbarContentWrap: {
			borderBottom: `1px solid ${theme.colors.base.disabled}`
		} as CSSProp,
		innerContent: {
			marginBottom: !hasError && wrap ? "56px" : "unset" //56px    
		} as CSSProp
	};
};
