import * as React from "react";
import { StyleDeclarationValue, css } from "wraps/styleCreators";
import { useCancellablePromise } from "hooks/useCancellablePromise";
import { apiClient } from "quiz/QuizApp";
import { getFileUrlForRequest } from "utils/helpers";
import { QuizPreloader } from "quiz/components/quizPreloader/QuizPreloader";
import { requiresXHRImgLoad } from "../photobrowser/PhotoBrowserService";

interface ImageProps {
	src: string;
	styles?: StyleDeclarationValue;
}

export const XHRImage: React.FC<ImageProps> = ({ src, styles }) => {
	const [imgSrc, setImgSrc] = React.useState<string>();
	let bUrl = "";

	useCancellablePromise(
		a => {
			return apiClient.fetchImage(getFileUrlForRequest(src), { signal: a });
		},
		{
			onSuccess: blob => {
				const blobUrl = URL.createObjectURL(blob);
				bUrl = blobUrl;
				setImgSrc(bUrl);
			}
		}
	);

	React.useEffect(() => {
		return () => {
			if (bUrl.length) {
				URL.revokeObjectURL(bUrl);
			}
		};
	});

	return imgSrc ? <img src={imgSrc} className={styles && css(styles)} /> : <QuizPreloader preloaderAreaWidth={100} />;
};

export const Image: React.FunctionComponent<ImageProps> = (props: ImageProps) => {
	const requiresXHRLoad = requiresXHRImgLoad(props.src);

	return requiresXHRLoad ? (
		<XHRImage {...props} />
	) : (
		<img src={props.src} className={props.styles && css(props.styles)} />
	);
};

Image.displayName = "Image";
