import { css } from "aphrodite";
import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { ColorTypes } from "theme/colors";
import { TextSizeType } from "theme/sizing";
import { filterProps } from "utils/helpers";
import { ControlLayout, ControlLayoutProps, ControlValue } from "../controlLayout/ControlLayout";
import { Text } from "../text/Text";
import { getFieldControlLayoutStyles } from "./fieldControlLayoutStyles";

const controlLayoutInheritProps = ["value", "onChange", "customControl", "readOnly", "onClearClick"] as const;

export interface FieldControlLayoutProps<T = ControlValue>
	extends Pick<ControlLayoutProps<T>, typeof controlLayoutInheritProps[number]> {
	//content
	label?: string;
	labelSecondary?: string;
	description?: string;

	//options
	colorType?: ColorTypes;
	descriptionColorType?: ColorTypes;
}

export const FieldControlLayout: React.FunctionComponent<FieldControlLayoutProps> = React.memo(props => {
	const styles = useStyles(getFieldControlLayoutStyles, props);

	const clProps = filterProps(props, controlLayoutInheritProps);
	return (
		<div className={css(styles.wrap)}>
			<div className={css(styles.top)}>
				<div className={css(styles.label)}>
					<Text
						size={TextSizeType.caption}
						styles={[styles.labelText, props.value == null && styles.labelTextEmptyValue]}
					>
						{props.label}
					</Text>
				</div>
				{props.labelSecondary != null && (
					<div className={css(styles.labelSecondary)}>
						<Text size={TextSizeType.caption} styles={styles.labelSecondaryText}>
							{props.labelSecondary}
						</Text>
					</div>
				)}
			</div>
			<ControlLayout value={props.value} {...clProps} />
			{props.description != null && (
				<div className={css(styles.bottom)}>
					<Text size={TextSizeType.caption} styles={styles.description}>
						{props.description}
					</Text>
				</div>
			)}
		</div>
	);
});
FieldControlLayout.displayName = "FieldControlLayout";
