import { Text } from "quiz/components/text/Text";
import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { KeyboardEventHandler, MouseEventHandler } from "react";
import { ColorTypes } from "theme/colors";
import { filterProps } from "utils/helpers";
import { css } from "wraps/styleCreators";
import { Icon, IconProp, IconProps } from "../icon/Icon";
import { getButtonStyles } from "./buttonStyles";

export enum ButtonTag {
	a = "a",
	button = "button"
}

export enum ButtonType {
	normal = "normal",
	link = "link",
	danger = "danger",
	transparent = "transparent"
}
export enum ButtonSize {
	normal = "normal",
	large = "large"
}

export interface ButtonProps {
	type?: ButtonType;
	size?: ButtonSize;
	colorType?: ColorTypes;
	/** Icon name */
	icon?: IconProp;
	iconRight?: IconProp;
	/** Icon props */
	iconProps?: IconProps;
	fullWidth?: boolean;

	/** `true` by default */
	enabled?: boolean;
	/** `button` by default */
	tag?: ButtonTag;

	onClick: MouseEventHandler<HTMLButtonElement | HTMLAnchorElement>;
	onKeyDown?: KeyboardEventHandler<HTMLButtonElement | HTMLAnchorElement>;
}

export const Button: React.FunctionComponent<ButtonProps> = function Button(props) {
	const styles = useStyles(getButtonStyles, props);

	const Tag = props.tag || "button";

	const elProps = filterProps(props, ["onClick", "onKeyDown"]);

	return (
		<Tag className={css(styles.button)} {...elProps} tabIndex={0}>
			<span className={css(styles.innerWrap)}>
				{props.icon && (
					<span className={css(styles.iconLeft)}>
						<Icon icon={props.icon} styles={styles.icon} {...props.iconProps} />
					</span>
				)}
				<span className={css(styles.textWrap)}>
					<Text styles={styles.text}>{props.children}</Text>
				</span>
				{props.iconRight && (
					<span className={css(styles.iconRight)}>
						<Icon icon={props.iconRight} styles={styles.icon} {...props.iconProps} />
					</span>
				)}
			</span>
		</Tag>
	);
};
