import { Divider } from "quiz/components/divider/Divider";
import { Block } from "quiz/components/block/Block";
import { QuestionBooleanControl } from "quiz/components/question/controls/QuestionBooleanControl";
import { QuestionOneOptionControl } from "quiz/components/question/controls/QuestionOneOptionControl";
import { useStyles } from "hooks/useStyles";
import { VisitManager, ValidationError, ValidationErrorType } from "quiz/pages/questionnaire/VisitManager";
import { AnswerType, IQuestion, QuestionAttachPhoto, VisitAnswer, VisitAnswerItem } from "quiz/services/models";
import * as React from "react";
import { QuestionTextControl } from "./controls/QuestionTextControl";
import { QuestionGalleryControl } from "./controls/QuestionGalleryControl";
import { QuestionManyOptionControl } from "./controls/QuestionManyOptionControl";
import { QuestionQrCodeControl } from "./controls/QuestionQrCodeControl";
import { getQuestionStyles } from "./questionStyles";
import { IconProp } from "quiz/components/icon/Icon";
import { QuizListInputItem } from "../quizListInputItem/QuizListInputItem";
import { css } from "wraps/styleCreators";
import { QuestionKpiControl } from "./controls/kpi/QuestionKpiControl";
import { AnswerSection, AnswerSectionData } from "./common/answerSection/AnswerSection";
import { QstContext } from "./common/QstContext";

export interface QuestionProps {
	question: IQuestion;
	visitManager: VisitManager;
	answer?: VisitAnswer;
	toNextQuestion(): void;
}

export interface QuestionControlProps extends QuestionProps {
	valueItems?: VisitAnswerItem[];
}

const requiredAnswer = (e: ValidationError) => e.type == ValidationErrorType.RequiredAnswer;
const requiredAttachPhoto = (e: ValidationError) => e.type == ValidationErrorType.RequiredPhoto;
const answerIsGroupInput = (question: IQuestion, answer: VisitAnswer) => {
	return [AnswerType.Text, AnswerType.Number].includes(answer?.answerType) && question?.items?.length;
};

export const Question: React.FunctionComponent<QuestionProps> = React.memo(props => {
	const { question, visitManager, answer, toNextQuestion } = { ...props };
	const setComment = React.useCallback(v => props.visitManager.setComment(question.id, v), [question.id]);
	const styles = useStyles(getQuestionStyles);
	const qstContext = React.useContext(QstContext);

	const isGalleryActive = React.useMemo(() => {
		return (
			question.questionAttachPhoto != QuestionAttachPhoto.None || question.answerType == AnswerType.AttachPhoto
		);
	}, [question.questionAttachPhoto, question.answerType]);

	const isComment = question.flagComment;
	return (
		<>
			{question.kpi?.length && <QuestionKpiControl question={question} />}
			<div className={css(styles.questionWrap)}>
				<QuestionControl
					question={question}
					visitManager={visitManager}
					valueItems={answer.items}
					toNextQuestion={() => toNextQuestion()}
					answer={answer}
				/>
				{isComment && (
					<Block secondary>
						<QuizListInputItem
							type="text"
							placeholder={"Enter a comment"}
							value={props.answer.comment}
							onChange={setComment}
							icon={["far", "comment-alt"] as IconProp}
						/>
					</Block>
				)}
				{isGalleryActive && (
					<AnswerSection
						description={"Need to add at least one photo"}
						isValid={!qstContext.validationErrors?.filter(requiredAttachPhoto).length}
						required={false}
						hideDescription={true}
					>
						<Block secondary>{isComment ? <Divider dash /> : null}</Block>
						<QuestionGalleryControl question={question} visitManager={visitManager} files={answer.files} />
					</AnswerSection>
				)}
			</div>
		</>
	);
});
Question.displayName = "Question";

type AnswerType2QuestionControl = {
	[key in AnswerType]?: [React.FC<QuestionControlProps>, AnswerSectionData];
};

const MapAnswerType2QuestionControl: AnswerType2QuestionControl = {
	[AnswerType.Text]: [QuestionTextControl, [null, "Need enter a text"]],
	[AnswerType.Number]: [QuestionTextControl, [null, "Need enter a numeric"]],
	[AnswerType.Boolean]: [QuestionBooleanControl, ["Select options", "Need to select one of the options"]],
	[AnswerType.OneOption]: [QuestionOneOptionControl, ["Select options", "Need to select one of the options"]],
	[AnswerType.ManyOptions]: [QuestionManyOptionControl, ["Check required items", " "]],
	[AnswerType.ScanQrCode]: [QuestionQrCodeControl, [null, "Need to scan QR-code"]],
	[AnswerType.ScanQrCodeWithHideText]: [QuestionQrCodeControl, [null, "Need to scan QR-code"]],
	[AnswerType.AttachPhoto]: [null, null]
};

export const QuestionControl: React.FunctionComponent<QuestionControlProps> = React.memo(props => {
	const [Component, AnswerSectionProps] = MapAnswerType2QuestionControl[props.question.answerType];
	const [header, desciption] = AnswerSectionProps || [null, null];
	const controlProps: QuestionControlProps = { ...props };

	if (Component) {
		const ctx = React.useContext(QstContext);
		return (
			<AnswerSection
				header={header}
				description={answerIsGroupInput(controlProps.question, controlProps.answer) ? " " : desciption}
				isValid={!ctx.validationErrors?.filter(requiredAnswer).length}
				required={controlProps.question.flagAnswerRequired}
			>
				<Component {...controlProps} />
			</AnswerSection>
		);
	}

	return null;
});
QuestionControl.displayName = "QuestionControl";
