import * as React from "react";
import { MobileActionButtonProps, MobileActionButton } from "./MobileActionButton";
import { Block } from "../../../quiz/components/block/Block";
import { useStyles } from "hooks/useStyles";
import { getWrappedMobileActionButtonStyle } from "./mobileButtonStyles";

export const WrappedMobileActionButton: React.FC<MobileActionButtonProps> = props => {
	const styles = useStyles(getWrappedMobileActionButtonStyle);

	return (
		<Block styles={styles.wrap}>
			<MobileActionButton {...props} />
		</Block>
	);
};
