import * as React from "react";
import { FileRecord } from "mdtApiClient/mdtApiClient";
import { Image } from "quiz/components/image/Image";
import { apiClient } from "quiz/QuizApp";
import { StyleDeclarationValue } from "wraps/styleCreators";

interface MDTImageOptions {
	fileRecord: FileRecord;
	styles?: StyleDeclarationValue;
}

export const MDTImage: React.FC<MDTImageOptions> = ({ fileRecord, styles }) => (
	<Image src={apiClient.imageUrl(fileRecord)} styles={styles} />
);
