import { css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import React from "react";
import { QuizNavBarProps } from "../QuizNavBar";
import { NavLeft, NavTitle } from "quiz/components/navbar/Navbar";
import { NavTitleText } from "../navTitleText/NavTitleText";
import { OfflineIndicator } from "../offlineIndicator/OfflineIndicator";
import { getQuizNavLeftStyles } from "./quizNavLeftStyles";
import { BackLink } from "../icons/backlink/BackLink";

export const QuizNavLeft: React.FC<Pick<QuizNavBarProps, "backLink" | "title" | "subtitle">> = props => {
	const styles = useStyles(getQuizNavLeftStyles, props);
	return (
		<NavLeft className={css(styles.navLeft)}>
			{props.backLink && <BackLink />}
			<NavTitle className={css(styles.navtitle)}>
				<NavTitleText text={props.title} />
				<OfflineIndicator />
			</NavTitle>
		</NavLeft>
	);
};
QuizNavLeft.displayName = "f7-nav-left";
