import { DatabaseExtension } from "./dbExtension";
import { openDB } from "idb";
import { QuizDB, draftStoreName, draftFilesStore, visitUploadStore, outletCacheStore } from "../../QuizDraftService";

export class BrowserDb implements DatabaseExtension {
	getDB() {
		return openDB<QuizDB>("Questionnaire_next", 2, {
			upgrade: (db, oldVersion, newVersion, tsx) => {
				if (tsx.mode == "versionchange") {
					if (oldVersion == 0) {
						db.createObjectStore(draftStoreName);
						db.createObjectStore(draftFilesStore);
						db.createObjectStore(visitUploadStore);
						db.createObjectStore(outletCacheStore);
					} else {
						const objects = Array.from(tsx.objectStoreNames);
						if (!objects.includes(outletCacheStore)) db.createObjectStore(outletCacheStore);
					}
				}
			}
		});
	}
}
