import * as React from "react";
import { t, quizAuthService, textService, quizUserService, nav } from "quiz/QuizApp";
import { ColorTypes } from "theme/colors";
import { ButtonType, ButtonSize } from "quiz/components/button/Button";
import { RadioList } from "quiz/components/radioList/RadioList";
import { Theme } from "theme/theme";
import { useStyles } from "hooks/useStyles";
import { css } from "wraps/styleCreators";
import { BlockDescription } from "quiz/components/block/Block";
import { ServerInteractionButton } from "quiz/components/button/ServerInteractionButton";

const getChooseLanguagePageStyles = (theme: Theme) => {
	return {
		languageList: {
			marginTop: theme.sizing.sizes.lg
		}
	};
};

export const ChooseLanguagePage: React.FC = () => {
	const styles = useStyles(getChooseLanguagePageStyles);
	const currentLangId = quizAuthService.state.user.languageId;
	const [langId, setLangId] = React.useState<number>(currentLangId);
	const [error, setError] = React.useState();
	const languageItems = textService.availableLangs.map(lang => ({ value: lang.ID, title: lang.Title }));

	const onSave = async () =>
		quizUserService.changeLanguage(langId).then(
			() => nav.back(),
			err => setError(err.toString())
		);

	return (
		<>
			<div className={css(styles.languageList)}>
				<RadioList
					items={languageItems}
					onChange={e => {
						setLangId(Number(e.target.value));
					}}
					value={langId}
				/>
			</div>
			<BlockDescription text={t(error)} colorType={ColorTypes.danger} />
			<ServerInteractionButton
				wrap
				enabled={() => langId != currentLangId}
				type={ButtonType.normal}
				colorType={ColorTypes.primary}
				fullWidth={true}
				size={ButtonSize.large}
				title={t("Apply")}
				command={() => {
					onSave();
				}}
			/>
		</>
	);
}; //toDO: button to component
