export type Lang = {
	ID: number;
	Code: string;
	Title: string;
};

export class TextService {
	dictionary = {};
	availableLangs: Lang[] = [];

	constructor(private apiClient: MDTApiClient) {}

	init() {
		return this.getLanguageDict();
	}

	getLanguageDict(): Promise<{}> {
		return this.apiClient
			.request("fetch/mdt.TextValue", {
				select: "ID_TextKey/Code as $key, Value as $value",
				filter: "ID_Language eq 'ID_CURRENT_LANGUAGE'",
				top: 1000
			} as Object)
			.then(MDTApiClient.handleAPIError)
			.then(r => r.json())
			.then(d => {
				this.dictionary = { ...((d[0] && d[0].Dict) || {}) };
				return this.dictionary;
			});
	}

	getAvailableLanguages(): Promise<Lang[]> {
		return this.apiClient
			.request("fetch/mdt.Language", {
				select: "*"
			} as Object)
			.then(MDTApiClient.handleAPIError)
			.then(r => r.json())
			.then(d => {
				this.availableLangs =
					(d[0] &&
						d[0].Rows.map(lang => ({
							ID: lang.ID,
							Code: lang.Code,
							Title: lang.Title
						}))) ||
					[];
				return this.availableLangs;
			});
	}

	setQuizLocalDict(langId: number) {
		const currentLang = this.availableLangs.find(lang => lang.ID == langId);
		if (currentLang?.Code == "en") {
			const enDict = Object.keys(DEFAULTS).reduce((dict, current) => {
				return { ...dict, current };
			}, {});
			return (this.dictionary = { ...this.dictionary, ...enDict });
		}
		return (this.dictionary = { ...this.dictionary, ...DEFAULTS });
	}

	t(v: string, args?: Object): string {
		const s = v != null ? this.dictionary[v.trim()] || v : v;
		if (!args) return s;

		const items = s.split(/\{(\w+)\}/);
		const res = [];
		for (let i = 0; i < items.length; i++) {
			let item = items[i];
			if (i % 2 == 1) {
				const val = args[items[i]];
				if (val instanceof Array) item = "(" + val.join(", ") + ")";
				else item = val;
			}
			res.push(item);
		}
		return res.join("").replace(/\*\*/g, "");
	}

	private translationExp = /^(\s*)(.+?)(\s*)$/;
	//fn for replacing message strings with translations
	templateFn = (literals, ...args): string => {
		return literals.reduce((str, v, idx) => {
			const res = this.translationExp.exec(v);
			if (res?.length) v = `${res[1]}${this.t(res[2])}${res[3]}`;
			return (str += v + (args[idx] ?? ""));
		}, "");
	};
}
