import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { QuizNavBarProps } from "./QuizNavBar";

export const getQuizNavBarStyles = (theme: Theme, props: Pick<QuizNavBarProps, "noShadow">) => {
	return {
		navbar: {
			boxShadow: props.noShadow ? "none" : theme.shadows.normal
		} as CSSProp
	};
};
