import React from "react";
import { css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import { getExtendableNavbarStyles } from "./extendableNavbarPageStyles";
import { Page } from "quiz/components/page/Page";

export type ExtendableNavbarProps = {
	navbarContent: () => JSX.Element;
	extendableContent?: () => JSX.Element;
	wrap?: boolean;
	hasError?: boolean;
};

const pageClassName = "extendable-navbar-page";

export const ExtendableNavbarPage: React.FC<ExtendableNavbarProps> = React.memo(props => {
	const { navbarContent, extendableContent, hasError, children } = props;
	const styles = useStyles(getExtendableNavbarStyles, props);
	const extendableWrapRef = React.useRef<HTMLDivElement>(null);
	const [navbarHeight, setNavbarHeight] = React.useState(0);

	React.useEffect(() => setNavbarHeight(extendableWrapRef.current?.getBoundingClientRect()?.height), [
		navbarHeight,
		hasError
	]);

	const pageStyles = React.useMemo(
		() =>
			`.${pageClassName} .page-content {
					padding-top: ${hasError ? 0 : navbarHeight}px;
				}`.trim(),
		[navbarHeight, hasError]
	);

	return (
		<>
			<div ref={extendableWrapRef} className={css(styles.extendableWrap)}>
				{navbarContent && <div className={css(styles.navbarContentWrap)}>{navbarContent()}</div>}
				{!hasError && extendableContent && extendableContent()}
			</div>
			{children && (
				<>
					<style>{pageStyles}</style>
					<Page className={pageClassName}>
						<div className={css(styles.innerContent)}>{children}</div>
					</Page>
				</>
			)}
		</>
	);
});

ExtendableNavbarPage.displayName = "ExtendableNavbarPage";
