import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { css } from "aphrodite";
import { Gauge } from "framework7-react";
import { getCSSSizeValue } from "utils/cssHelpers";
import { ThemeContext } from "themeContext/ThemeContext";
import { Text } from "quiz/components/text/Text";
import { T } from "quiz/components/text/T";
import { getDefectBlockStyles } from "./defectStyles";
import { increaseIcon, decreaseIcon } from "./defectIcons";
import { Icon } from "quiz/components/icon/Icon";
import { TextSizeType } from "theme/sizing";
import { VisitAnswerItem, AnswerType, VisitAnswer } from "quiz/services/models";
import { formatDateTime } from "utils/helpers";
import { QstContext } from "quiz/components/question/common/QstContext";

export type DefectProps = {
	title: string;
};

const getDefectRate = (items: VisitAnswerItem[], answerType: AnswerType) => {
	const length = items?.length;
	if (answerType == AnswerType.OneOption) return length ? 0 : 100;
	return length ? Math.round((items.filter(x => !x.value).length / length) * 100) : 100;
};

const getDefectData = (answerType: AnswerType, cAnswer: VisitAnswer, pAnswer: VisitAnswer) => {
	const currentRate = getDefectRate(cAnswer?.items, answerType);
	let defectDiff = null;
	if (pAnswer) defectDiff = currentRate - getDefectRate(pAnswer.items, answerType);
	return { currentRate, defectDiff };
};

export const Defect: React.FC<DefectProps> = ({ title }) => {
	const styles = useStyles(getDefectBlockStyles);
	const theme = React.useContext(ThemeContext);
	const { answer, prevAnswer, prevVisitDate } = React.useContext(QstContext);
	const { currentRate, defectDiff } = getDefectData(answer.answerType, answer, prevAnswer);

	const [icon, sign, color] =
		defectDiff > 0
			? [increaseIcon, "+", theme.colors.danger.main]
			: defectDiff < 0
			? [decreaseIcon, "-", theme.colors.success.active]
			: [null, null, "unset"];

	return (
		answer.answerType !== AnswerType.AttachPhoto && (
			<div className={css(styles.wrap)}>
				<div className={css(styles.chartWrap)}>
					<Gauge
						className={css(styles.gaugeCustom)}
						type="circle"
						value={(100 - currentRate) / 100}
						size={56}
						borderColor={theme.colors.success.active}
						borderBgColor={theme.colors.danger.main}
						borderWidth={getCSSSizeValue(theme.sizing.sizes.sm)}
						valueText={`${currentRate}%`}
						valueFontSize={getCSSSizeValue(theme.sizing.fontSizes.caption)}
						valueTextColor={theme.colors.base.text}
					/>
				</div>
				<div className={css(styles.content)}>
					<div className={css(styles.headerWrap)}>
						<T styles={styles.title} bold>
							{title}
						</T>
						{defectDiff != null && prevVisitDate && (
							<div className={css(styles.lastVisitWrap)}>
								<div className={css(styles.iconWrap)}>
									<Icon icon={["far", "history"]} />
								</div>
								<T size={TextSizeType.caption}>
									{formatDateTime(prevVisitDate, null, { hour: "numeric", minute: "numeric" })}
								</T>
							</div>
						)}
					</div>
					<div className={css(styles.footerWrap)}>
						<T muted styles={styles.description}>
							Share of missing items
						</T>
						{defectDiff != null && prevVisitDate && (
							<div className={css(styles.diffWrap)}>
								<div className={css(styles.iconWrap)}>{icon}</div>
								<div style={{ color: color }}>
									<Text styles={styles.percent} size={TextSizeType.bodyMin}>
										{`${sign + Math.abs(defectDiff)}%`}
									</Text>
								</div>
							</div>
						)}
					</div>
				</div>
			</div>
		)
	);
};

Defect.displayName = "Defect";
