import { useStyles } from "hooks/useStyles";
import { VisitManager } from "quiz/pages/questionnaire/VisitManager";
import { Visit } from "quiz/services/models";
import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";
import { MDTImage } from "../mdtImages/MdtImage";
import { getSignatureControlStyles } from "./SignatureControlStyles";
import { css } from "wraps/styleCreators";
import { Icon } from "quiz/components/icon/Icon";
import { QuizListInputItem } from "quiz/components/quizListInputItem/QuizListInputItem";
import { Divider } from "quiz/components/divider/Divider";
import { Text } from "quiz/components/text/Text";
import { useForceUpdate } from "hooks/useForceUpdate";
import { Signature } from "./Signature";

type SignatureControlProps = {
	readonly?: boolean;
	visitManager: VisitManager;
	visit: Visit;
	emptyText: string;
	width?: number;
	onSignatureStateChange: () => void;
};

export const SignatureControl: React.FC<SignatureControlProps> = props => {
	const { readonly, visitManager, visit, emptyText } = { readonly: false, ...props };
	const theme = React.useContext(ThemeContext);
	const styles = useStyles(getSignatureControlStyles);
	const update = useForceUpdate();

	return (
		<div className={css(styles.signature)}>
			{!readonly ? (
				<>
					<Signature
						signature={visitManager.getFileSignature()}
						onSave={data => {
							visitManager.setFileSignature(data);
							props.onSignatureStateChange();
							update();
						}}
					/>
					<QuizListInputItem
						type="text"
						icon={["far", "file-alt"]}
						value={visitManager.getCommentSignature() || ""}
						onChange={v => {
							visitManager.setCommentSignature(v);
							update();
							props.onSignatureStateChange();
						}}
						placeholder={"Enter full name"}
						required
					/>
					<Divider dash />
					<QuizListInputItem
						type="text"
						icon={["far", "comment-alt"]}
						value={visitManager.getVisitComment() || ""}
						onChange={v => {
							visitManager.setVisitComment(v);
							update();
						}}
						placeholder={"Enter a comment"}
					/>
				</>
			) : (
				<>
					{visit.signatureId && !visit.signatureFile && (
						<MDTImage fileRecord={{ id: visit.signatureId, uid: visit.signatureUid }} styles={styles.img} />
					)}
					{visit.commentSignature && (
						<div className={css(styles.infoItemWrap)}>
							<div className={css(styles.iconWrap)}>
								<Icon color={theme.colors.primary.main} icon={["far", "file-alt"]} />
							</div>
							<Text>{visit.commentSignature}</Text>
						</div>
					)}
					{visit.comment && (
						<>
							<Divider dash />
							<div className={css(styles.infoItemWrap)}>
								<div className={css(styles.iconWrap)}>
									<Icon color={theme.colors.primary.main} icon={["far", "comment-alt"]} />
								</div>
								<Text>{visit.comment}</Text>
							</div>
						</>
					)}
				</>
			)}
		</div>
	);
};
