import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export function getFeedbackStyles(theme: Theme) {
	return {
		wrap: {
			display: "flex",
			flexDirection: "column"
		} as CSSProp,
		footer: {
			backgroundColor: theme.colors.base.background,
			padding: theme.sizing.sizes.md
		} as CSSProp,
		feedback: {
			marginTop: theme.sizing.sizes.md,
			marginBottom: theme.sizing.sizes.md
		},
		select: {
			marginTop: theme.sizing.sizes.md
		},
		faqLinkWrap: {
			marginTop: theme.sizing.sizes.md
		}
	};
}
