import { SwipeoutActions, SwipeoutButton } from "framework7-react";
import { t } from "quiz/QuizApp";
import React, { Fragment } from "react";

type QuizDeleteSwipeoutProps = {
	onDelete: () => void;
};
const deleteText = () => t("Delete");

export const QuizDeleteSwipeout: React.FC<QuizDeleteSwipeoutProps> = ({ onDelete }) => {
	return (
		<Fragment>
			{Array.from(Array(2).keys()).map((k, idx) => (
				<SwipeoutActions key={idx} left={!idx} right={!!idx}>
					<SwipeoutButton onClick={onDelete} delete>
						{deleteText()}
					</SwipeoutButton>
				</SwipeoutActions>
			))}
		</Fragment>
	);
};

QuizDeleteSwipeout.displayName = "QuizDeleteSwipeout";
