import React from "react";
import { Defect } from "quiz/components/question/controls/kpi/defect/Defect";
import { Block } from "quiz/components/block/Block";
import { IQuestion, KPIType } from "quiz/services/models";

type QuestionKpiControlProps = {
	question: IQuestion;
};

const mapKPIType2Kpi = {
	[KPIType.Defect]: Defect
};

export const QuestionKpiControl: React.FC<QuestionKpiControlProps> = React.memo(({ question }) => {
	return (
		<>
			{question.kpi?.map((k, idx) => {
				const Component = mapKPIType2Kpi[k.type];
				return Component ? (
					<Block secondary key={idx}>
						<Component {...{ title: k.name }} />
					</Block>
				) : null;
			})}
		</>
	);
});

QuestionKpiControl.displayName = "QuestionKpiControl";
