import * as React from "react";
import { FunctionComponent } from "react";
import { FieldControlLayout, FieldControlLayoutProps } from "../fieldControlLayout/FieldControlLayout";
import { css } from "wraps/styleCreators";
import { filterProps } from "utils/helpers";
import { inheritFieldControlProps } from "../input/InputControl";
import { useStyles } from "hooks/useStyles";
import { getTextareaStyles } from "./textareaStyles";

type TextareaHTMLProps = React.DetailedHTMLProps<
	React.TextareaHTMLAttributes<HTMLTextAreaElement>,
	HTMLTextAreaElement
>;

interface TextareaControlProps
	extends Pick<FieldControlLayoutProps, typeof inheritFieldControlProps[number]>,
		Omit<TextareaHTMLProps, "onChange" | "value"> {}

export const TextareaControl: FunctionComponent<TextareaControlProps> = function InputCtrl(props) {
	const s = useStyles(getTextareaStyles);
	const fieldControlLayoutProps = filterProps(props, inheritFieldControlProps);
	const htmlProps = filterProps(props, inheritFieldControlProps, false);
	const textAreaRef = React.useRef<HTMLTextAreaElement>();

	return (
		<FieldControlLayout
			{...fieldControlLayoutProps}
			value={props.value}
			onClearClick={() => textAreaRef && textAreaRef.current.focus()}
			customControl={(props, styles) => (
				<textarea
					rows={4}
					{...props}
					{...htmlProps}
					className={css(styles, s.textarea)}
					value={props.value != null ? props.value : ""}
					ref={textAreaRef}
				/>
			)}
		/>
	);
};
