import { QrScannerExtension } from "./scanner/qrScanner";
import { BrowserQrScanner } from "./scanner/browser/BrowserQrCodeScanner";
import { DatabaseExtension } from "./db/dbExtension";
import { BrowserDb } from "./db/browserDb";
import { NativeDb } from "./db/nativeDb";
import { NativeQrScanner } from "./scanner/native/nativeQrScanner";
import { QuizStatusBar } from "./statusBar/statusBar";
import { updateManager, quizDraftService, CONFIG } from "quiz/QuizApp";
import { applyCssVars } from "utils/cssHelpers";
import { NativeStatusBarIndent } from "utils/constants";
import { UploadPhoto } from "./uploadPhoto/models";
import { CustomPhotoExtension } from "./uploadPhoto/CustomPhotoExtension";
import { UploadPhotoExtension } from "./uploadPhoto/UploadPhotoExtension";

const CORDOVA_USER_AGENT = "ics-cordova";

export enum AppEnvironment {
	native = "native",
	browser = "browser"
}

interface Extension {
	qrScanner: QrScannerExtension;
	db: DatabaseExtension;
}

export class QuizExtensionService implements Extension {
	readonly qrScanner: QrScannerExtension;
	readonly onLogoClick: () => void;
	readonly statusBar = new QuizStatusBar();
	uploadPhoto: UploadPhoto;
	db: DatabaseExtension;
	platform: string;

	constructor() {
		this.qrScanner =
			this.defineEnvironment === AppEnvironment.browser ? new BrowserQrScanner() : new NativeQrScanner();
		this.db = new BrowserDb();
		this.uploadPhoto = new UploadPhotoExtension();
	}

	customizeStatusBar() {
		if (window.cordova && cordova.platformId === "android") {
			applyCssVars({
				statusBarBackgroundColor: QuizStatusBar.defaultStatusBarColor,
				statusBarIndent: NativeStatusBarIndent.statusBar,
				navbarPhotoBrowserIndent: NativeStatusBarIndent.navbarPhotoBrowser
			}); //todo     
		}
	}

	private get defineEnvironment(): AppEnvironment {
		return new RegExp(CORDOVA_USER_AGENT, "i").test(navigator.userAgent)
			? AppEnvironment.native
			: AppEnvironment.browser;
	}

	startApp(startApp: (onInit?: () => void) => void) {
		this.defineEnvironment === AppEnvironment.native ? this.initialize(startApp) : startApp();
	}

	private initialize(startApp: (onInit?: () => void) => void) {
		document.addEventListener("deviceready", this.onDeviceReady.bind(this, startApp), false);
	}

	private onDeviceReady(startApp: (onInit?: () => void) => void) {
		(cordova as any).getAppVersion.getVersionNumber(function(version) {
			CONFIG.version = version;
		});

		if (cordova.platformId == "android") {
			this.platform = "android";
		} else if (cordova.platformId == "ios") {
			this.platform = "ios";
		}

		const update = () => {
			updateManager.checkForUpdate();
			window.codePush.notifyApplicationReady();
		};

		startApp(() => {
			update();
		});

		document.addEventListener("resume", () => {
			update();
		});

		this.customizeStatusBar();
		window.addEventListener("keyboardWillHide", function() {
			window.scrollTo(0, 0);
		});

		if (cordova.platformId === "android") {
			this.uploadPhoto = new CustomPhotoExtension();
		}
	}
}
