import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getSendPasswordPageStyles = (theme: Theme) => {
	return {
		wrap: {
			height: "100%",
			display: "flex",
			flexFlow: "column"
		} as CSSProp,
		error: {
			color: theme.colors.danger.main,
			marginBottom: "0"
		} as CSSProp
	};
};
