import * as React from "react";
import { getOutletExtendableItemStyles } from "./outletExtendableItemStyles";
import { OutletFullData } from "quiz/services/models";
import { useStyles } from "hooks/useStyles";
import { ThemeContext } from "themeContext/ThemeContext";
import { ListItem } from "quiz/components/listView/ListView";
import { AccordionContent, List } from "framework7-react";
import { Block } from "quiz/components/block/Block";
import { Icon, IconProp } from "quiz/components/icon/Icon";
import { css } from "wraps/styleCreators";
import { Text } from "quiz/components/text/Text";
import { TextSizeType } from "theme/sizing";
import { T } from "quiz/components/text/T";
import { QuizList } from "quiz/components/quizList/QuizList";
import { Avatar } from "quiz/components/user/Avatar";
import { abbr, getOutletChainName } from "utils/helpers";
import { Divider } from "quiz/components/divider/Divider";
import { Row } from "../row/Row";

type RowItem = {
	title: string;
	value: string;
	format?: (v: string) => string;
};

const getRowData = (props: OutletFullData): RowItem[] => [
	{ title: "Code", value: props?.code, format: (x: string) => x && `#${x}` },
	{ title: "Outlet type", value: props?.type },
	{ title: "Chain type", value: props?.outletChain?.type },
	{ title: "Chain", value: props?.outletChain?.name },
	{ title: "Title", value: props?.name },
	{ title: "INN", value: props?.legalEntity?.inn },
	{ title: "Legal Entity", value: props?.legalEntity?.name },
	{ title: "Phone", value: props?.phone }
];

const employeeListItemClassName = "employee__listItem";
const employeeStyles = `.${employeeListItemClassName} .item-content {
			padding-left: unset;
		}
		.${employeeListItemClassName} .item-link .item-inner:before {
			right: 0
		}
		`.trim();

export const OutletExtendableItem: React.FC<OutletFullData> = React.memo(props => {
	const theme = React.useContext(ThemeContext);
	const styles = useStyles(getOutletExtendableItemStyles);
	const { name, iconName, outletChain, address, format, employees } = props;

	return (
		<>
			<QuizList isLoading={false} isEmpty={false} errors={[]}>
				<ListItem accordionItem>
					<div className={css(styles.header)} slot="inner-start">
						<Row>
							<div className={css(styles.title)}>
								{iconName && (
									<div className={css(styles.iconWrap)}>
										<Icon icon={["far", iconName] as IconProp} color={theme.colors.primary.main} />
									</div>
								)}
								<Text size={TextSizeType.body}>
									{outletChain ? getOutletChainName(outletChain) : name}
								</Text>
							</div>
						</Row>
						<Row>
							<Text muted size={TextSizeType.caption}>
								{address}
							</Text>
						</Row>

						<div>
							{format && (
								<Text size={TextSizeType.caption} muted>
									{`${format}`.concat(
										outletChain && outletChain.type ? ` / ${outletChain.type}` : ""
									)}
								</Text>
							)}
						</div>
					</div>
					<Block secondary>
						<AccordionContent>
							{getRowData(props).map((item, idx) => (
								<React.Fragment key={idx}>
									<Divider dash />
									<div className={css(styles.row)} key={item.title}>
										<T styles={styles.rowName} size={TextSizeType.caption} muted>
											{item.title}
										</T>
										<Text styles={styles.text} size={TextSizeType.caption}>
											{item.format ? item.format(item.value) : item.value}
										</Text>
									</div>
								</React.Fragment>
							))}
							{employees && (
								<>
									<style>{employeeStyles}</style>
									<Divider dash />
									<div className={css(styles.employeeRow)}>
										<T styles={styles.rowName} size={TextSizeType.caption} muted>
											Employees
										</T>
									</div>
									<List noHairlinesBetweenMd noChevron>
										{employees.map((employee, idx) => (
											<ListItem key={idx} className={employeeListItemClassName}>
												<div className={css(styles.employeeBlock)}>
													<Avatar text={abbr(employee.name)} />
													<div className={css(styles.employeeInfo)}>
														<Text styles={styles.text} size={TextSizeType.caption}>
															{employee.name}
														</Text>
														<Text styles={styles.positionText} size={TextSizeType.caption}>
															{employee.positionName}
														</Text>
													</div>
												</div>
											</ListItem>
										))}
									</List>
								</>
							)}
						</AccordionContent>
					</Block>
				</ListItem>
			</QuizList>
		</>
	);
});

OutletExtendableItem.displayName = "OutletExtendableItem";
