import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";
import { QuizSkeleton } from "../QuizSkeleton";
import { SkeletonItemWrap } from "../SkeletonItem/SkeletonItem";
import { SkeletonIcon } from "../SkeletonItem/SkeletonIcon";
import { SkeletonRow } from "../SkeletonItem/SkeletonRow";
import { QuizListSkeletonProps, QuizSkeletonList } from "../SkeletonList/SkeletonList";
import { Theme } from "theme/theme";
import { useStyles } from "hooks/useStyles";
import { CSSProp, css } from "wraps/styleCreators";
import { Page } from "quiz/components/page/Page";
import { EmptyDivider } from "quiz/components/divider/EmptyDivider";
import { BackLink } from "quiz/components/quizNavBar/icons/backlink/BackLink";

const getQuestionnaireSkeletonStyles = (theme: Theme) => {
	return {
		navbarSkeleton: {
			boxShadow: theme.shadows.down.md,
			height: "calc(60px + var(--f7-safe-area-top))", //todo: 60px
			justifyContent: "space-between",
			background: theme.colors.primary.background,
			paddingLeft: theme.constants.gridPadding,
			paddingRight: theme.constants.gridPadding,
			display: "flex",
			alignItems: "center"
		} as CSSProp
	};
};

export const QuestionnaireSkeleton: React.FC<QuizListSkeletonProps> = props => {
	const theme = React.useContext(ThemeContext);
	const styles = useStyles(getQuestionnaireSkeletonStyles);

	return (
		<Page>
			<div className={css(styles.navbarSkeleton)}>
				<SkeletonItemWrap align="center">
					<BackLink />
					<SkeletonRow type="title" width={164} />
				</SkeletonItemWrap>
				<SkeletonIcon size="small" />
			</div>
			<QuizSkeletonList>
				<QuizSkeleton key={0}>
					<SkeletonItemWrap spacing={"space-between"} align="center">
						<SkeletonItemWrap direction="column">
							<SkeletonItemWrap align="center" marginBottom={12}>
								<SkeletonIcon size="small" marginRight={12} />
								<SkeletonRow type="title" width={164} />
							</SkeletonItemWrap>
							<SkeletonRow type="text" width={216} />
						</SkeletonItemWrap>
						<SkeletonIcon size="small" />
					</SkeletonItemWrap>
				</QuizSkeleton>
			</QuizSkeletonList>
			<EmptyDivider height={theme.sizing.sizes.lg} />
			<QuizSkeletonList>
				{Array.from(new Array(props.totalBlocks)).map((el, idx) => (
					<QuizSkeleton key={++idx}>
						<SkeletonItemWrap align="center">
							<SkeletonIcon size="small" marginRight={20} />
							<SkeletonRow type="text" width={164} />
						</SkeletonItemWrap>
					</QuizSkeleton>
				))}
			</QuizSkeletonList>
		</Page>
	);
};

QuestionnaireSkeleton.displayName = "QuestionnaireSkeleton";
