import * as React from "react";
import { Image } from "quiz/components/image/Image";
import { Theme } from "theme/theme";
import { CSSProp, css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import { Icon } from "quiz/components/icon/Icon";
import { ThemeContext } from "themeContext/ThemeContext";
import { Divider } from "quiz/components/divider/Divider";
import { nav } from "quiz/QuizApp";
import { QuestionActionButton } from "quiz/components/question/common/questionActionButton/QuestionActionButton";

export type SugnatureProps = {
	signature?: string;
	onSave: () => void;
};

const getSignatureStyles = (theme: Theme) => {
	return {
		image: {
			width: "100%"
		} as CSSProp,
		editing: {
			float: "right"
		} as CSSProp,
		asteriksIcon: {
			fontWeight: theme.sizing.fontWeights.bold,
			fontSize: theme.sizing.sizes.sm,
			color: theme.colors.danger.main
		} as CSSProp,
		emptySignature: {
			marginTop: `-${theme.sizing.sizes.md}`
		} as CSSProp
	};
};

export const Signature = ({ signature, onSave }) => {
	const styles = useStyles(getSignatureStyles);
	const theme = React.useContext(ThemeContext);

	const edit = () => {
		nav.toSignature({
			onEnd: onSave,
			img: signature
		});
	};

	return signature ? (
		<div onClick={edit}>
			<div className={css(styles.editing)}>
				<Icon icon={["far", "edit"]} color={theme.colors.primary.main} />
			</div>
			<Image src={signature} styles={styles.image} />
			<Divider dash />
		</div>
	) : (
		<div className={css(styles.emptySignature)} onClick={edit}>
			<QuestionActionButton
				icon={["far", "signature"]}
				text={"Set signature"}
				onClick={() => {}}
				fullWidth
				{...{ iconRight: ["fas", "asterisk"], iconRightStyles: styles.asteriksIcon }}
			/>
			<Divider dash />
		</div>
	);
};

Signature.displayName = "Signature";
