import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { getAvatarStyles } from "./avatarStyles";
import { css } from "wraps/styleCreators";
import { colorGen } from "utils/helpers";
import { Text } from "quiz/components/text/Text";

export interface AvatarProps {
	text: string;
	type?: "small" | "big" | "normal" | "badge";
}

export const Avatar: React.FC<AvatarProps> = React.memo(props => {
	const styles = useStyles(getAvatarStyles);
	const color = colorGen(props.text);

	const size = props.type || "normal";

	const sizeStyle = {
		normal: {
			wrap: styles.wrap,
			text: styles.text
		},
		small: {
			wrap: styles.wrapSmall,
			text: styles.textSmall
		},
		big: {
			wrap: styles.wrapBig,
			text: styles.textBig
		},
		badge: {
			wrap: styles.wrapBadge,
			text: styles.textBadge
		}
	}; //toDO: from styles? typing

	return (
		<span className={css(sizeStyle[size].wrap)} style={{ background: color }}>
			<Text styles={sizeStyle[size].text}>{props.text}</Text>
		</span>
	);
});
Avatar.displayName = "Avatar";
