import * as deepExtend from "deep-extend";
import { ThemeDefaults } from "./themeDefaults";

export interface ThemeZIndexes {
	appBar: number;
	sideBar: number;
	modal: number;
	notification: number;
	dropdown: number;
	tooltip: number;
}

export function createZIndexes(options: Partial<ThemeZIndexes> = {}): ThemeZIndexes {
	return deepExtend(ThemeDefaults.zIndexes, options);
}
