import { useState, useEffect } from "react";

export function useLongTap(callback: () => void, ms = 300) {
	const [startLongTap, setStartTap] = useState(false);

	useEffect(() => {
		let timer: number;
		if (startLongTap) {
			timer = window.setTimeout(callback, ms);
		} else {
			clearTimeout(timer);
		}

		return () => {
			clearTimeout(timer);
		};
	}, [startLongTap]);

	return {
		onTouchStart: () => setStartTap(true),
		onTouchEnd: () => setStartTap(false)
	};
}
