// Enum helpers
function enumFilterKey(key, i) {
	return isNaN(Number(key));
}

export function forEachEnumKey(
	enumObj: any,
	callback: (enumKey: string, index: number) => void
) {
	let keys = Object.keys(enumObj);
	return keys.filter(enumFilterKey).forEach(callback);
}

export function mapEnumKeys(
	enumObj: any,
	callback: (enumKey: string, index: number) => void
) {
	let keys = Object.keys(enumObj);
	return keys.filter(enumFilterKey).map(callback);
}

export function objectFromEnum<T = any>(
	enumObj: any,
	callback: (enumKey: T, index: number) => any = k => k
): any {
	let result = {};
	forEachEnumKey(enumObj, (k, i) => (result[k] = callback(k as any, i)));
	return result;
}
