import { List } from "quiz/components/listView/ListView";
import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";

export const QuestionList: React.FC = props => {
	const theme = React.useContext(ThemeContext);

	const styles = React.useMemo(
		() =>
			`
.questionList__block .item-content {
	--f7-list-item-padding-vertical: ${theme.sizing.sizes.md};
	--f7-list-item-media-margin: ${theme.sizing.sizes.sm};
	--f7-checkbox-extra-margin: ${theme.sizing.sizes.md};
	--f7-radio-extra-margin: ${theme.sizing.sizes.md};
	--f7-list-item-title-line-height: ${theme.sizing.sizes.lg};
}`.trim(),
		[]
	);

	return (
		<>
			<style>{styles}</style>
			<List noHairlinesBetween noHairlines className="questionList__block">
				<ul>{props.children}</ul>
			</List>
		</>
	);
};
QuestionList.displayName = "f7-list";
