import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getDefectBlockStyles = (theme: Theme) => {
	return {
		wrap: {
			padding: `${theme.sizing.sizes.md} 0`,
			display: "flex",
			width: "100%",
			alignItems: "center"
		} as CSSProp,
		chartWrap: {
			marginRight: theme.sizing.sizes.md,
			minHeight: "56px",
			minWidth: "56px"
		} as CSSProp,
		gaugeCustom: {
			display: "flex"
		} as CSSProp,
		content: {
			width: "100%",
			display: "flex",
			flexFlow: "column nowrap",
			justifyContent: "space-between"
		} as CSSProp,
		headerWrap: {
			display: "flex",
			justifyContent: "space-between",
			alignItems: "center",
			paddingBottom: theme.sizing.sizes.sm
		} as CSSProp,
		title: {
			fontSize: theme.sizing.fontSizes.body,
			lineHeight: "100%"
		} as CSSProp,
		lastVisitWrap: {
			display: "flex",
			justifyContent: "center",
			alignItems: "center"
		} as CSSProp,
		iconWrap: {
			display: "flex",
			width: theme.sizing.sizes.lg,
			height: theme.sizing.sizes.lg,
			marginRight: theme.sizing.sizes.sm,
			alignItems: "center",
			justifyContent: "center"
		} as CSSProp,
		footerWrap: {
			display: "flex",
			justifyContent: "space-between",
			alignItems: "center"
		} as CSSProp,
		description: {
			fontSize: theme.sizing.fontSizes.caption,
			lineHeight: "120%"
		} as CSSProp,
		diffWrap: {
			display: "flex",
			justifyContent: "center"
		} as CSSProp,
		percent: {
			color: "inherit"
		} as CSSProp
	};
};
