import { Theme } from "theme/theme";
import { CSSProp, CSSSizeValue } from "wraps/styleCreators";
import { SkeletonItemWrapProps } from "./SkeletonItem";
import { SkeletonRowProps } from "./SkeletonRow";
import { SkeletonIconProps } from "./SkeletonIcon";

export const getSkeletonItemWrapStyles = (theme: Theme, props: SkeletonItemWrapProps) => {
	return {
		wrap: {
			...props,
			flexDirection: props.direction || "initial",
			justifyContent: props.spacing || "initial",
			alignItems: props.align || "initial",
			display: "flex"
		} as CSSProp
	};
};

export const getSkeletonRowStyles = (theme: Theme, props: SkeletonRowProps) => {
	return {
		row: {
			...props,
			height: props.type === "title" ? theme.sizing.sizes.md : theme.sizing.sizes.sm,
			borderRadius: theme.sizing.sizes.xxs
		} as CSSProp
	};
};

export const getSkeletonIconStyles = (theme: Theme, props: SkeletonIconProps) => {
	// todo 56px
	const size =
		props.size === "large"
			? ("56px" as CSSSizeValue)
			: props.size === "medium"
			? theme.sizing.sizes.xl
			: theme.sizing.sizes.md;
	return {
		icon: {
			...props,
			width: size,
			height: size,
			borderRadius: "50%"
		} as CSSProp
	};
};
