import * as React from "react";
import { ListItem, ListInput } from "quiz/components/listView/ListView";
import F7ListItem from "framework7-react/components/list-item";
import { useStyles } from "hooks/useStyles";
import { getQstListItemStyles } from "./QuestionListItemStyles";
import { css } from "wraps/styleCreators";
import { F7ListInput } from "framework7-react";
import { Divider } from "quiz/components/divider/Divider";

export interface QstListItemProps extends F7ListItem.Props {
	dash?: boolean;
	groups?: boolean;
	className?: string;
	danger?: boolean;
}

export const QuestionListItem: React.FC<QstListItemProps> = React.memo(props => {
	const styles = useStyles(getQstListItemStyles);
	const isDivider = props.dash || props.groups;

	return (
		<>
			<ListItem {...props}>{props.children}</ListItem>
			{isDivider && (
				<div slot="root" className={css(props.groups ? null : styles.wrap)}>
					<Divider dash />
				</div>
			)}
		</>
	);
});
QuestionListItem.displayName = "f7-list-item";

interface QuestionListInputProps extends F7ListInput.Props {
	isDash?: boolean;
}

export const QuestionListInput: React.FC<QuestionListInputProps> = React.memo(props => {
	const styles = useStyles(getQstListItemStyles);
	return (
		<ListInput
			// className={css(props.isDash && styles.dash)}
			{...props}
			value={props.value != null ? props.value : ""}
		/>
	);
});
QuestionListInput.displayName = "f7-list-input";
