import * as React from "react";
import { QuizPage } from "./components/quizPage/QuizPage";
import { ForgotPasswordPage } from "./pages/forgotPasswordPage/ForgotPasswordPage";
import { SendPasswordPage } from "./pages/sendPasswordPage/SendPasswordPage";
import { LoginPage } from "./pages/loginPage/LoginPage";
import { RegistrationPage } from "./pages/registrationPage/registrationPage";
import { nav, quizAuthService, logger, t, quizUserService } from "./QuizApp";
import { FullscreenErrorPage } from "./components/infoBlock/fullscreenInfoBlock/FullscreenErrorPage";
import { ColorTypes } from "theme/colors";
import { QuizFeedbackPage } from "./pages/feedback/QuizFeedbackPage";
import { FeedbackTypeEnum } from "quiz/components/feedback/Feedback";
import { MDTApiError } from "mdtApiClient/mdtApiClient";
import { QuizError } from "./components/errors/QuizError";
import { AcceptPrivacyPolicyPage } from "./pages/acceptPrivacyPolicy/AcceptPrivacyPolicyPage";
import { serverErrorIcon } from "./components/infoScreenIcons/serverErrorIcon";
import { FullscreenInfoProps } from "./components/infoBlock/fullscreenInfoBlock/FullscreenInfo";
import { parseErrorMessage } from "utils/helpers";
import { f7 } from "framework7-react";
import { RegistrationSuccessPage } from "./pages/registrationSuccessPage/RegistrationSuccessPage";

export interface QuizPageComponentProps {
	setTitle(title: string): void;
	setSubtitle(subtitle: string): void;
	setActions(actions: React.ReactNode): void;
	setSearch(active: boolean): void;
	context: any; //todo typing context?
}

export const routes = (error: Error | MDTApiError) => [
	{
		path: "/password/:passwordKey", //todo: from ApiClientPassword
		component: SendPasswordPage
	},
	{
		path: "/registration/",
		component: RegistrationPage
	},
	{
		path: "/registration-success/",
		component: RegistrationSuccessPage
	},
	{
		path: "/forgot-password/",
		component: ForgotPasswordPage
	},
	{
		path: "/feedback/",
		component: QuizFeedbackPage
	},
	{
		path: "(.*)",
		async(routeTo, routeFrom, resolve) {
			logger.log("navigation", {
				pathTo: routeTo.url,
				pathFrom: routeFrom.url
			});

			if (error) {
				const props: FullscreenInfoProps = {
					colorType: ColorTypes.danger,
					title: t("Critical App Error"),
					message: parseErrorMessage(error),
					icon: serverErrorIcon,
					primaryButton: {
						title: t("Refresh"),
						icon: ["far", "sync"],
						command: () => f7.views.main.router.refreshPage()
					},
					secondaryButton: {
						title: t("Complain"),
						icon: ["far", "comment-alt-lines"],
						command: () => nav.toFeedback({ type: FeedbackTypeEnum.Problem, value: error.message })
					}
				};

				return resolve({ component: FullscreenErrorPage }, { props });
			}
			if (!quizAuthService.isLoggedIn()) {
				return resolve({ component: LoginPage }, {});
			}
			if (!quizUserService.privacyPolicyIsAccepted()) {
				return resolve({ component: AcceptPrivacyPolicyPage }, {});
			}
			const { component, wrapProps, componentProps, pageEvents } = nav.getNavData(routeTo.url, routeTo.context);
			const props = {
				component,
				componentProps,
				backLink: true
			};
			return resolve({ component: QuizPage }, { props: { ...props, ...wrapProps, pageEvents } });
		}
	}
];
