import { List, ListItem as ListItemBase, ListInput, F7ListItem } from "framework7-react";
import { quizListItemStyles } from "./quizListItemStyles";
import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";

export const QuizListItem: React.FC<F7ListItem.Props> = props => {
	const theme = React.useContext(ThemeContext);
	return (
		<>
			<style>{quizListItemStyles(theme)}</style>
			<ListItemBase {...props} className="quiz_ListItem" />
		</>
	);
};

QuizListItem.displayName = "f7-list-item";
