import { UploadPhoto, GetUploadButtonParams } from "./models";
import * as React from "react";
import { ChosePhotoButton } from "./common/choosePhotoButton/ChosePhotoButton";
import { uploadHelper } from "quiz/QuizApp";
import { uploadHelperOptions } from "quiz/components/question/controls/QuestionGalleryControl";
import { UploadPhotoButton } from "./common/uploadPhotoButton/UploadPhotoButton";

export const prepareFiles = async (e: HTMLInputElement, cb: (photos) => Promise<void>) => {
	const targetFiles = e.files;
	const filteredFiles = uploadHelper.upload(targetFiles, uploadHelperOptions);
	cb(Array.from(filteredFiles));
};

export class UploadPhotoExtension implements UploadPhoto {
	getUploadButton(params: GetUploadButtonParams) {
		return (
			<ChosePhotoButton
				onChange={e => {
					prepareFiles(e, params.commonLogic);
				}}
				button={() => <UploadPhotoButton icon={["far", "camera"]} required={params.required} />}
			/>
		);
	}
}
