import { CSSProp } from "wraps/styleCreators";
import { Theme } from "theme/theme";

export const getQuizListInputItemStyles = (theme: Theme) => ({
	input: {
		outline: "none",
		"::placeholder": {
			color: theme.colors.base.texts.muted
		},
		width: "100%"
	} as CSSProp,
	inputWrap: {
		display: "flex",
		height: "100%",
		alignItems: "center",
		width: "100%",
		marginRight: theme.sizing.sizes.sm,
		color: theme.colors.base.text,
		flex: 1
	} as CSSProp,
	icon: {
		color: theme.colors.base.border
	},
	dash: {
		borderBottom: `1px dashed ${theme.colors.base.border}`
	},
	danger: {
		color: theme.colors.danger.main
	},
	asterisk: {
		color: theme.colors.danger.main,
		fontWeight: theme.sizing.fontWeights.bold,
		fontSize: theme.sizing.sizes.sm
	} as CSSProp
});
