import * as deepExtend from "deep-extend";
import { parseUnitCSSSizeValue } from "../utils/cssHelpers";
import { objectFromEnum } from "../utils/enum";
import { ThemeDefaults } from "./themeDefaults";
import { SizeType } from "./themeTypes";
import { DeepPartial } from "types";
import { CSSProps, CSSSizeValue } from "wraps/styleCreators";

export enum TextSizeType {
	/** 96px */
	mega = "mega",
	/** 64px */
	super = "super",
	/** 48px */
	display = "display",
	/** 32px */
	heading = "heading",
	/** 24px */
	title = "title",
	/** 18px */
	bodyMax = "bodyMax",
	/** 16px */
	body = "body",
	/** 14px */
	bodyMin = "bodyMin",
	/** 12px */
	caption = "caption"
}

export enum TextWeightType {
	light = "light",
	medium = "normal",
	bold = "bold"
}

export interface ThemeSizing {
	sizes: { [key in keyof typeof SizeType]: CSSSizeValue };
	fontSizes: { [key in keyof typeof TextSizeType]: CSSProps["fontSize"] };
	lineHeights: { [key in keyof typeof TextSizeType]: CSSProps["lineHeight"] };
	fontWeights: { [key in keyof typeof TextWeightType]: CSSProps["fontWeight"] };
}

export function createSizeVariants(minSize: CSSSizeValue): { [key in keyof typeof SizeType]: string } {
	const data = parseUnitCSSSizeValue(minSize);
	return objectFromEnum(SizeType, (size, i) => (data.valid ? `${(i + 1) * data.value}${data.unit}` : minSize));
}

export function createThemeSizing(options: DeepPartial<ThemeSizing> = {}): ThemeSizing {
	let min: CSSSizeValue = "8px";
	if (ThemeDefaults.sizing && ThemeDefaults.sizing.sizes && ThemeDefaults.sizing.sizes.xxs)
		min = ThemeDefaults.sizing.sizes.xxs;
	return deepExtend(
		{
			sizes: createSizeVariants(min)
		},
		ThemeDefaults.sizing,
		options
	);
}
