import { useStyles } from "hooks/useStyles";
import { T } from "quiz/components/text/T";
import React from "react";
import { TextSizeType } from "theme/sizing";
import { getNavTitleTextStyles } from "./navTitleTextStyles";

type NavTitleProps = {
	text: string;
};

export const NavTitleText: React.FC<NavTitleProps> = props => {
	const styles = useStyles(getNavTitleTextStyles);

	return (
		<T styles={styles.text} bold size={TextSizeType.body}>
			{props.text}
		</T>
	);
};
