import { VisitAnswer } from "quiz/services/models";
import { ValidationError } from "quiz/pages/questionnaire/VisitManager";
import React from "react";

export type QuestionnaireContext = {
	answer: VisitAnswer;
	prevAnswer: VisitAnswer;
	prevVisitDate: Date;
	validationErrors?: ValidationError[];
	slideActive?: boolean;
	answerIsValid?: boolean;
	attachPhotoIsValid?: boolean;
	required?: boolean;
};

export const QstContext = React.createContext<QuestionnaireContext>({
	answer: null,
	prevAnswer: null,
	prevVisitDate: null,
	validationErrors: [],
	slideActive: false,
	answerIsValid: false,
	attachPhotoIsValid: false,
	required: false
});
