import { MDTApiError } from "mdtApiClient/mdtApiClient";

export type QuizAppState = {
	ready: boolean;
	error: Error | MDTApiError;
	deps: {
		user: boolean;
		data: boolean;
	};
};

export enum quizAppActions {
	"USER_READY" = "USER_READY",
	"DATA_READY" = "DATA_READY",
	"APP_READY" = "APP_READY",
	"ERROR" = "ERROR"
}

export type Action = {
	type: string;
	payload?: any;
};

export const initialQuizAppState: QuizAppState = {
	ready: false,
	error: null,
	deps: {
		user: false,
		data: false
	}
};

export const quizAppReducer = (state: QuizAppState, action: Action): QuizAppState => {
	switch (action.type) {
		case quizAppActions.APP_READY:
			return {
				...state,
				ready: true
			};
		case quizAppActions.DATA_READY:
			return {
				...state,
				deps: {
					...state.deps,
					data: true
				}
			};
		case quizAppActions.USER_READY:
			return {
				...state,
				deps: {
					...state.deps,
					user: true
				}
			};
		case quizAppActions.ERROR:
			return {
				...state,
				ready: true,
				error: action.payload
			};
		default:
			return state;
	}
};
