import { CSSTransition, TransitionGroup } from "react-transition-group";
import React from "react";
import { useStyles } from "hooks/useStyles";
import { getQuizFadeStyles, customFadeStyles } from "./quizFadeStyles";
import { css } from "wraps/styleCreators";

type FadeProps = {
	animationKey: string;
};

export const QuizFade: React.FC<FadeProps> = props => {
	const transitionProps = {
		appear: props.animationKey === "loading",
		in: props.animationKey === "ready"
	};
	const styles = useStyles(getQuizFadeStyles);

	return (
		<div className={css(styles.fadeWrap)}>
			<style>{customFadeStyles}</style>
			<TransitionGroup>
				<CSSTransition {...transitionProps} key={props.animationKey} classNames="custom-fade" timeout={1000}>
					<div>{props.children}</div>
				</CSSTransition>
			</TransitionGroup>
		</div>
	);
};
