import * as React from "react";
import { Block } from "quiz/components/block/Block";
import { SkeletonItemWrap } from "../SkeletonItem/SkeletonItem";
import { SkeletonRow } from "../SkeletonItem/SkeletonRow";
import { ThemeContext } from "themeContext/ThemeContext";
import { useStyles } from "hooks/useStyles";
import { Theme } from "theme/theme";
import { css } from "wraps/styleCreators";

const getBlockTitleStyles = (theme: Theme) => ({
	block: {
		marginBottom: theme.sizing.sizes.sm,
		marginTop: theme.sizing.sizes.md
	},
	wrap: {
		paddingTop: theme.sizing.sizes.xs,
		paddingBottom: theme.sizing.sizes.xs
	}
});

type BlockTitleSkeletonProps = {
	titleOnly?: boolean;
};

export const BlockTitleSkeleton: React.FC<BlockTitleSkeletonProps> = React.memo(props => {
	const theme = React.useContext(ThemeContext);
	const styles = useStyles(getBlockTitleStyles);

	return (
		<Block styles={styles.block}>
			<div className={css(styles.wrap)}>
				<SkeletonItemWrap spacing={"space-between"} align="center">
					<SkeletonRow type="text" width={92} backgroundColor={theme.colors.base.border} />
					{!props.titleOnly && (
						<SkeletonRow type="text" width={40} backgroundColor={theme.colors.base.border} />
					)}
				</SkeletonItemWrap>
			</div>
		</Block>
	);
});

BlockTitleSkeleton.displayName = "BlockTitleSkeleton";
