import React, { useContext } from "react";
import { css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import { getInstanceBarStyles, AppMode } from "./instanceBarStyles";
import { Text } from "quiz/components/text/Text";
import { ThemeContext } from "themeContext/ThemeContext";
import { applyCssVars } from "utils/cssHelpers";

type InstanceBarProps = {
	appMode: AppMode;
	text?: string;
};

export const InstanceBar: React.FC<InstanceBarProps> = React.memo(props => {
	const appMode = props.appMode;
	const theme = useContext(ThemeContext);

	const sizeOfBar = React.useMemo(() => theme.sizing.sizes.sm, []);

	const styles = useStyles(getInstanceBarStyles, { appMode, sizeOfBar });

	React.useEffect(() => {
		applyCssVars({
			"--instanceBarIndent": sizeOfBar
		});
	}, []);

	return <div className={css(styles.bar)}>{props.text && <Text styles={styles.text}>{props.text}</Text>}</div>;
});
InstanceBar.displayName = "InstanceBar";
