import * as React from "react";
import { Block } from "quiz/components/block/Block";
import { logo } from "../../../QuizApp";
import { Image } from "quiz/components/image/Image";
import { useStyles } from "hooks/useStyles";
import { getAboutPageStyles } from "./aboutPageStyles";
import { Version } from "../Version";
import { css } from "wraps/styleCreators";

export const AboutPage: React.FC = () => {
	const styles = useStyles(getAboutPageStyles);
	return (
		<>
			<Block secondary styles={styles.wrap}>
				<div className={css(styles.logo)}>
					<Image src={logo} />
				</div>
				<div>
					<Version />
				</div>
			</Block>
		</>
	);
};
