import * as React from "react";
import { Tabs as TabsBase, Toolbar as ToolbarBase, Tab as TabBase, F7Toolbar } from "framework7-react";
import { ThemeContext } from "themeContext/ThemeContext";
import { applyTheme, tabsStyles } from "./tabsStyles";

export const Tabs = TabsBase;

export const Toolbar: React.FunctionComponent<F7Toolbar.Props> = props => {
	const theme = React.useContext(ThemeContext);
	React.useEffect(() => {
		applyTheme(theme);
	});

	return (
		<div>
			<style>{tabsStyles(theme)}</style>
			<ToolbarBase {...props}>{props.children} </ToolbarBase>
		</div>
	);
};
export const Tab = TabBase;
