import { ApiUrlHelper, Query } from "./helpers/urlHelper";
import { ApiClientAuth } from "./modules/auth/auth";
import { ApiClientOAuth } from "./modules/auth/oauth";
import { ApiClientPassword } from "./modules/auth/password";
import { appIsOnline } from "utils/helpers";
import { OfflineError } from "quiz/services/OfflineService";
import { docsApiUrl } from "utils/constants";
import { VisitAnswerFile } from "quiz/services/models";
import { uploadHelper } from "quiz/QuizApp";
import { APIFile } from "quiz/services/data/QuizQstService";

export type MDTRecordId = number | string;

export type MDTRecord = {
	$table: string;
	$id?: MDTRecordId;

	$params?: any; //todo: what for? Examples: formId, StatusComment
};

export interface MDTRequestSettings {
	camel?: boolean;
}

export interface MDTModules {
	oauth?: boolean;
	password?: boolean;
}

export type FileUploadRes = {
	ID: number;
	Name: string;
	ShortUid: string;
	Size: number;
	UID: string;
};

export type FileRecord = {
	id?: MDTRecordId;
	uid?: string;
};

export type DependentTable = {
	[key: string]: {
		$table: string;
		[key: string]: any;
	};
};

export type SaveParams = Object;

interface MDTApiClientOptions {
	onRequestError?: (err, url, data) => Promise<void>;
	appUrl?: string;
}

export class MDTApiClient {
	modules: MDTModules = {};
	auth = new ApiClientAuth(this);
	oauth: ApiClientOAuth;
	password: ApiClientPassword;

	apiUrl: string;
	appUrl: string;
	apiVersion: string;

	constructor(apiUrl: string, apiVersion: string, modules?: MDTModules, private options?: MDTApiClientOptions) {
		this.apiUrl = apiUrl.replace(/\/$/, "");
		this.appUrl = options && options.appUrl;
		this.apiVersion = apiVersion;
		this.modules = modules || {
			password: true,
			oauth: true
		};

		if (this.modules.password) this.password = new ApiClientPassword(this);
		if (this.modules.oauth) this.oauth = new ApiClientOAuth(this);
	}

	async request(
		url: string,
		data?: Object | [],
		settings: MDTRequestSettings = {},
		requestOptions: RequestInit = {}
	) {
		const options: RequestInit = {
			credentials: "include",
			method: "POST",
			headers: {
				"Content-Type": "application/json"
			}
		};
		if (data) options.body = JSON.stringify(data);
		if (settings.camel) url += "?camel=true";
		return fetch(`${this.apiUrl}/${url}`, { ...options, ...requestOptions })
			.then(MDTApiClient.handleAPIError)
			.catch(async err => {
				if (!appIsOnline()) {
					throw new OfflineError();
				}
				await this.options?.onRequestError?.(err, url, data);
				throw err;
			});
	}

	fetch<T = any>(
		query: Query,
		table?: string,
		settings?: MDTRequestSettings,
		requestOptions?: RequestInit
	): Promise<PreparedFetchResult<T>>;

	//todo: typing multiple types
	fetch<T = any>(
		query: Query[],
		table?: string,
		settings?: MDTRequestSettings,
		requestOptions?: RequestInit
	): Promise<PreparedFetchResult<T>[]>;

	async fetch<T = any>(
		query: Query | Query[],
		table?: string,
		settings: MDTRequestSettings = {},
		requestOptions: RequestInit = {}
	): Promise<PreparedFetchResult<T> | PreparedFetchResult<T>[]> {
		let t = table || (!Array.isArray(query) && query.table);
		let url = "fetch" + (t ? "/" + t : "");

		let data = Array.isArray(query) ? query.map(q => ApiUrlHelper.query2Str(q)) : ApiUrlHelper.query2Str(query);

		return this.request(url, data, settings, requestOptions)
			.then(result => result.json())
			.then((data: FetchResult[]) =>
				data.length == 1 ? this.prepareFetchResult(data[0]) : data.map(d => this.prepareFetchResult(d))
			);
	}

	private prepareFetchResult<T>(data: FetchResult<T>): PreparedFetchResult<T> {
		let d: Partial<FetchResult> = {};
		Object.keys(data).forEach(k => (d[k.toLowerCase()] = data[k]));
		let result: PreparedFetchResult<T> = { records: d.rows, count: d.count };
		let groups = d.items && d.items[0] && d.items[0].groups;
		if (groups) result.groups = groups;
		return result;
	}

	async save(data: MDTRecord | MDTRecord[], params?: SaveParams | SaveParams[]): Promise<ModificationResult> {
		let url = "save/" + (Array.isArray(data) ? data[0].$table : data.$table);
		let r = Array.isArray(data) ? data : [data];
		if (params) {
			Array.isArray(params)
				? r.forEach((d, idx) => params[idx] && (d.$params = params[idx]))
				: r.forEach(d => (d.$params = params));
		}
		return this.request(url, r).then(r => r.json());
	}

	async saveWithPhoto(
		file: VisitAnswerFile,
		photoName: string,
		depTable: DependentTable,
		fileFK: string
	): Promise<any> {
		const depTableKey = Object.keys(depTable)[0];
		if (this.apiVersion == "new") {
			const xhr = new XMLHttpRequest();
			xhr.withCredentials = true;
			xhr.responseType = "json";
			xhr.open("POST", this.apiUrl + "/file/upload");

			const formData = new FormData();
			formData.append("file", file.data.full, photoName);

			try {
				const res: FileUploadRes = await new Promise((resolve, reject) => {
					xhr.onerror = () => reject("XMLHttpRequest error");
					xhr.onload = () => {
						if (xhr.status == 200) {
							resolve(xhr.response);
							return;
						}
						reject(`Status ${xhr.status}: ${xhr.response}`);
					};

					xhr.send(formData);
				});
				var photoId = res.ID;

				return this.save({
					...depTable[depTableKey],
					[fileFK]: photoId
				} as any);
			} catch (err) {
				if (photoId) {
					await this.destroy([photoId], "mdt.File");
				}
				return err;
			}
		}

		const base64Data = await uploadHelper.prepareImage(file.data.full);
		return this.save({
			$table: "mdt.File",
			data: base64Data.replace(/^data:((image|application)\/)?.*;base64,/, ""),
			mdt_dateCreate: new Date(),
			name: "VisitItemPhoto", //todo: photo name
			type: "image/jpeg",
			[depTableKey]: [
				{
					...depTable[depTableKey]
				}
			]
		} as APIFile);
	}

	async destroy(ids: MDTRecordId[], table: string): Promise<ModificationResult> {
		return this.request("save", { tableName: table, idList: ids }, void 0, { method: "DELETE" }).then(r =>
			r.json()
		);
	}

	async fetchImage(imgUrl: string, requestOptions?: RequestInit) {
		return this.request(imgUrl, {}, {}, requestOptions).then(res => res.blob());
	}

	async fetchHTML(url: string, requestOptions?: RequestInit): Promise<string> {
		return fetch(`${docsApiUrl}${url}`, requestOptions)
			.then(async res => {
				const text = await res.text();
				if (res.status > 400) throw new Error(text);
				return text;
			})
			.catch(err => {
				if (!appIsOnline()) throw new OfflineError();
				throw err;
			});
	}

	/**        MDT API */
	static handleAPIError = async (r: Response): Promise<Response> => {
		if (r.status > 400) {
			return r.json().then(data => {
				if (MDTApiClient.isAPIError(data)) throw new MDTApiError(data);
				return r;
			});
		}
		return r;
	};

	private static isAPIError(data: any): boolean {
		return data.Message != null;
	}

	// FILES & IMAGES
	imageUrl(file: FileRecord, options: ImageFetchOptions = {}): string {
		const param = this.apiVersion == "new" ? `uid=${file.uid}` : `id=${file.id}`;
		let url = `${this.apiUrl}/file/image?${param}`;
		if (options.height && options.width) url += `&size=${options.width}x${options.height}`;
		if (options.mode) url += `&mode=${options.mode}`;
		if (options.hash) url += `&hash=${options.hash}`;
		return url;
	}
}

// FETCH
export interface FetchResult<T = any> {
	custom: boolean;
	debug?: {
		sql: string;
		time: number;
	};
	items?: { groups?: Group[] }[];
	count?: number;
	rows: T[];
}

export interface PreparedFetchResult<T = any> {
	records: T[];
	count: number;
	groups?: Group[];
}

interface Group {
	count: number;
	object: Object;
	groups?: Group[];
	value?: string;
}

// SAVE+DELETE
export interface ModificationResult {
	$: string;
	type: string;
	items: {
		stats: {
			inserted: number;
			updated: number;
			deleted: number;
		};
		ids?: MDTRecordId[];
		extIds?: {
			ID: MDTRecordId;
			Table: string;
		}[];
	}[];
}

// ERROR
export class MDTApiError extends Error {
	name = "MDT API Error";
	message: string;
	code: string;
	args: Object;

	constructor(public data: MDTApiErrorData) {
		super();
		this.message = data.Message;
		this.code = data.Code;
		this.args = data.Args;
		Object.setPrototypeOf(this, MDTApiError.prototype);
	}

	toString() {
		return `${this.name}: ${this.message}`;
	}
}

export interface MDTApiErrorData {
	Args: Object;
	Code: string;
	Debug: {
		Data: Object;
		HResult: number;
		Message: string;
		Source: string;
		StackTrace: string;
		TargetSite: {
			Name: string;
			AssemblyName: string;
			ClassName: string;
			GenericArguments?: string;
			MemberType: number;
			Signature: string;
			Signature2: string;
		};
	};
	Message: string;
}

// FILE & IMAGE
export interface ImageFetchOptions {
	height?: number;
	width?: number;
	mode?: "fit" | "fill";
	hash?: string;
}
