import { Text } from "quiz/components/text/Text";
import { useStyles } from "hooks/useStyles";
import { MDTRecordId, FileRecord } from "mdtApiClient/mdtApiClient";
import { MDTFileGallery } from "quiz/components/mdtImages/MdtFileGallery";
import { IQuestion } from "quiz/services/models";
import * as React from "react";
import { TextSizeType } from "theme/sizing";
import { css } from "wraps/styleCreators";
import { getQuestionView } from "./questionViewStyles";
import { List, ListItem } from "quiz/components/listView/ListView";
import { AccordionContent } from "framework7-react";

export interface QuestionViewProps {
	question: IQuestion;
	idx: number;
	totalQuestions: number;
	danger?: boolean;
}

const QuestionnaireDescription: React.FC<QuestionViewProps> = props => {
	const styles = useStyles(getQuestionView, props);
	const { name, description } = props.question;

	const Question = (
		<div className={css(styles.headerWrap)}>
			<div className={css(styles.titleWrap)}>
				<Text styles={styles.index} muted size={TextSizeType.bodyMin}>
					{`${props.idx}/${props.totalQuestions}`}
				</Text>
				<div className={css(styles.titleTextWrap)}>
					<Text size={TextSizeType.title} styles={styles.titleText}>
						{name}
					</Text>
				</div>
			</div>
		</div>
	);

	return description ? (
		<List accordionList noHairlinesMd className="question-accordion">
			<style>
				{`
					.question-accordion .item-content {
						padding-left: 0;
					}

					.question-accordion .item-inner {
						padding-top: 0;
						padding-bottom: 0;
					}
				`}
			</style>
			<ListItem accordionItem>
				<div slot="inner-start">{Question}</div>
				<AccordionContent>
					<div className={css(styles.description)}>
						<Text size={TextSizeType.caption} muted={true}>
							{description}
						</Text>
					</div>
				</AccordionContent>
			</ListItem>
		</List>
	) : (
		Question
	);
};

export const QuestionView: React.FC<QuestionViewProps> = React.memo(props => {
	const styles = useStyles(getQuestionView, props);
	const { images = [] } = props.question;
	const imgs = React.useMemo<FileRecord[]>(() => images.map(i => ({ id: i.fileId, uid: i.uid })), [images]);

	return (
		<>
			<div className={css(styles.questionViewWrap)}>
				<QuestionnaireDescription {...props} />
			</div>
			{images && !!images.length && (
				<div className={css(styles.wrapGallery)}>
					<MDTFileGallery fileRecords={imgs} />
				</div>
			)}
		</>
	);
});
QuestionView.displayName = "QuestionView";
