import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getMobileLoginPageStyles = (theme: Theme) => {
	return {
		wrap: {
			position: "relative",
			top: 0,
			bottom: 0,
			left: 0,
			right: 0,
			height: "100%",
			boxSizing: "border-box"
		} as CSSProp,
		content: {
			display: "flex",
			width: "auto",
			height: `calc(100% - ${theme.sizing.sizes.lg} - ${theme.sizing.sizes.lg})`,
			flexDirection: "column",
			alignContent: "space-between",
			padding: `${theme.sizing.sizes.lg} 0`
		} as CSSProp,
		iosSaveArea: {
			height: "100%",
			paddingBottom: "env(safe-area-inset-bottom)"
		},
		logoWrap: {
			width: "100%",
			flexGrow: 1,
			alignItems: "center",
			textAlign: "center",
			justifyContent: "center",
			display: "flex",
			marginBottom: theme.sizing.sizes.xl
		} as CSSProp,
		relativeForm: {
			display: "flex",
			flexGrow: 1,
			flexFlow: "column",
			alignItems: "center",
			position: "relative",
			width: "100%"
		} as CSSProp,
		inputsWrap: {
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			marginBottom: theme.sizing.sizes.xl,
			flexFlow: "column",
			width: "100%"
		} as CSSProp,
		buttonsWrap: {
			display: "flex",
			flexFlow: "column",
			width: "100%",
			height: "100%",
			justifyContent: "space-between",
			flexGrow: 1
		} as CSSProp,
		signInBtnBlock: {
			paddingBottom: theme.sizing.sizes.xl
		} as CSSProp,
		error: {
			color: theme.colors.danger.main,
			marginBottom: "0"
		} as CSSProp
	};
};
