import { useContext } from "react";
import { Theme } from "theme/theme";
import { ThemeContext } from "themeContext/ThemeContext";
import { StyleDeclaration, StyleDeclarationValues, styleSheet } from "wraps/styleCreators";

export type StyleCreatorFunction<A, S> = (theme: Theme, args?: A) => StyleDeclaration<S>;

export function useStyles<A, S>(getStyles: StyleCreatorFunction<A, S>, args?: A): StyleDeclarationValues<S> {
	const theme = useContext(ThemeContext);
	const styles = getStyles(theme, args);
	return styleSheet(styles);
}
