import { useStyles } from "hooks/useStyles";
import React from "react";
import { css } from "wraps/styleCreators";
import { CSSPropertiesComplete } from "aphrodite";
import { getSkeletonItemWrapStyles } from "./SkeletonItemStyles";

export const skeletonClass = `skeleton-text skeleton-effect-fade`;

export type SkeletonItemStyles = {
	backgroundColor?: CSSPropertiesComplete["backgroundColor"];
	margin?: CSSPropertiesComplete["margin"];
	marginTop?: CSSPropertiesComplete["marginTop"];
	marginRight?: CSSPropertiesComplete["marginRight"];
	marginBottom?: CSSPropertiesComplete["marginBottom"];
	marginLeft?: CSSPropertiesComplete["marginLeft"];
};

export type SkeletonItemWrapProps = {
	spacing?: CSSPropertiesComplete["justifyContent"];
	direction?: CSSPropertiesComplete["flexDirection"];
	align?: CSSPropertiesComplete["alignItems"];
} & { [key in keyof SkeletonItemStyles]: SkeletonItemStyles[key] };

export const SkeletonItemWrap: React.FC<SkeletonItemWrapProps> = props => {
	const { children, ...styleProps } = props;
	const styles = useStyles(getSkeletonItemWrapStyles, styleProps);
	return <div className={css(styles.wrap)}>{children}</div>;
};

SkeletonItemWrap.displayName = "SkeletonItemWrap";
