import * as React from "react";

type PromiseResolveData = any;

interface Data<T> {
	onSuccess(x: T): void;
	onError?(err: any): void;
	deps?: ReadonlyArray<unknown>;
}

export function useCancellablePromise<T = PromiseResolveData>(
	exec: (abortSignal: AbortSignal) => Promise<T> | null,
	{ deps, onSuccess, onError }: Data<T>
): void {
	React.useEffect(() => {
		let active = true;
		const abortCtrl = new AbortController();

		const result = exec(abortCtrl.signal);
		if (result instanceof Promise)
			result
				.then(x => active && onSuccess(x))
				.catch(err => {
					active && onError && onError(err);
					if (!onError && active) {
						throw err;
					}
				});
		return () => {
			active = false;
			abortCtrl.abort();
		};
	}, deps || []);
}
