import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { ReactNode, useRef, useState } from "react";
import { ColorTypes } from "theme/colors";
import { replaceClass } from "utils/cssHelpers";
import { v } from "utils/helpers";
import { css, StyleDeclarationValue } from "wraps/styleCreators";
import { Icon } from "../icon/Icon";
import { getControlLayoutStyles } from "./controlLayoutStyles";

export type ControlValue = string;

interface ControlProps<T> {
	tabIndex: number;
	value: T;
	onChange(v: T): void;
	onFocus(): void;
	onBlur(): void;
}

type CustomControlProps<T = ControlValue> = Omit<ControlProps<T>, "onChange"> & { onChange: (e) => void };

export interface ControlLayoutProps<T = ControlValue> {
	colorType?: ColorTypes;
	value: T;
	onChange?: (v: T) => void;

	customControl?: (controlProps: CustomControlProps<T>, styles: StyleDeclarationValue) => ReactNode;

	rightZone?: ReactNode;
	leftZone?: ReactNode;
	readOnly?: boolean;
	onClearClick?: () => void;
}

export const ControlLayout: React.FunctionComponent<ControlLayoutProps> = React.memo(props => {
	const styles = useStyles(getControlLayoutStyles, props);
	const wrapRefEl = useRef(null);

	const value = props.value;

	const wrapClass = css(styles.wrap);
	const wrapActiveClass = css(styles.wrap, styles.wrapActive);

	const controlProps = {
		tabIndex: 0,
		value: value,
		onChange: e => v(props.onChange, e.target.value),
		onFocus: () => replaceClass(wrapRefEl.current, wrapClass, wrapActiveClass),
		onBlur: () => replaceClass(wrapRefEl.current, wrapActiveClass, wrapClass)
	};

	return (
		<div className={wrapClass} ref={wrapRefEl}>
			{props.leftZone != null && <div className={css(styles.left)}>{props.leftZone}</div>}
			<div className={css(styles.center)}>
				{props.customControl ? (
					props.customControl(controlProps, styles.control)
				) : (
					<input className={css(styles.control)} {...controlProps} />
				)}
				{props.value != null && !props.readOnly && (
					<div
						className={css(styles.clearWrap)}
						onClick={() => {
							props.onChange && props.onChange(null);
							props.onClearClick && props.onClearClick();
						}}
					>
						<Icon icon={["far", "times"]} styles={styles.icon} />
					</div>
				)}
			</div>
			{props.rightZone != null && <div className={css(styles.right)}>{props.rightZone}</div>}
		</div>
	);
});
ControlLayout.displayName = "ControlLayout";

export const StatedControlLayout: React.FC = props => {
	const [v, setValue] = useState("");
	return <ControlLayout {...props} value={v} onChange={v => setValue(v)} />;
};
