import { useNavigatorOnLine } from "hooks/useNavigatorOnline";
import { useStyles } from "hooks/useStyles";
import { T } from "quiz/components/text/T";
import React from "react";
import { TextSizeType } from "theme/sizing";
import { css } from "wraps/styleCreators";
import { OfflineIcon } from "./OfflineIcon";
import { getOfflineIndicatorStyles } from "./offlineIndicatorStyles";

export const OfflineIndicator: React.FC = () => {
	const styles = useStyles(getOfflineIndicatorStyles);
	const isOnline = useNavigatorOnLine();
	return (
		!isOnline && (
			<div className={css(styles.wrap)}>
				<div className={css(styles.iconWrap)}>
					<OfflineIcon />
				</div>
				<T muted size={TextSizeType.caption}>
					{"Wating for connection ..."}
				</T>
			</div>
		)
	);
};
