import { Block } from "quiz/components/block/Block";
import { DatePickerButton } from "quiz/components/datepicker/DatePicker";
import { Toggle } from "quiz/components/toggle/Toggle";
import { OutletItem } from "quiz/components/outletItem/OutletItem";
import { usePosition } from "hooks/usePosition";
import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { quizDataService, t } from "../../QuizApp";
import { QuizList } from "../quizList/QuizList";
import { T } from "../text/T";
import { getOutletRoutesStyles } from "./outletRoutesStyles";
import { OutletFullData, ID } from "quiz/services/models";
import { useAnimation } from "hooks/useAnimation";
import { css } from "wraps/styleCreators";
import { OutletSkeleton } from "../quizSkeleton/skeletons/OutletSkeleton";
import { InlineGroup } from "../quizPage/InlineGroup";
import { Divider } from "quiz/components/divider/Divider";
import { useCancellablePromise } from "hooks/useCancellablePromise";
import { F7Toggle } from "framework7-react";
import { getAnimationKey } from "utils/helpers";
import { QuizFade } from "../fade/QuizFade";
import { TextSizeType } from "theme/sizing";
import { Toast } from "../interaction/toast/Toast";

interface OutletRoutesProps {
	onOutletClick: (outlet: OutletFullData) => void;
}

export const OutletRoutes: React.FC<OutletRoutesProps> = props => {
	const [outlets, setOutlets] = React.useState<OutletFullData[]>();
	const [date, setDate] = React.useState(new Date());
	const [requestError, setRequestError] = React.useState();
	const { latitude, longitude, error } = usePosition();
	const [hideVisited, setHideVisited] = React.useState(true);
	const styles = useStyles(getOutletRoutesStyles);
	const animation = useAnimation();
	const toggleRef = React.useRef<F7Toggle>();
	const [todayRoute, setTodayRoute] = React.useState<ID[]>([]);
	const mounted = React.useRef<boolean>(false);

	useCancellablePromise(
		a => {
			setOutlets(null);
			if (latitude && longitude) {
				return quizDataService.getRouteOutlets(date, { latitude, longitude }, { signal: a });
			} else return new Promise(() => void 0);
		},
		{
			onSuccess: (data: OutletFullData[]) => {
				if (!mounted.current) {
					mounted.current = true;
					setTodayRoute(data ? data.map(d => d.id) : []);
				}
				setOutlets(data);
			},
			onError: err => setRequestError(err),
			deps: [date, latitude, longitude, error]
		}
	);

	const resultOutlets = outlets && (hideVisited ? outlets.filter(outlet => !outlet.flagVisited) : outlets);
	const fadeAnimationKey = getAnimationKey(!resultOutlets, [requestError, error]);

	return (
		<div>
			<Block styles={styles.content} secondary>
				<div className={css(styles.contentDatePicker)}>
					<DatePickerButton onDateClick={d => setDate(d)} title={t("Today")} enabled={mounted.current} />
				</div>
				<div className={css(styles.contentToggle)}>
					<InlineGroup styles={styles.toggle}>
						<span onClick={() => toggleRef.current?.f7Toggle?.toggle()}>
							<T size={TextSizeType.bodyMin} styles={styles.toggleText}>
								Hide visited
							</T>
						</span>
						<Toggle
							ref={toggleRef}
							onToggleChange={(checked: boolean) => setHideVisited(checked)}
							defaultChecked={hideVisited}
						/>
					</InlineGroup>
				</div>
			</Block>
			<Divider />
			<QuizFade animationKey={fadeAnimationKey}>
				<QuizList
					isEmpty={resultOutlets?.length == 0}
					isLoading={!resultOutlets}
					errors={[requestError, error]}
					loading={() => <OutletSkeleton totalBlocks={3} />}
					noHairlines
				>
					{resultOutlets &&
						resultOutlets.map((outlet: OutletFullData, idx) => (
							<OutletItem
								key={idx}
								outlet={outlet}
								onClick={outlet => {
									if (!todayRoute.includes(outlet.id)) {
										Toast.create({
											type: "warn",
											text: t("The visit will not be counted as part of the route"),
											closeTimeout: 3000
										}).open();
									}
									props.onOutletClick(outlet);
								}}
								style={animation.animationDelay(idx)}
							/>
						))}
				</QuizList>
			</QuizFade>
		</div>
	);
};
OutletRoutes.displayName = "OutletRoutes";
