import { BlockTitle } from "quiz/components/block/Block";
import { OutletItem, OutletItemProps } from "quiz/components/outletItem/OutletItem";
import { useCancellablePromise } from "hooks/useCancellablePromise";
import { quizDraftService, quizQstService, t, nav } from "quiz/QuizApp";
import { VisitUploadData } from "quiz/services/QuizDraftService";
import * as React from "react";
import { ErrorBoundary } from "../errorBoundary/ErrorBoundary";
import { QuizList } from "../quizList/QuizList";
import { UnsentReportIcon } from "./UnsentReportIcon";

type UnsentReportsProps = {
	reports: VisitUploadData[];
};

type OutletItemInteraction = Pick<OutletItemProps, 'itemLink' | 'onClick' | 'onDeleteSwipeout'>;

export const UnsentReports: React.FC<UnsentReportsProps> = props => {
	const [reports, setReports] = React.useState<VisitUploadData[]>(props.reports);
	const [updateKey, setUpdateKey] = React.useState(Math.random());

	useCancellablePromise(() => quizDraftService.getVisitUploadData(), {
		onSuccess: reports => {
			setReports(reports.length > 0 ? reports : null);
		},
		onError: e => {
			throw e;
		},
		deps: [updateKey]
	});

	const toUnsentReport = React.useCallback(
		(visitData: VisitUploadData) => {
			nav.toUnsentReport({
				visitData,
				open: false,
				isCompleted: true
			});
		},
		[reports]
	);

	React.useEffect(() => {
		const unbindProgress = quizQstService.events.on("visitUploadProgress", () => setUpdateKey(Math.random()));
		const unbindRetry = quizQstService.events.on("visitUploadRetry", () => setUpdateKey(Math.random()));
		const unbindDelete = quizQstService.events.on("visitUploadDelete", () => setUpdateKey(Math.random()));
		return () => {
			unbindProgress();
			unbindRetry();
			unbindDelete();
		};
	}, [reports]);

	if (!reports || !reports.length) return null;

	return (
		<ErrorBoundary>
			<div>
				<BlockTitle title={t("Unsent Reports")} counter={reports.length} />
				<QuizList isLoading={false} isEmpty={false} errors={[]}>
					{reports.map(r => {
						const interactionProps: OutletItemInteraction = quizQstService.hasUploadErrors(r)
							? { itemLink: true, onClick: () => toUnsentReport(r), onDeleteSwipeout: () => quizQstService.deleteUploadData(r) }
							: { itemLink: false, onClick: null, onDeleteSwipeout: null }
						return <OutletItem
							{...interactionProps}
							key={r.key}
							outlet={{
								...r.visit.outlet,
								employee: r.visit.employee,
								outletChain: r.visit.outlet?.outletChain,
								type: null,
								questionnaireName: r.visit.questionnaire.name,
								time: r.visit.dateEnd,
								iconView: <UnsentReportIcon data={r} />
							}}
						/>
					})}
				</QuizList>
			</div>
		</ErrorBoundary>
	);
};
UnsentReports.displayName = "UnsentReports";
