import { QrCodeScannerOptions } from "./extension/scanner/qrScanner";
import { quizExtensionService } from "quiz/QuizApp";

export class QuizQrService {
	scan(
		onScan: (result: string) => void,
		onError: (err: any) => void,
		setQrCallback: React.Dispatch<any>,
		options?: QrCodeScannerOptions
	) {
		const qr = quizExtensionService.qrScanner.scan(onScan, onError, options);
		setQrCallback(qr);
	}
}
