import { ColorTypes } from "theme/colors";
import { TextSizeType } from "theme/sizing";
import { Theme } from "theme/theme";
import { CSSProps } from "wraps/styleCreators";
import { TextProps } from "./Text";

export const getTextStyles = (theme: Theme, props: TextProps) => {
	const colorType = props.colorType || ColorTypes.base;
	const size = props.size || TextSizeType.body;
	const s = theme.sizing;

	const vars = {
		fontFamily: theme.typography.fonts.base,
		fontSize: s.fontSizes[size],
		lineHeight: s.lineHeights[size],
		fontWeight: props.bold ? s.fontWeights.bold : s.fontWeights.medium,
		fontStyle: <CSSProps["fontStyle"]>(props.italic ? "italic" : "normal"),
		color: props.muted
			? theme.colors[colorType].texts.muted
			: props.accent
			? theme.colors[colorType].texts.accent
			: theme.colors[colorType].texts.main
	};

	return {
		text: {
			fontFamily: vars.fontFamily,
			fontSize: vars.fontSize,
			lineHeight: vars.lineHeight,
			fontWeight: vars.fontWeight,
			fontStyle: vars.fontStyle,
			color: vars.color,
			whiteSpace: "normal"
		} as CSSProps
	};
};
