import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getAnswerSectionStyles = (theme: Theme) => {
	const common = { padding: `0 ${theme.constants.gridPadding}` };
	return {
		wrap: {
			display: "flex",
			flexDirection: "column"
		} as CSSProp,
		header: {
			display: "flex",
			justifyContent: "space-between",
			alignItems: "center",
			marginBottom: theme.sizing.sizes.sm,
			...common
		} as CSSProp,
		headerText: {
			color: theme.colors.base.hover,
			lineHeight: "100%"
		} as CSSProp,
		description: {
			height: "40px",
			display: "flex",
			alignItems: "center"
		} as CSSProp,
		descriptionText: {
			fontSize: theme.sizing.fontSizes.caption,
			lineHeight: "128%",
			color: theme.colors.danger.main,
			padding: `8px 20px 16px`
		} as CSSProp,
		asterisk: {
			color: theme.colors.danger.main,
			fontWeight: theme.sizing.fontWeights.bold,
			fontSize: theme.sizing.sizes.sm
		} as CSSProp,
		foo: {
			height: "16px"
		} as CSSProp
	};
};
