import { PhotoBrowser as F7PB, F7PhotoBrowser } from "framework7-react";
import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";
import { preparePhotoBrowserStyles } from "./photoBrowserStyles";
import { passRef } from "utils/reactUtils";
import { t, quizExtensionService } from "quiz/QuizApp";
import { PhotoBrowserService } from "./PhotoBrowserService";

export type PhotoBrowserActions = F7PhotoBrowser;

export interface PhotoBrowserProps extends F7PB.Props {}

const swiperSpaceBetweenSlidesPx = 100;
const swiperSlideSpeedMs = 350;

const swiper = {
	pagination: {
		el: ".swiper-pagination-pb",
		type: "bullets"
	},
	spaceBetween: swiperSpaceBetweenSlidesPx,
	speed: swiperSlideSpeedMs,
	passiveListeners: false
};

const toolbarRender = () =>
	`
<div class="swiper-container-horizontal toolbar toolbar-bottom"">
	<div class="swiper-pagination-pb swiper-pagination swiper-pagination-bullets"></div>
</div>`.trim();

export const PhotoBrowser = React.forwardRef<F7PhotoBrowser, PhotoBrowserProps>(function PhotoBrowser(
	props,
	ref: React.Ref<F7PhotoBrowser>
) {
	const theme = React.useContext(ThemeContext);
	const pbRef = React.useRef<F7PhotoBrowser>();
	const photoBrowserService = React.useMemo(() => new PhotoBrowserService(), []);

	React.useEffect(
		() => () => {
			photoBrowserService.abortFetchs();
			photoBrowserService.revokeBlobUrls();
		},
		[]
	);

	return (
		<F7PB
			ref={pb => {
				pbRef.current = pb;
				passRef(pb, ref);
			}}
			exposition
			expositionHideCaptions={false}
			theme="dark"
			swiper={{
				...swiper,
				lazy: quizExtensionService.platform !== "ios",
				on: {
					slideNextTransitionEnd: () => photoBrowserService.handleSwipe(pbRef.current.f7PhotoBrowser.$el[0]),
					slidePrevTransitionEnd: () => photoBrowserService.handleSwipe(pbRef.current.f7PhotoBrowser.$el[0]),
					slideResetTransitionEnd: () => photoBrowserService.handleSwipe(pbRef.current.f7PhotoBrowser.$el[0])
				}
			}}
			renderToolbar={toolbarRender}
			type="page"
			navbarOfText={t("of")}
			{...props}
			onPhotoBrowserOpen={() => {
				photoBrowserService.handleOpen(pbRef.current.f7PhotoBrowser.$el[0]);
				preparePhotoBrowserStyles(theme, pbRef.current.f7PhotoBrowser.$el[0]);
				quizExtensionService.statusBar.setDarkStyle();
				quizExtensionService.statusBar.setFullScreen();
			}}
			onPhotoBrowserClose={() => {
				quizExtensionService.statusBar.setLightStyle();
			}}
		/>
	);
});
