import { useEffect } from "react";
import * as React from "react";
import { t, logger, nav } from "quiz/QuizApp";
import { LogType } from "quiz/services/Logger";
import { ErrorUnexpected } from "../errorUnexpected/errorUnexpected";
import { FeedbackTypeEnum } from "quiz/components/feedback/Feedback";
import { Icon } from "quiz/components/icon/Icon";
import { T } from "../text/T";
import { useStyles } from "hooks/useStyles";
import { getGlobalErrorHandlerStyles } from "./globalErrorHandlerStyles";
import { css } from "wraps/styleCreators";
import { getApiErrorForT } from "utils/helpers";
import { Button } from "../button/MobileButton";

type WindowError = {
	title: string;
	text: string;
	context?: any;
};

export function setupErrorHandling(setError: (error: WindowError) => void): void {
	window.onerror = (message, file, line, col, error) => {
		logger.log("unhandled-error", { error, message, file, line, col }, LogType.error);
		const context = (error as any).context || null;
		setError({
			title: message?.toString() || error.toString(),
			text: `
File: ${file}.
Ln: ${line}, Col: ${col}.
${error.stack}`.trim(),
			context
		});
	};

	window.addEventListener("unhandledrejection", function(e) {
		logger.log("unhandled-rejection", { error: e }, LogType.error);
		const context = e.reason?.context || null;
		const [error, args] = getApiErrorForT(e);
		setError({ title: t("Promise unhandled rejection"), text: t(error, args), context });
	});
}

export const GlobalErrorHandler: React.FC = () => {
	const styles = useStyles(getGlobalErrorHandlerStyles);
	const [error, setError] = React.useState<WindowError>();
	const [errorOpened, setErrorOpened] = React.useState<boolean>(!!error);
	useEffect(
		() =>
			setupErrorHandling((error: WindowError) => {
				setError(error);
				setErrorOpened(true);
			}),
		[]
	);
	return (
		<>
			{error && (
				<ErrorUnexpected
					isOpen={errorOpened}
					title={error.title}
					errorText={error.text}
					onClose={() => setErrorOpened(false)}
					buttons={() => (
						<Button
							className={css(styles.button)}
							outline
							onClick={() => {
								nav.toFeedback({
									value: error.text,
									type: FeedbackTypeEnum.Problem,
									errorContext: error.context
								});
								setErrorOpened(false);
							}}
						>
							<Icon icon={["far", "comment-alt-lines"]} styles={styles.icon} />
							<T styles={styles.iconText}>Complain</T>
						</Button>
					)}
				/>
			)}
		</>
	);
};
GlobalErrorHandler.displayName = "GlobalErrorHandler";
