import { CSSSizeValue } from "wraps/styleCreators";

const CSSLengthUnits = ["em", "ex", "%", "px", "cm", "mm", "in", "pt", "pc", "ch", "rem", "vh", "vw", "vmin", "vmax"];

type ParseUnitCSSLengthResult = { value: number; unit: string; valid: true } | { valid: false };

export function parseUnitCSSSizeValue(value: CSSSizeValue): ParseUnitCSSLengthResult {
	let v, unit;
	if (typeof value == "number") {
		v = value;
	} else {
		v = parseFloat(value);
		const unitMatch = value.match(/\D+$/);
		unit = unitMatch && unitMatch[0];
	}
	if (isNaN(v) || (unit && CSSLengthUnits.indexOf(unit.trim()) === -1)) return { valid: false };
	return {
		unit: unit ? unit.trim() : "",
		value: v,
		valid: true
	};
}

export function getCSSSizeValue(value: CSSSizeValue) {
	let x = parseUnitCSSSizeValue(value);
	if (!x.valid) throw new Error("Can't parse CSS value!");
	return x.value;
}

export type CssVariablesMap = { [key: string]: string | number };

export function applyCssVars(vars: CssVariablesMap, el?: HTMLElement) {
	Object.keys(vars).map(k => {
		let v = vars[k];
		//allow passing with or without --
		if (k[0] != "-") k = "--" + k;
		(el || document.documentElement).style.setProperty(k, v as string);
	});
}

export function replaceClass(el: HTMLInputElement, replace: string, to: string) {
	el.classList.remove(replace);
	el.classList.add(to);
}

// Scale
export enum ScaleEnum {
	/** x / 2 */
	Half = "Half",
	/** x * 2 */
	Double = "Double",
	/** x * 3 */
	Triple = "Triple"
}

type ScaleEnumMapType = {
	[key in ScaleEnum]: number;
};

const ScaleEnumMap: ScaleEnumMapType = {
	[ScaleEnum.Half]: 0.5,
	[ScaleEnum.Double]: 2,
	[ScaleEnum.Triple]: 3
};

export function scale(value: CSSSizeValue, scale: ScaleEnum): CSSSizeValue {
	const val = parseUnitCSSSizeValue(value);
	if (!val.valid) throw new Error("Can't parse CSS value!");
	return `${Math.round(val.value * ScaleEnumMap[scale])}${val.unit || "px"}`;
}
