import { useStyles, StyleCreatorFunction } from "hooks/useStyles";
import * as React from "react";
import { ColorTypes } from "theme/colors";
import { TextSizeType } from "theme/sizing";
import { css, StyleDeclarationValue, StyleDeclarationValues } from "wraps/styleCreators";
import { getTextStyles } from "./textStyles";

type TextTags = "div" | "span" | "p" | "i" | "b" | "strong" | "pre" | "code";

export interface TextProps {
	colorType?: ColorTypes;
	size?: TextSizeType;
	tag?: TextTags;
	italic?: boolean;
	bold?: boolean;
	muted?: boolean;
	accent?: boolean;
	/** CSS in JS styling */
	styles?: StyleDeclarationValue;
}

export const Text: React.FunctionComponent<TextProps> = React.memo(props => {
	const styles = useStyles(getTextStyles as StyleCreatorFunction<React.PropsWithChildren<TextProps>, unknown>, props);

	const Tag = props.tag || "span";

	return (
		<Tag className={css((styles as { text: StyleDeclarationValues<unknown> }).text, props.styles)}>
			{props.children}
		</Tag>
	);
});
Text.displayName = "Text";
