import { Text } from "quiz/components/text/Text";
import { Image } from "quiz/components/image/Image";
import { PhotoBrowser, PhotoBrowserActions } from "quiz/components/photobrowser/PhotoBrowser";
import { Swiper, SwiperSlide } from "framework7-react";
import { useLongTap } from "hooks/useLongTap";
import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { TextSizeType } from "theme/sizing";
import { GallerySettings, MobileSettings, SwiperSettings } from "utils/constants";
import { css } from "wraps/styleCreators";
import { getGalleryStyles } from "./galleryStyles";
import { getCSSSizeValue } from "utils/cssHelpers";
import { ThemeContext } from "themeContext/ThemeContext";

export type GalleryImage = {
	thumbUrl?: string;
	url: string;
	id?: string | number;
	title?: string;
};

export interface GalleryProps {
	images: GalleryImage[];
	readonly?: boolean;
	slidesOnScreen?: number;
	spaceBetweenSlidesPx?: number;
	title?: string;
	popupCloseLinkText?: string;
	onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
	onImageClick?: (index: number) => void;
	onLongTap?: (index: number) => void;
}

export const Gallery: React.FunctionComponent<GalleryProps> = React.memo(props => {
	const styles = useStyles(getGalleryStyles);
	const theme = React.useContext(ThemeContext);

	const photoBrowserRef = React.useRef<PhotoBrowserActions>();
	const tapIdxRef = React.useRef<number>();
	const isLongTapRef = React.useRef(true);
	const readonly = props.readonly || false;

	const longTap = useLongTap(() => {
		isLongTapRef.current = false;
		!readonly && props.onLongTap && props.onLongTap(tapIdxRef.current);
	}, MobileSettings.longTouchSpeedMs);

	return (
		<>
			{props.title && (
				<div className={css(styles.title)}>
					<Text size={TextSizeType.bodyMax}>{props.title}</Text>
				</div>
			)}
			{props.images.length ? (
				<>
					<Swiper
						params={{
							spaceBetween: props.spaceBetweenSlidesPx || SwiperSettings.spaceBetweenPx,
							slidesPerView: props.slidesOnScreen || GallerySettings.slidesOnScreenMultiple,
							observer: true,
							nested: true,
							passiveListeners: false,
							slidesOffsetBefore: getCSSSizeValue(theme.constants.gridPadding),
							slidesOffsetAfter: getCSSSizeValue(theme.constants.gridPadding)
						}}
					>
						{props.images.map((image, idx) => {
							return (
								<SwiperSlide key={image.id || image.thumbUrl || image.url}>
									<div
										className={css(styles.container)}
										onTouchStart={() => {
											tapIdxRef.current = idx;
											!readonly && longTap.onTouchStart();
											isLongTapRef.current = true;
										}}
										onTouchEnd={() => {
											!readonly && longTap.onTouchEnd();
										}}
										onClick={e => {
											e.stopPropagation();
											isLongTapRef.current && photoBrowserRef.current.open(tapIdxRef.current);
										}}
									>
										<div className={css(styles.wrapper)}>
											<div className={css(styles.content)}>
												<Image src={image.thumbUrl || image.url} styles={styles.image} />
											</div>
										</div>
									</div>
								</SwiperSlide>
							);
						})}
					</Swiper>
					<PhotoBrowser
						key={props.images.length}
						photos={props.images.map(image => ({ caption: image.title, url: image.url }))}
						ref={photoBrowserRef}
					/>
				</>
			) : null}
		</>
	);
});
Gallery.displayName = "Gallery";
