import { usePosition, Coords } from "hooks/usePosition";
import { quizDataService, t } from "quiz/QuizApp";
import * as React from "react";
import { QuizList } from "../quizList/QuizList";
import { OutletFullData } from "quiz/services/models";
import { OutletItem } from "quiz/components/outletItem/OutletItem";
import { OutletSkeleton } from "../quizSkeleton/skeletons/OutletSkeleton";
import { useCancellablePromise } from "hooks/useCancellablePromise";
import { QuizFade } from "../fade/QuizFade";
import { getAnimationKey, formatDateTime } from "utils/helpers";
import { compareCoords, getTimeChange, getCacheOutlets } from "./helpers";
import { Block } from "quiz/components/block/Block";
import { InlineGroup } from "../quizPage/InlineGroup";
import { css } from "aphrodite";
import { Icon, IconProp } from "quiz/components/icon/Icon";
import { T } from "../text/T";
import { TextSizeType } from "theme/sizing";
import { useStyles } from "hooks/useStyles";
import { getListNearOutletsStyles } from "./listNearOutletsStyles";
import { ThemeContext } from "themeContext/ThemeContext";
import { Divider } from "quiz/components/divider/Divider";
import { ButtonType, ButtonSize } from "quiz/components/button/Button";
import { ColorTypes } from "theme/colors";
import { MobileButton } from "../button/MobileButton";
import { listNearOutletsReducer, listNearOutletsActions, NearOutletsState } from "./listNearOutletsReducer";
import { getCoords } from "utils/geolocation";

interface ListNearOutletsProps {
	onOutletClick: (outlet: OutletFullData) => void;
}

export const outletWithDistanceCacheCode = "outletWithDistance";

export type CoordsCacheType = {
	latitude: string;
	longitude: string;
	timestamp: number;
};

let coordsCache: CoordsCacheType = {
	latitude: "",
	longitude: "",
	timestamp: new Date().getTime()
};

export const nearOutletsInitialState: NearOutletsState = {
	outlets: null,
	coords: null,
	lastUpdateDate: null,
	errors: [],
	isFetching: true,
	isRefreshing: false
};

let lastUpdateDate: Date = null;

export const ListNearOutlets: React.FC<ListNearOutletsProps> = props => {
	const [state, dispatch] = React.useReducer(listNearOutletsReducer, {
		...nearOutletsInitialState,
		outlets: getCacheOutlets(),
		isFetching: getCacheOutlets() ? false : true
	});
	const { outlets, coords, errors, isFetching } = state;

	const styles = useStyles(getListNearOutletsStyles);
	const theme = React.useContext(ThemeContext);

	const refreshNeraestOutlets = () => {
		quizDataService.clearCache(outletWithDistanceCacheCode);
		dispatch({
			type: listNearOutletsActions.REFRESHING
		});
	};

	React.useEffect(() => {
		const isTimeChange = getTimeChange(coordsCache.timestamp);
		if (coords) {
			const isCoordsChange = compareCoords({ ...coords }, coordsCache);
			if (isCoordsChange && isTimeChange) {
				coordsCache = {
					...coords,
					timestamp: new Date().getTime()
				};
				quizDataService.clearCache(outletWithDistanceCacheCode);
				dispatch({
					type: listNearOutletsActions.REFRESHING
				});
			}
		} else {
			coordsCache = {
				...coordsCache
			};
		}
	}, [coords?.latitude, coords?.longitude]);

	React.useEffect(() => {
		if (coords || errors.length) {
			return void 0;
		}

		let active = true;
		const onSuccess = (coords: Coords) => {
			active &&
				dispatch({
					type: listNearOutletsActions.UPDATE_COORDS,
					payload: {
						coords
					}
				});
		};

		const onError = error => {
			active &&
				!outlets &&
				dispatch({
					type: listNearOutletsActions.LOADNIG_ERROR,
					payload: {
						error
					}
				});
		};

		getCoords(onSuccess, onError);

		return () => (active = false);
	});

	useCancellablePromise<OutletFullData[]>(
		signal => {
			if (!coords) {
				return null;
			}

			return !outlets ? quizDataService.getOutletWithDistance({ ...coords }, { signal }) : null;
		},
		{
			onSuccess: data => {
				lastUpdateDate = new Date();
				coordsCache = {
					...coordsCache,
					...coords
				};
				dispatch({
					type: listNearOutletsActions.LOADING_SUCCESS,
					payload: {
						outlets: data
					}
				});
			},
			onError: err => {
				dispatch({
					type: listNearOutletsActions.LOADNIG_ERROR,
					payload: {
						error: err
					}
				});
			},
			deps: [coords?.latitude, coords?.longitude]
		}
	);

	const list = () => (
		<QuizList
			isLoading={isFetching}
			isEmpty={outlets?.length == 0}
			errors={errors}
			loading={() => <OutletSkeleton totalBlocks={10} />}
			noHairlines
		>
			{outlets &&
				outlets?.map((outlet: OutletFullData) => (
					<OutletItem key={outlet.id} outlet={outlet} onClick={props.onOutletClick} />
				))}
		</QuizList>
	);

	const fadeAnimationKey = getAnimationKey(!outlets, errors);
	return (
		<div>
			{!errors.length && (
				<>
					<Block styles={styles.content} secondary>
						<InlineGroup styles={styles.lastUpdate}>
							{lastUpdateDate && (
								<>
									<div className={css(styles.icon)}>
										<Icon
											icon={["far", "sync"] as IconProp}
											color={theme.colors.secondary.border}
										/>
									</div>
									<T size={TextSizeType.caption} muted>
										{formatDateTime(lastUpdateDate, null, { hour: "numeric", minute: "numeric" })}
									</T>
								</>
							)}
						</InlineGroup>
						<InlineGroup>
							<MobileButton
								type={ButtonType.transparent}
								colorType={ColorTypes.info}
								fullWidth={true}
								size={ButtonSize.normal}
								fontSize={theme.sizing.fontSizes.bodyMin}
								icon={["far", "sync"]}
								onClick={refreshNeraestOutlets}
								enabled={!!outlets}
							>
								{t("Refresh")}
							</MobileButton>
						</InlineGroup>
					</Block>
					<Divider />
				</>
			)}
			<QuizFade animationKey={fadeAnimationKey}>{list()}</QuizFade>
		</div>
	);
};
ListNearOutlets.displayName = "f7-list-item";
