import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getSpinnerStyles = (theme: Theme) => ({
	hexagonWrapper: {
		fontSize: "10px",
		position: "relative",
		width: "4em",
		height: "4em",
		animation: "spin .6s infinite",
		animationTimingFunction: "steps(6, end)"
	} as CSSProp,
	triangle: {
		"-webkit-clip-path": "polygon(50% 0, 0 86%, 100% 86%)",
		clipPath: "polygon(50% 0, 0 86%, 100% 86%)",
		width: "2em",
		height: "2em",
		background: theme.colors.primary.main,
		position: "absolute",
		bottom: "0",
		left: "1em",
		transformOrigin: "top"
	} as CSSProp
});
