import * as React from "react";
import { AnswerType } from "quiz/services/models";
import { QuestionControlProps } from "../Question";
import { QrScannerSettings } from "utils/constants";
import { QrScannerErrorControl } from "./qrScanner/QrScannerErrorControl";
import { QuestionActionButton } from "../common/questionActionButton/QuestionActionButton";
import { useStyles } from "hooks/useStyles";
import { questionQrCodeControlStyles } from "./styles/questionQrCodeControlStyles";
import { css } from "wraps/styleCreators";
import { quizQrService } from "quiz/QuizApp";
import { QstContext } from "../common/QstContext";

type QrCodeControlState = {
	error: any;
};

export const QuestionQrCodeControl: React.FC<QuestionControlProps> = props => {
	const data = props.valueItems.length > 0 && props.valueItems[0].value;
	const [state, setState] = React.useState<QrCodeControlState>({ error: null });
	const [qr, setQr] = React.useState(null);
	const styles = useStyles(questionQrCodeControlStyles);
	const hideText = props.question.answerType === AnswerType.ScanQrCodeWithHideText;
	const ctx = React.useContext(QstContext);

	const setAnswer = React.useCallback(
		(qrData: string) => {
			props.visitManager.setAnswer(props.question.id, [{ value: qrData }]);
		},
		[props.question.id]
	);
	const onError = React.useCallback(err => {
		setState(prev => ({ ...prev, error: err }));
		setQr(null);
	}, []);

	const onScan = React.useCallback(qrData => {
		if (qrData) {
			setState(prev => ({ ...prev }));
			setAnswer(qrData);
			setQr(null);
		}
	}, []);

	const [iconStyles, rightIconStyles] = ctx.answerIsValid
		? [null, null]
		: [styles.danger, [{ ...styles.danger, ...styles.asterisk }]];

	return (
		<>
			{state.error ? (
				<QrScannerErrorControl error={state.error} />
			) : (
				<>
					<div className={css(styles.result)}>
						<QuestionActionButton
							text={data ? (hideText ? "QR code successfully recognized" : data) : "Scan the QR-Code"}
							icon={["far", "qrcode"]}
							onClick={() => {
								quizQrService.scan(onScan, onError, setQr, {
									onScanDelayMs: QrScannerSettings.onScanDelayMs
								});
								setAnswer("");
								setState(prev => ({ ...prev }));
							}}
							textStyles={data ? styles.success : null}
							iconStyles={iconStyles}
							iconRight={data ? ["far", "sync"] : ctx.answerIsValid ? null : ["fas", "asterisk"]}
							iconRightStyles={rightIconStyles}
						/>
					</div>
					{qr}
				</>
			)}
		</>
	);
};

QuestionQrCodeControl.displayName = "QuestionQrCodeControl";
