import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

const sharedRowStyles = (theme: Theme) => ({
	display: "flex",
	padding: `${theme.sizing.sizes.sm} 0`,
	margin: 0,
	wordWrap: "break-word"
});

export const getOutletExtendableItemStyles = (theme: Theme) => {
	return {
		header: {
			width: "100%"
		} as CSSProp,
		title: {
			display: "flex",
			flexWrap: "nowrap",
			whiteSpace: "nowrap",
			overflow: "hidden",
			alignItems: "baseline"
		} as CSSProp,
		iconWrap: {
			height: theme.sizing.sizes.lg,
			width: theme.sizing.sizes.lg,
			alignItems: "center",
			marginRight: theme.sizing.sizes.xs
		} as CSSProp,
		row: {
			...sharedRowStyles(theme),
			justifyContent: "space-between",
			textAlign: "right"
		} as CSSProp,
		rowName: {
			marginRight: theme.sizing.sizes.xxl,
			flexWrap: "nowrap",
			whiteSpace: "nowrap"
		} as CSSProp,
		text: {
			overflow: "hidden",
			textOverflow: "ellipsis",
			color: theme.colors.base.texts.accent
		} as CSSProp,
		positionText: {
			overflow: "hidden",
			textOverflow: "ellipsis",
			color: theme.colors.base.texts.muted
		} as CSSProp,
		icon: {
			color: theme.colors.primary.main,
			fontSize: theme.sizing.fontSizes.body
		},
		employeeRow: {
			...sharedRowStyles(theme),
			padding: `${theme.sizing.sizes.sm} 0 0`,
			flexDirection: "column"
		} as CSSProp,
		employeeBlock: {
			display: "flex",
			alignItems: "stretch"
		} as CSSProp,
		employeeInfo: {
			display: "flex",
			flexDirection: "column",
			justifyContent: "space-between",
			flexGrow: 1,
			marginLeft: theme.sizing.sizes.sm
		} as CSSProp
	};
};
