import { apiClient, quizExtensionService } from "quiz/QuizApp";
import { getFileUrlForRequest } from "utils/helpers";

const spinner = require("./spinner.svg");

export function requiresXHRImgLoad(imgUrl: string) {
	const isMdtFile = imgUrl.includes("file/image");
	const requiresXHRLoad = isMdtFile && quizExtensionService.platform == "ios";

	return requiresXHRLoad;
}

export class PhotoBrowserService {
	private pbEl: Element;
	private handledUrls: { [url: string]: string } = {};
	private abortCtrls: AbortController[] = [];
	private blobUrls: string[] = [];

	async loadUrl(img: HTMLImageElement) {
		const src = img.dataset?.src || img.src;
		if (src.includes("blob:") || !requiresXHRImgLoad(src)) {
			return;
		}
		img.src = spinner;

		if (Object.keys(this.handledUrls).findIndex(key => key == src) > -1) {
			const blobUrl = this.handledUrls[src];
			img.src = blobUrl;
			return;
		}

		const abortCtrl = new AbortController();
		const signal = abortCtrl.signal;
		this.abortCtrls.push(abortCtrl);

		const blob = await apiClient.fetchImage(getFileUrlForRequest(src), { signal });
		const blobUrl = URL.createObjectURL(blob);
		this.handledUrls[src] = blobUrl;

		img.src = blobUrl;
	}

	processImagesUrls() {
		if (quizExtensionService.platform !== "ios") {
			return;
		}
		const imgs = this.pbEl.querySelectorAll("img");
		imgs.forEach(img => this.loadUrl(img));
	}

	handleOpen(el: Element) {
		this.pbEl = el;
		this.processImagesUrls();
	}

	handleSwipe(pbEl: Element) {
		if (!this.pbEl) {
			this.pbEl = pbEl;
		}
		this.processImagesUrls();
	}

	abortFetchs() {
		this.abortCtrls.forEach(ctrl => ctrl.abort());
	}

	revokeBlobUrls() {
		this.blobUrls.forEach(url => URL.revokeObjectURL(url));
	}
}
