import { Questionnaire } from "quiz/components/questionnaire/Questionnaire";
import { MDTRecordId } from "mdtApiClient/mdtApiClient";
import { quizDataService, quizDraftService, quizCompareQstService, t } from "quiz/QuizApp";
import { QuizPageComponentProps } from "quiz/QuizAppRoutes";
import { IQuestionnaire, OutletFullData, Visit } from "quiz/services/models";
import * as React from "react";
import { Page } from "quiz/components/page/Page";
import { PageWrap } from "quiz/components/pageWrap/PageWrap";
import { QuestionnaireSkeleton } from "quiz/components/quizSkeleton/skeletons/QuestionnaireSkeleton";
import { QuestionnaireNavbar } from "quiz/components/questionnaire/navbar/QuestionnaireNavbar";
import { QuizError } from "quiz/components/errors/QuizError";

interface QuestionnairePageProps extends QuizPageComponentProps {
	qstId: MDTRecordId;
	outletId: MDTRecordId;
}

export interface QuestionnairePageState {
	visit: Visit;
	qst?: IQuestionnaire;
	outlet?: OutletFullData;
	loading?: boolean;
}

export const QuestionnairePage: React.FC<QuestionnairePageProps> = props => {
	const [state, setState] = React.useState<QuestionnairePageState>({ visit: null, loading: true });

	const onChange = React.useCallback(
		(visit: Visit) => {
			quizDraftService.set(visit);
			setState({ ...state, visit: visit });
		},
		[state.loading]
	);

	return (
		<PageWrap
			fetchData={a => quizDataService.getQuestionnaireData(props.outletId, props.qstId, { signal: a })}
			onSuccess={({ q, o, v, online }) => {
				const actualVisit = online ? quizCompareQstService.getActualVisit(v, q) : v;
				const visit = {
					flagRoute: props.context?.flagRoute,
					...(actualVisit ? actualVisit : {})
				};
				setState({ visit, qst: q, outlet: o } as QuestionnairePageState);
			}}
			isState={!!state.outlet && !!state.qst}
			errorBlock={error => (
				<Page>
					<QuestionnaireNavbar title={t("Error getting questionnaire")} />
					<QuizError error={error} fullScreen fullHeight={false} />
				</Page>
			)}
			loading={() => <QuestionnaireSkeleton totalBlocks={30} />}
		>
			<Page>
				<Questionnaire
					setActions={props.setActions.bind(props)}
					visit={state.visit}
					questionnaire={state.qst}
					outlet={state.outlet}
					onChange={onChange}
				/>
			</Page>
		</PageWrap>
	);
};
QuestionnairePage.displayName = "QuestionnairePage";
