import * as React from "react";
import { ErrorBoundary } from "quiz/components/errorBoundary/ErrorBoundary";
import { QuestionView } from "../../questionView/QuestionView";
import { Block } from "quiz/components/block/Block";
import { Divider } from "quiz/components/divider/Divider";
import { Question } from "../../question/Question";
import { VisitManager } from "quiz/pages/questionnaire/VisitManager";
import { IQuestion, VisitAnswer } from "quiz/services/models";
import { css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import { getQuestionSlideStyles } from "./questionSlideStyles";

type SwiperSlideProps = {
	visitManager: VisitManager;
	swiperRef: any;
	answer: VisitAnswer;
	question: IQuestion;
	questionIdx: number;
};

export const QuestionSlide: React.FC<SwiperSlideProps> = React.memo(
	({ visitManager, swiperRef, answer, question, questionIdx }) => {
		const s = useStyles(getQuestionSlideStyles, !!question.description);
		return (
			<ErrorBoundary>
				<div className={css(s.top)}>
					<Block styles={s.block} secondary>
						<QuestionView
							question={question}
							idx={questionIdx + 1}
							totalQuestions={visitManager.getTotalQuestions()}
						/>
					</Block>
					<Divider />
					<Question
						question={question}
						visitManager={visitManager}
						answer={answer}
						toNextQuestion={() => swiperRef.current && swiperRef.current.swiper.slideNext()} //toDO: timeout
					/>
				</div>
			</ErrorBoundary>
		);
	}
);

QuestionSlide.displayName = "QuestionSlide";
