import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";
import { QuizSkeleton } from "../QuizSkeleton";
import { SkeletonItemWrap } from "../SkeletonItem/SkeletonItem";
import { SkeletonIcon } from "../SkeletonItem/SkeletonIcon";
import { SkeletonRow } from "../SkeletonItem/SkeletonRow";
import { QuizListSkeletonProps, QuizSkeletonList } from "../SkeletonList/SkeletonList";
import { BlockTitleSkeleton } from "./BlockTitleSkeleton";
import { OutletExtendableItemSkeleton } from "./OutletExtendableItemSkeleton";
import { Divider } from "quiz/components/divider/Divider";
import { CSSProp, css } from "wraps/styleCreators";
import { Theme } from "theme/theme";
import { useStyles } from "hooks/useStyles";

const getStyles = (theme: Theme) => {
	return {
		questionDivider: {
			backgroundColor: theme.colors.base.background,
			height: theme.sizing.sizes.lg
		} as CSSProp
	};
};

export const VisitReportSkeleton: React.FC<QuizListSkeletonProps> = props => {
	const theme = React.useContext(ThemeContext);
	const styles = useStyles(getStyles);

	return (
		<>
			<BlockTitleSkeleton titleOnly />
			<OutletExtendableItemSkeleton totalBlocks={1} />
			<QuizSkeletonList>
				<QuizSkeleton>
					<Divider />
					<SkeletonItemWrap spacing={"space-between"} align="center">
						<SkeletonItemWrap direction="column">
							<SkeletonRow type="title" width={164} marginBottom={18} />
							<SkeletonRow type="text" width={216} marginBottom={theme.sizing.sizes.sm} />
							<SkeletonRow type="text" width={116} />
						</SkeletonItemWrap>
					</SkeletonItemWrap>
				</QuizSkeleton>
				<QuizSkeleton>
					<SkeletonItemWrap spacing={"space-between"} align="center">
						<SkeletonRow type="text" width={80} />
						<SkeletonRow type="text" width={100} />
					</SkeletonItemWrap>
				</QuizSkeleton>
				<QuizSkeleton>
					<SkeletonItemWrap spacing={"space-between"} align="center">
						<SkeletonRow type="text" width={100} />
						<SkeletonRow type="text" width={80} />
					</SkeletonItemWrap>
				</QuizSkeleton>
			</QuizSkeletonList>
			{Array.from(new Array(props.totalBlocks)).map((el, idx) => (
				<React.Fragment key={idx}>
					<div className={css(styles.questionDivider)} />
					<QuizSkeletonList>
						<QuizSkeleton>
							<SkeletonItemWrap direction="column">
								<SkeletonRow type="title" width={164} marginBottom={theme.sizing.sizes.md} />
								<SkeletonRow type="text" width={216} />
							</SkeletonItemWrap>
						</QuizSkeleton>
						<QuizSkeleton>
							<SkeletonItemWrap align="center">
								<SkeletonIcon size="small" marginRight={20} />
								<SkeletonRow type="text" width={164} />
							</SkeletonItemWrap>
						</QuizSkeleton>
					</QuizSkeletonList>
				</React.Fragment>
			))}
		</>
	);
};
VisitReportSkeleton.displayName = "VisitReportSkeleton";
