import * as React from "react";
import { Page } from "quiz/components/page/Page";
import { navigate, quizAuthService, t } from "quiz/QuizApp";
import { QuizNavBar } from "quiz/components/quizNavBar/QuizNavBar";
import { css } from "aphrodite";
import { Block, BlockDescription, BlockTitle } from "quiz/components/block/Block";
import { ButtonType, ButtonSize } from "quiz/components/button/Button";
import { Toast } from "quiz/components/interaction/toast/Toast";
import { QuizListInputItem } from "quiz/components/quizListInputItem/QuizListInputItem";
import { ColorTypes } from "theme/colors";
import { getApiErrorForT } from "utils/helpers";
import { useStyles } from "hooks/useStyles";
import { getForgotPasswordPageStyles } from "./forgotPasswordPageStyles";
import { ServerInteractionButton } from "quiz/components/button/ServerInteractionButton";

type ForgotPasswordPageProps = {
	$f7route?: any; //toDo: typing?
};

type ForgotPasswordPageState = {
	value: string;
	error: any;
};

export const ForgotPasswordPage: React.FC<ForgotPasswordPageProps> = props => {
	const [state, setState] = React.useState<ForgotPasswordPageState>({
		value: props?.$f7route?.context?.login,
		error: null
	});
	const style = useStyles(getForgotPasswordPageStyles);

	const onChange = React.useCallback((v: string) => setState({ value: v, error: null }), []);
	const submitEnabled = React.useCallback(() => !!state.value, [state.value]);
	const command = React.useCallback(() => {
		return new Promise<void>((resolve, reject) => {
			quizAuthService
				.forgotPassword(state.value)
				.then(() => {
					Toast.create({
						type: "success",
						position: "bottom",
						text: t("The letter has been sent to your mail"),
						on: {
							closed: () => navigate("/")
						}
					}).open();
					return resolve();
				})
				.catch(err => {
					const [error, args] = getApiErrorForT(err);
					setState(prev => ({ ...prev, error: t(error, args) }));
					reject();
				});
		});
	}, [state.value]);

	return (
		<Page>
			<QuizNavBar backLink title={t("Forgot password?")} faq />
			<div className={css(style.forgotPage)}>
				<BlockTitle title={t("Reset password")} />
				<Block secondary>
					<QuizListInputItem
						required
						type="text"
						inputMode="email"
						icon={["far", "envelope"]}
						value={state.value}
						onChange={onChange}
						placeholder={t("Enter e-mail")}
					/>
				</Block>
				{state.error && <BlockDescription text={state.error} styles={style.error} />}
				<BlockDescription
					muted={false}
					colorType={ColorTypes.base}
					styles={style.description}
					text={t(
						"A letter will be sent to the specified e-mail address with a link to reset password and install a new one"
					)}
				/>
				<Block>
					<ServerInteractionButton
						type={ButtonType.normal}
						colorType={ColorTypes.primary}
						fullWidth={true}
						size={ButtonSize.large}
						title={t("Reset password")}
						enabled={submitEnabled}
						command={command}
					/>
				</Block>
			</div>
		</Page>
	);
};

ForgotPasswordPage.displayName = "ForgotPasswordPage";
