import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { ContextInfoBlockProps } from "./ContextInfoBlock";

export const getContextInfoBlockStyles = (theme: Theme, props: ContextInfoBlockProps) => {
	const color = theme.colors[props.colorType] || theme.colors.primary;

	return {
		errorBlock: {
			paddingTop: props.separateBlock ? theme.sizing.sizes.lg : "0"
		},
		wrap: {
			background: theme.colors.primary.background,
			display: "flex",
			flexDirection: "column",
			paddingTop: theme.constants.gridPadding,
			paddingBottom: theme.sizing.sizes.md
		} as CSSProp,
		customIcon: {
			width: theme.sizing.sizes.xxl,
			height: theme.sizing.sizes.xxl
		},
		buttonIcon: {
			color: color.main
		} as CSSProp,
		actionButtons: {
			display: "flex",
			justifyContent: "flex-end",
			flexWrap: "wrap"
		} as CSSProp,
		actionButton: {
			padding: theme.sizing.sizes.sm
		},
		iconColor: {
			color: color.main
		}
	};
};

export const getErrorSectionStyles = (theme: Theme) => {
	return {
		errorSection: {
			display: "flex",
			marginBottom: theme.sizing.sizes.md
		} as CSSProp,
		icon: {
			display: "flex",
			alignItems: "center",
			marginRight: theme.constants.gridPadding,
			width: theme.sizing.sizes.xxl,
			height: theme.sizing.sizes.xxl
		} as CSSProp,
		errorInfo: {
			display: "flex",
			flexDirection: "column"
		} as CSSProp,
		title: {
			marginBottom: theme.sizing.sizes.sm
		} as CSSProp,
		errorText: {
			margin: "0"
		} as CSSProp
	};
};
