import { ButtonType } from "quiz/components/button/Button";
import { MobileActionButton, MobileActionButtonProps } from "quiz/components/button/MobileActionButton";
import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { css } from "wraps/styleCreators";
import { getSwiperBarStyles } from "./swiperBarStyles";
import { Text } from "quiz/components/text/Text";
import { t } from "quiz/QuizApp";
import { CheckIcon, LeftAngleIcon, ReportIcon, RightAngleIcon } from "./CustomIcons";

type SwiperButtonProps = Pick<MobileActionButtonProps, "command" | "icon">;

const SwiperButton: React.FC<SwiperButtonProps> = React.memo(props => {
	return (
		<MobileActionButton type={ButtonType.link} fullWidth fullHeight command={props.command}>
			{props.children}
		</MobileActionButton>
	);
});
SwiperButton.displayName = "SwiperButton";

type SwiperBarProps = {
	finish: boolean;
	start: boolean;
	prevClick(): void;
	nextClick(): void;
	reportClick(): void;
};

export const SwiperBar: React.FC<SwiperBarProps> = React.memo(props => {
	const styles = useStyles(getSwiperBarStyles);
	const { finish, start, prevClick, nextClick, reportClick } = props;

	return (
		<div className={css(styles.tabBar)}>
			<div className={css(styles.swiperButtons)}>
				<div className={css(styles.buttonWrap)}>
					{!start && (
						<SwiperButton command={prevClick.bind(props)}>
							<LeftAngleIcon />
						</SwiperButton>
					)}
				</div>
				<div className={css(styles.buttonWrap)}>
					{!finish && (
						<SwiperButton command={() => reportClick()}>
							<ReportIcon size={48} />
						</SwiperButton>
					)}
				</div>
				{finish ? (
					<SwiperButton command={reportClick.bind(props)}>
						<CheckIcon />
					</SwiperButton>
				) : (
					<SwiperButton command={nextClick.bind(props)}>
						<RightAngleIcon />
					</SwiperButton>
				)}
			</div>
			<div className={css(styles.buttonsSignatures)}>
				<div className={css(styles.signature)}>
					{!start && <Text styles={styles.signature}>{t("Previous")}</Text>}
				</div>
				<div className={css(styles.signature)}>
					{!finish && <Text styles={styles.signature}>{t("Report")}</Text>}
				</div>
				<Text styles={styles.signature}>{finish ? t("Finish") : t("Next")}</Text>
			</div>
		</div>
	);
});
SwiperBar.displayName = "SwiperBar";
