import * as React from "react";
import { UploadPhoto, GetUploadButtonParams } from "./models";
import { UploadPhotoButton } from "./common/uploadPhotoButton/UploadPhotoButton";
import { TActions } from "quiz/components/actions/Actions";
import { ActionSheetItem } from "quiz/components/actionSheetItem/ActionSheetItem";
import { t, logger, nav } from "quiz/QuizApp";
import { ChosePhotoButton } from "./common/choosePhotoButton/ChosePhotoButton";
import { prepareFiles } from "./UploadPhotoExtension";
import { LogType } from "quiz/services/Logger";
import { QuizActions } from "quiz/components/actions/QuizActions";
import "react-html5-camera-photo/build/css/index.css";

const checkPermission = () => {
	return new Promise((resolve, reject) => {
		const permissions = (window as any).cordova && (window as any).cordova.plugins.permissions;
		if (permissions) {
			permissions.checkPermission(permissions.CAMERA, function(status) {
				if (status.hasPermission) {
					resolve();
				} else {
					permissions.requestPermission(
						permissions.CAMERA,
						status => {
							if (status.hasPermission) {
								resolve();
							} else reject("Has not camera permissions");
						},
						err => {
							reject(err);
						}
					);
				}
			});
		} else reject("cordova permissions plugin not allowed");
	});
}; //toDO refactor

declare global {
	interface Navigator {
		camera: {
			getPicture: (onSuccess: (photos: string) => void, onError: (e: string) => void, options?: any) => void;
		};
	}
}

export class CustomPhotoExtension implements UploadPhoto {
	getUploadButton(params: GetUploadButtonParams) {
		const actionsRef = React.useRef<TActions>();

		return (
			<>
				<UploadPhotoButton
					icon={["far", "camera"]}
					onClick={() => actionsRef.current.open()}
					required={params.required}
				/>

				<QuizActions
					ref={actionsRef}
					actions={() => (
						<>
							<ActionSheetItem
								text={t("Take picture")}
								icon={["far", "camera"]}
								onClick={() => {
									return checkPermission()
										.then(() => {
											return nav.toCamera({
												context: { onTakePhoto: img => params.commonLogic(img) }
											});
										})
										.catch(err =>
											logger.log(
												"camera",
												{
													error: err || "Camera permission error"
												},
												LogType.error
											)
										);
								}}
							/>
							<ChosePhotoButton
								onChange={e => prepareFiles(e, params.commonLogic)}
								button={() => <ActionSheetItem text={t("Gallery")} icon={["far", "images"]} />}
							/>
						</>
					)}
				/>
			</>
		);
	}
}
