import React from "react";
import { List, ListItem } from "framework7-react";
import { QrNotAllowedError, QrNoVideoInputDevicesError, QrUnknownError } from "./error/QrScannerError";

enum QrScannerErrorEnum {
	NotAllowedError = "NotAllowedError",
	NoVideoInputDevicesError = "NoVideoInputDevicesError"
}

type QrScannerError = {
	error: any;
};

const QrScannerErrorMap = {
	[QrScannerErrorEnum.NotAllowedError]: QrNotAllowedError,
	[QrScannerErrorEnum.NoVideoInputDevicesError]: QrNoVideoInputDevicesError
};

export const QrScannerErrorControl: React.FC<QrScannerError> = props => {
	const ErrorComponent = QrScannerErrorMap[props.error && props.error.name];
	return (
		<List>
			<ListItem>{ErrorComponent ? <ErrorComponent /> : <QrUnknownError />}</ListItem>
		</List>
	);
};

QrScannerErrorControl.displayName = "QrScannerErrorControl";
