import { TActions } from "quiz/components/actions/Actions";
import { Swiper, SwiperSlide, Page, f7 } from "framework7-react";
import { useStyles } from "hooks/useStyles";
import { useVisitManager } from "quiz/pages/questionnaire/visitManagerHook";
import { nav, quizAuthService, quizDraftService, t } from "quiz/QuizApp";
import { QuizPageComponentProps } from "quiz/QuizAppRoutes";
import { IQuestion, IQuestionnaire, OutletFullData, Visit } from "quiz/services/models";
import * as React from "react";
import { ColorTypes } from "theme/colors";
import { SwiperSettings } from "utils/constants";
import { css } from "wraps/styleCreators";
import { SwiperBar } from "./swiperBar/SwiperBar";
import { ActionSheetReportIcon } from "./swiperBar/CustomIcons";
import { getQuestionnaireStyles } from "./questionnaireStyles";
import { ActionSheetItem } from "quiz/components/actionSheetItem/ActionSheetItem";
import { ProgressBlockState } from "quiz/components/progressBar/ProgressBlock";
import { ProgressBar } from "quiz/components/progressBar/ProgressBar";
import { QuizActions } from "quiz/components/actions/QuizActions";
import { QuestionSlide } from "./questionSlide/QuestionSlide";
import { useVirtualDataManager } from "quiz/pages/questionnaire/virtualDataManagerHook";
import { Toast } from "quiz/components/interaction/toast/Toast";
import { Dialog } from "quiz/components/interaction/Dialog";
import { QuestionnaireNavbar } from "./navbar/QuestionnaireNavbar";
import { FullscreenInfo } from "quiz/components/infoBlock/fullscreenInfoBlock/FullscreenInfo";
import { emptyIcon } from "quiz/components/infoScreenIcons/emptyIcon";
import { FeedbackTypeEnum } from "quiz/components/feedback/Feedback";
import { ValidationErrorType } from "quiz/pages/questionnaire/VisitManager";
import { QstContext } from "../question/common/QstContext";

export interface QuestionnaireProps {
	questionnaire: IQuestionnaire;
	outlet: OutletFullData;
	visit?: Visit;
	onChange?: (visit: Visit) => void;
	onFinish?: (visit: Visit) => void;
	onDelete?: (visit: Visit) => void;
	setActions?: QuizPageComponentProps["setActions"];
}

export interface QuestionnaireSlideState {
	finished: boolean;
	currentSlide: number;
}

type VirtualData = {
	offset: number;
	from: number;
	to: number;
	slides: IQuestion[];
};

const qstSlideClassName = "qst-slide";

export const questionnaireBeforeIn = () => nav.events.emit("questionnaireBeforeIn", void 0);
export const questionnaireBeforeOut = (page: any) => nav.events.emit("questionnaireBeforeOut", page);

export const Questionnaire: React.FunctionComponent<QuestionnaireProps> = React.memo(props => {
	const actionsRef = React.useRef<TActions>();
	const [qstChanged, setQstChanged] = React.useState(false);
	const s = useStyles(getQuestionnaireStyles);
	const swiperRef = React.useRef<any>();
	const [mounted, setMounted] = React.useState<boolean>(false);

	const questions = React.useMemo(() => props.questionnaire.questions, [props.questionnaire.questions.length]);

	const [slideState, setSlideState] = React.useState<QuestionnaireSlideState>({
		finished: questions.length <= 1,
		currentSlide: 0
	});
	const [virtualData, setVirtualData] = React.useState<VirtualData>({
		offset: 0,
		from: 0,
		to: 1,
		slides: [...questions.slice(0, 2)]
	});
	const [reportOpen, setReportOpen] = React.useState(false);

	const { visit, visitManager } = useVisitManager(
		props.questionnaire,
		props.outlet,
		quizAuthService.state.user.employee,
		{ initVisit: props.visit }
	);

	const prevVisit = visitManager.getPrevVisit();
	const virtualDataManager = useVirtualDataManager(virtualData);
	const virtualSlides = virtualDataManager.virtualData;
	const [clean, setClean] = React.useState<ProgressBlockState[]>(visitManager.getProgress());
	const toReport = React.useCallback(() => {
		setReportOpen(true);
		nav.toReport({
			visit,
			visitManager,
			outlet: props.outlet,
			open: true,
			isCompleted: false,
			onAnswerClick: answer => {
				const qIdx = questions.findIndex(q => q.id == answer.question.id);
				if (qIdx >= 0) {
					nav.back();
					setReportOpen(false);
					swiperRef.current?.swiper.slideTo(qIdx, 0, true);
				}
			}
		});
	}, [visit]);

	React.useEffect(() => {
		visitManager.subscribe("changed", ({ visit }) => {
			props.onChange(visit);
			setQstChanged(true);
		});
		virtualDataManager.subscribe("changed", setVirtualData);
		setMounted(true);
	}, []);

	const contentClassName = React.useMemo(() => css(s.content), [s.content]);

	const style = React.useMemo(
		() =>
			`.${contentClassName} .swiper-container { height: 100%; } .${contentClassName} .swiper-slide { overflow: auto; }`,
		[contentClassName]
	);

	const updateQuestionnaireData = () => {
		setSlideState({
			finished: swiperRef.current.swiper.isEnd,
			currentSlide: swiperRef.current.swiper.realIndex
		});
		setClean(
			visitManager.changeProgress(swiperRef.current.swiper.previousIndex, swiperRef.current.swiper.realIndex)
		);
		setQstChanged(true);
	};

	React.useEffect(() => {
		const qstBeforeInUnsub = nav.subscribe("questionnaireBeforeIn", () => setReportOpen(false));
		const qstBeforeOutUnsub = nav.subscribe("questionnaireBeforeOut", (page: any) => {
			qstChanged &&
				!reportOpen &&
				page?.direction === "backward" &&
				Toast.create({
					type: "info",
					text: t("Questionnaire draft was saved")
				}).open();
		});

		return () => {
			qstBeforeInUnsub();
			qstBeforeOutUnsub();
		};
	}, [qstChanged, reportOpen]);

	const openActions = () => {
		actionsRef.current.open();
	};

	return (
		<Page>
			<QuestionnaireNavbar title={props.questionnaire.name} actions={{ show: true, cb: openActions }} noShadow>
				<ProgressBar items={clean} />
			</QuestionnaireNavbar>
			{questions.length === 0 ? (
				<FullscreenInfo
					icon={emptyIcon}
					title={t("There are no questions in the questionnaire")}
					message={t("Sorry, there are no questions in the questionnaire. Contact administrator")}
					colorType={ColorTypes.warning}
					primaryButton={{
						title: t("Refresh"),
						icon: ["far", "sync"],
						command: () => f7.views.main.router.refreshPage()
					}}
					secondaryButton={{
						title: t("Complain"),
						icon: ["far", "exclamation-triangle"],
						command: () => nav.toFeedback({ type: FeedbackTypeEnum.Problem, value: "" })
					}}
				/>
			) : (
				<div className={css(s.wrap)}>
					<div className={contentClassName}>
						<style>{style}</style>
						<Swiper
							params={{
								virtual: {
									slides: questions,
									renderExternal: data => {
										virtualDataManager.setVD(data);
									}
								},
								spaceBetween: SwiperSettings.spaceBetweenPx,
								noSwipingSelector: "img",
								on: {
									slideChangeTransitionEnd: () => {
										virtualDataManager.updateVD();
										updateQuestionnaireData();
									},
									slideResetTransitionEnd: () => {
										virtualDataManager.updateVD();
										if (swiperRef.current?.swiper?.previousIndex !== undefined) {
											updateQuestionnaireData();
										}
									},
									transitionStart: () => {
										f7.$(`.${qstSlideClassName}`)
											.find("input:focus")
											.blur();
									}
								},
								passiveListeners: false,
								threshold: 21
							}}
							ref={swiperRef}
						>
							{virtualSlides?.slides &&
								mounted &&
								virtualSlides.slides.map((question: IQuestion) => {
									const answer = visitManager.getAnswer(question.id);
									const prevAnswer = visitManager.getPrevAnswer(question.id);
									const questionIdx = questions.findIndex(q => q.id === question.id);
									const slideActive = questions[swiperRef.current.swiper.realIndex].id == question.id;
									const validationErrors = visitManager.validateAnswer(answer);

									return (
										<SwiperSlide
											style={{ left: `${virtualSlides.offset}px` }}
											key={question.id}
											className={qstSlideClassName}
										>
											<QstContext.Provider
												value={{
													answer,
													prevAnswer,
													prevVisitDate: prevVisit?.dateEnd,
													slideActive: slideActive,
													validationErrors: validationErrors,
													answerIsValid: !validationErrors.filter(
														e => e.type == ValidationErrorType.RequiredAnswer
													).length,
													attachPhotoIsValid: !validationErrors.filter(
														e => e.type == ValidationErrorType.RequiredPhoto
													).length
												}}
											>
												<QuestionSlide
													question={question}
													answer={answer}
													swiperRef={swiperRef}
													visitManager={visitManager}
													questionIdx={questionIdx}
												/>
											</QstContext.Provider>
										</SwiperSlide>
									);
								})}
						</Swiper>
					</div>
				</div>
			)}
			{questions.length === 0 ? null : (
				<SwiperBar
					finish={slideState.finished}
					start={swiperRef.current ? swiperRef.current.swiper.realIndex === 0 : true}
					prevClick={() => swiperRef.current && swiperRef.current.swiper.slidePrev()}
					nextClick={() => swiperRef.current && swiperRef.current.swiper.slideNext()}
					reportClick={toReport}
				/>
			)}
			<QuizActions
				ref={actionsRef}
				extend
				actions={() => (
					<>
						<ActionSheetItem
							onClick={() => toReport()}
							text={t("Report")}
							renderIcon={() => <ActionSheetReportIcon />}
							iconStyles={s.reportIcon}
						/>

						<ActionSheetItem
							onClick={() => {
								const closed = new Promise(resolve =>
									actionsRef.current.f7Actions.once("closed", () => resolve())
								);
								const dialog = Dialog.create({
									title: t("Delete draft"),
									text: t("Are you sure you want to delete the draft?"),
									closeByBackdropClick: true,
									buttons: [
										{
											text: t("Cancel")
										},
										{
											text: t("Delete"),
											onClick: () => {
												const visitAnswersFiles = visitManager.getAnswersFiles();
												const deleteAnswersFiles = quizDraftService.removeFiles(
													visitAnswersFiles
												);
												const deleteDraft = quizDraftService.delete(
													props.outlet.id,
													props.questionnaire.id
												);
												return Promise.all([closed, deleteDraft, deleteAnswersFiles]).then(
													() => {
														setQstChanged(false);
														Toast.create({
															type: "error",
															text: t("Questionnaire draft was deleted")
														}).open();
														nav.back();
														return;
													}
												);
											}
										}
									]
								});
								dialog.open();
								return void 0;
							}}
							text={t("Delete draft")}
							icon={["far", "trash-alt"]}
							colorType={ColorTypes.danger}
						/>
					</>
				)}
			/>
		</Page>
	);
});
Questionnaire.displayName = "Questionnaire";
