import { Link } from "framework7-react";
import { useStyles } from "hooks/useStyles";
import { IconWrap } from "quiz/components/iconWrap/IconWrap";
import { nav } from "quiz/QuizApp";
import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";
import { css } from "wraps/styleCreators";
import { getBackLinkStyles } from "./backLinkStyles";

const navbackCb = () => nav.back();

export type BackLinkProps = {
	noMargin?: boolean;
};

export const BackLink: React.FC<BackLinkProps> = props => {
	const styles = useStyles(getBackLinkStyles, props);
	const theme = React.useContext(ThemeContext);
	return (
		<Link onClick={navbackCb} className={css(styles.link)}>
			<IconWrap icon={["far", "angle-left"]} alignment="center-left" iconSize={theme.sizing.sizes.md} />
		</Link>
	);
};

BackLink.displayName = "BackLink";
