import { MDTRecordId } from "mdtApiClient/mdtApiClient";
import NanoEvents from "nanoevents";
import * as immutable from "object-path-immutable";
import { t } from "quiz/QuizApp";
import { QuizQstService } from "quiz/services/data/QuizQstService";
import {
	AnswerType,
	Employee,
	IQuestion,
	QuestionAttachPhoto,
	Visit,
	VisitAnswer,
	VisitAnswerFile,
	VisitAnswerItem,
	ID
} from "quiz/services/models";
import { QuizDraftService } from "quiz/services/QuizDraftService";
import { updateArray } from "utils/helpers";
import { IQuestionnaire, OutletFullData } from "./../../services/models";
import { ProgressBlockState } from "quiz/components/progressBar/ProgressBlock";
import { Photo } from "quiz/services/extension/uploadPhoto/models";

//typing fix
declare module "object-path-immutable" {
	export function update<T = object>(src: T, path?: Path, updater?: (formerValue: any) => any): T;
}

export interface VisitManagerOptions {
	initVisit?: Partial<Visit>;
}

export interface VisitManagerEvents {
	changed: {
		visit: Visit;
		prev: Visit;
	};
}

export enum ValidationErrorType {
	RequiredAnswer,
	RequiredComment,
	RequiredPhoto
}

export interface ValidationError {
	title: string;
	type: ValidationErrorType;
	itemsId?: ID[];
}

export class VisitManager {
	private visit: Visit;
	private prevVisit: Visit;
	private events = new NanoEvents<VisitManagerEvents>();
	private progress: ProgressBlockState[];

	constructor(
		private questionnaire: IQuestionnaire,
		private outlet: OutletFullData,
		private employee: Employee,
		private options: VisitManagerOptions,
		private quizDraftService: QuizDraftService,
		private quizQstService: QuizQstService
	) {
		this.visit = { ...this.createVisit(), ...this.options.initVisit };
		navigator.geolocation.getCurrentPosition(position => {
			this.visit.latitude = position.coords.latitude;
			this.visit.longitude = position.coords.longitude;
		}); //toDO: refactor
		this.prevVisit = this.visit?.questionnaire?.prevVisit;
		questionnaire.questions.forEach(q => {
			const curAnswer = this.visit.answers.find(a => a.questionId == q.id);
			if (!curAnswer) {
				const answer = { ...this.createAnswer(q), items: [] };
				this.visit.answers.push(answer);
				if (q.items?.length) {
					// for boolean type with items, all question items should have answer
					if (q.answerType == AnswerType.Boolean || q.answerType == AnswerType.ManyOptions) {
						q.items.forEach(item => {
							answer.items.push({
								id: item.id,
								value: false,
								answer: item.name
							});
						});
					} else if (q.answerType == AnswerType.Text || q.answerType == AnswerType.Number) {
						q.items.forEach(item => {
							answer.items.push({
								id: item.id,
								value: "",
								answer: item.name
							});
						});
					}
				}
			}
		});
		this.visit.answers.sort((a, b) => a.question.sortOrder - b.question.sortOrder);
		this.progress = this.prepareProgress(this.visit);
	}

	private prepareProgress(visit: Visit): ProgressBlockState[] {
		return visit.answers.map((a, idx) => {
			if (idx == 0) {
				return ProgressBlockState.Viewed;
			}
			if (a.viewed) {
				const isValid = this.validateAnswer(a).length == 0;
				return isValid ? ProgressBlockState.Success : ProgressBlockState.Invalid;
			} else return ProgressBlockState.Unchanged;
		});
	}

	getVisit(): Visit {
		return this.visit;
	}

	getPrevVisit(): Visit {
		return this.prevVisit;
	}

	getProgress(): ProgressBlockState[] {
		return this.progress;
	}

	createVisit(): Visit {
		return {
			dateBegin: new Date(),
			outletId: this.outlet.id,
			outlet: this.outlet,
			employeeId: this.employee.id,
			employee: this.employee,
			questionnaireId: this.questionnaire.id,
			questionnaire: this.questionnaire,
			answers: []
		};
	}

	getAnswer(questionId: MDTRecordId) {
		return this.visit.answers.find(a => a.questionId == questionId);
	}

	getPrevAnswer(questionId: MDTRecordId) {
		return this.prevVisit?.answers?.find(a => a.questionId == questionId);
	}

	getAnswerItems(questionId: MDTRecordId) {
		return this.getAnswer(questionId)?.items;
	}

	getComment(questionId: MDTRecordId) {
		return this.getAnswer(questionId)?.comment;
	}

	getAnswersFiles(): VisitAnswerFile[] {
		return [].concat(...this.visit.answers?.filter(a => a?.files?.length).map(a => a.files));
	}

	getTotalQuestions(): number {
		return this.visit.questionnaire.questions.length || 0;
	}

	private createAnswer(question: IQuestion) {
		return {
			questionId: question.id,
			question,
			questionnaireId: this.questionnaire.id,
			answerType: question.answerType
		};
	}

	private updateVisitAnswer<T extends VisitAnswer>(
		questionId: MDTRecordId,
		data: Partial<T>,
		prepareData?: (o: T, v: T) => T
	) {
		const visit = immutable.update(this.visit, "answers", (answers: VisitAnswer[]) => {
			const question = this.questionnaire.questions.find(q => q.id == questionId);
			const answer: Partial<VisitAnswer> = {
				...this.createAnswer(question),
				...data
			};
			return updateArray(answers, [answer], (o, v) => o.questionId == v.questionId, prepareData).sort(
				(a, b) => a.question.sortOrder - b.question.sortOrder
			);
		});
		this.update(visit);
	}

	setComment(questionId: MDTRecordId, comment: string) {
		this.updateVisitAnswer(questionId, { comment }, (o, n) => ({ ...o, ...n }));
	}

	setAnswer(questionId: MDTRecordId, items: VisitAnswerItem[], replace?: boolean) {
		this.updateVisitAnswer(questionId, { items }, (originalAnswer, newAnswer) => {
			return {
				...originalAnswer,
				items:
					!replace && originalAnswer?.items
						? updateArray(originalAnswer.items, items, (o, v) => o.id == v.id)
						: newAnswer.items
			};
		});
	}

	setViewed(questionId: MDTRecordId) {
		this.updateVisitAnswer(questionId, { viewed: true }, (o, n) => ({ ...o, ...n }));
	}

	private static fileId = new Date().getTime();
	createFileId(): number {
		return VisitManager.fileId++;
	}

	async addFiles(questionId: MDTRecordId, files: Photo[]): Promise<void> {
		const fileItems: VisitAnswerFile[] = files.map(data => {
			return {
				draftFileId: data.photoId,
				data
			};
		});

		await this.quizDraftService.setFiles(fileItems);
		fileItems.forEach(file => delete file.data);

		this.updateVisitAnswer(questionId, {}, origAnswer => {
			return {
				...origAnswer,
				files: [...(origAnswer.files || []), ...fileItems]
			};
		});
	}

	async removeFile(questionId: MDTRecordId, fileIndex: number): Promise<void> {
		const answer = this.visit.answers.find(answer => answer.questionId == questionId);
		if (answer.files?.[fileIndex]) await this.quizDraftService.removeFiles([answer.files[fileIndex]]);
		this.updateVisitAnswer(questionId, {}, origAnswer => ({
			...origAnswer,
			files: immutable.del(origAnswer.files, `${fileIndex}`)
		}));
	}

	async getFilesSrc(files: VisitAnswerFile[]): Promise<VisitAnswerFile[]> {
		return this.quizDraftService.getFiles(files);
	}

	// 
	setVisitComment(comment: string) {
		const visit = immutable.update(this.visit, "comment", () => comment);
		this.update(visit);
	}

	getVisitComment() {
		return this.visit.comment;
	}

	setCommentSignature(comment: string) {
		const visit = immutable.update(this.visit, "commentSignature", () => comment);
		this.update(visit);
	}

	getCommentSignature() {
		return this.visit.commentSignature;
	}

	setFileSignature(data: string) {
		const visit = immutable.set(this.visit, "signatureFile", data);
		this.update(visit);
	}

	getFileSignature() {
		return this.visit.signatureFile;
	}

	validateSignature() {
		return !!this.visit.commentSignature && !!this.visit.signatureFile;
	}

	isValidQuestionnaire() {
		return this.visit.questionnaire.flagSignature
			? this.isValidAnswers() && this.validateSignature()
			: this.isValidAnswers();
	}

	subscribe<U extends keyof VisitManagerEvents>(event: U, callback: (arg: VisitManagerEvents[U]) => any) {
		return this.events.on(event, callback);
	}

	isValidAnswers() {
		return this.visit.answers.every(a => this.isAnswerValid(a)) && this.visit.questionnaire.questions.length;
	}

	changeProgress(changeIdx: number, currentIdx?: number): ProgressBlockState[] {
		const viewedIdx = this.progress.findIndex(progressVal => progressVal === ProgressBlockState.Viewed);
		if (viewedIdx !== changeIdx) {
			this.setProgressValidateStatus(viewedIdx);
		} else if (changeIdx != currentIdx) {
			this.setProgressValidateStatus(changeIdx);
		}
		this.progress[currentIdx] = ProgressBlockState.Viewed;

		this.setViewed(this.visit.answers[changeIdx].questionId);

		return this.progress;
	} //todo:        ,    questionId

	setProgressValidateStatus(idx: number) {
		this.progress[idx] = this.isAnswerValid(this.visit.answers[idx])
			? ProgressBlockState.Success
			: ProgressBlockState.Invalid;
	}

	isAnswerValid(answer: VisitAnswer): boolean {
		return !this.validateAnswer(answer).length;
	}

	private isEmpty(v) {
		return v != null && v !== "" && !Number.isNaN(v);
	}

	//todo validation refactoring
	validateAnswer(answer: VisitAnswer): ValidationError[] {
		const errors: ValidationError[] = [];
		const question = this.questionnaire.questions.find(q => q.id == answer.question.id);
		let invalidItemsIds: MDTRecordId[] = [];
		if (question?.flagAnswerRequired) {
			let valid = true;
			if (question.answerType === AnswerType.Text || question.answerType === AnswerType.Number) {
				if (question.items?.length > 0) {
					invalidItemsIds = question.items.reduce((acc, questionItem) => {
						if (!answer.items.filter(answerItem => answerItem.id == questionItem.id)[0]?.value) {
							acc.push(questionItem.id);
						}
						return acc;
					}, []);

					valid = invalidItemsIds.length == 0;
				} else {
					valid = answer.items?.some(i => this.isEmpty(i.value));
				}
			} else if (question.answerType == AnswerType.OneOption) {
				valid = answer.items.some(i => this.isEmpty(i.id));
			} else if (question.answerType == AnswerType.AttachPhoto) {
				valid = answer.files?.length > 0;
			} else if (question.answerType != AnswerType.ManyOptions) {
				valid = answer.items.some(i => this.isEmpty(i.value));
			}

			if (!valid) {
				errors.push({
					title: t("Answer required"),
					type: ValidationErrorType.RequiredAnswer,
					itemsId: invalidItemsIds
				});
			}
		}

		if (
			answer.question.questionAttachPhoto === QuestionAttachPhoto.Required ||
			question.answerType == AnswerType.AttachPhoto
		) {
			if (!answer.files?.length) {
				errors.push({
					title: t("Photo required"),
					type: ValidationErrorType.RequiredPhoto
				});
			}
		}

		return errors;
	}

	private update(visit: Visit) {
		const prev = this.visit;
		this.visit = visit;
		this.events.emit("changed", { visit: this.visit, prev });
	}

	public isQuickTransition(q: IQuestion): boolean {
		const res = !(
			q.flagComment ||
			q.questionAttachPhoto !== QuestionAttachPhoto.None ||
			q.answerType === AnswerType.AttachPhoto
		);
		return res;
	}

	async finishVisit(): Promise<Visit> {
		const visit: Visit = await new Promise(resolve => {
			navigator.geolocation.getCurrentPosition(
				({ coords: { latitude, longitude } }) =>
					resolve({ ...this.visit, latitudeEnd: latitude, longitudeEnd: longitude }),
				() => resolve(this.visit)
			);
		});
		visit.dateEnd = new Date();
		visit.elapsedSeconds = Math.round((visit.dateEnd.getTime() - visit.dateBegin.getTime()) / 1000);

		await this.quizQstService.finishVisit(visit);
		return visit;
	}
}
