import * as React from "react";
import { List, ListItem } from "quiz/components/listView/ListView";
import { SkeletonBlock } from "framework7-react";
import { Block } from "quiz/components/block/Block";

export type QuizListSkeletonProps = {
	totalBlocks: number;
	blockTitle?: boolean;
};

export const QuizSkeletonList: React.FC = props => {
	return (
		<List>
			<Block secondary>{props.children}</Block>
		</List>
	);
};

export const SimpleListSkeleton: React.FC<QuizListSkeletonProps> = props => {
	return (
		<List>
			{Array.from(new Array(props.totalBlocks)).map((el, idx) => (
				<ListItem key={idx} link={"#"} noChevron>
					<SkeletonBlock />
				</ListItem>
			))}
		</List>
	);
};
