import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getQuizEmptyListStyles = (theme: Theme, separateBlock: boolean) => {
	const wrapMarginTop = separateBlock ? theme.sizing.sizes.md : "unset";

	return {
		wrap: {
			padding: `${theme.sizing.sizes.md} 0`,
			display: "flex",
			justifyContent: "center",
			marginTop: wrapMarginTop
		} as CSSProp,
		content: {
			display: "flex",
			padding: `${theme.sizing.sizes.md}`
		} as CSSProp,
		icon: {
			display: "flex",
			justifyContent: "center",
			marginRight: theme.sizing.sizes.md
		} as CSSProp,
		title: {
			color: theme.colors.base.border
		} as CSSProp
	};
};
