import Framework7React, { App as FWApp, F7App, View } from "framework7-react";
import { Framework7Params } from "framework7/components/app/app-class";
import "framework7/css/framework7.bundle.css";
import Framework7 from "framework7/framework7.esm.bundle.js";
import * as React from "react";
import { useEffect } from "react";
import { Theme } from "theme/theme";
import { theme } from "themes/defaultTheme";
import { createThemeApplier } from "wraps/component";
import "inobounce";
import { scale, ScaleEnum } from "utils/cssHelpers";
import { InstanceBar } from "quiz/components/instanceBar/InstanceBar";

import "./bugFix.css"; //for ripple effect safari fix

Framework7.use(Framework7React);

const f7params: Framework7Params = {
	theme: "md",
	view: {
		pushState: true,
		pushStateRoot: location.pathname == "/" ? null : location.pathname
	},
	statusbar: {
		enabled: true,
		iosOverlaysWebView: true,
		androidOverlaysWebView: true,
		scrollTopOnClick: false
	},
	swipeout: {
		removeElements: false,
		noFollow: true
	}
};

const applyTheme = createThemeApplier((theme: Theme) => {
	return {
		"f7-font-family": theme.typography.fonts.base,
		"f7-text-color": theme.colors.base.text,
		"f7-line-height": theme.sizing.lineHeights.body,
		"f7-font-size": theme.sizing.fontSizes.body,
		"f7-theme-color": theme.colors.primary.main,
		"f7-button-letter-spacing": "normal",

		//Page
		"f7-page-bg-color": theme.colors.base.background,

		//Button
		"f7-button-large-font-size": theme.sizing.fontSizes.body,
		"f7-button-large-font-weight": theme.sizing.fontWeights.medium,
		"f7-button-large-height": theme.sizing.sizes.xxl,
		"f7-button-text-transform": "none",

		//List
		"f7-list-margin-vertical": 0,
		"f7-list-border-color": theme.colors.base.background,
		"f7-list-in-list-padding-left": "0px",

		//ListItem
		"f7-list-item-border-color": theme.colors.base.background,
		"f7-list-item-padding-horizontal": theme.constants.gridPadding,
		"f7-list-item-padding-vertical": scale(theme.sizing.sizes.lg, ScaleEnum.Half),
		"f7-list-chevron-icon-color": theme.colors.primary.main,
		"f7-list-chevron-icon-font-size": theme.sizing.fontSizes.body,
		"f7-checkbox-inactive-color": theme.colors.primary.main,
		"f7-radio-inactive-color": theme.colors.primary.main,
		"f7-checkbox-size": theme.sizing.sizes.md,
		"f7-radio-size": theme.sizing.sizes.md,

		//Swipeout
		"f7-swipeout-delete-button-bg-color": theme.colors.danger.main,

		//Skeleton
		"f7-skeleton-color": theme.colors.base.disabled,

		//Preloader
		"f7-preloader-modal-bg-color": "white",
		"f7-preloader-color": theme.colors.primary.main,

		//Dialogs
		"f7-dialog-title-text-color": theme.colors.base.text,
		"f7-dialog-title-font-size": theme.sizing.fontSizes.bodyMax,
		"f7-dialog-title-font-weight": theme.sizing.fontWeights.medium,

		"f7-link-highlight-color": "rgba(0, 0, 0, 0.1)",

		//Notification
		"f7-notification-padding": scale(theme.sizing.sizes.lg, ScaleEnum.Half),
		"f7-notification-box-shadow": "none",
		"f7-notification-bg-color": "transparent",

		//Toast
		"f7-toast-font-size": theme.sizing.fontSizes.caption,
		"f7-toast-border-radius": theme.sizing.sizes.xl,
		"f7-toast-padding-vertical": theme.sizing.sizes.sm,
		"f7-toast-padding-horizontal": theme.sizing.sizes.md,

		//Navbar
		"f7-navbar-bg-color": theme.colors.primary.background,
		"f7-navbar-height": scale(theme.sizing.sizes.lg, ScaleEnum.Double),
		"f7-navbar-text-color": theme.colors.base.text,
		"f7-navbar-title-font-size": theme.sizing.fontSizes.body,
		"f7-navbar-link-color": theme.colors.primary.main,
		"f7-navbar-subtitle-font-size": theme.sizing.fontSizes.caption,
		"f7-navbar-shadow-image": "none",
		"f7-navbar-title-margin-left": "0",

		//Actions
		"f7-actions-button-text-color": theme.colors.base.text,
		"f7-actions-group-border-color": theme.colors.base.border,
		"f7-actions-button-height": theme.sizing.sizes.xxl,
		"f7-actions-button-height-landscape": theme.sizing.sizes.xxl,
		"f7-actions-button-padding": `0 ${theme.constants.gridPadding}`,

		//Photbrowser
		"f7-photobrowser-dark-bars-bg-color": "#000000" //toDO:      
	};
});

export interface AppProps extends Pick<F7App.Props, "routes"> {
	appParams: Framework7Params;
	CONFIG: any;
}

export const App: React.FunctionComponent<AppProps> = props => {
	useEffect(() => {
		applyTheme(theme);
	});

	return (
		<FWApp params={{ ...f7params, ...props.appParams }} routes={props.routes}>
			<View stackPages main>
				{props.children}
				{props.CONFIG.appMode !== "prod" && (
					<InstanceBar
						appMode={props.CONFIG.appMode}
						text={`${props.CONFIG.version}:${props.CONFIG.revision}  ${props.CONFIG.apiUrl}  ${props.CONFIG.appMode}`}
					/>
				)}
			</View>
		</FWApp>
	);
};
