import {
	css as aCss,
	CSSProperties,
	CSSPropertiesComplete,
	StyleDeclaration as AphStyleDeclaration,
	StyleDeclarationValue as AphStyleDeclarationValue,
	StyleSheet as aStyleSheet
} from "aphrodite";
import * as deepExtend from "deep-extend";

export type CSSProps = CSSPropertiesComplete;
export type CSSProp = CSSProperties;
export type CSSSizeValue = string | number;
export type StyleDeclaration<T> = AphStyleDeclaration<T>;
export type StyleDeclarationValue = AphStyleDeclarationValue;
export type StyleDeclarationExtend<T> = Partial<StyleDeclaration<T>> | undefined;
export type StyleDeclarationValues<T> = { [K in keyof T]: StyleDeclarationValue };

export const styleSheet = <T>(styles: StyleDeclaration<T>): StyleDeclarationValues<T> => aStyleSheet.create(styles);
export const styleSheetEx = <T>(
	styles: StyleDeclaration<T>,
	...args: StyleDeclarationExtend<T>[]
): { [K in keyof T]: StyleDeclarationValue } => aStyleSheet.create(deepExtend(styles, ...args) as StyleDeclaration<T>);

export const css = aCss;
