import * as React from "react";
import { Gallery, GalleryImage } from "quiz/components/gallery/Gallery";
import { FileRecord } from "mdtApiClient/mdtApiClient";
import { apiClient } from "quiz/QuizApp";

interface MDTFileGalleryOptions {
	fileRecords: FileRecord[];
}

export const MDTFileGallery: React.FC<MDTFileGalleryOptions> = ({ fileRecords }) => {
	const images = React.useMemo<GalleryImage[]>(
		() =>
			fileRecords.map(file => ({
				thumbUrl: apiClient.imageUrl(file, { height: 100, width: 100, mode: "fill" }),
				url: apiClient.imageUrl(file)
			})),
		[fileRecords]
	);
	return <Gallery images={images} />;
};
