import * as React from "react";
import { VisitReport } from "./VisitReport";
import { ContextInfoBlock, InfoBlockActionButton } from "quiz/components/infoBlock/contextInfoBlock/ContextInfoBlock";
import { t, quizAuthService, quizQstService, nav } from "quiz/QuizApp";
import { useVisitManager } from "../questionnaire/visitManagerHook";
import { VisitUploadData, VisitUploadStatus } from "quiz/services/QuizDraftService";
import { ColorTypes } from "theme/colors";
import { serverErrorIcon } from "quiz/components/infoScreenIcons/serverErrorIcon";
import { accessDeniedIcon } from "quiz/components/infoScreenIcons/accessDeniedIcon";
import { FeedbackTypeEnum } from "quiz/components/feedback/Feedback";
import { FullscreenInfo } from "quiz/components/infoBlock/fullscreenInfoBlock/FullscreenInfo";
import { f7 } from "framework7-react";

export interface UnsentReportPageContext {
	open: boolean;
	visitData: VisitUploadData;
	isCompleted: boolean;
}

type UnsentReportPageProps = {
	context?: UnsentReportPageContext;
};

export const UnsentReportPage: React.FC<UnsentReportPageProps> = props => {
	const { visitData } = props.context || {
		visitData: null
	};

	const visit = visitData ? visitData.visit : null;
	const { visitManager } = visit
		? useVisitManager(visit.questionnaire, visit.outlet, quizAuthService.state.user.employee, {
				initVisit: visit
		  })
		: { visitManager: null };

	let errorText = "";
	if (visitData) {
		if (visitData.status == VisitUploadStatus.error) errorText += t("Visit save failed!");
		const failedFiles = visitData.files.filter(f => f.status == VisitUploadStatus.error);
		if (failedFiles.length) {
			errorText += "\n";
			errorText += t("{failed} of {total} photos failed to upload.", {
				total: visitData.files.length,
				failed: failedFiles.length
			});
		}
	}

	const actionButtons: InfoBlockActionButton[] = [
		{
			title: "Retry",
			icon: ["far", "sync"],
			command: () => {
				nav.back();
				quizQstService.retrySend(visitData);
			}
		},
		{
			title: "Delete",
			icon: ["far", "trash-alt"],
			command: () => {
				nav.back();
				quizQstService.deleteUploadData(visitData);
			}
		}
	];

	return (
		<>
			{visit ? (
				<>
					<ContextInfoBlock
						icon={serverErrorIcon}
						title="Visit synchronization error"
						message={errorText}
						buttons={actionButtons}
						colorType={ColorTypes.danger}
						separateBlock
					/>
					<VisitReport visit={visit} visitManager={visitManager} isCompleted={true} />
				</>
			) : (
				<FullscreenInfo
					icon={accessDeniedIcon}
					title={t("Access error")}
					message={t("Access denied")}
					colorType={ColorTypes.danger}
					primaryButton={{
						title: t("Complain"),
						icon: ["far", "comment-alt-lines"],
						command: () => nav.toFeedback({ type: FeedbackTypeEnum.Problem, value: t("Access denied") })
					}}
					secondaryButton={{
						title: t("Refresh"),
						icon: ["far", "sync"],
						command: () => f7.views.main.router.refreshPage()
					}}
				/>
			)}
		</>
	);
}; //toDO: fetch data?
UnsentReportPage.displayName = "UnsentReportPage";
