import { ApiUrlHelper } from "mdtApiClient/helpers/urlHelper";
import { MDTApiClient } from "mdtApiClient/mdtApiClient";

export interface OAuthService {
	id: number;
	code: string;
	icon: string;
	title: string;
}

export class ApiClientOAuth {
	constructor(private apiClient: MDTApiClient) {}

	async list(): Promise<OAuthService[]> {
		return this.apiClient.request("oauth/list", void 0, { camel: true }).then(r => r.json());
	}

	authenticate(code: string) {
		location.href = `${this.apiClient.apiUrl}/oauth/${code}${ApiUrlHelper.getQueryString({
			referrer: location.href
		})}`;
	}
}
