import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getQuestionReadonlyControlStyles = (theme: Theme) => {
	return {
		wrap: {
			background: theme.colors.primary.background
		} as CSSProp,
		gallery: {
			padding: `${theme.sizing.sizes.md} 0`,
			borderBottom: `1px dashed ${theme.colors.base.border}`,
			":last-child": {
				borderBottom: "none"
			}
		} as CSSProp
	};
};
