import cloneDeep from "lodash.clonedeep";
import {
	IQuestionnaire,
	Visit,
	IQuestion,
	VisitAnswer,
	AnswerType,
	VisitAnswerItem,
	QuestionAttachPhoto
} from "../models";
import { quizDraftService } from "quiz/QuizApp";

export class QuizCompareQstService {
	getActualVisit(visit: Visit, questionnaire: IQuestionnaire): Visit | null {
		if (!visit) return null;
		const newVisit = cloneDeep<Visit>(visit);
		newVisit.questionnaire = questionnaire;
		const comparedNewVisit = this.compareAnswers(newVisit, questionnaire);
		quizDraftService.set(comparedNewVisit);
		return comparedNewVisit;
	}

	private compareAnswers(visit: Visit, questionnaire: IQuestionnaire): Visit {
		const answers: VisitAnswer[] = [];
		questionnaire.questions.forEach(question => {
			const answer = visit.answers.find(answer => answer.questionId === question.id);
			if (!answer) {
				return;
			}
			let newAnswer = answer;
			newAnswer.question = question;
			if (answer.answerType !== question.answerType) {
				newAnswer = {
					questionId: question.id,
					question,
					questionnaireId: questionnaire.id,
					answerType: question.answerType,
					items:
						question.answerType === AnswerType.Boolean && question.items.length
							? question.items.map(item => ({ id: item.id, value: false, answer: item.name }))
							: []
				};
				if (answer.files) {
					newAnswer.files = answer.files;
				}
				if (answer.comment) {
					newAnswer.comment = answer.comment;
				}
			}
			newAnswer = this.compareAnswerProperties(question, newAnswer);
			if (question.items) {
				newAnswer = this.compareAnswersItems(question, newAnswer);
			}
			answers.push(newAnswer);
		});
		visit.answers = answers.sort((a1, a2) => a1.question.sortOrder - a2.question.sortOrder);
		return visit;
	}

	private compareAnswerProperties(question: IQuestion, answer: VisitAnswer): VisitAnswer {
		const newAnswer = answer;
		if (question.questionAttachPhoto === QuestionAttachPhoto.None && newAnswer.files) {
			delete newAnswer.files;
		}
		if (!question.flagComment && newAnswer.comment) {
			delete newAnswer.comment;
		}
		return newAnswer;
	}

	private compareAnswersItems(question: IQuestion, answer: VisitAnswer): VisitAnswer {
		const newAnswer: VisitAnswer = answer;
		const answerItems: VisitAnswerItem[] = [];
		question.items &&
			question.items.forEach(qItem => {
				const aItem = answer.items.find(aItem => aItem.id === qItem.id);
				if (!aItem) {
					if (question.answerType === AnswerType.Boolean) {
						answerItems.push({
							id: qItem.id,
							value: false,
							answer: qItem.name
						});
					}
					return;
				}
				answerItems.push(aItem);
			});
		return newAnswer;
	}
}
