import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getQuizSkeletonStyles = (theme: Theme) => {
	return {
		skeleton: {
			padding: `${theme.sizing.sizes.md} 0`
		} as CSSProp,
		skeletonBorder: {
			borderBottom: `1px solid ${theme.colors.base.disabled}`,
			":last-child": {
				borderBottom: "none"
			}
		} as CSSProp
	};
};
