import { CSSProps } from "wraps/styleCreators";
import { DeepPartial } from "../types";
import { ThemeDefaults } from "./themeDefaults";
import * as deepExtend from "deep-extend";

type CSSStyleBoxShadow = CSSProps["boxShadow"];

type CSSStyleBoxShadowSizes = {
	md: CSSStyleBoxShadow;
	lg: CSSStyleBoxShadow;
	xl: CSSStyleBoxShadow;
	xxl: CSSStyleBoxShadow;
};

export interface ThemeShadows {
	normal: CSSStyleBoxShadow;
	down: CSSStyleBoxShadowSizes;
	up: CSSStyleBoxShadowSizes;
	right: CSSStyleBoxShadowSizes;
	left: CSSStyleBoxShadowSizes;
}

export function createThemeShadows(options: DeepPartial<ThemeShadows> = {}): ThemeShadows {
	let normal = options.normal || ThemeDefaults.shadows.normal || "none";
	return deepExtend(
		{
			normal,
			down: {
				md: normal,
				lg: normal,
				xl: normal,
				xxl: normal
			},
			up: {
				md: normal,
				lg: normal,
				xl: normal,
				xxl: normal
			},
			right: {
				md: normal,
				lg: normal,
				xl: normal,
				xxl: normal
			},
			left: {
				md: normal,
				lg: normal,
				xl: normal,
				xxl: normal
			}
		},
		ThemeDefaults.shadows,
		options
	);
}
