import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getQuestionnaireNavbarStyles = (theme: Theme, noShadow: boolean) => {
	return {
		navbar: {
			boxShadow: noShadow ? "none" : theme.shadows.down.md,
			height: "calc(60px + var(--f7-safe-area-top))" //toDO
		} as CSSProp
	};
};
