import React from "react";
import { IconProp, Icon } from "quiz/components/icon/Icon";
import { ActionsButton } from "quiz/components/actions/Actions";
import { Text } from "quiz/components/text/Text";
import { ThemeContext } from "themeContext/ThemeContext";
import { useStyles } from "hooks/useStyles";
import { getActionSheetItemStyles } from "./actionSheetItemStyles";
import { css, StyleDeclarationValue } from "wraps/styleCreators";
import { ColorTypes } from "theme/colors";

export type ActionSheetItemProps = {
	icon?: IconProp;
	iconStyles?: StyleDeclarationValue;
	text: string;
	renderIcon?: () => React.ReactElement;
	colorType?: ColorTypes;
	onClick?: () => void;
};

export const ActionSheetItem: React.FC<ActionSheetItemProps> = props => {
	const theme = React.useContext(ThemeContext);
	const styles = useStyles(getActionSheetItemStyles);

	return (
		<ActionsButton onClick={props.onClick} className={css(styles.actionButton)}>
			<div className={css(styles.wrap)}>
				<div className={css(styles.iconWrap)}>
					{props.icon && !props.renderIcon && (
						<Icon
							icon={props.icon}
							color={theme.colors[props.colorType || ColorTypes.primary].main}
							styles={props.iconStyles}
						/>
					)}
					{props.renderIcon && !props.icon && props.renderIcon()}
				</div>
				<Text colorType={props.colorType}>{props.text}</Text>
			</div>
		</ActionsButton>
	);
};
ActionSheetItem.displayName = "ActionSheetItem";
