import { Action } from "quiz/quizAppReducer";
import { OutletFullData } from "quiz/services/models";
import { Coords } from "hooks/usePosition";
import { quizDataService } from "quiz/QuizApp";
import { outletWithDistanceCacheCode } from "./ListNearOutlets";

export enum listNearOutletsActions {
	UPDATE_COORDS = "UPDATE_COORDS",
	IS_FETCHING = "ALLOW_FETCH",
	LOADNIG_ERROR = "LOADING_ERROR",
	LOADING_SUCCESS = "LOADING_SUCCESS",
	REFRESHING = "REFRESHING"
}

export interface NearOutletsState {
	outlets: OutletFullData[];
	coords: Coords;
	lastUpdateDate: Date;
	errors: any[];
	isFetching: boolean;
	isRefreshing: boolean;
}

export const listNearOutletsReducer = (state, action: Action): NearOutletsState => {
	const payload = action?.payload;

	switch (action.type) {
		case listNearOutletsActions.UPDATE_COORDS:
			return {
				...state,
				coords: payload?.coords
			};
		case listNearOutletsActions.IS_FETCHING:
			return {
				...state,
				isFetching: true
			};
		case listNearOutletsActions.LOADING_SUCCESS:
			return {
				...state,
				outlets: payload?.outlets,
				lastUpdateDate: payload?.lastUpdateDate,
				isFetching: false,
				errors: []
			};
		case listNearOutletsActions.LOADNIG_ERROR:
			return {
				...state,
				outlets: [],
				isFetching: false,
				errors: [payload?.error]
			};
		case listNearOutletsActions.REFRESHING:
			return {
				...state,
				coords: null,
				outlets: null,
				errors: [],
				isFetching: true
			};
		default:
			return state;
	}
};
