import { IconWrap } from "quiz/components/iconWrap/IconWrap";
import { Link } from "quiz/components/link/Link";
import { nav } from "quiz/QuizApp";
import * as React from "react";
import { ThemeContext } from "themeContext/ThemeContext";

const searchCb = () => nav.toSearchPage();

export const SearchLink: React.FC = () => {
	const theme = React.useContext(ThemeContext);
	return (
		<Link onClick={searchCb}>
			<IconWrap icon={["far", "search"]} alignment="center-right" iconSize={theme.sizing.sizes.md} />
		</Link>
	);
};

SearchLink.displayName = "SearchLink";
