import React from "react";
import { QstListItemProps, QuestionListItem } from "quiz/components/questionListView/QuestionListItem";
import { Text } from "quiz/components/text/Text";
import { useStyles } from "hooks/useStyles";
import { getGroupedAnswerHeaderStyles } from "./groupedAnswerHeaderStyles";
import { css } from "wraps/styleCreators";

type GroupedAnswerHeaderProps = {
	header: string;
	counter?: string;
} & QstListItemProps;

export const GroupedAnswerHeader: React.FC<GroupedAnswerHeaderProps> = React.memo(props => {
	const { header, counter, ...listItemProps } = props;
	const styles = useStyles(getGroupedAnswerHeaderStyles);

	return (
		<QuestionListItem {...listItemProps} groups>
			<div className={css(styles.groupWrap)}>
				<Text styles={styles.groupTitle} bold>
					{header}
				</Text>
				{counter && (
					<Text muted styles={styles.countInGroup}>
						{counter}
					</Text>
				)}
			</div>
		</QuestionListItem>
	);
});

GroupedAnswerHeader.displayName = "GroupAnswerHeader";
