import * as React from "react";
import { FieldControlLayout, FieldControlLayoutProps } from "../fieldControlLayout/FieldControlLayout";
import { css } from "wraps/styleCreators";
import { filterProps } from "utils/helpers";
import { useStyles } from "hooks/useStyles";
import { inheritFieldControlProps } from "../input/InputControl";
import { getSelectStyles } from "./selectControlStyles";

type SelectHTMLProps = React.DetailedHTMLProps<React.SelectHTMLAttributes<HTMLInputElement>, HTMLInputElement>;

interface SelectControlProps
	extends Pick<FieldControlLayoutProps, typeof inheritFieldControlProps[number]>,
		Omit<SelectHTMLProps, "onChange" | "value"> {}

export const SelectControl: React.FC<SelectControlProps> = props => {
	const s = useStyles(getSelectStyles);
	const fieldControlLayoutProps = filterProps(props, inheritFieldControlProps);
	const htmlProps = filterProps(props, inheritFieldControlProps, false);

	return (
		<FieldControlLayout
			{...fieldControlLayoutProps}
			value={props.value}
			customControl={(controlProps, styles) => (
				<select
					{...controlProps}
					{...htmlProps}
					className={css(styles, s.input)}
					value={controlProps.value != null ? controlProps.value : ""}
				>
					{props.children}
				</select>
			)}
		/>
	);
};
