import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { QuestionReadonlyItemProps } from "./QuestionReadonlyItem";

export const getQuestionReadonlyItemStyles = (theme: Theme, props: QuestionReadonlyItemProps) => {
	const [iconColor, textColor] = props.errors.length
		? [theme.colors.danger.main, theme.colors.danger.main]
		: props.text
		? [theme.colors.primary.main, theme.colors.base.texts.accent]
		: [theme.colors.base.texts.muted, theme.colors.base.texts.muted];

	return {
		questionControlItem: {
			padding: `0 ${theme.constants.gridPadding}`,
			display: "flex",
			alignItems: "center",
			justifyContent: "space-between",
			borderBottom: `1px dashed ${theme.colors.base.border}`,
			":last-child": {
				borderBottom: "none"
			}
		} as CSSProp,
		textWrap: {
			display: "flex",
			alignItems: "center"
		} as CSSProp,
		iconWrap: {
			display: "flex",
			height: theme.sizing.sizes.lg,
			width: theme.sizing.sizes.lg,
			alignItems: "center",
			justifyContent: "center",
			marginRight: theme.sizing.sizes.md
		} as CSSProp,
		iconColor: { color: iconColor } as CSSProp,
		textColor: { color: textColor } as CSSProp,
		text: {
			paddingBottom: theme.sizing.sizes.md,
			paddingTop: theme.sizing.sizes.md
		} as CSSProp
	};
};
