import * as React from "react";
import { css, StyleDeclarationValue } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";
import { getQuizSkeletonStyles } from "./quizSkeletonStyles";

export type QuizSkeletonProps = {
	styles?: StyleDeclarationValue;
};

export const QuizSkeleton: React.FC<QuizSkeletonProps> = props => {
	const styles = useStyles(getQuizSkeletonStyles);
	return <div className={css(styles.skeleton || props.styles, styles.skeletonBorder)}>{props.children}</div>;
};

QuizSkeleton.displayName = "QuizSkeleton";
