import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const getQuestionStyles = (theme: Theme) => {
	return {
		questionWrap: {
			paddingTop: theme.sizing.sizes.lg,
			paddingBottom: scale(theme.sizing.sizes.xl, ScaleEnum.Triple)
		} as CSSProp,
		divider: {
			borderTop: `1px dashed ${theme.colors.base.border}`
		} as CSSProp
	};
};
