import { MDTRecordId } from "mdtApiClient/mdtApiClient";
import {
	outletCacheStore,
	OutletCache,
	QuizDB,
	draftFilesStore,
	VisitUploadData,
	visitUploadStore,
	draftStoreName
} from "./QuizDraftService";
import { cacheIsValid } from "utils/helpers";
import { DatabaseExtension } from "./extension/db/dbExtension";
import { DBService } from "./extension/db/dbService";
import { IDBPDatabase } from "idb";
import { Visit, VisitAnswer } from "./models";

export class CleaningService {
	private db: Promise<DBService> | Promise<IDBPDatabase<QuizDB>>;
	constructor(db: DatabaseExtension) {
		this.db = db.getDB();
	}

	async removeOutletCache(outletId: MDTRecordId) {
		return await (await this.db).delete(outletCacheStore, outletId);
	}

	async removeAllInvalidOutletCache() {
		const oldCache = (await this.getAllOutletCache()).filter(c => !cacheIsValid(c.dateExpired));
		await Promise.all(oldCache.map(c => this.removeOutletCache(c.outlet.id.toString())));
	}

	private async getAllOutletCache() {
		return (await (await this.db).getAll(outletCacheStore)) as Promise<OutletCache[]>;
	}

	async removeInvalidFiles() {
		const db = await this.db;
		const [drafts, visits] = await Promise.all([this.getAllDrafts(), this.getAllVisits()]);

		const draftFilesId = drafts.reduce((acc, draft) => {
			const filesId = this.getFilesId(draft.answers);
			return [...acc, ...filesId];
		}, []);

		const visitFilesId = visits.reduce((acc, visit) => {
			const filesId = this.getFilesId(visit.visit.answers);
			return [...acc, ...filesId];
		}, []);

		const existingFilesId = new Set([...draftFilesId, ...visitFilesId]);
		const allFilesId = await db.getAllKeys(draftFilesStore);
		const filesForDelete = allFilesId.filter(id => !existingFilesId.has(id));

		await Promise.all(filesForDelete.map(fileId => db.delete(draftFilesStore, fileId)));
		return;
	}

	private getFilesId(answers: VisitAnswer[]): number[] {
		const filesId = answers.reduce((acc, answer) => {
			if (answer.files) {
				const ids = answer.files.map(file => file.draftFileId);
				return [...acc, ...ids];
			}
			return acc;
		}, []);
		return filesId;
	}

	private async getAllDrafts() {
		return (await (await this.db).getAll(draftStoreName)) as Promise<Visit[]>;
	}

	private async getAllVisits() {
		return (await (await this.db).getAll(visitUploadStore)) as Promise<VisitUploadData[]>;
	}
}
