import { SkeletonItemStyles, skeletonClass } from "./SkeletonItem";
import React from "react";
import { useStyles } from "hooks/useStyles";
import { CSSPropertiesComplete } from "aphrodite";
import { getSkeletonRowStyles } from "./SkeletonItemStyles";
import { SkeletonBlock } from "framework7-react";
import { css } from "wraps/styleCreators";

type SkeletonRowType = "title" | "text";

export type SkeletonRowProps = {
	type: SkeletonRowType;
	width: CSSPropertiesComplete["width"];
} & { [key in keyof SkeletonItemStyles]: SkeletonItemStyles[key] };

export const SkeletonRow: React.FC<SkeletonRowProps> = props => {
	const styles = useStyles(getSkeletonRowStyles, props);
	return <SkeletonBlock className={`${skeletonClass} ${css(styles.row)}`} />;
};

SkeletonRow.displayName = "SkeletonRow";
