import { VisitedOutlets, ViewVisitInfo } from "quiz/components/visitedOutets/VisitedOutlets";
import { nav, t, quizDataService, quizAuthService } from "quiz/QuizApp";
import * as React from "react";
import { DatePickerButton } from "quiz/components/datepicker/DatePicker";
import { useCancellablePromise } from "hooks/useCancellablePromise";
import { ExtendableNavbarPage } from "quiz/components/extendableNavbarPage/ExtendableNavbarPage";
import { QuizNavBar } from "quiz/components/quizNavBar/QuizNavBar";
import { ExtendableNavbarBlock } from "quiz/components/extendableNavbarPage/ExtendableNavbarBlock";
import { QuizError } from "quiz/components/errors/QuizError";

type SupervisorMainPageState = {
	date?: Date;
	visits?: ViewVisitInfo[];
	requestError?: any;
};

export const SupervisorMainPage: React.FC = () => {
	const [state, setState] = React.useState<SupervisorMainPageState>({ date: null, visits: null, requestError: null });
	useCancellablePromise(
		a =>
			state.date
				? quizDataService.getVisitData(
						{ userId: quizAuthService.state.user.id, date: state.date },
						{ signal: a }
				  )
				: quizDataService.getVisitData({ userId: quizAuthService.state.user.id }, { signal: a }),

		{
			onSuccess: data => setState(prev => ({ ...prev, visits: data, requestError: null })),
			onError: err => setState(prev => ({ ...prev, requestError: err })),
			deps: [state?.date]
		}
	);

	return (
		<ExtendableNavbarPage
			wrap
			hasError={state?.requestError}
			navbarContent={() => <QuizNavBar title="Visited outlets" noShadow />}
			extendableContent={() => (
				<ExtendableNavbarBlock>
					<DatePickerButton
						resetButton={!!state?.date}
						resetButtonTitle={t("Reset")}
						onResetButtonClick={() => setState({ requestError: null, date: null, visits: null })}
						onDateClick={d => setState({ requestError: null, date: d, visits: null })}
						title={t("Choose")}
					/>
				</ExtendableNavbarBlock>
			)}
		>
			{!state?.requestError ? (
				<VisitedOutlets
					onOutletClick={o => nav.toVisit(o)}
					visits={state.visits}
					requestError={state?.requestError}
				/>
			) : (
				<QuizError error={state?.requestError} fullScreen />
			)}
		</ExtendableNavbarPage>
	);
};
SupervisorMainPage.displayName = "SupervisorMainPage";
