import { Filter } from "mdtApiClient/helpers/filter";
import { MDTApiClient, MDTRecordId, PreparedFetchResult } from "mdtApiClient/mdtApiClient";
import { TOutletVisits } from "quiz/components/outletVisits/OutletVisits";
import { TQstList } from "quiz/components/qstList/QstList";
import { t, quizQstService, quizDraftService, quizAuthService } from "quiz/QuizApp";
import { toLocalISOString, appIsOnline, formatDateTime, removeAllSymbols } from "utils/helpers";
import { APILegalEntity, APIOutlet, APIOutletChain, QuizQstService } from "./data/QuizQstService";
import { LegalEntity, Outlet, OutletChain, OutletFullData, IQuestionnaire, Visit } from "./models";
import { ViewVisitInfo } from "quiz/components/visitedOutets/VisitedOutlets";
import { Coords } from "hooks/usePosition";
import { outletWithDistanceCacheCode } from "quiz/components/listNearOutlets/ListNearOutlets";
import { OutletCache } from "./QuizDraftService";

type QstData = {
	q: IQuestionnaire;
	o: OutletFullData;
	v?: Visit;
	online?: boolean;
};

interface LatLng {
	latitude: string;
	longitude: string;
}

class NotFoundError extends Error {
	constructor() {
		super();
		this.message = t("Outlet not found or you do not have access");
	}
}

interface GetVisitDataParams {
	userId: MDTRecordId;
	date?: Date;
}

export class QuizDataService {
	constructor(private apiClient: MDTApiClient) {}

	//   
	async getVisitData(params: GetVisitDataParams, requestOptions?: RequestInit) {
		const { userId, date } = params;

		const d = date && toLocalISOString(date);
		const from = d?.replace(/T.*/, "T00:00:00.0000");
		const to = d?.replace(/T.*/, "T23:59:59.0000");
		const sendDateFilter = date ? [Filter.ge("DateSubmitLocal", from), Filter.le("DateSubmitLocal", to)] : [];

		return this.apiClient
			.fetch(
				{
					sorting: [{ path: "DateEnd", asc: false }],
					select: [
						"*",
						"ID_Outlet/*",
						"items/id_questionnaire/*",
						"ID_Outlet/Positions/*",
						"ID_Outlet/Positions/ID_Employee/*",
						"ID_Outlet/ID_OutletChain/*",
						"ID_Outlet/ID_OutletChain/NameShort",
						"ID_Outlet/ID_OutletChain/ID_Parent/NameShort",
						"ID_Outlet/ID_OutletChain/ID_Parent/*",
						"ID_Outlet/ID_OutletType/*",
						"ID_Outlet/ID_OutletFormat/*",
						"ID_em_Employee/*"
					],
					top: date ? null : 20,
					filter: Filter.and(Filter.eq("MDT_ID_PrincipalCreatedBy", userId), ...sendDateFilter)
				},
				"qst.Visit",
				{ camel: true },
				requestOptions
			)
			.then(d => {
				const data = d.records;
				return data.map(record => {
					return {
						...QuizDataService.outletApiToUi(record.id_Outlet$),
						lastVisit: record.dateEnd && {
							...record.id_VisitLast$,
							dateBegin: new Date(record.dateBegin),
							dateEnd: new Date(record.dateEnd),
							dateSubmitLocal: new Date(record.dateSubmitLocal)
						},
						questionnaireName: record.items && record.items[0].id_Questionnaire$.name, //toDO: typing
						idVisit: record.items && record.items[0].id_Visit
					} as ViewVisitInfo;
				});
			});
	}

	static outletApiToUi(apiOutlet: APIOutlet): Outlet {
		if (!apiOutlet) return void 0;
		return {
			id: apiOutlet.id,
			code: apiOutlet.code,
			iconName: apiOutlet.id_OutletType$?.icon,
			name: apiOutlet.name,
			address: apiOutlet.address,
			phone: apiOutlet.phone,
			type: apiOutlet.id_OutletType$?.name,
			format: apiOutlet.id_OutletFormat$?.name,
			legalEntity: this.legalEntityApiToUi(apiOutlet.id_LegalEntity$),
			outletChain: this.outletChainApiToUi(apiOutlet.id_OutletChain$),
			employees: apiOutlet.positions && this.positionsToUi(apiOutlet.positions)
		};
	}

	static legalEntityApiToUi(apiLegalEntity: APILegalEntity): LegalEntity {
		if (!apiLegalEntity) return void 0;
		return {
			id: apiLegalEntity.id,
			name: apiLegalEntity.legalName,
			inn: apiLegalEntity.inn
		};
	}

	static outletChainApiToUi(outletChain: APIOutletChain): OutletChain {
		if (!outletChain) return void 0;
		return {
			id: outletChain.id,
			name: outletChain.name,
			nameShort: outletChain.nameShort,
			type: outletChain.id_OutletChainType$?.name,
			parent: outletChain.id_Parent$
		};
	}

	static positionsToUi(positions) {
		return positions
			.map(position => {
				return {
					name: position.id_Employee$?.name || "",
					positionName: position.id_BindingFunction$?.name?.toLowerCase() || "",
					postLevel: position.postLevel || 0
				};
			})
			.sort((posA, posB) => posA.postLevel - posB.postLevel);
	}

	//   
	async getRouteOutlets(date: Date, coords: LatLng, requestOptions?: RequestInit) {
		return this.apiClient
			.fetch(
				{
					select: [
						"ID_Outlet",
						"Distance",
						"FlagQuestionnairesEmpty",
						"FlagVisited",
						"ID_Outlet/*",
						"ID_Outlet/Positions/*",
						"ID_Outlet/ID_OutletChain/NameShort",
						"ID_Outlet/ID_OutletChain/ID_Parent/NameShort",
						"ID_Outlet/ID_OutletType/*",
						"ID_VisitLast/DateBegin",
						"ID_VisitLast/DateEnd",
						"ID_VisitLast/DateSubmitLocal",
						"ID_VisitLast/ID_em_Employee/Name"
					],
					params: `["${toLocalISOString(date)}", ${coords.latitude || "null"}, ${coords.longitude ||
						"null"}]`,
					sorting: [{ path: "SortOrder" }]
				},
				"qst.udf_RouteOutlets",
				{ camel: true },
				requestOptions
			)
			.then(d => {
				const data = d.records;
				return data.map(record => {
					return {
						...QuizDataService.outletApiToUi(record.id_Outlet$),
						distance: record.distance && Math.round(record.distance),
						flagQuestionnairesEmpty: record.flagQuestionnairesEmpty,
						employee: record.id_VisitLast$ && {
							id: record.id_VisitLast$.id_em_Employee,
							...record.id_VisitLast$.id_em_Employee$
						},
						lastVisit: record.id_VisitLast$ && {
							employee: record.id_VisitLast$.id_em_Employee$,
							dateBegin: new Date(record.id_VisitLast$.dateBegin),
							dateEnd: new Date(record.id_VisitLast$.dateEnd),
							dateSubmitLocal: new Date(record.id_VisitLast$.dateSubmitLocal)
						},
						flagVisited: record.flagVisited
					} as OutletFullData;
				});
			});
	}

	private CACHE_LIFETIME = 2 * 60 * 60 * 1000; // 2h in ms
	private _cache = {};

	private addCache(code, data) {
		this._cache[code] = {
			time: new Date().getTime(),
			data
		};
	}

	public clearCache(code: string) {
		delete this._cache[code];
	}

	public getCache(code: string) {
		const curtime = new Date().getTime();
		if (this._cache[code] && curtime - this._cache[code].time < this.CACHE_LIFETIME) return this._cache[code].data;
		return;
	}

	//  
	prepareOutletWithDistance(records: any) {
		return records.map(record => {
			return {
				...QuizDataService.outletApiToUi(record.id_Outlet$),
				distance: Math.ceil(record.distance),
				flagQuestionnairesEmpty: record.flagQuestionnairesEmpty,
				employee: record.id_Outlet$?.positions?.map(p => p.id_Employee$).filter(x => x),
				lastVisit: record.id_VisitLast$ && {
					employee: record.id_VisitLast$.id_em_Employee$,
					dateBegin: new Date(record.id_VisitLast$.dateBegin),
					dateEnd: new Date(record.id_VisitLast$.dateEnd),
					dateSubmitLocal: new Date(record.id_VisitLast$.dateSubmitLocal)
				}
			} as OutletFullData;
		});
	}

	async getOutletWithDistance(coords: LatLng, requestOptions?: RequestInit, clearCache?: boolean) {
		const code = outletWithDistanceCacheCode;
		if (clearCache) this.clearCache(code);
		const data = this.getCache(code);

		const fetch = data
			? new Promise<PreparedFetchResult>(resolve => resolve(data))
			: this.apiClient.fetch(
					{
						select: [
							"Distance",
							"FlagQuestionnairesEmpty",
							"ID_Outlet/ID",
							"ID_Outlet/Code",
							"ID_Outlet/Name",
							"ID_Outlet/ID_OutletChain/NameShort",
							"ID_Outlet/ID_OutletChain/ID_Parent/NameShort",
							"ID_Outlet/ID_OutletFormat/Name",
							"ID_Outlet/Address",
							"ID_Outlet/Positions/ID_Employee/*",
							"ID_Outlet/Positions/PostLevel",
							"ID_Outlet/ID_OutletType/*",
							"ID_Outlet/Positions/ID_BindingFunction/*",
							"ID_VisitLast/DateBegin",
							"ID_VisitLast/DateEnd",
							"ID_VisitLast/DateSubmitLocal",
							"ID_VisitLast/ID_em_Employee/Name"
						],
						params: `[${coords.latitude}, ${coords.longitude}, 1000]`,
						sorting: [{ path: "Distance", asc: true }]
					},
					"qst.udf_OutletsWithDistance",
					{ camel: true },
					requestOptions
			  );
		return fetch.then(d => {
			this.addCache(code, d);

			return this.prepareOutletWithDistance(d.records);
		});
	}

	//    
	async getOutletInfo(id: MDTRecordId, requestOptions?: RequestInit): Promise<OutletFullData> {
		return (
			(await quizDraftService.getOutletCache(id).then(cache => cache?.outlet)) ||
			this.apiClient
				.fetch(
					{
						select: [
							"*",
							"Positions/*",
							"ID_OutletChain/*",
							"ID_OutletChain/NameShort",
							"ID_OutletChain/ID_Parent/NameShort",
							"ID_OutletType/*",
							"ID_OutletFormat/*",
							"ID_LegalEntity/*"
						],
						filter: Filter.eq("ID", id),
						params: { ID_Language_ext: quizAuthService.state.user.languageId }
					},
					"qst.CustomerCache",
					{ camel: true },
					requestOptions
				)
				.then(data => {
					const record = data.records[0];
					if (!record) throw new NotFoundError();
					return QuizDataService.outletApiToUi(record) as OutletFullData;
				})
		);
	}

	async getDataForOutletCaching(outletId: MDTRecordId, requestOptions?: RequestInit): Promise<OutletCache> {
		return this.apiClient
			.fetch(
				[
					{
						select: [
							"*",
							"Positions/*",
							"ID_OutletChain/*",
							"ID_OutletChain/NameShort",
							"ID_OutletChain/ID_Parent/NameShort",
							"ID_OutletType/*",
							"ID_OutletFormat/*",
							"ID_LegalEntity/*"
						],
						table: "qst.CustomerCache",
						filter: Filter.eq("ID", outletId),
						params: { ID_Language_ext: quizAuthService.state.user.languageId }
					},
					{
						select: ["*", "id/*"],
						table: "qst.udf_Questionnaires",
						params: `[${outletId}]`
					}
				],
				"",
				{ camel: true },
				requestOptions
			)
			.then(async ([outletData, qstListData]) => {
				const outletRecord = outletData.records[0];
				if (!outletRecord) throw new NotFoundError();

				const outlet = QuizDataService.outletApiToUi(outletRecord);
				const qstList = qstListData.records.map(
					q =>
						({
							id: q.id,
							name: q.iD$.name,
							description: q.iD$.description,
							flagDisable: q.iD$.flagDisable,
							lastVisit: q.lastVisitDate && formatDateTime(new Date(q.lastVisitDate))
						} as TQstList)
				);
				const qsts = await quizQstService.getQuestionnaires(
					qstList.map(q => q.id),
					outletId,
					requestOptions
				);

				const dateBegin = new Date();
				const dateExpired = new Date(dateBegin);

				dateExpired.setHours(dateExpired.getHours() + 1);
				return { outlet: outlet, qstList: qstList, qsts: qsts, dateExpired: dateExpired };
			});
	}

	//      
	async getOutletQuestionnaires(id: MDTRecordId, requestOptions?: RequestInit) {
		return (
			(await quizDraftService.getOutletCache(id).then(cache => cache?.qstList)) ||
			this.apiClient
				.fetch(
					{
						select: ["*", "id/*"],
						params: `[${id}]`,
						table: "qst.udf_Questionnaires"
					},
					null,
					{ camel: true },
					requestOptions
				)
				.then(res => {
					return res.records.map(
						o =>
							({
								id: o.id,
								name: o.iD$.name,
								description: o.iD$.description,
								flagDisable: o.iD$.flagDisable,
								lastVisit: o.lastVisitDate && formatDateTime(new Date(o.lastVisitDate))
							} as TQstList)
					);
				})
		);
	}

	//   
	async getOutletVisits(id: MDTRecordId, skip: number, top: number, requestOptions?: RequestInit) {
		return this.apiClient
			.fetch(
				{
					select: ["*"],
					table: "qst.Visit",
					filter: Filter.eq("ID_Outlet", id),
					skip,
					sorting: [{ path: "DateEnd", asc: false }],
					top
				},
				null,
				{ camel: true },
				requestOptions
			)
			.then(res =>
				res.records.map(
					v =>
						({
							id: v.id,
							name: v.id_Employee$ && v.id_Employee$.name,
							dateEnd: new Date(v.dateEnd)
						} as TOutletVisits)
				)
			);
	}

	//   
	async findOutlets(
		value: string,
		coords: Coords,
		skip: number,
		top: number,
		onlyEmployeeOutlets: boolean,
		requestOptions?: RequestInit
	) {
		const sendValue = value ? removeAllSymbols(value) : "";

		return sendValue !== "" || onlyEmployeeOutlets
			? this.apiClient
					.fetch(
						{
							select: [
								"ID/ID",
								"ID/Name",
								"ID/Code",
								"ID/Address",
								"ID/Positions/ID_Employee/*",
								"ID/Positions/ID_BindingFunction/Name",
								"ID/ID_OutletChain/*",
								"ID/ID_OutletChain/NameShort",
								"ID/ID_OutletChain/ID_Parent/NameShort",
								"ID/ID_OutletFormat/*",
								"ID/ID_OutletType/*",
								"Distance",
								"ID_VisitLast/ID_em_Employee/Name",
								"ID_VisitLast/DateBegin",
								"ID_VisitLast/DateEnd",
								"ID_VisitLast/DateSubmitLocal"
							],
							table: "qst.udf_SearchCustomerWithDistance",
							skip,
							params: `["${sendValue}", ${coords.latitude}, ${coords.longitude}, ${top}, ${onlyEmployeeOutlets}]`,
							sorting: [{ path: "SearchRank", asc: false }]
						},
						null,
						{ camel: true },
						requestOptions
					)
					.then(res =>
						res.records.map(record => {
							return {
								...QuizDataService.outletApiToUi(record.iD$),
								flagQuestionnairesEmpty: record.flagQuestionnairesEmpty,
								employee: record.iD$.positions && record.iD$.positions.map(pos => pos.id_Employee$),
								distance: record.distance && Math.ceil(record.distance),
								lastVisit: record.id_VisitLast$ && {
									employee: record.id_VisitLast$.id_em_Employee$,
									dateBegin: new Date(record.id_VisitLast$.dateBegin),
									dateEnd: new Date(record.id_VisitLast$.dateEnd),
									dateSubmitLocal: new Date(record.id_VisitLast$.dateSubmitLocal)
								}
							} as OutletFullData;
						})
					)
			: [];
	}

	//    ,    
	// todo -> move to qstOfflineService
	async getQuestionnaireData(outletId: MDTRecordId, qstId: MDTRecordId, requestOptions?: RequestInit) {
		let outlet: OutletFullData = null;
		let qst: IQuestionnaire = null;
		const visitDraft = await quizDraftService.get(outletId, qstId);

		if (appIsOnline()) {
			[outlet, qst] = await Promise.all([
				this.getOutletInfo(outletId, requestOptions),
				quizQstService.getQuestionnaire(qstId, outletId, requestOptions)
			]);

			return { q: qst, o: outlet, v: visitDraft, online: true };
		} else {
			[outlet, qst] = visitDraft
				? [visitDraft.outlet, visitDraft.questionnaire]
				: await Promise.all([
						this.getOutletInfo(outletId, requestOptions),
						quizQstService.getQuestionnaire(qstId, outletId, requestOptions)
				  ]);
			return { q: qst, o: outlet, v: visitDraft, online: false };
		}
	}
}
