import { Filter } from "mdtApiClient/helpers/filter";
import { MDTApiClient, MDTRecordId } from "mdtApiClient/mdtApiClient";
import { ApiClientUser } from "mdtApiClient/modules/auth/auth";
import { OAuthService } from "mdtApiClient/modules/auth/oauth";
import { setPasswordData, changePasswordData } from "mdtApiClient/modules/auth/password";
import { Employee, UserSettings } from "./models";
import { quizDataService } from "quiz/QuizApp";
import { quizUserService } from "quiz/QuizApp";

export interface QuizUser extends ApiClientUser {
	flagAdmin?: boolean;
	flagGetLog?: boolean;
	flagRouteExists?: boolean;
	employee?: Employee;
	isSuperVisor?: boolean;
	userSettings?: UserSettings;
}

interface QuizAuthServiceState {
	user: QuizUser;
}

interface QuizAuthServiceOptions {
	initDependency?(user: QuizUser): Promise<unknown>;
}

export class QuizAuthService {
	state: QuizAuthServiceState = { user: null };

	constructor(private apiClient: MDTApiClient, private options: QuizAuthServiceOptions = {}) {}

	async init() {
		const user = await this.apiClient.auth.user();
		let quizUserData = {};
		if (QuizAuthService.isUserLoggedIn(user)) {
			quizUserData = await this.getQuizUserData(user.id);
		}
		this.updateState({
			user: {
				...user,
				...quizUserData
			}
		});
		await quizUserService.updateUserSettings(this.state.user);
		if (this.options.initDependency) await this.options.initDependency(user);
		return this.state;
	}

	async signIn(login: string, password: string) {
		await this.apiClient.auth.signIn(login, password, true);
		return await this.init();
	}

	async signOut() {
		await this.apiClient.auth.signOut();
		await this.init();
	}

	async forgotPassword(login: string): Promise<Response> {
		return this.apiClient.password.sendForgotPassword(login);
	}

	async setPassword(data: setPasswordData) {
		return this.apiClient.password.setPassword(data);
	}

	async changePassword(data: changePasswordData) {
		return this.apiClient.password.changePassword(data, false);
	}

	async getQuizUserData(userId: MDTRecordId) {
		const res = await this.apiClient.fetch(
			{
				table: "qst.PrincipalCache",
				filter: Filter.eq("ID", userId),
				select: [
					"FlagAdmin",
					"FlagGetLog",
					"FlagRouteExists",
					"FlagSuperVisor",
					"ID_Position",
					"ID_Employee/*",
					"ID_UserSettings/*"
				]
			},
			void 0,
			{ camel: true }
		);

		const r = res.records?.[0] || {};
		const user = {
			...r,
			employee: r.id_Employee$,
			userSettings: r.id_UserSettings$
		};

		user.isSuperVisor = r.flagSuperVisor;

		return user;
	}

	private updateState(newState) {
		this.state = {
			...this.state,
			...newState
		};
	}

	isLoggedIn() {
		return QuizAuthService.isUserLoggedIn(this.state.user);
	}

	isSupervisor(): boolean {
		return this.state.user.isSuperVisor;
	}

	private readonly QUIZ_GROUPS = ["Questionnaire", "QuestionnaireAdministrator"];
	hasAccessToQuiz() {
		const user = this.state.user;
		return user.roles.some(role => this.QUIZ_GROUPS.includes(role)) || user.isAdmin;
	}

	static isUserLoggedIn(user: QuizUser) {
		return user.isAnonymous === false;
	}

	getLoginData(): Promise<QuizAuthData> {
		return new Promise(async resolve => {
			//const oAuthListReq = this.apiClient.oauth.list(); uncomment when oauth will be enabled for mobile app
			const regReq = this.apiClient.password.checkRegistration();
			const [reg] = await Promise.all([regReq]);
			resolve({
				registrationEnabled: reg && reg.enabled
				//oAuthServices
			});
		});
	}

	oAuthAuthenticate(code: string) {
		return this.apiClient.oauth.authenticate(code);
	}

	registerUser(Code: string, Title: string, Email: string) {
		return this.apiClient.password.register({ Code, Title, Email });
	}
}

export interface QuizAuthData {
	registrationEnabled: boolean;
	oAuthServices?: OAuthService[];
}
