import { Button as F7Button } from "framework7-react";
import React, { MouseEventHandler } from "react";
import { Icon, IconProp, IconProps } from "quiz/components/icon/Icon";
import { useStyles } from "hooks/useStyles";
import { css, CSSProp } from "wraps/styleCreators";
import { ButtonType, ButtonSize } from "quiz/components/button/Button";
import { ColorTypes } from "theme/colors";
import { getMobileButtonStyles } from "./mobileButtonStyles";
import { filterProps } from "utils/helpers";

export const Button = F7Button;

export type F7ButtonPartialProps = Omit<
	F7Button.Props,
	"icon" | "color" | "onClick" | "type" | "fill" | "size" | "large" | "disabled" | "text"
>;

export interface MobileButtonProps extends F7ButtonPartialProps {
	onClick: MouseEventHandler<HTMLButtonElement | HTMLAnchorElement>;
	type?: ButtonType;
	size?: ButtonSize;
	fontSize?: React.ReactText;
	colorType?: ColorTypes;
	icon?: IconProp;
	iconProps?: IconProps;
	fullWidth?: boolean;
	fullHeight?: boolean;
	enabled?: boolean;
	childrenExists?: boolean;
}

const allowedMobileButtonProps = ["type", "size", "colorType", "icon", "iconProps", "fullWidth", "enabled", "onClick"];

export const MobileButton: React.FC<MobileButtonProps> = props => {
	const { children, ...btnProps } = props;
	const styles = useStyles(getMobileButtonStyles, {
		...props,
		childrenExists: !!children
	});
	const f7BtnProps = filterProps(btnProps, allowedMobileButtonProps, false);

	return (
		<F7Button
			{...f7BtnProps}
			large={props.size == ButtonSize.large}
			fill={props.type == ButtonType.normal}
			disabled={!props.enabled}
			className={css(styles.button)}
			onClick={props.onClick}
		>
			<span className={css(styles.innerWrap)}>
				{props.icon && (
					<span className={css(styles.iconWrap)}>
						<Icon icon={props.icon} styles={styles.icon} {...props.iconProps} />
					</span>
				)}
				<span className={css(styles.textWrap)}>
					<span className={css(styles.text)}>{children}</span>
				</span>
			</span>
		</F7Button>
	);
};

MobileButton.displayName = "MobileButton";
