import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { Icon, IconProp } from "../icon/Icon";
import { css } from "wraps/styleCreators";
import { getIconWrapStyles } from "./iconWrapStyles";

type TopAlign = "top-left" | "top-center" | "top-right";
type CenterAlign = "center-left" | "center" | "center-right";
type BottomAlign = "bottom-left" | "bottom-center" | "bottom-right";

export type IconAlignment = TopAlign | CenterAlign | BottomAlign;

export type IconWrapProps = {
	alignment?: IconAlignment;
	icon: IconProp;
	iconColor?: string;
	iconSize?: string | number;
};

export const IconWrap: React.FC<IconWrapProps> = props => {
	const styles = useStyles(getIconWrapStyles, props);
	return (
		<div className={css(styles.iconWrap)}>
			<Icon styles={styles.icon} icon={props.icon} explicitSize={props.iconSize} />
		</div>
	);
};

IconWrap.defaultProps = {
	alignment: "center"
};
