import { Theme } from "theme/theme";

export const radioListStyles = (theme: Theme, danger: boolean) =>
	`
.check .icon-radio {
	border-width: 5px !important;
}
.check .icon-radio::after {
	display: none;
}
.questionList__block .radio-danger .item-radio {
	color: ${theme.colors.danger.main} !important;
	--f7-radio-inactive-color: ${theme.colors.danger.main} !important;
}`.trim();
