import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const questionQrCodeControlStyles = (theme: Theme) => ({
	result: {
		display: "flex",
		background: theme.colors.primary.background
	} as CSSProp,
	icon: {
		color: theme.colors.primary.main
	},
	iconWrap: {
		width: theme.sizing.sizes.lg,
		height: theme.sizing.sizes.lg,
		display: "flex",
		alignItems: "center",
		justifyContent: "center",
		margin: `${scale(theme.sizing.sizes.lg, ScaleEnum.Half)} 0`
	} as CSSProp,
	success: {
		color: theme.colors.success.main
	},
	danger: {
		color: theme.colors.danger.main
	} as CSSProp,
	asterisk: {
		fontWeight: theme.sizing.fontWeights.bold,
		fontSize: theme.sizing.sizes.sm,
		color: theme.colors.danger.main
	} as CSSProp
});
