import { Button, ButtonType } from "quiz/components/button/Button";
import { Text } from "quiz/components/text/Text";
import { Calendar } from "framework7/components/calendar/calendar";
import * as React from "react";
import { useRef, useState } from "react";
import { ColorTypes } from "theme/colors";
import { ThemeContext } from "themeContext/ThemeContext";
import { getTodayOrFormatDateTime } from "utils/helpers";
import { applyTheme, datePickerStyles, DatePickerButtonStyles } from "./datePickerStyles";
import { TextSizeType } from "theme/sizing";
import { useStyles } from "hooks/useStyles";
import { css } from "wraps/styleCreators";
import { T } from "quiz/components/text/T";
import { quizUserService, textService } from "quiz/QuizApp";

export interface MobileDatepickerProps {
	calendar?: Calendar.Parameters;
	date?: Date;
	onClick: (calendarDate: Date) => void;
	onInit?: (getCalendar: (params?: Calendar.Parameters) => Calendar.Calendar) => void;
}

interface DatePickerState {
	date: Date;
}

export class DatePicker extends React.Component<MobileDatepickerProps, DatePickerState> {
	constructor(props) {
		super(props);
		this.state = { date: new Date() };
	}

	componentDidMount() {
		applyTheme(this.context);
		this.$f7ready(() => {
			this.props.onInit((params = {}) => {
				return this.$f7.calendar.create({
					locale:
						textService.availableLangs.find(lang => lang.ID == quizUserService.user.languageId).Code ||
						"ru",
					openIn: "auto",
					routableModals: false,
					closeByOutsideClick: true,
					backdrop: true,
					closeByBackdropClick: true,
					closeOnSelect: true,
					firstDay: 1,
					value: this.props.date ? [this.props.date] : [new Date()],
					on: {
						dayClick: (calendar, dayEl, year, month, day) => {
							const date = new Date(year, month, day);
							this.props.onClick(date);
						}
					},
					...params
				} as Calendar.Parameters);
			});
		});
	}

	render() {
		return (
			<div>
				<style>{datePickerStyles()}</style>
				<div id="date-time-picker"></div>
			</div>
		);
	}
}

DatePicker.contextType = ThemeContext;

export interface DatePickerButtonProps {
	title?: string;
	resetButton?: boolean;
	resetButtonTitle?: string;
	onResetButtonClick?: () => void;
	onDateClick?: (date: Date) => void;
	enabled?: boolean;
}

export const DatePickerButton: React.FC<DatePickerButtonProps> = (props: DatePickerButtonProps) => {
	const { title, resetButton, resetButtonTitle, onResetButtonClick, onDateClick, enabled = true } = props;
	const [date, setDate] = useState<Date>();
	const calendar = useRef<() => Calendar.Calendar>();
	const styles = useStyles(DatePickerButtonStyles);

	return (
		<div className={css(styles.wrap)}>
			<div>
				<Button
					onClick={() => {
						const c = calendar.current();
						c.open();
					}}
					{...(!enabled ? { enabled: false, onClick: () => null } : {})}
					onKeyDown={() => void 0}
					type={ButtonType.link}
					colorType={ColorTypes.primary}
					icon={["far", "calendar-alt"]}
					iconRight={["far", "angle-down"]}
				>
					<T size={TextSizeType.bodyMin}>{date ? getTodayOrFormatDateTime(date) : title || "Today"}</T>
				</Button>
				<DatePicker
					onInit={x => (calendar.current = x)}
					onClick={date => {
						setDate(date);
						onDateClick && onDateClick(date);
					}}
					date={date}
				/>
			</div>
			{resetButton && (
				<Button
					onClick={() => {
						setDate(null);
						onResetButtonClick?.();
					}}
					type={ButtonType.link}
					colorType={ColorTypes.primary}
					iconRight={["far", "backspace"]}
				>
					<Text size={TextSizeType.bodyMin}>{resetButtonTitle}</Text>
				</Button>
			)}
		</div>
	);
};
DatePickerButton.displayName = "DatePickerButton";
