import * as React from "react";
import { InfoBlockContext } from "../errors/QuizError";
import { IconName, IconPrefix } from "@fortawesome/fontawesome-common-types";
import { FullscreenInfo, FullscreenInfoProps } from "./fullscreenInfoBlock/FullscreenInfo";
import { ContextInfoBlock, ContextInfoBlockProps, InfoBlockActionButton } from "./contextInfoBlock/ContextInfoBlock";
import { ColorTypes } from "theme/colors";

export interface InfoBlockProps {
	icon: [IconPrefix, IconName] | JSX.Element;
	title: string;
	message: string;
	colorType?: ColorTypes;
	primaryButton?: InfoBlockActionButton;
	secondaryButton?: InfoBlockActionButton;
}

export const InfoBlock: React.FC<InfoBlockProps> = props => {
	const { icon, title, message, colorType, primaryButton, secondaryButton } = props;
	const context = React.useContext(InfoBlockContext);
	const sharedProps = { icon, title, message, colorType };

	if (context.fullScreen) {
		const fullScreenProps: FullscreenInfoProps = {
			...sharedProps,
			primaryButton,
			secondaryButton,
			fullHeight: context.fullHeight
		};
		return <FullscreenInfo {...fullScreenProps} />;
	}

	const contextBlockProps: ContextInfoBlockProps = {
		...sharedProps,
		separateBlock: context.separateBlock,
		buttons: [primaryButton, secondaryButton].filter(x => x)
	};

	return <ContextInfoBlock {...contextBlockProps} />;
};
