import * as React from "react";
import { GeolocationError } from "quiz/services/GeolocationError";
import { InfoBlock } from "../infoBlock/InfoBlock";
import { ColorTypes } from "theme/colors";
import { t } from "quiz/QuizApp";
import { InfoBlockActionButton } from "../infoBlock/contextInfoBlock/ContextInfoBlock";
import { f7 } from "framework7-react";

export type QuizGeolocationError = {
	error: GeolocationError;
};

export enum GeolocationErrorCodes {
	PERMISSION_DENIED = 1,
	POSITION_UNAVAILABLE = 2,
	TIMEOUT = 3
}

const GeoErrorMessages = {
	[GeolocationErrorCodes.PERMISSION_DENIED]: "Allow geolocation in app settings to determine location",
	[GeolocationErrorCodes.POSITION_UNAVAILABLE]:
		"Device location not available. Check your geolocation settings on device",
	[GeolocationErrorCodes.TIMEOUT]: "Failed to get location. Check your geolocation settings on device"
};

export const QuizGeolocationError: React.FC<QuizGeolocationError> = ({ error }) => {
	const { message: errMessage, code } = error;
	const message = GeoErrorMessages[code] || errMessage;

	const buttons: { [key: string]: InfoBlockActionButton } = {
		primaryButton: {
			title: t("Refresh"),
			icon: ["far", "sync"],
			command: () => f7.views.main.router.refreshPage()
		}
	};

	return (
		<InfoBlock
			icon={["far", "location-slash"]}
			title={t("Failed to determine location")}
			message={t(message)}
			{...buttons}
			colorType={ColorTypes.danger}
		/>
	);
};
