import * as React from "react";
import { Page } from "quiz/components/page/Page";
import { t, quizAuthService } from "quiz/QuizApp";
import { QuizNavBar } from "quiz/components/quizNavBar/QuizNavBar";
import { css } from "wraps/styleCreators";
import { getRegistrationPageStyles } from "./registrationPageStyles";
import { useStyles } from "hooks/useStyles";
import { Block, BlockDescription, BlockTitle } from "quiz/components/block/Block";
import { QuizListInputItem } from "quiz/components/quizListInputItem/QuizListInputItem";
import { ColorTypes } from "theme/colors";
import { ServerInteractionButton } from "quiz/components/button/ServerInteractionButton";
import { ButtonSize, ButtonType } from "quiz/components/button/Button";
import { getApiErrorForT } from "utils/helpers";
import { f7 } from "framework7-react";

type RegistrationPageState = {
	name?: string;
	email?: string;
	error?: any;
};

const initialState: RegistrationPageState = { name: "", email: "", error: null };

export const RegistrationPage: React.FC = () => {
	const [state, setState] = React.useState<RegistrationPageState>(initialState);
	const styles = useStyles(getRegistrationPageStyles);

	const onNameChange = React.useCallback((v: string) => setState(prev => ({ ...prev, name: v, error: null })), []);
	const onEmailChange = React.useCallback((v: string) => setState(prev => ({ ...prev, email: v, error: null })), []);
	const submitEnabled = React.useCallback(() => !!(state.name && state.email), [state]);
	const command = React.useCallback(() => {
		return new Promise<void>((resolve, reject) => {
			quizAuthService
				.registerUser(state.name, null, state.email)
				.then(() => {
					setState(prev => ({ ...prev, error: null }));
					f7.views.main.router.navigate("/registration-success/");
					return resolve();
				})
				.catch(err => {
					const [error, args] = getApiErrorForT(err);
					setState(prev => ({ ...prev, error: t(error, args) }));
					reject();
				});
		});
	}, [state]);

	return (
		<Page>
			<QuizNavBar backLink title={t("Registration")} faq />
			<div className={css(styles.wrap)}>
				<BlockTitle title={t("Personal data")} />
				<Block secondary>
					<QuizListInputItem
						required
						type="text"
						inputMode="email"
						icon={["far", "user"]}
						value={state.name}
						onChange={onNameChange}
						placeholder={t("Enter your name")}
						dash
					/>
				</Block>
				<Block secondary>
					<QuizListInputItem
						required
						type="text"
						inputMode="email"
						icon={["far", "envelope"]}
						value={state.email}
						onChange={onEmailChange}
						placeholder={t("Enter e-mail")}
					/>
				</Block>
				{state.error && <BlockDescription text={state.error} styles={styles.error} />}
				<BlockDescription
					muted={false}
					colorType={ColorTypes.base}
					text={t("A letter with a link to confirm registration will be sent to the specified e-mail")}
				/>
				<ServerInteractionButton
					wrap
					type={ButtonType.normal}
					colorType={ColorTypes.primary}
					fullWidth={true}
					size={ButtonSize.large}
					title={t("Registration")}
					icon={["far", "user-plus"]}
					enabled={submitEnabled}
					command={command}
				/>
			</div>
		</Page>
	);
};

RegistrationPage.displayName = "QuizRegistrationPage";
