import { Theme } from "theme/theme";
import { scale, ScaleEnum } from "utils/cssHelpers";
import { CSSProp } from "wraps/styleCreators";

export const getOutletViewStyles = (theme: Theme) => {
	return {
		wrap: {
			width: "100%"
		} as CSSProp,
		titleRow: {
			justifyContent: "space-between"
		} as CSSProp,
		bottowRow: {
			width: "100%"
		} as CSSProp,
		iconAndTitle: {
			display: "flex",
			alignItems: "center"
		} as CSSProp,
		captionText: {
			lineHeight: theme.sizing.lineHeights.caption
		},
		distance: {
			display: "flex"
		} as CSSProp,
		distanceContent: {
			width: "max-content"
		} as CSSProp,
		title: {
			whiteSpace: "nowrap",
			overflow: "hidden",
			textOverflow: "ellipsis",
			paddingLeft: theme.sizing.sizes.xs
		} as CSSProp,
		outletTitle: {
			overflow: "hidden",
			flexWrap: "nowrap",
			whiteSpace: "nowrap",
			paddingRight: theme.sizing.sizes.sm
		} as CSSProp,
		information: {
			lineHeight: scale(theme.sizing.sizes.lg, ScaleEnum.Half)
		} as CSSProp,
		lastRow: {
			marginBottom: 0
		},
		address: {
			paddingRight: theme.sizing.sizes.sm
		}
	};
};
