import * as React from "react";
import { Button } from "quiz/components/button/MobileButton";
import { t, quizAuthService, nav } from "quiz/QuizApp";
import { OutletVisits } from "quiz/components/outletVisits/OutletVisits";
import { ListNearOutlets } from "quiz/components/listNearOutlets/ListNearOutlets";

export const FilterPage: React.FC = props => {
	return (
		<>
			<div>
				<Button onClick={() => {}}>{t("Filter")}</Button>
			</div>
			{quizAuthService.isSupervisor() ? (
				<OutletVisits id={"131877"} />
			) : (
				<ListNearOutlets onOutletClick={outlet => nav.toOutlet(outlet.id)} />
			)}
		</>
	);
};
