import React from "react";
import { useStyles } from "hooks/useStyles";
import { getSearchFilterStyles } from "./searchFilterStyles";
import { css } from "wraps/styleCreators";
import { TextSizeType } from "theme/sizing";
import { T } from "quiz/components/text/T";
import { Toggle } from "quiz/components/toggle/Toggle";
import F7Toggle from "framework7-react/components/toggle";

type SearchFilterProps = {
	onToggleChange: (checked: boolean) => void;
	title: string;
};

export const SearchFilter: React.FC<SearchFilterProps> = React.memo(props => {
	const { title, onToggleChange } = props;
	const styles = useStyles(getSearchFilterStyles);
	const toggleRef = React.useRef<F7Toggle>();

	return (
		<div className={css(styles.wrap)}>
			<div className={css(styles.inlineGroup)}>
				<div onClick={() => toggleRef.current?.f7Toggle?.toggle()}>
					<T size={TextSizeType.bodyMin}>{title}</T>
				</div>
				<div>
					<Toggle ref={toggleRef} onToggleChange={onToggleChange} defaultChecked={false} />
				</div>
			</div>
		</div>
	);
});

SearchFilter.displayName = "SearchFilter";
