import { createThemeApplier } from "wraps/component";
import { Theme } from "theme/theme";

export const applyTheme = createThemeApplier((theme: Theme) => {
	return {
		"f7-toolbar-bg-color": theme.colors.primary.background,
		"f7-toolbar-border-color": theme.colors.base.background,
		"f7-tabbar-link-text-transform": "none",
		"f7-tabbar-link-active-color": theme.colors.base.text,
		"f7-toolbar-height": "40px",
		"f7-toolbar-top-shadow-image": "none"
	};
});

export const tabsStyles = (theme: Theme) => {
	return `
		.toolbar-top {
			top: 0 !important
		}
		.md .tabbar .tab-link-highlight {
			z-index: 20
		}
	`.trim();
};
