import { DBSchema, openDB } from "idb";
import { MDTRecordId } from "mdtApiClient/mdtApiClient";
import { ID, Visit, VisitAnswerFile, OutletFullData, IQuestionnaire } from "./models";
import { quizExtensionService } from "quiz/QuizApp";
import { TQstList } from "quiz/components/qstList/QstList";
import { cacheIsValid } from "utils/helpers";

export const draftStoreName = "Draft";
export const draftFilesStore = "Draft_Files";
export const visitUploadStore = "Visits";
export const outletCacheStore = "Outlet_Cache";

export type OutletCache = {
	outlet: OutletFullData;
	qstList: TQstList[];
	qsts: IQuestionnaire[];
	dateExpired: Date;
};

interface QuizDraftServiceEvents {
	modified: ((q: QuizDraftService) => void)[];
}

interface QuizDraftServiceOptions {
	$initialData: any;
	events: QuizDraftServiceEvents;
}

export interface QuizDB extends DBSchema {
	[draftStoreName]: {
		key: string;
		value: Visit;
	};
	[draftFilesStore]: {
		value: VisitAnswerFile;
		key: ID;
	};
	[visitUploadStore]: {
		value: VisitUploadData;
		key: number;
	};
	[outletCacheStore]: {
		value: OutletCache;
		key: number;
	};
}

export enum VisitUploadStatus {
	notSent,
	success,
	error
}

export interface VisitUploadData {
	key: number;
	visit: Visit;
	status: VisitUploadStatus;
	files: {
		status: VisitUploadStatus;
		answerFile: VisitAnswerFile;
		visitItemIndex: number;
	}[];
}

let storeKey = new Date().getTime();

export class QuizDraftService {
	events: QuizDraftServiceEvents;
	$data: any;

	db = quizExtensionService.db.getDB();

	constructor(options: QuizDraftServiceOptions) {
		this.events = {
			modified: options.events.modified || []
		};
		this.$data = options.$initialData;
	}

	async set(visit: Visit): Promise<IDBValidKey> {
		const db = await this.db;
		const r = await db.put(draftStoreName, visit, this.createDbKey(visit.outletId, visit.questionnaireId));
		this.events.modified.forEach(cb => cb(this));
		return r;
	}

	async get(outletId: MDTRecordId, questionnaireId: MDTRecordId): Promise<Visit> {
		return (await this.db).get(draftStoreName, this.createDbKey(outletId, questionnaireId));
	}

	async delete(outletId: MDTRecordId, questionnaireId: MDTRecordId) {
		let r = await (await this.db).delete(draftStoreName, this.createDbKey(outletId, questionnaireId));
		this.events.modified.forEach(cb => cb(this));
		return r;
	}

	async getAllDrafts(): Promise<Visit[]> {
		return await (await this.db).getAll(draftStoreName);
	}

	private createDbKey(outletId: MDTRecordId, questionnaireId: MDTRecordId) {
		return `${outletId}-${questionnaireId}`;
	}

	async setFiles(files: VisitAnswerFile[]) {
		let db = await this.db;
		await Promise.all(files.map(file => db.put(draftFilesStore, file, file.draftFileId)));
	}

	async removeFiles(files: VisitAnswerFile[]) {
		let db = await this.db;
		for (const file of files) {
			await db.delete(draftFilesStore, file.draftFileId);
		}
	}

	async getFiles(files: VisitAnswerFile[]): Promise<VisitAnswerFile[]> {
		const db = await this.db;
		const results = [];
		const ids = files.map(f => f.draftFileId);
		for (const id of ids) {
			const data = await db.get(draftFilesStore, id);
			results.push(data);
		}
		return results;
	}

	// UPLOAD & FILES
	async storeFinishedVisit(visit: Visit): Promise<number> {
		const data: VisitUploadData = {
			key: storeKey++,
			visit,
			status: VisitUploadStatus.notSent,
			files: visit.answers.reduce(
				(arr, answer, idx) =>
					answer.files
						? arr.concat(
								answer.files.map(answerFile => ({
									status: VisitUploadStatus.notSent,
									answerFile,
									visitItemIndex: idx
								}))
						  )
						: arr,
				[]
			)
		};
		return await (await this.db).put(visitUploadStore, data, data.key);
	}

	async getVisitUploadData(): Promise<VisitUploadData[]> {
		return await (await this.db).getAll(visitUploadStore);
	}

	async updateVisitUploadData(uploadData: VisitUploadData): Promise<number> {
		return await (await this.db).put(visitUploadStore, uploadData, uploadData.key);
	}

	async removeVisitUploadDataPhotos(uploadData: VisitUploadData): Promise<void> {
		return await this.removeFiles(uploadData.files.map(f => f.answerFile));
	}

	async removeVisitUploadData(uploadData: VisitUploadData): Promise<undefined> {
		await this.removeVisitUploadDataPhotos(uploadData);
		return await (await this.db).delete(visitUploadStore, uploadData.key);
	}

	async setOutletCache(outletId: MDTRecordId, data: Omit<OutletCache, "dateExpired">) {
		return await (await this.db).put(outletCacheStore, data, outletId);
	}

	async getOutletCache(outletId: MDTRecordId): Promise<OutletCache> {
		const cache = await (await this.db).get(outletCacheStore, outletId);
		return cache && cacheIsValid(cache?.dateExpired) ? cache : null;
	}
}
