import * as React from "react";
import { ButtonSize, ButtonType } from "quiz/components/button/Button";
import { ColorTypes } from "theme/colors";
import { t, quizAuthService, apiClient, quizUserService, nav } from "quiz/QuizApp";
import { useStyles } from "hooks/useStyles";
import { getPrivacyPolicyStyles } from "./privacyPolicyStyles";
import { css } from "wraps/styleCreators";
import { useCancellablePromise } from "hooks/useCancellablePromise";
import { QuizError } from "../errors/QuizError";
import { QuizPreloader } from "../quizPreloader/QuizPreloader";
import { ServerInteractionButton } from "../button/ServerInteractionButton";

type PrivacyPolicyProps = {
	isButtonShow?: boolean;
};

export const PrivacyPolicy: React.FC<PrivacyPolicyProps> = React.memo(props => {
	const styles = useStyles(getPrivacyPolicyStyles);
	const ref = React.useRef<HTMLDivElement>();
	const [error, setError] = React.useState();
	const [policyLoaded, setPolicyLoaded] = React.useState<boolean>(false);

	useCancellablePromise(signal => apiClient.fetchHTML("/quiz/documents/privacyPolicy", { signal }), {
		onSuccess: html => {
			setPolicyLoaded(true);
			if (ref.current) {
				const target = window.cordova ? "system" : "blank";
				// eslint-disable-next-line no-useless-escape
				ref.current.innerHTML = html?.replace(/(target=\"\_blank\")/g, `target="_${target}" class="link external"`);
			}
		},
		onError: error => setError(error)
	});

	const ulStyles = `
		.${css(styles.textWrapper)} ul {
			list-style: none;
			padding: 0;
			margin: 0;
		}
	`;

	return error ? (
		<div className={css(styles.errorWrapper)}>
			<QuizError error={error} fullHeight={false} fullScreen />
		</div>
	) : (
		<div className={css(styles.wrap)}>
			{!policyLoaded ? (
				<div className={css(styles.preloaderWrapper)}>
					<QuizPreloader preloaderAreaWidth={100} />
				</div>
			) : (
				<div className={css(styles.content)}>
					<style>{ulStyles}</style>
					<div className={css(styles.textWrapper)} ref={ref}></div>
				</div>
			)}
			{props.isButtonShow != null && (
				<div className={css(styles.buttonsWrapper)}>
					<div className={css(styles.button)}>
						<ServerInteractionButton
							type={ButtonType.normal}
							colorType={ColorTypes.primary}
							fullWidth={true}
							size={ButtonSize.large}
							title={t("Accept")}
							icon={["far", "check"]}
							command={async () => {
								await quizUserService.savePrivacyPolicyAcceptToServer();
								window.location.reload();
							}}
						/>
					</div>
					<div className={css(styles.button)}>
						<ServerInteractionButton
							type={ButtonType.transparent}
							colorType={ColorTypes.info}
							fullWidth={true}
							size={ButtonSize.large}
							title={t("Reject")}
							icon={["far", "ban"]}
							command={async () => {
								await quizAuthService.signOut();
								location.reload();
							}}
						/>
					</div>
				</div>
			)}
		</div>
	);
});

PrivacyPolicy.displayName = "PrivacyPolicy";
