import { Link } from "quiz/components/link/Link";
import { Tab, Tabs, Toolbar } from "quiz/components/tabs/Tabs";
import { Draft } from "quiz/components/draft/Draft";
import { ListNearOutlets } from "quiz/components/listNearOutlets/ListNearOutlets";
import { OutletRoutes } from "quiz/components/outletRoutes/OuletRoutes";
import { T } from "quiz/components/text/T";
import { UnsentReports } from "quiz/components/unsentReports/UnsentReports";
import { nav, quizAuthService, t, quizDraftService } from "quiz/QuizApp";
import { QuizPageComponentProps } from "quiz/QuizAppRoutes";
import * as React from "react";
import { TextSizeType } from "theme/sizing";
import { ErrorBoundary } from "quiz/components/errorBoundary/ErrorBoundary";
import { useStyles } from "hooks/useStyles";
import { getMerchandiserMainPageStyles } from "./merchandiserMainPageStyles";
import { css } from "wraps/styleCreators";
import { BlockTitle } from "quiz/components/block/Block";
import { OutletFullData, Visit } from "quiz/services/models";
import { VisitUploadData } from "quiz/services/QuizDraftService";

type MerchandiserMainPageState = {
	drafts: Visit[];
	reports: VisitUploadData[];
	allDataLoaded: boolean;
	routesActive: boolean;
};

export const beforeMerchandiserPageInit = async () => {
	const drafts = await quizDraftService.getAllDrafts();
	const reports = await quizDraftService.getVisitUploadData();
	nav.events.emit("beforeMerchandiserPageInit", { drafts, reports });
};

export const afterMerchandiserPageOut = () => nav.events.emit("afterMerchandiserPageOut", void 0);

export const MerchandiserMainPage: React.FC<QuizPageComponentProps> = React.memo(() => {
	const routeExists = quizAuthService.state.user.flagRouteExists;
	const styles = useStyles(getMerchandiserMainPageStyles, routeExists);
	const outletsCb = React.useCallback((outlet: OutletFullData) => nav.toOutlet(outlet.id), []);
	const [state, setState] = React.useState<MerchandiserMainPageState>({
		drafts: null,
		reports: null,
		allDataLoaded: false,
		routesActive: quizAuthService.state.user.flagRouteExists
	});

	React.useEffect(() => {
		const unsubscribeBeforeEvent = nav.subscribe("beforeMerchandiserPageInit", data =>
			setState(prev => ({
				...prev,
				routesActive: routeExists,
				drafts: data.drafts,
				reports: data.reports,
				allDataLoaded: true
			}))
		);
		const unsubscribeAfterEvent = nav.subscribe("afterMerchandiserPageOut", () =>
			setState(prev => ({ ...prev, allDataLoaded: false }))
		);

		return () => {
			unsubscribeBeforeEvent();
			unsubscribeAfterEvent();
		};
	});

	return (
		<>
			{state.allDataLoaded && (
				<>
					<ErrorBoundary separateBlock>
						<UnsentReports reports={state.reports} />
					</ErrorBoundary>
					<ErrorBoundary separateBlock>
						<Draft
							drafts={state.drafts}
							onDraftClick={draft => nav.toQst(draft.outletId, draft.qstId)}
							onDeleteSwipeout={async d => {
								await quizDraftService.delete(d.outletId, d.qstId);
								const drafts = await quizDraftService.getAllDrafts();
								setState(prev => ({ ...prev, drafts: drafts }));
							}}
						/>
					</ErrorBoundary>
					<div className={css(styles.tabs)}>
						{routeExists ? (
							<>
								<Toolbar tabbar noBorder top className={css(styles.toolbar)}>
									<Link
										className={css(styles.tabLink)}
										tabLink="#tab-1"
										tabLinkActive={state.routesActive}
										onClick={() => setState(prev => ({ ...prev, routesActive: true }))}
									>
										<T size={TextSizeType.bodyMin}>Route</T>
									</Link>
									<Link
										className={css(styles.tabLink)}
										tabLink="#tab-2"
										tabLinkActive={!state.routesActive}
										onClick={() => setState(prev => ({ ...prev, routesActive: false }))}
									>
										<T size={TextSizeType.bodyMin}>Closest</T>
									</Link>
								</Toolbar>
								<Tabs>
									<Tab id="tab-1" tabActive={state.routesActive}>
										{state.routesActive && (
											<OutletRoutes onOutletClick={outlet => nav.toOutlet(outlet.id)} />
										)}
									</Tab>
									<Tab id="tab-2" tabActive={!state.routesActive}>
										{!state.routesActive && <ListNearOutlets onOutletClick={outletsCb} />}
									</Tab>
								</Tabs>
							</>
						) : (
							<>
								<BlockTitle title={t("Closest")} />
								<ListNearOutlets onOutletClick={outletsCb} />
							</>
						)}
					</div>
				</>
			)}
		</>
	);
});

MerchandiserMainPage.displayName = "MerchandiserMainPage";
