import { Theme } from "theme/theme";
import { scale, ScaleEnum } from "utils/cssHelpers";
import { CSSProp } from "wraps/styleCreators";

export const getHintsStyles = (theme: Theme) => {
	return {
		wrap: {
			height: `calc(100 - ${theme.sizing.sizes.md})`,
			width: "100%",
			display: "flex",
			flexFlow: "column",
			justifyContent: "flex-start",
			alignItems: "center",
			marginTop: theme.sizing.sizes.xl,
			color: theme.colors.base.texts.muted,
			padding: `0px ${scale(theme.sizing.sizes.lg, ScaleEnum.Double)}`
		} as CSSProp,
		description: {
			marginBottom: theme.sizing.sizes.md
		} as CSSProp,
		exampleTitle: {
			width: "100%",
			marginBottom: theme.sizing.sizes.sm
		} as CSSProp,
		title: {
			margin: `0px 0px ${theme.sizing.sizes.md} 0px`
		} as CSSProp,
		exampleText: {
			borderBottom: "1px dashed",
			marginLeft: theme.sizing.sizes.xs
		} as CSSProp,
		searchFields: {
			listStyle: "none",
			paddingLeft: "0px",
			margin: "0px",
			fontSize: theme.sizing.fontSizes.bodyMax,
			display: "flex",
			flexFlow: "column",
			justifyContent: "center",
			alignItems: "center"
		} as CSSProp,
		searchElement: {
			width: "100%",
			":before": {
				content: "'\\2022'",
				display: "inline-block",

				width: theme.sizing.fontSizes.bodyMax
			}
		} as CSSProp
	};
};
