import { Theme } from "theme/theme";
import { createThemeApplier } from "wraps/component";

const applyTheme = createThemeApplier((theme: Theme) => {
	return {
		"f7-photobrowser-bg-color": "#fff",
		"f7-photobrowser-bars-bg-image": "none",
		"f7-photobrowser-bars-bg-color": "rgba(var(--f7-bars-bg-color-rgb), 0.95)",
		"f7-photobrowser-bars-text-color": "var(--f7-bars-text-color)",
		"f7-photobrowser-bars-link-color": "var(--f7-bars-link-color)",
		"f7-photobrowser-caption-font-size": theme.sizing.fontSizes.body as string,
		"f7-photobrowser-caption-light-text-color": "#000",
		"f7-photobrowser-caption-light-bg-color": "rgba(255, 255, 255, 0.8)",
		"f7-photobrowser-caption-dark-text-color": "#fff",
		"f7-photobrowser-caption-dark-bg-color": "transparent",
		"f7-photobrowser-exposed-bg-color": "#000",
		"f7-photobrowser-dark-bg-color": "#000",
		"f7-photobrowser-dark-bars-bg-color": "unset",
		"f7-photobrowser-dark-bars-text-color": "#fff",
		"f7-photobrowser-dark-bars-link-color": "#fff",
		"f7-navbar-title-font-size": theme.sizing.fontSizes.bodyMax,
		"f7-navbar-title-font-weight": theme.sizing.fontWeights.medium
	};
}, false);

const getGradient = (deg: number) => `linear-gradient(${deg}deg, rgba(0, 0, 0, 0.48) 0%, rgba(255, 255, 255, 0) 100%)`;
const tweakClass = "pb-tweaks";
const tweakStyles = (theme: Theme) =>
	`
	.${tweakClass} .swiper-pagination {
		line-height: ${theme.sizing.sizes.sm};
		bottom: ${theme.sizing.sizes.lg};
	}
	.${tweakClass} .photo-browser-caption {
		padding: 0;
	}
	.${tweakClass} .swiper-pagination-bullet {
		background: #fff;
		opacity: 0.26;
	}
	.${tweakClass} .swiper-pagination-bullet-active {
		background: ${theme.colors.primary.main};
		opacity: 1;
	}
	.${tweakClass} .navbar .title {
		width: 100%;
		text-align: center;
		position: absolute;
		z-index: 1;
		margin-left: 0;
		left: 0 !important;
		color: ${theme.colors.base.texts.contrast}
	}
	.${tweakClass} .navbar-bg {
		background: ${getGradient(180)}
	}
	.${tweakClass} .toolbar {
		background: ${getGradient(360)}
	}
`.trim();

export const preparePhotoBrowserStyles = (theme: Theme, el) => {
	const styleTag = document.createElement("style");
	styleTag.type = "text/css";
	styleTag.appendChild(document.createTextNode(tweakStyles(theme)));

	el.appendChild(styleTag);
	el.classList.add(tweakClass);
	applyTheme(theme, el);
};
