import * as React from "react";
import { QuizList } from "../quizList/QuizList";
import { Visit } from "quiz/services/models";
import { MDTRecordId } from "mdtApiClient/mdtApiClient";
import { OutletSkeleton } from "../quizSkeleton/skeletons/OutletSkeleton";
import { OutletItem } from "quiz/components/outletItem/OutletItem";
import { BlockTitle } from "quiz/components/block/Block";
import { t } from "quiz/QuizApp";

type DraftCb = (draft: { outletId: MDTRecordId; qstId: MDTRecordId }) => void;

type DraftProps = {
	drafts: Visit[];
	onDraftClick: DraftCb;
	onDeleteSwipeout?: DraftCb;
};

export const Draft: React.FC<DraftProps> = props => {
	if (!props.drafts?.length) return null;

	return (
		<div>
			<BlockTitle title={t("Incomplete visits")} counter={props.drafts.length} />
			<QuizList
				isLoading={!props.drafts[0].outlet}
				isEmpty={false}
				errors={[]}
				loading={() => <OutletSkeleton totalBlocks={1} />}
			>
				{props.drafts.map((d: Visit) => (
					<OutletItem
						key={`${d.outletId}-${d.questionnaireId}`}
						onDeleteSwipeout={() =>
							props.onDeleteSwipeout({ outletId: d.outletId, qstId: d.questionnaireId })
						}
						onClick={() => props.onDraftClick({ outletId: d.outlet.id, qstId: d.questionnaire.id })}
						outlet={{
							...d.outlet,
							outletChain: d.outlet.outletChain,
							type: null,
							questionnaireName: d.questionnaire.name,
							time: d.dateBegin
						}}
					/>
				))}
			</QuizList>
		</div>
	);
};
