import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const getSearchBarStyles = (theme: Theme) => {
	return {
		backlink: {
			justifyContent: "center",
			paddingRight: 0
		} as CSSProp,
		title: {
			width: "100%",
			marginLeft: scale(theme.sizing.sizes.xs, ScaleEnum.Triple),
			marginRight: "0px"
		} as CSSProp,
		input: {
			width: "100%",
			fontSize: theme.sizing.fontSizes.body,
			"::after": {
				height: "0"
			}
		} as CSSProp,
		iconWrap: {
			display: "flex",
			height: theme.sizing.sizes.lg,
			width: theme.sizing.sizes.lg,
			alignItems: "center",
			justifyContent: "center"
		} as CSSProp
	};
};
