import { createThemeApplier } from "wraps/component";
import { Theme } from "theme/theme";

export const applyTheme = createThemeApplier((theme: Theme) => {
	return {
		"f7-toggle-height": theme.sizing.sizes.md,
		"f7-toggle-width": theme.sizing.sizes.xl,
		"f7-toggle-active-color": theme.colors.primary.main,
		"f7-toggle-inactive-color": theme.colors.base.border
	};
});

export const toggleStyles = (theme: Theme): string =>
	`
	.md .toggle input[type="checkbox"]:checked + .toggle-icon {
		background: ${theme.colors.base.border} !important;
	}
	.md .toggle-icon:after {
		height: calc(var(--f7-toggle-height)) !important;
		width: calc(var(--f7-toggle-height) ) !important;
		top: 0;
		background: ${theme.colors.secondary.border};
		box-shadow: none !important;
	}
	.md .toggle input[type="checkbox"]:checked + .toggle-icon:after {
		transform: translateX(calc(var(--f7-toggle-width) - var(--f7-toggle-height) )) !important;
	}
`.trim();
