import { TActions } from "quiz/components/actions/Actions";
import { Page } from "quiz/components/page/Page";
import { quizAuthService, t } from "quiz/QuizApp";
import * as React from "react";
import { QuizNavBar, QuizNavBarProps } from "../quizNavBar/QuizNavBar";
import { ErrorBoundary } from "../errorBoundary/ErrorBoundary";
import { F7PageEvents } from "quiz/services/QuizNavigationManager";
import { QuizActions } from "../actions/QuizActions";
import { f7 } from "framework7-react";
import { FullscreenInfo } from "../infoBlock/fullscreenInfoBlock/FullscreenInfo";
import { ColorTypes } from "theme/colors";
import { userNotMatchedIcon } from "../infoScreenIcons/userNotMatchedIcon";

const PERMISSION_ERROR = "User does not have permission to access the application";
const PROFILE_ERROR = "Your profile is not mapped to the employee directory";

interface QuizPageProps {
	component: React.FC | React.ComponentClass;
	componentProps: any;
	navbar?: QuizNavBarProps;
	backLink?: boolean;
	onSearch?: (query: string) => void;
	onError?: () => void;
	pageEvents?: F7PageEvents;
}

export const QuizPage: React.FC<QuizPageProps> = props => {
	const [title, setTitle] = React.useState<string>(props.navbar?.title);
	const [subtitle, setSubtitle] = React.useState<string>();
	const [actions, setActions] = React.useState();
	const [search, setSearch] = React.useState();
	const actionsRef = React.useRef<TActions>();

	const errors = [
		!quizAuthService.hasAccessToQuiz() && PERMISSION_ERROR,
		!quizAuthService.state.user.employee && PROFILE_ERROR
	].filter(x => x);

	const content = React.useCallback(() => {
		const Component = props.component;
		return (
			<ErrorBoundary>
				<Component
					{...props.componentProps}
					setTitle={setTitle}
					setSubtitle={setSubtitle}
					setActions={setActions}
					setSearch={setSearch}
				/>
			</ErrorBoundary>
		);
	}, [props.component, props.componentProps]);

	return (
		<Page {...props.pageEvents}>
			{props.navbar && title && (
				<QuizNavBar
					backLink={props.backLink}
					{...props.navbar}
					title={title}
					subtitle={subtitle ?? props.navbar.subtitle}
					search={errors.length ? false : search ?? props.navbar.search}
					actions={{
						show: props.navbar.actions?.show,
						cb: () => props.navbar.actions && actionsRef.current.open()
					}}
				/>
			)}
			{errors.length ? (
				<FullscreenInfo
					title={t("Access error")}
					icon={userNotMatchedIcon}
					message={errors.reduce((prev, curr) => prev + t(curr) + ".\n", "")}
					colorType={ColorTypes.danger}
					primaryButton={{
						title: t("Logout"),
						icon: ["far", "sign-out-alt"],
						command: async () => {
							await quizAuthService.signOut();
							f7.views.main.router.refreshPage();
						}
					}}
				/>
			) : (
				content()
			)}
			<QuizActions extend ref={actionsRef} actions={() => actions} />
		</Page>
	);
};
QuizPage.displayName = "QuizPage";
