import * as React from "react";
import { T } from "quiz/components/text/T";
import { TextSizeType } from "theme/sizing";
import { CONFIG } from "quiz/QuizApp";
import { Text } from "quiz/components/text/Text";

export const Version: React.FC = () => (
	<div>
		<T size={TextSizeType.caption}>Version</T>
		<Text size={TextSizeType.caption}>: {`${CONFIG.version}:${CONFIG.revision}`}</Text>
	</div>
); //toDo: to extManager
