import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getMerchandiserMainPageStyles = (theme: Theme, flagRouteExists: boolean) => {
	return {
		toolbar: {
			marginTop: theme.sizing.sizes.lg
		} as CSSProp,
		routeLink: (flagRouteExists ? {} : { pointerEvents: "none", opacity: 0.4 }) as CSSProp,
		tabs: {
			paddingBottom: theme.sizing.sizes.md
		} as CSSProp,
		listNearOutlets: {
			paddingBottom: theme.sizing.sizes.md
		},
		tabLink: {
			borderBottom: `1px solid ${theme.colors.base.background}`
		} as CSSProp
	};
};
