import { t, quizExtensionService } from "quiz/QuizApp";
import { Dialog } from "quiz/components/interaction/Dialog";
import { Toast } from "quiz/components/interaction/toast/Toast";

declare global {
	interface Window {
		codePush: {
			checkForUpdate(onSuccess, onError?, deploymentKey?: string);
			restartApplication();
			notifyApplicationReady();
		};
	}
}

export class UpdateManager {
	checkForUpdate() {
		window.codePush.checkForUpdate(this.onSuccess);
	}

	onSuccess(val) {
		if (val) {
			const options = {
				title: t("Update the app"),
				text: t("A new version of the app has been released, please update"),
				buttons: [
					{
						close: true,
						text: t("Cancel")
					},
					{
						text: t("Update"),
						onClick: () => {
							onUpdateCheck(val);
							d.close();
						}
					}
				]
			};
			const d = Dialog.create(options);
			d.open();
		}
	}
}

function onError() {
	Toast.create({
		type: "error",
		text: t("Update error!")
	}).open();
}

function onPackageDownloaded(localPackage, close) {
	localPackage.install(() => {
		close();
		window.codePush.restartApplication(), onError;
	});
}

function onUpdateCheck(remotePackage) {
	if (!remotePackage.failedInstall) {
		const dialog = Dialog.preloader(t("Update preparation"));
		dialog.open();
		remotePackage.download(localPackage => onPackageDownloaded(localPackage, () => dialog.close()), onError);
	}
}
