import * as React from "react";
import { useStyles } from "hooks/useStyles";
import { quizPreloaderStyles } from "./quizPreloaderStyles";
import { css } from "wraps/styleCreators";
import { Preloader } from "framework7-react";

export type QuizPreloaderProps = {
	preloaderAreaWidth: number;
	preloaderAreaHeight?: number;
};

export const QuizPreloader: React.FC<QuizPreloaderProps> = props => {
	const styles = useStyles(quizPreloaderStyles, props);

	return (
		<div className={css(styles.wrapper)}>
			<Preloader color="blue" style={styles.preloader} />
		</div>
	);
};
