import React from "react";
import { Block } from "quiz/components/block/Block";
import { T } from "../text/T";
import { useStyles } from "hooks/useStyles";
import { getQuizEmptyListStyles } from "./quizEmpyListStyles";
import { css } from "wraps/styleCreators";
import { TextSizeType } from "theme/sizing";
import { t } from "quiz/QuizApp";
import { ThemeContext } from "themeContext/ThemeContext";

export interface QuizEmptyListProps {
	icon?: JSX.Element;
	title?: string;
	separateBlock?: boolean;
}

const emptyQuiz = (color: string): JSX.Element => {
	return (
		<svg width="22" height="24" viewBox="0 0 22 24" fill="none" xmlns="http://www.w3.org/2000/svg">
			<path
				d="M19 12V3C19 1.89543 18.1046 1 17 1H3C1.89543 1 1 1.89543 1 3V21C1 22.1046 1.89543 23 3 23H10"
				stroke={color}
				strokeWidth="2"
				strokeLinecap="round"
			/>
			<path d="M5 6H6" stroke={color} strokeWidth="2" strokeLinecap="round" />
			<path d="M9 6H15" stroke={color} strokeWidth="2" strokeLinecap="round" />
			<path d="M5 11H6" stroke={color} strokeWidth="2" strokeLinecap="round" />
			<path d="M9 11H15" stroke={color} strokeWidth="2" strokeLinecap="round" />
			<path d="M5 16H6" stroke={color} strokeWidth="2" strokeLinecap="round" />
			<path d="M9 16H11" stroke={color} strokeWidth="2" strokeLinecap="round" />
			<path d="M15 17L21 23" stroke={color} strokeWidth="2" strokeLinecap="round" />
			<path d="M21 17L15 23" stroke={color} strokeWidth="2" strokeLinecap="round" />
		</svg>
	);
};

export const QuizEmptyList: React.FC<QuizEmptyListProps> = props => {
	const styles = useStyles(getQuizEmptyListStyles, props.separateBlock);
	const theme = React.useContext(ThemeContext);

	return (
		<Block styles={styles.wrap} secondary>
			<div className={css(styles.content)}>
				<div className={css(styles.icon)}>{props.icon || emptyQuiz(theme.colors.base.border)}</div>
				<T styles={styles.title} size={TextSizeType.body}>
					{props.title || t("Empty list")}
				</T>
			</div>
		</Block>
	);
};
