import React, { useEffect } from "react";
import { Navbar, NavLeft, NavTitle, NavRight } from "quiz/components/navbar/Navbar";
import { css } from "wraps/styleCreators";
import { Link } from "quiz/components/link/Link";
import { ThemeContext } from "themeContext/ThemeContext";
import { useStyles } from "hooks/useStyles";
import { getSearchBarStyles } from "./getSearchBarStyles";
import { Icon } from "quiz/components/icon/Icon";
import { useKeyboardEnter } from "hooks/useKeyboardEnter";
import { t } from "quiz/QuizApp";
import { BackLink } from "quiz/components/quizNavBar/icons/backlink/BackLink";

type SearchBarProps = {
	onInputChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
	onInputClear: () => void;
	value: string;
};

export const SearchBar: React.FC<SearchBarProps> = props => {
	const { onInputChange, onInputClear, value } = props;
	const styles = useStyles(getSearchBarStyles);
	const theme = React.useContext(ThemeContext);
	const inputRef = React.useRef<HTMLInputElement>(null);
	const enter = useKeyboardEnter();

	useEffect(() => inputRef?.current?.focus(), [value]);

	return (
		<Navbar>
			<NavLeft>
				<BackLink noMargin />
			</NavLeft>
			<NavTitle className={css(styles.title)}>
				<input
					ref={inputRef}
					className={css(styles.input)}
					type="text"
					value={value || ""}
					placeholder={t("Search")}
					onChange={onInputChange}
					onKeyDown={enter}
				/>
			</NavTitle>
			{value && (
				<NavRight>
					<Link onClick={onInputClear}>
						<div className={css(styles.iconWrap)}>
							<Icon
								explicitSize={theme.sizing.sizes.md}
								color={theme.colors.base.border}
								icon={["far", "times"]}
							/>
						</div>
					</Link>
				</NavRight>
			)}
		</Navbar>
	);
};

SearchBar.displayName = "SearchBar";
