import { CSSProp } from "wraps/styleCreators";

export const getSwitchCameraButtonStyles = () => {
	const size = "28px";
	const wrapSize = "58px";

	return {
		icon: {
			height: size,
			width: size
		} as CSSProp,
		wrap: {
			width: wrapSize,
			height: wrapSize,
			display: "flex",
			justifyContent: "center",
			alignItems: "center",
			background: "rgba(24, 59, 74, 0.24)",
			borderRadius: "50%"
		} as CSSProp
	};
};
