import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { ButtonSize, ButtonType } from "quiz/components/button/Button";
import { MobileButtonProps } from "./MobileButton";
import { scale, ScaleEnum } from "utils/cssHelpers";
import Color from "color";
import { ColorTypes } from "theme/colors";

export function getMobileButtonStyles(theme: Theme, props: MobileButtonProps) {
	const colors = theme.colors[props.colorType] || theme.colors.primary;
	const vars = {
		button: {
			width: "fit-content",
			fontSize: props.fontSize || theme.sizing.fontSizes.body,
			lineHeight: theme.sizing.fontSizes.body,
			background: colors.main,
			color: colors.texts.contrast,
			padding: `${theme.sizing.sizes.xs} ${theme.sizing.sizes.xs}`,
			height: props.fullHeight ? "100%" : "auto",
			borderRadius: props.fullHeight ? 0 : theme.sizing.sizes.xs,
			border: "none",
			":active": {
				background: colors.active,
				color: colors.texts.contrast
			}
		},
		innerWrap: {
			height: "100%"
		},
		textWrap: {
			display: "flex",
			justifyContent: "center",
			alignItems: "center",
			height: "100%",
			margin: props.childrenExists ? `0 ${theme.sizing.sizes.xs}` : "0"
		} as CSSProp,
		text: {
			color: "inherit",
			fontWeight: theme.sizing.fontWeights.medium
		},
		iconWrap: {
			width: theme.sizing.sizes.lg,
			height: "100%",
			display: "flex",
			justifyContent: "center",
			alignItems: "center"
		} as CSSProp,
		icon: {
			color: "inherit",
			fontSize: theme.sizing.sizes.md
		}
	};

	if (props.size == ButtonSize.large) {
		vars.button.padding = `${scale(theme.sizing.sizes.xs, ScaleEnum.Triple)} ${theme.sizing.sizes.md}`;
	}
	if (props.type == ButtonType.link) {
		vars.button.padding = "0";
		const textColor = props.colorType ? colors : theme.colors.base;
		vars.button.background = "transparent";
		vars.button.color = textColor.text;
		vars.button[":active"] = {
			background: "transparent",
			color: textColor.active
		};
	}
	if (props.type == ButtonType.transparent) {
		vars.button.padding = "0";
		const textColor = props.colorType == ColorTypes.danger ? colors : theme.colors.base;
		vars.button.background = "transparent";
		vars.button.color = textColor.text;
		vars.button[":active"] = {
			background: "transparent",
			color: textColor.active
		};
		vars.icon.color = colors.main;
	}
	if (props.type == ButtonType.danger) {
		vars.button.padding = `${theme.sizing.sizes.sm} ${theme.sizing.sizes.sm}`;
		const textColor = props.colorType ? colors : theme.colors.base;
		vars.button.background = "transparent";
		vars.button.color = textColor.text;
		vars.button.border = `1px solid ${theme.colors.danger.main}`;
		vars.button[":active"] = {
			background: "transparent",
			color: textColor.active
		};
	}
	if (props.fullWidth) {
		vars.button.width = "100%";
	}
	return {
		button: {
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			outline: "none",
			...vars.button
		} as CSSProp,
		innerWrap: {
			display: "flex",
			...vars.innerWrap
		} as CSSProp,
		textWrap: {
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			overflow: "hidden",
			...vars.textWrap
		} as CSSProp,
		text: {
			...vars.text,
			textTransform: "none"
		} as CSSProp,
		iconWrap: {
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			...vars.iconWrap
		} as CSSProp,
		icon: {
			...vars.icon
		}
	};
}

export const getWrappedMobileActionButtonStyle = (theme: Theme) => ({
	wrap: {
		marginTop: theme.sizing.sizes.md,
		marginBottom: theme.sizing.sizes.xxl
	}
});
