import { ActionsButton } from "framework7-react";
import React from "react";
import { T } from "quiz/components/text/T";
import { IconProp, Icon } from "quiz/components/icon/Icon";
import { useStyles } from "hooks/useStyles";
import { getQuestionActionButtonStyles } from "./questionActionButtonStyles";
import { StyleDeclarationValue, css } from "wraps/styleCreators";
import { TextWithIcon } from "../textWithIcon/TextWithIcon";

export type QuestionActionButtonProps = {
	icon: IconProp;
	text: string;
	textStyles?: StyleDeclarationValue;
	iconStyles?: StyleDeclarationValue;
	onClick?: () => void;
	iconRight?: IconProp;
	iconRightStyles?: StyleDeclarationValue;
	onIconRightClick?: () => void;
	fullWidth?: boolean;
};

export const QuestionActionButton: React.FC<QuestionActionButtonProps> = React.memo(props => {
	const styles = useStyles(getQuestionActionButtonStyles, props);

	return (
		<ActionsButton className={css(styles.wrap)} onClick={props.onClick}>
			<div className={css(styles.content)}>
				<TextWithIcon icon={props.icon} styles={props.iconStyles}>
					<T styles={[styles.text, props.textStyles]}>{props.text}</T>
				</TextWithIcon>
				{props.iconRight && (
					<div className={css(styles.iconRightWrap)}>
						<Icon icon={props.iconRight} styles={props.iconRightStyles || styles.iconRight}></Icon>
					</div>
				)}
			</div>
		</ActionsButton>
	);
});
QuestionActionButton.displayName = "QuestionActionButton";
