import * as React from "react";
import { QuizOfflineError } from "./QuizOfflineError";
import { QuizServerError } from "./QuizServerError";
import { QuizUnexpectedError } from "./QuizUnexpectedError";
import { QuizGeolocationError } from "./QuizGeolocationError";

export enum CustomErrorName {
	MdtApiError = "MDT API Error",
	OfflineError = "Offline Error",
	GeolocationError = "Geolocation Error"
}

export type QuizErrorProps = {
	error: any;
	separateBlock?: boolean;
	fullScreen?: boolean;
	fullHeight?: boolean;
};

const MapErrorName2QuizError = {
	[CustomErrorName.OfflineError]: QuizOfflineError,
	[CustomErrorName.MdtApiError]: QuizServerError,
	[CustomErrorName.GeolocationError]: QuizGeolocationError
};

export const InfoBlockContext = React.createContext({
	separateBlock: false,
	fullScreen: false,
	fullHeight: true
});

export const QuizError: React.FC<QuizErrorProps> = ({
	error,
	separateBlock = false,
	fullScreen = false,
	fullHeight = true
}) => {
	const ErrorComponent = MapErrorName2QuizError[error.name] || QuizUnexpectedError;

	return (
		<InfoBlockContext.Provider value={{ separateBlock, fullScreen, fullHeight }}>
			<ErrorComponent error={error} />
		</InfoBlockContext.Provider>
	);
};
