import { BlockTitle } from "quiz/components/block/Block";
import { useAnimation } from "hooks/useAnimation";
import { MDTRecordId } from "mdtApiClient/mdtApiClient";
import { quizDataService, t, nav } from "quiz/QuizApp";
import * as React from "react";
import { css } from "wraps/styleCreators";
import { QuizList } from "../quizList/QuizList";
import { useCancellablePromise } from "hooks/useCancellablePromise";
import { QstListItemView } from "../qstListItem/QstListItemView";
import { QstItemSkeleton } from "../quizSkeleton/skeletons/QstItemSkeleton";
import { QuizListItem } from "../quizListItem/QuizListItem";
import { getAnimationKey } from "utils/helpers";
import { QuizFade } from "../fade/QuizFade";

type QstListProps = {
	id: MDTRecordId;
	context: any;
};

export type TQstList = {
	id: MDTRecordId;
	name: string;
	description: string;
	flagDisable: boolean;
	//todo
	lastVisit?: string;
};

export const QstList: React.FC<QstListProps> = props => {
	const [qst, setQst] = React.useState<TQstList[]>();
	const [requestError, setRequestError] = React.useState();
	const animation = useAnimation();

	useCancellablePromise(a => quizDataService.getOutletQuestionnaires(props.id, { signal: a }), {
		onSuccess: data => setQst(data),
		onError: err => setRequestError(err)
	});

	const fadeAnimationKey = getAnimationKey(!qst, [requestError]);

	return (
		<>
			<BlockTitle title={t("Questionnaires")} counter={qst?.length} />
			<QuizFade animationKey={fadeAnimationKey}>
				<QuizList
					isLoading={!qst}
					isEmpty={qst && qst.length === 0}
					emptyList={{ title: "No questionnaires" }}
					errors={[requestError]}
					loading={() => <QstItemSkeleton totalBlocks={10} />}
				>
					{qst &&
						qst.map((q, idx) => (
							<QuizListItem
								link="#"
								key={idx}
								style={animation.animationDelay(idx)}
								onClick={() => nav.toQst(props.id, q.id, props.context)}
								{...(q.flagDisable ? { link: null, onClick: () => null } : {})}
							>
								<QstListItemView
									name={q.name}
									description={q.description}
									flagDisable={q.flagDisable}
									lastVisit={q.lastVisit}
								/>
							</QuizListItem>
						))}
				</QuizList>
			</QuizFade>
		</>
	);
};
QstList.displayName = "QstList";
