import * as React from "react";
import { Theme } from "theme/theme";
import { CSSProp, css } from "wraps/styleCreators";
import { useStyles } from "hooks/useStyles";

const getStyles = (theme: Theme, height) => {
	return {
		questionDivider: {
			backgroundColor: theme.colors.base.background,
			height: height || theme.sizing.sizes.md
		} as CSSProp
	};
};

export type EmptyDividerProps = {
	height?: React.ReactText;
};

export const EmptyDivider: React.FC<EmptyDividerProps> = props => {
	const styles = useStyles(getStyles, props.height);

	return <div className={css(styles.questionDivider)} />;
};
