import * as React from "react";
import { QuestionControlProps } from "../Question";
import { RadioList } from "quiz/components/radioList/RadioList";
import { SwiperSettings } from "utils/constants";
import { useContext } from "react";
import { QstContext } from "../common/QstContext";

export const QuestionOneOptionControl: React.FC<QuestionControlProps> = React.memo(props => {
	const id = props.question.id;
	const items = props.question.items;
	const ctx = useContext(QstContext);

	const onChange = React.useCallback(
		e => {
			const itemId = parseInt(e.target.value);
			const item = items.find(item => item.id == itemId);
			props.visitManager.setAnswer(
				id,
				[
					{
						answer: item.name,
						id: itemId
					}
				],
				true
			);
			if (props.visitManager.isQuickTransition(props.question)) {
				const timerId = setTimeout(() => {
					props.toNextQuestion();
					clearInterval(timerId);
				}, SwiperSettings.quickTransitionDelayMs);
			}
		},
		[id, items]
	);

	const radioItems = React.useMemo(
		() => items.map(item => ({ value: item.id, title: item.name, group: item.group })),
		[items]
	);
	const item = (props.valueItems && props.valueItems[0]) || {};

	return <RadioList items={radioItems} onChange={e => onChange(e)} value={item.id} danger={!ctx.answerIsValid} />;
});
QuestionOneOptionControl.displayName = "QuestionOneOptionControl";
