import { useStyles } from "hooks/useStyles";
import * as React from "react";
import { css, CSSProp, StyleDeclarationValue } from "wraps/styleCreators";

const getInlineGroupStyles = () => {
	return {
		inlineGroupWrap: {
			display: "flex",
			alignItems: "center"
		} as CSSProp
	};
};

interface InlineGroupProps {
	styles?: StyleDeclarationValue;
}

export const InlineGroup: React.FunctionComponent<InlineGroupProps> = props => {
	const styles = useStyles(getInlineGroupStyles);
	return <div className={css(styles.inlineGroupWrap, props.styles)}>{props.children}</div>;
};
