import * as React from "react";
import { Block } from "quiz/components/block/Block";
import { useStyles } from "hooks/useStyles";
import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

const getExtendableNavbarBlockStyles = (theme: Theme) => {
	return {
		block: {
			height: scale(theme.sizing.sizes.lg, ScaleEnum.Double),
			display: "flex",
			alignItems: "center"
		} as CSSProp
	};
};

export const ExtendableNavbarBlock: React.FC = props => {
	const styles = useStyles(getExtendableNavbarBlockStyles);

	return (
		<Block secondary styles={styles.block}>
			{props.children}
		</Block>
	);
};
