import { createThemeApplier } from "wraps/component";
import { Theme } from "theme/theme";
import { MobileDatepickerProps } from "./DatePicker";
import { CSSProp } from "wraps/styleCreators";

export const applyTheme = createThemeApplier((theme: Theme) => {
	return {
		"f7-toolbar-bg-color": theme.colors.primary.background,
		"f7-calendar-week-header-bg-color": theme.colors.info.texts.contrast,
		"f7-calendar-week-header-text-color": theme.colors.base.texts.muted,
		"f7-calendar-week-header-font-size": theme.sizing.fontSizes.bodyMin,
		"f7-calendar-day-text-color": theme.colors.base.texts.accent,
		"f7-calendar-prev-next-text-color": theme.colors.secondary.border,
		"f7-toolbar-text-color": theme.colors.base.texts.accent,
		"f7-toolbar-font-size": theme.sizing.fontSizes.body,
		"f7-calendar-today-bg-color": theme.colors.primary.background
	};
});

export const getMobileDatePickerStyles = (props: MobileDatepickerProps, theme: Theme) => {
	return {
		wrap: {
			position: "relative"
		} as CSSProp
	};
};

export const datePickerStyles = () =>
	`
	.calendar-day .calendar-day-number {
		border-radius: 0 !important;
	}
	.calendar-week-day {
		text-transform: uppercase
	}
`.trim();

export const DatePickerButtonStyles = () => {
	return {
		wrap: {
			display: "flex",
			justifyContent: "space-between",
			width: "100%"
		} as CSSProp
	};
};
