import * as React from "react";
import { nav, t } from "quiz/QuizApp";
import { FeedbackTypeEnum } from "quiz/components/feedback/Feedback";
import { MDTApiError } from "mdtApiClient/mdtApiClient";
import { serverErrorIcon } from "../infoScreenIcons/serverErrorIcon";
import { ColorTypes } from "theme/colors";
import { InfoBlock } from "../infoBlock/InfoBlock";
import { parseErrorMessage } from "utils/helpers";
import { f7 } from "framework7-react";

export const errorsDictionary = {
	Timeout: "The allowed timeout for the request from the server has been exceeded"
};

export type QuizServerErrorProps = {
	error: MDTApiError;
};

export const QuizServerError: React.FC<QuizServerErrorProps> = ({ error }) => {
	const message = parseErrorMessage(error);

	return (
		<InfoBlock
			icon={serverErrorIcon}
			title={t("Data retrieval error")}
			message={t(message, error.args as any)}
			primaryButton={{
				title: t("Complain"),
				icon: ["far", "comment-alt-lines"],
				command: () => nav.toFeedback({ type: FeedbackTypeEnum.Problem, value: error.message })
			}}
			secondaryButton={{
				title: t("Refresh"),
				icon: ["far", "sync"],
				command: () => f7.views.main.router.refreshPage()
			}}
			colorType={ColorTypes.danger}
		/>
	);
};
