import { Icon, IconProp } from "quiz/components/icon/Icon";
import { Text } from "quiz/components/text/Text";
import { Block } from "quiz/components/block/Block";
import { List } from "quiz/components/listView/ListView";
import { f7 } from "framework7-react";
import { useStyles } from "hooks/useStyles";
import { T } from "quiz/components/text/T";
import { Avatar } from "quiz/components/user/Avatar";
import { nav, quizAuthService, t } from "quiz/QuizApp";
import * as React from "react";
import { TextSizeType } from "theme/sizing";
import { abbr, navToFAQ } from "utils/helpers";
import { css } from "wraps/styleCreators";
import { getMenuListItemStyles, getProfilePageStyles } from "./profilePageStyles";
import { Version } from "../Version";
import { Divider } from "quiz/components/divider/Divider";
import { ColorTypes } from "theme/colors";
import { Dialog, F7Dialog } from "quiz/components/interaction/Dialog";
import { TextWithIcon } from "quiz/components/question/common/textWithIcon/TextWithIcon";

type MenuListItemProps = {
	icon: IconProp;
	text: string;
	isDash?: boolean;
	color?: ColorTypes;
	onClick: () => void;
};

export const MenuListItem: React.FC<MenuListItemProps> = props => {
	const isDanger = props.color === "danger";
	const styles = useStyles(getMenuListItemStyles, isDanger);

	return (
		<div className={`item-link ${css(styles.list)}`} onClick={props.onClick}>
			<li className={css([styles.wrap])}>
				<div className={css([styles.content])}>
					<TextWithIcon icon={props.icon} styles={styles.icon}>
						<T colorType={props.color || ColorTypes.base}>{props.text}</T>
					</TextWithIcon>
					<Icon icon={["far", "angle-right"]} styles={styles.icon} />
				</div>
				{props.isDash && <Divider dash />}
			</li>
		</div>
	);
};
MenuListItem.displayName = "f7-list-item";

export const FAQListItem = (props: { isDash?: boolean }) => {
	return <MenuListItem isDash={props.isDash} text={t("FAQ")} icon={["far", "life-ring"]} onClick={navToFAQ} />;
};
FAQListItem.displayName = "f7-list-item";

const handleLogOutClick = () => {
	const options: F7Dialog.Parameters = {
		title: t("Exit"),
		text: t("Are you sure you want to exit the application?"),
		buttons: [
			{
				text: t("Logout"),
				onClick: async () => {
					return quizAuthService.signOut().then(() => f7.views.main.router.refreshPage());
				}
			},
			{
				close: true,
				text: t("Cancel")
			}
		]
	};
	Dialog.create(options).open();
};

export const ProfilePage: React.FC = () => {
	const styles = useStyles(getProfilePageStyles);
	const { fullName, email } = quizAuthService.state.user;

	return (
		<>
			<Block secondary styles={styles.userInfoBlock}>
				<div className={css(styles.wrap)}>
					<div className={css(styles.avatar)}>
						<Avatar text={abbr(fullName)} type={"big"} />
					</div>
					<div className={css(styles.fullName)}>
						<Text size={TextSizeType.title}>{fullName}</Text>
					</div>
					<div>
						<Text size={TextSizeType.bodyMin} muted>
							{email}
						</Text>
					</div>
				</div>
			</Block>

			<div className={css(styles.block)}>
				<List>
					<MenuListItem
						isDash
						text={t("Change password")}
						icon={["far", "unlock"]}
						onClick={() => nav.toChangePassword()}
					/>
					<MenuListItem
						text={t("Interface language")}
						icon={["far", "language"]}
						onClick={() => nav.toChangeLanguage()}
					/>
				</List>
			</div>

			<div className={css(styles.block)}>
				<List>
					<FAQListItem isDash />
					<MenuListItem
						isDash
						text={t("Feedback")}
						icon={["far", "comment-alt-lines"]}
						onClick={() => nav.toFeedback()}
					/>
					<MenuListItem
						isDash
						text={t("About App")}
						icon={["far", "info-circle"]}
						onClick={() => nav.toAbout()}
					/>
					<MenuListItem
						text={t("Privacy policy")}
						icon={["far", "user-shield"]}
						onClick={() => nav.toPrivacyPolicy()}
					/>
				</List>
			</div>
			<div className={css(styles.block)}>
				<List>
					<MenuListItem
						text={t("Logout")}
						icon={["far", "sign-out-alt"]}
						onClick={handleLogOutClick}
						color={ColorTypes.danger}
					/>
				</List>
			</div>

			<Block>
				<Version />
			</Block>
		</>
	);
};
ProfilePage.displayName = "ProfilePage";
