import { Toast as F7Toast } from "framework7/components/toast/toast";
import { f7 } from "framework7-react";
import { applyCssVars } from "utils/cssHelpers";
import { theme } from "themes/defaultTheme";
import { ColorTypes } from "theme/colors";
import { NotificationSettings } from "utils/constants";
import { styleSheet, css } from "wraps/styleCreators";
import { getToastStyles } from "./toastStyles";

type ToastType = "info" | "success" | "warn" | "error";

export interface QuizToastParams
	extends Omit<F7Toast.Parameters, "closeButton" | "closeButtonColor" | "closeButtonText" | "render" | "cssClass"> {
	type: ToastType;
}

const typeToColorMap: { [key in ToastType]: ColorTypes } = {
	info: ColorTypes.primary,
	success: ColorTypes.success,
	warn: ColorTypes.warning,
	error: ColorTypes.danger
};

export class Toast {
	static create(props: QuizToastParams) {
		const { position = "top", ...toastProps } = props;
		const styles = styleSheet(getToastStyles(theme, position));
		applyCssVars({
			"f7-toast-bg-color": theme.colors[typeToColorMap[props.type]].main
		});

		return f7.toast.create({
			...toastProps,
			destroyOnClose: true,
			position: "center",
			closeTimeout: props.closeTimeout || NotificationSettings.closeTimeoutMs,
			cssClass: css(styles.toast)
		});
	} //todo:     ?    
}
