import * as React from "react";
import { Text, TextProps } from "quiz/components/text/Text";
import { t } from "quiz/QuizApp";

export interface TProps extends TextProps {
	children: string;
	args?: Record<string, string>;
}

export const T: React.FC<TProps> = React.memo(props => {
	return <Text {...props}>{t(props.children, props.args) || props.children}</Text>;
});
T.displayName = "Text";
