import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";
import { appMode2ColorMap } from "../instanceBar/instanceBarStyles";
import { CONFIG } from "quiz/QuizApp";

const opacityKeyframes = {
	from: {
		opacity: 0
	},

	to: {
		opacity: 1
	}
};

export const getTabNavigationStyles = (theme: Theme, count: number) => {
	const environmentColorKey = appMode2ColorMap[CONFIG.appMode];
	const activeColor = theme.colors[environmentColorKey].main;
	const baseColor = theme.colors.secondary.border;
	const baseWidth = 100 / count;
	return {
		wrap: {} as CSSProp,
		content: {
			marginBottom: `calc(${scale(theme.sizing.sizes.lg, ScaleEnum.Double)} + var(--instanceBarIndent, 0px))`,
			animationName: [opacityKeyframes],
			animationDuration: "250ms"
		} as CSSProp,
		tabs: {
			position: "fixed",
			bottom: 0,
			zIndex: theme.zIndex.appBar,
			background: theme.colors.base.main,
			display: "flex",
			width: "100%",
			alignItems: "center",
			justifyContent: "space-evenly",
			boxShadow: theme.shadows.up.md,
			paddingBottom: "calc(env(safe-area-inset-bottom) + var(--instanceBarIndent))"
		} as CSSProp,
		tab: {
			width: baseWidth + "%",
			flexGrow: 1,
			display: "flex",
			justifyContent: "center",
			alignItems: "center",
			transition: theme.transitions.base
		} as CSSProp,
		tabActive: {} as CSSProp,
		tabIconFill: {
			fill: baseColor
		},
		tabIconFillActive: {
			fill: activeColor
		},
		tabIconStroke: {
			stroke: baseColor
		},
		tabIconStrokeActive: {
			stroke: activeColor
		},
		line: {
			width: baseWidth + "%",
			position: "absolute",
			height: theme.sizing.sizes.xxs,
			background: theme.colors[environmentColorKey].main,
			top: 0,
			left: 0,
			transition: theme.transitions.base
		} as CSSProp
	};
};
