import * as React from "react";
import { GeolocationError } from "quiz/services/GeolocationError";
import { GeolocationErrorCodes } from "quiz/components/errors/QuizGeolocationError";

export type Coords = {
	latitude: string;
	longitude: string;
};

export function usePosition(watch = false) {
	const [position, setPosition] = React.useState({
		latitude: "",
		longitude: ""
	});
	const [error, setError] = React.useState(null);

	React.useEffect(() => {
		const geo = navigator.geolocation;
		if (!geo) {
			setError(
				new GeolocationError({
					code: 4,
					message: `"Navigator.geolocation" is not available to determine the location of the device`,
					PERMISSION_DENIED: GeolocationErrorCodes.PERMISSION_DENIED,
					POSITION_UNAVAILABLE: GeolocationErrorCodes.POSITION_UNAVAILABLE,
					TIMEOUT: GeolocationErrorCodes.TIMEOUT
				})
			);
			return void 0;
		}

		let active = true;
		const onChange = ({ coords: { latitude, longitude } }) => active && setPosition({ latitude, longitude });
		const onError = (error: PositionError) => active && setError(new GeolocationError(error));

		let watcher;
		if (watch) {
			watcher = geo.watchPosition(onChange, onError);
		} else {
			geo.getCurrentPosition(onChange, onError, { timeout: 5000 });
		}
		return () => {
			active = false;
			watcher && geo.clearWatch(watcher);
		};
	}, []);
	return { ...position, error };
}
