import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getActionSheetItemStyles = (theme: Theme) => {
	return {
		actionButton: {
			height: "100%"
		} as CSSProp,
		wrap: {
			display: "flex",
			height: "100%",
			alignItems: "center"
		} as CSSProp,
		iconWrap: {
			display: "flex",
			height: theme.sizing.sizes.lg,
			width: theme.sizing.sizes.lg,
			alignItems: "center",
			marginRight: theme.sizing.sizes.md,
			padding: `${theme.sizing.sizes.md} 0`
		} as CSSProp
	};
};
