import * as keycode from "keycode";
import * as React from "react";
import { ColorTypes } from "theme/colors";
import { v, vd } from "utils/helpers";
import { IconProp, IconProps } from "../icon/Icon";
import { UISettings } from "utils/constants";

export interface ActionItem {
	title?: string;
	icon?: IconProp;
	colorType?: ColorTypes;

	command?: (action: ActionItem) => void | Promise<void>;
	enabled?: (action: ActionItem) => boolean;
	visible?: (action: ActionItem) => boolean;
}

const enum ActionWaitState {
	waiting,
	ok,
	error
}

const StateIcons: { [key: string]: IconProp } = {
	[ActionWaitState.waiting]: "spinner",
	[ActionWaitState.ok]: "check",
	[ActionWaitState.error]: "times"
};

const StateColorTypes: { [key: string]: ColorTypes } = {
	[ActionWaitState.ok]: ColorTypes.success,
	[ActionWaitState.error]: ColorTypes.danger
};

export function useActionItem(actionItem: ActionItem) {
	let [waitingState, setWaitingState] = React.useState<ActionWaitState>();
	let waitingStateTimeout = React.useRef<number>();
	let mounted = React.useRef<boolean>();

	React.useEffect(() => {
		mounted.current = true;
		return () => {
			mounted.current = false;
			clearTimeout(waitingStateTimeout.current);
		};
	}, []);

	const icon = () => {
		if (waitingState != null) return StateIcons[waitingState];
		return actionItem.icon;
	};

	const iconProps = () => (waitingState == ActionWaitState.waiting ? ({ pulse: true } as IconProps) : null);

	const colorType = () => StateColorTypes[waitingState] || actionItem.colorType;

	const enabled = () => vd(actionItem.enabled, true, actionItem);

	const visible = () => vd(actionItem.visible, true, actionItem);

	const setResultState = (state: ActionWaitState) => {
		if (!mounted.current) return;
		setWaitingState(state);
		clearTimeout(waitingStateTimeout.current);
		if (state == ActionWaitState.ok || state == ActionWaitState.error) {
			waitingStateTimeout.current = window.setTimeout(
				() => mounted.current && setWaitingState(null),
				UISettings.userTimeouts.button
			);
		}
	};

	const command = () => {
		if (!enabled() || waitingState != null) return;
		let res = v(actionItem.command, actionItem);
		if (res instanceof Promise) {
			setResultState(ActionWaitState.waiting);
			res.then(
				() => setResultState(ActionWaitState.ok),
				() => setResultState(ActionWaitState.error)
			);
		}
	};

	return {
		icon,
		iconProps,
		title: actionItem.title,
		enabled,
		visible,
		colorType,
		keyboardHandler: e => {
			let k = keycode(e);
			if (k === "enter" || k === "space") {
				if (actionItem.command) {
					command();
					e.preventDefault();
					return;
				}
			}
		},
		clickHandler: () => command()
	};
}
