import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";
import { scale, ScaleEnum } from "utils/cssHelpers";

export const getProfilePageStyles = (theme: Theme) => ({
	userInfoBlock: {
		marginTop: theme.sizing.sizes.lg,
		marginBottom: theme.sizing.sizes.xl
	} as CSSProp,
	wrap: {
		display: "flex",
		flexFlow: "column",
		alignItems: "center",
		marginTop: theme.sizing.sizes.lg,
		marginBottom: theme.sizing.sizes.lg
	} as CSSProp,
	avatar: {
		marginBottom: scale(theme.sizing.sizes.lg, ScaleEnum.Half)
	},
	fullName: {
		marginBottom: theme.sizing.sizes.sm
	},
	block: {
		marginBottom: theme.sizing.sizes.lg
	}
});

export const getMenuListItemStyles = (theme: Theme, isDanger: boolean) => {
	return {
		list: {
			backgroundColor: theme.colors.primary.background
		},
		wrap: {
			paddingLeft: theme.constants.gridPadding,
			paddingRight: theme.constants.gridPadding
		} as CSSProp,
		icon: {
			color: isDanger ? theme.colors.danger.main : theme.colors.primary.main
		} as CSSProp,
		content: {
			display: "flex",
			justifyContent: "space-between",
			alignItems: "center"
		} as CSSProp
	};
};
