import { Theme } from "theme/theme";
import { CSSProp } from "wraps/styleCreators";

export const getGroupedAnswerHeaderStyles = (theme: Theme) => {
	return {
		groupWrap: {
			display: "flex",
			justifyContent: "space-between",
			width: "100%"
		} as CSSProp,
		groupTitle: {
			textTransform: "uppercase"
		} as CSSProp,
		countInGroup: {
			color: theme.colors.base.hover
		} as CSSProp
	};
};
