import { LogItem, LogType } from "./Logger";
import { toLocalISOString, guid } from "utils/helpers";
import { apiClient, quizAuthService, logger, quizLocalStorageService } from "quiz/QuizApp";
import { jsonDateParser } from "json-date-parser";

type Key = string;

export interface Metrics extends LogItem {}

type DBMetrics = {
	key: string;
	dbValue: string;
}; //toDO

const initSentMetricsDelay = 60000,
	sentMetricsDelay = 60000,
	countMetricsForDelete = 5,
	keyStamp = "-metrics";

export class QuizMetricsService {
	metricsSessionId = guid();

	constructor() {
		const watch = () =>
			setInterval(() => {
				this.send();
			}, sentMetricsDelay);
		setTimeout(() => {
			this.send();
			watch();
		}, initSentMetricsDelay);
	}

	async set(metrics: Metrics): Promise<void> {
		const key: string = metrics.time.toString() + keyStamp;
		return quizLocalStorageService.set(
			key,
			JSON.stringify({
				code: metrics.code,
				data: metrics.data,
				type: metrics.type,
				time: toLocalISOString(new Date(metrics.time))
			})
		);
	}

	private getAll() {
		const metricsWithKeys: DBMetrics[] = [];
		for (let i = 0; i < quizLocalStorageService.length; i++) {
			const key = quizLocalStorageService.getKey(i);
			if (key.includes(keyStamp)) metricsWithKeys.push({ key: key, dbValue: localStorage.getItem(key) });
		}
		return metricsWithKeys;
	}

	private async removeMetrics(keys: Key[]) {
		quizLocalStorageService.remove(keys);
	}

	private prepareMetrics(metrics: DBMetrics[]) {
		return metrics
			.map(({ dbValue }) => {
				const value = JSON.parse(dbValue, jsonDateParser) as Metrics;
				const result = {
					code: value.code,
					data: JSON.stringify(value.data),
					type: value.type,
					dateSubmit: value.time,
					sessionID: this.metricsSessionId,
					$table: "qst.Metric",
					$new: true
				};
				return result;
			})
			.sort((a, b) => a.dateSubmit - b.dateSubmit);
	}

	async send() {
		const metrics = this.getAll();

		if (metrics.length > countMetricsForDelete) {
			const m: any = [...this.prepareMetrics(metrics)];

			if (quizAuthService?.isLoggedIn()) {
				apiClient
					.save(m)
					.then(() => this.removeMetrics(metrics.map(m => m.key)))
					.catch(e => {
						this.removeMetrics(metrics.map(m => m.key));
						logger.log(
							"metrics",
							{
								error: e
							},
							LogType.error
						);
					});
			} else {
				this.removeMetrics(metrics.map(m => m.key));
			}
		}
	}
}
