import { IconProp } from "@fortawesome/fontawesome-svg-core";
import { ValidationError } from "quiz/pages/questionnaire/VisitManager";
import { useStyles } from "hooks/useStyles";
import React from "react";
import { css } from "aphrodite";
import { Icon } from "quiz/components/icon/Icon";
import { t } from "quiz/QuizApp";
import { Text } from "quiz/components/text/Text";
import { getQuestionReadonlyItemStyles } from "./questionReadonlyItemStyles";

export type QuestionReadonlyItemProps = {
	icon: IconProp;
	text: string;
	errors: ValidationError[];
	rightZone?(): React.ReactElement;
};

export const QuestionReadonlyItem: React.FC<QuestionReadonlyItemProps> = React.memo(props => {
	const styles = useStyles(getQuestionReadonlyItemStyles, props);
	const text = props.errors.length ? props.errors.map(err => err.title).join(", ") : props.text;

	return (
		<div className={css(styles.questionControlItem)}>
			<div className={css(styles.textWrap)}>
				<div className={css(styles.iconWrap)}>
					<Icon styles={styles.iconColor} icon={props.icon} />
				</div>
				<div className={css(styles.text)}>
					<Text styles={styles.textColor}>{text || t("No answer")}</Text>
				</div>
			</div>
			{props.rightZone != null && <div>{props.rightZone()}</div>}
		</div>
	);
});

QuestionReadonlyItem.displayName = "f7-list-item";
